<template>
  <transition name="slide">
    <div v-if="isOpenedTeam" class="show eqp drawer">
      <div class="header_drawer">
        <div class="title">
          <h4>
            {{
              model.id == null
                ? $t('components.team.add')
                : $t('components.team.edit')
            }}
          </h4>
        </div>
        <div class="content-button btn-add">
          <button class="close mr-5" @click="close">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <Button
            :loading="FetchingButton"
            @when-cliked="handleOnSubmit()"
            :text="$globals.capitalizeFirstLetter($t('common.save'))"
          />
        </div>
      </div>
      <div class="body_drawer">
        <div class="form-group-flex border-b">
          <div class="form-field">
            <input-field
              class="mb-7"
              :field_name="
                $globals.capitalizeFirstLetter($t('components.team.name'))
              "
              :label="
                $globals.capitalizeFirstLetter($t('components.team.name'))
              "
              id="1"
              @input="
                (e) => {
                  this.setModel({
                    ...this.getModel,
                    ['name']: e.target.value,
                  });
                }
              "
              @blur="verifyName()"
              :hasError="errors != null && errors.name != null"
              :errorMessage="
                errors && errors.name != null ? $t('errors.' + errors.name) : ''
              "
              :defaultValue="model.name"
              name="name"
            />
          </div>
          <div class="form-field ml-4">
            <SelectSingle
              :class="dynamicClass"
              :options="
                !this.$globals.isRoleAllowed('roleAllowed', this.role)
                  ? collection
                  : this.accessCallCenterOptions
                    ? this.accessCallCenterOptions
                    : null
              "
              :keyLabel="'name'"
              :keyValue="'id'"
              :hasError="errors != null && errors.callCenter != null"
              :errorMessage="
                errors && errors.callCenter != null
                  ? $t('errors.' + errors.callCenter[0])
                  : ''
              "
              :defaultSelected="
                !$globals.isRoleAllowed('roleAllowed', this.role)
                  ? model.callCenterSelected != null
                    ? model.callCenterSelected.id
                    : null
                  : accessCallCenterIds != null
                    ? accessCallCenterIds[0]
                    : null
              "
              :label="
                $globals.capitalizeFirstLetter($t('components.team.callcenter'))
              "
              :onSelect="
                (item) => {
                  handleItemSelected(item);
                }
              "
              :isDisabled="
                model.agentsSelected.length > 0 ||
                model.supervisersSelected.length > 0 ||
                this.$globals.isRoleAllowed('roleAllowed', this.role) == true
              "
            />
          </div>
        </div>
        <!-- <div class="form-group border-b my-7">
          <Autocomplete
            type="text"
            keyLabel="name"
            keyValue="campagneId"
            :hasError="errors != null && errors.campagne != null"
            :errorMessage="
              errors && errors.campagne != null
                ? $t('errors.' + errors.campagne[0])
                : ''
            "
            keyArray="campagnesSelected"
            :label="
              $globals.capitalizeFirstLetter($t('components.team.campagnes'))
            "
            class="search-field"
            :placeholder="
              $globals.capitalizeFirstLetter(
                $t('components.team.searchcampagne'),
              )
            "
            :items="model && model.campagnes ? model.campagnes : []"
            :itemsSelected="model.campagnesSelected"
            :WithIconUser="false"
            :isAgent="false"
            @item-selected="handleItemSelectedItem"
            @remove-item="removeItem"
          />
        </div> -->
        <div
          class="form-group border-b mt-7"
          v-if="model.callCenterSelected && model.callCenterSelected.id != null"
        >
          <Autocomplete
            type="text"
            keyLabel="completeName"
            keyLabelhermesId="hermesId"
            keyValue="id"
            keyArray="supervisersSelected"
            :label="
              $globals.capitalizeFirstLetter($t('components.team.superviser'))
            "
            class="search-field"
            :placeholder="
              $globals.capitalizeFirstLetter(
                $t('components.team.searchsuperviser'),
              )
            "
            :items="model && model.supervisers ? model.supervisers : []"
            :itemsSelected="model.supervisersSelected"
            :hasError="errors != null && errors.superviser != null"
            :errorMessage="
              errors && errors.superviser != null
                ? $t('errors.' + errors.superviser[0])
                : ''
            "
            :WithIconUser="true"
            :isAgent="false"
            @item-selected="handleItemSelectedItem"
            @remove-item="removeItem"
            @blur="checkItemSelected()"
          />
        </div>
        <div class="relative-table">
          <div
            class="form-group mt-7"
            v-if="
              model.callCenterSelected && model.callCenterSelected.id != null
            "
          >
            <Autocomplete
              type="text"
              keyLabel="completeName"
              keyLabelhermesId="hermesId"
              keyValue="id"
              keyArray="agentsSelected"
              :label="
                $globals.capitalizeFirstLetter($t('components.team.agents'))
              "
              class="search-field"
              :placeholder="
                $globals.capitalizeFirstLetter(
                  $t('components.team.searchagent'),
                )
              "
              :items="model && model.agents ? model.agents : []"
              :itemsSelected="
                model && model.agentsSelected ? model.agentsSelected : []
              "
              :WithIconUser="false"
              :isAgent="true"
              @item-selected="handleItemSelectedItem"
              @remove-item="removeItem"
            />
          </div>

          <div
            class="table teams"
            v-if="
              model.callCenterSelected && model.callCenterSelected.id != null
            "
          >
            <div class="table-row header">
              <div class="table-cell">
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.team.columns.completename'),
                  )
                }}
              </div>
              <div class="table-cell">
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.team.columns.identifier'),
                  )
                }}
              </div>
              <div class="table-cell">
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.team.columns.state'),
                  )
                }}
              </div>
              <div class="table-cell"></div>
            </div>
            <div
              class="table-row"
              v-for="(item, index) of model.agentsSelected"
              :key="index"
            >
              <div class="table-cell">
                <i class="icon-user pr-1"></i>
                {{ item.firstName }} {{ item.lastName }}
              </div>
              <div class="table-cell">
                {{ $globals.abbreviateName(item.hermesId) }}
              </div>
              <div class="table-cell">
                <SelectSingle
                  :keyLabel="'label'"
                  :keyValue="'value'"
                  :options="dynamicOptions(item) === null ? options1 : options2"
                  :defaultSelected="dynamicOptions(item) === null ? 1 : 0"
                  :onSelect="
                    (itemFromSelect) => {
                      handelStateAgents(itemFromSelect, item);
                    }
                  "
                />
              </div>
              <div class="table-cell">
                <i
                  class="icon-remove"
                  @click="
                    $store.commit('team/SET_SHOW_AGENT_MODAL', {
                      show: true,
                      itemId: item.id,
                    })
                  "
                ></i>
              </div>
            </div>
          </div>
          <!-- <Pagination storeModuleName="exemple" /> -->

          <modal
            :showModal="agentToRemove.showAgentModal"
            :cancelText="$t('views.agents.modalBtnleft')"
            :okText="$t('views.agents.modalBtnrightDelete')"
            :item="agentToRemove.agent"
            @onConfirm="() => deleteTeamSelected()"
            @close="
              (item) => {
                closeModal(item);
              }
            "
          >
            <template v-slot:title>
              {{ $t('views.agents.modalTitle') }}
            </template>
            <template v-slot:content>
              <p class="pb-5">
                {{
                  model.id === null
                    ? $globals
                        .capitalizeFirstLetter(
                          $t('views.agents.modalQuestionTeamNew'),
                        )
                        .replace('agentname', agentToRemove.agent.completeName)
                    : $globals
                        .capitalizeFirstLetter(
                          $t('views.agents.modalQuestionTeam'),
                        )
                        .replace('agentname', agentToRemove.agent.completeName)
                        .replace(
                          'teamname',
                          $globals.capitalizeFirstLetter($t(model.name)),
                        )
                }}

                <!-- {{ $t('views.agents.modalArchiveOneItemText') }}
              <strong>{{ agentToRemove.agent.completeName }}</strong> -->
              </p>
            </template>
          </modal>
        </div>
      </div>
      <div id="overlay-" @click="close"></div>
    </div>
  </transition>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import SelectSingle from '@/components/FormWidgets/SelectSingle/SelectSingle.vue';
// import Pagination from '@/components/Table/Pagination.vue';
import Autocomplete from '@/components/FormWidgets/Autocomplete.vue';
import Modal from '@/components/Modal/Modal.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  components: {
    InputField,
    Button,
    SelectSingle,
    // Pagination,
    Autocomplete,
    Modal,
  },
  data() {
    return {
      showDrawer: false,
      selectedItem: null,

      modalVisible: false,
      btnleft: 'Annuler',
      btnright: 'Supprimer',
      options1: [
        {
          label: 'Inactif',
          value: 0,
          leftContent: `<div class="unActiffAgent" ></div>`,
          hidden: false,
        },
        {
          label: 'Actif',
          value: 1,
          leftContent: `<div class="actifAgent" ></div>`,
          hidden: true,
        },
      ],
      options2: [
        {
          label: 'Actif',
          value: 1,
          leftContent: `<div class="actifAgent" ></div>`,
          hidden: false,
        },
        {
          label: 'Inactif',
          value: 0,
          leftContent: `<div class="unActiffAgent" ></div>`,
          hidden: true,
        },
      ],
    };
  },
  props: ['show', 'isOpenedTeam'],
  computed: {
    ...mapGetters('callCenter', [
      'getIdWithName',
      'getCollection',
      'getCollectionTeam',
    ]),
    ...mapGetters('team', [
      'getModel',
      'getState',
      'getErrors',
      'getAgentToRemove',
    ]),
    collection() {
      return this.$store.getters['callCenter/getIdWithName'].map((x) => ({
        id: x.id,
        name: x.name,
        text: x.name,
      }));
    },
    model() {
      return this.getModel;
    },
    FetchingButton() {
      return this.getState.loadingButton;
    },
    agentToRemove() {
      return this.getAgentToRemove;
    },
    collectionTeam() {
      return this.getCollectionTeam;
    },
    errors() {
      return this.getErrors;
    },
    dynamicClass() {
      return {
        'disable-select': this.shouldDisableSelect,
      };
    },
    shouldDisableSelect() {
      return (
        this.model.agentsSelected.length > 0 ||
        this.model.supervisersSelected.length > 0 ||
        this.$globals.isRoleAllowed('roleAllowed', this.role)
      );
    },
  },
  mounted() {
    this.role = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';

    const userString = localStorage.getItem('user');
    const userObject = JSON.parse(userString);
    const callCenterId = userObject.callCenterId;

    this.accessCallCenterOptions = [callCenterId];
    this.accessCallCenterIds = callCenterId != null ? [callCenterId.id] : null;
    if (!this.$globals.isRoleAllowed('roleAllowed', this.role)) {
      if (this.accessCallCenterOptions != null) {
        this.setModel({
          ...this.getModel,
          ['callCenterSelected']:
            callCenterId != null ? this.accessCallCenterOptions[0] : null,
        });
      }
    }
  },
  methods: {
    closeModal() {
      this.$store.commit('team/SET_SHOW_AGENT_MODAL', { show: false });
    },
    deleteTeamSelected() {
      this.$store.commit('team/REMOVE_SELECTED', {
        sourceArray: 'agents',
        targetArray: 'agentsSelected',
        itemId: this.agentToRemove.itemId,
      });
      this.closeModal();
    },
    async handleOnSubmit() {
      this.$store.commit('team/SET_IS_LOADING_BUTTON', true);
      let isValid = true;
      let errors = {};
      // this.setCampagnesIds();

      if (this.model.name === '') {
        errors.name = ['NAME_REQUIRED'];
        isValid = false;
      }

      if (this.$globals.isRoleAllowed('roleAllowed', this.role)) {
        if (this.accessCallCenterOptions != null) {
          this.setModel({
            ...this.getModel,
            ['callCenterSelected']: this.accessCallCenterOptions[0],
          });
        }
      }

      if (
        this.model.callCenterSelected === null ||
        Object.keys(this.model.callCenterSelected).length == 0
      ) {
        errors.callCenter = ['CALLCENTER_REQUIRED'];
        isValid = false;
      } else {
        if (
          this.model.supervisersSelected === null ||
          this.model.supervisersSelected.length == 0
        ) {
          isValid = false;
          errors.superviser = ['SUPERVISER_REQUIRED'];
        } else {
          errors.superviser = null;
        }
      }

      // if (this.model.campagnesIds === '') {
      //   errors.campagne = ['CAMPAGNEID_REQUIRED'];
      //   isValid = false;
      // }

      this.setErrors(errors);

      if (isValid) {
        if (this.model.id) {
          await this.updateTeam()
            .then(() => {
              this.close();
            })
            .catch(() => {});
        } else {
          await this.createTeam()
            .then(() => {
              this.closeAction();
            })
            .catch(() => {});
        }
      }
      this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
    },
    ...mapActions('callCenter', ['getCollectionCallCentersCampagnes']),
    ...mapActions('team', [
      'setModel',
      'setCampagnesIds',
      'createTeam',
      'updateTeam',
      'setErrors',
      'setErrorsByName',
      'resetErrors',
    ]),
    close() {
      this.resetErrors();
      this.showDrawer = false;

      this.$emit('close');
    },
    closeAction() {
      this.resetErrors();
      this.showDrawer = false;

      this.$emit('closeAction');
    },
    checkItemSelected() {},
    async handleItemSelected(selectedItem) {
      this.selectedItem = selectedItem;
      if (this.selectedItem != null) {
        this.$store.commit('team/SET_MODEL_CALLCENTER', this.selectedItem);
        this.setErrorsByName({ callCenter: null });
        await this.getCollectionCallCentersCampagnes({ id: selectedItem.id })
          .then(() => {
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'supervisers',
              items: this.collectionTeam.supervisers,
            });
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'agents',
              items: this.collectionTeam.agents,
            });
          })
          .catch(() => {});
      }
    },
    handleItemSelectedItem(item, array_name) {
      this.$store.commit('team/SET_SELECTED_ARRAY', {
        sourceArray: array_name.slice(0, -8),
        targetArray: array_name,
        item,
      });
      if (array_name === 'supervisersSelected') {
        if (
          this.model.supervisersSelected === null ||
          this.model.supervisersSelected.length == 0
        ) {
          // isValid = false;
          this.setErrorsByName({ superviser: ['SUPERVISER_REQUIRED'] });
        } else {
          this.setErrorsByName({ superviser: null });
        }
      }

      // errors.superviser
    },
    removeItem(id, array_name) {
      this.$store.commit('team/REMOVE_SELECTED', {
        sourceArray: array_name.slice(0, -8),
        targetArray: array_name, // Assuming array_name is a variable
        itemId: id,
      });
    },
    verifyName() {
      if (this.getModel.name == '') {
        this.setErrorsByName({ name: ['NAME_REQUIRED'] });
      } else {
        this.setErrorsByName({ name: null });
      }
    },
    handelStateAgents(itemFromSelect, item) {
      const dateDeleted = itemFromSelect.id === 0 ? new Date() : null;

      this.$store.commit('team/SET_MODEL_ETAT', {
        item: item,
        dateDeleted: dateDeleted,
      });
    },
    dynamicOptions(item) {
      const index = this.model.toEditStatus.findIndex(
        (agent) => agent.id === item.id,
      );
      if (index !== -1) {
        return this.model.toEditStatus[index].deleted_at;
      } else {
        return item.deleted_at;
      }
    },
  },
};
</script>
