<template>
  <transition name="slide">
    <div v-if="isOpenedGestion" :class="{ drawer: true }" class="show eqp">
      <div class="header_drawer">
        <div class="title">
          <h4>{{ $t('views.agents.manageAgents') }}</h4>
        </div>
        <div class="content-button btn-add">
          <button class="close mr-5" @click="cancel">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <Button
            :loading="isLoading"
            :text="$globals.capitalizeFirstLetter($t('common.save'))"
            @when-cliked="saveModal()"
          />
        </div>
      </div>
      <div class="body_drawer gestion-agent">
        <div class="form" v-if="stage == 1">
          <h4 @click="addAgentInArray">
            <i class="icon-add mr-2"></i>
            {{ $globals.capitalizeFirstLetter($t('views.agents.addAgent')) }}
          </h4>
          <div class="form-select" v-for="agent in arrayAgents" :key="agent.id">
            <div class="form-item">
              <SelectSingle
                :options="getOptions(agent)"
                :keyLabel="'name'"
                :keyValue="'id'"
                @lengthInputValue="(item) => handleSearch(item, agent.id)"
                :onSelect="
                  (item) => {
                    handleItemSelected(item, agent.id);
                  }
                "
                :defaultSelected="getDefaultSelected(agent.id)"
                :hasError="agent.errors != null"
                :errorMessage="
                  agent.errors && agent.errors != null ? agent.errors : ''
                "
              />
              <button @click="removeAgentInArray(agent.id)">
                <i class="icon-delete-agt"></i>
              </button>
            </div>
          </div>

          <div class="list-agent" v-if="agentsInThisTeam.agents.length > 0">
            <h5>Actuellement dans lquipe</h5>
            <ul v-for="(agent, index) in agentsInThisTeam.agents" :key="index">
              <li
                class="item item"
                :class="{ 'line-through': isRemoved(agent) }"
              >
                <div class="content">
                  <div class="user">
                    <i class="icon-headphones-with-mic pr-2"></i>
                    {{ agent.firstName }} {{ agent.lastName }}
                  </div>
                  <div class="id">{{ agent.hermesId }}</div>
                </div>
                <div class="action" @click="removeAgent(index)">
                  <i v-if="isRemoved(agent)" class="icon-migrer"></i>
                  <i v-else class="icon-delete-agt"></i>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div v-if="stage === 2">
          <div class="liste-agents">
            <p>
              Vous avez choisi dajouter des agents rattachs  une autre quipe
              :
            </p>
            <ul v-for="(item, index) in arrayAgentsToConfirme" :key="index">
              <li>
                {{ item.name }}
                <span>({{ item.team.name }})</span>
                <button @click="removeFromAgentsToConfirme(item)">
                  <i class="icon-exit-"></i>
                </button>
              </li>
              <!-- <li>
                Riadh ARFAOUI
                <span>(quipe #2)</span>
                <button><i class="icon-exit-"></i></button>
              </li> -->
            </ul>
            <p class="info">
              <i class="icon-info-light pr-2"></i>
              En validant, ces agents seront retirs de leur quipe actuelle.
            </p>
          </div>
        </div>
      </div>
      <div id="overlay-" @click="closeModal(0)"></div>
    </div>
  </transition>
</template>

<script>
import SelectSingle from '@/components/FormWidgets/SelectSingle/SelectSingle.vue';
import Button from '@/components/FormWidgets/Button.vue';
import team from '@/store/modules/team';
import { mapGetters, mapActions } from 'vuex';

export default {
  components: {
    Button,
    SelectSingle,
  },
  data() {
    return {
      showDrawer: false,
      selectedItem: null,
      isFetching: null,
      arrayAgents: [],
      arrayAgentsToConfirme: [],
      showSearchedData: [],
      removedAgents: [],
      stage: 1,
      modalVisible: false,
      btnleft: 'Annuler',
      btnright: 'Supprimer',
    };
  },
  props: ['isOpenedGestion', 'teamIndex', 'agentsInThisTeam'],
  methods: {
    ...mapActions('agent', ['getDataSearched']),
    ...mapActions('team', ['setModel', 'updateTeam']),

    cancel() {
      this.stage === 1 ? this.closeModal(1) : (this.stage = 1);
    },
    closeModal(closeWay) {
      this.$emit('close', closeWay);
      this.resetThisModel();
    },
    resetThisModel() {
      this.stage = 1;
      this.arrayAgents = [];
      this.removedAgents = [];
      this.showSearchedData = [];
    },
    checkForErrors() {
      this.arrayAgentsToConfirme = this.arrayAgents
        .filter((agent) => agent.errors !== null && agent.errors.length > 0)
        .map((agent) => agent.Selected);
    },
    removeFromAgentsToConfirme(item) {
      const index = this.arrayAgentsToConfirme.indexOf(item);
      if (index !== -1) {
        this.arrayAgentsToConfirme.splice(index, 1);
      }
      if (this.arrayAgentsToConfirme.length === 0) {
        this.stage = 1;
      }
    },
    async saveModal() {
      if (this.stage == 1) {
        this.checkForErrors();
        if (this.arrayAgentsToConfirme.length > 0) {
          this.stage = 2;
        } else {
          if (this.arrayAgents.length == 0 && this.removedAgents.length == 0) {
            this.closeModal(0);
          } else {
            this.$store.commit('team/SET_IS_LOADING_BUTTON', true);

            const selectedAgents = this.arrayAgents.map(
              (agent) => agent.Selected,
            );
            this.setModel({
              ...this.modelTeam,
              agentTransfertCollection: selectedAgents,
              agentsToRemove: this.removedAgents,
              id: this.agentsInThisTeam.id,
            });
            await this.updateTeam()
              .then(() => {
                this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
                this.closeModal(1);
              })
              .catch(() => {
                this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
              });
          }
        }
      } else {
        this.$store.commit('team/SET_IS_LOADING_BUTTON', true);

        const selectedAgents = this.arrayAgents.map((agent) => agent.Selected);
        this.setModel({
          ...this.modelTeam,
          agentTransfertCollection: selectedAgents,
          agentsToRemove: this.removedAgents,
          id: this.agentsInThisTeam.id,
        });
        this.setModel({
          ...this.modelTeam,
          ['agentsToRemove']: this.removedAgents,
        });
        this.setModel({
          ...this.modelTeam,
          ['id']: this.agentsInThisTeam.id,
        });

        await this.updateTeam()
          .then(() => {
            this.$store.commit('team/SET_NEW_COLLECTION_AGENT', {
              agentToRemove: selectedAgents,
            });
            this.$store.commit('team/SET_IS_LOADING_BUTTON', false);

            this.closeModal(1);
          })
          .catch(() => {
            this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
          });
      }
    },
    async handleSearch(item, agentId) {
      const index = this.arrayAgents.findIndex((agent) => agent.id === agentId);
      if (index !== -1) {
        this.$set(this.arrayAgents[index], 'keySearch', item);
        if (item.length > 2) {
          await this.getDataSearched(item)
            .then((res) => {
              const filteredData = res.data.filter((x) => x.roleId === '1');
              const formattedData = filteredData.map((x) => ({
                id: x.id,
                name: `${x.firstName} ${x.lastName} (${x.hermesId})`,
                team: x.teamId,
              }));
              this.$set(this.showSearchedData, agentId, formattedData);
            })
            .catch(() => {});
        }
      }
    },
    removeAgentInArray(agentId) {
      const index = this.arrayAgents.findIndex((agent) => agent.id === agentId);
      if (index !== -1) {
        this.arrayAgents.splice(index, 1);
        this.$delete(this.showSearchedData, agentId);
      }
    },
    addAgentInArray() {
      const newAgent = {
        id: Math.random().toString(36).substr(2, 9),
      };
      this.arrayAgents.push(newAgent);
      const newAgentId = this.arrayAgents[this.arrayAgents.length - 1].id;
      this.$set(this.showSearchedData, newAgentId, []);
    },
    handleItemSelected(selectedItem, agentId) {
      const index = this.arrayAgents.findIndex((agent) => agent.id === agentId);
      this.$set(this.arrayAgents[index], 'Selected', selectedItem);

      if (selectedItem.team == null) {
        this.$set(this.arrayAgents[index], 'errors', null);
      } else {
        this.$set(
          this.arrayAgents[index],
          'errors',
          this.$globals
            .capitalizeFirstLetter(this.$t('errors.AGENT_HAVE_ALREADY_A_TEAM'))
            .replace('teamname', selectedItem.team.name),
        );
      }
    },
    getOptions(agent) {
      if (agent.keySearch && agent.keySearch.length > 2) {
        return this.showSearchedData[agent.id];
      } else {
        return this.collectionAgents;
      }
    },
    getDefaultSelected(agentId) {
      const index = this.arrayAgents.findIndex((agent) => agent.id === agentId);
      if (index !== -1 && this.arrayAgents[index].Selected) {
        return this.arrayAgents[index].Selected.id;
      }
      return null;
    },
    isRemoved(agent) {
      return this.removedAgents.some((a) => a.id === agent.id);
    },
    removeAgent(index) {
      const agentToRemove = this.agentsInThisTeam.agents[index];
      const existingIndex = this.removedAgents.findIndex(
        (agent) => agent.id === agentToRemove.id,
      );
      if (existingIndex === -1) {
        this.removedAgents.push(agentToRemove);
      } else {
        this.removedAgents.splice(existingIndex, 1);
      }
    },
  },
  computed: {
    ...mapGetters('team', ['getModel', 'getState']),
    isLoading() {
      return this.getState.loadingButton;
    },
    modelTeam() {
      return this.getModel;
    },
    collectionAgents() {
      const agents = this.$store.getters['agent/getCollection'];
      const filteredAgents = agents.filter((x) => x.roleId === '1');
      return filteredAgents.map((x) => ({
        id: x.id,
        name: x.firstName + ' ' + x.lastName + ' (' + x.hermesId + ')',
        team: x.teamId,
      }));
    },
  },
};
</script>
