<template>
  <div class="content_page page_details compte_rendu">
    <div class="flexbox__">
      <div class="content_page_o">
        <!-- {{ this.activeRecord }} -->

        <div class="header_details">
          <div class="main-title">
            <h1>
              {{ calibrationData.calibration?.title }} -
              {{ formatDate(calibrationData.calibration?.created_at) }}
            </h1>
            <div class="rdv">
              Rendez-vous :
              {{ formatFullDate(calibrationData.calibration?.start_date) }}
            </div>
          </div>
          <div class="main-account">
            <h5 class="pr-5">Participants :</h5>
            <template
              v-for="(member, index) in calibrationData.member_calibration"
            >
              <!-- checkscorigvoted(note.id, item) -->

              <Account
                :firstName="member.first_name"
                :lastName="member.last_name"
                :bgColor="member.avatar_color"
                :key="index + '_avatar'"
              />
            </template>
          </div>
        </div>

        <div class="tabs mt-4 mb-2">
          <button
            v-for="item in playlist"
            :key="item.id"
            @click="
              () => {
                handleSetActiveRecord(item);
              }
            "
            :class="{ active: activeParentRecord?.id === item.id }"
          >
            {{ item.name }}
          </button>
        </div>

        <div class="content_page__">
          <div class="title__">
            <div class="main-title">{{ activeRecord?.name }}</div>
            <div class="date">
              <div class="flex align-items">
                <i class="icon-home mr-2"></i>
                {{ activeRecord?.agentId?.callCenterId?.name }}
              </div>
              <div class="flex align-items">
                <i class="icon-calendar ml-4 mr-2"></i>
                {{ $globals.formatDateWithoutTime(activeRecord?.createdAt) }}
              </div>
              <div class="flex align-items">
                <i class="icon-time-circle ml-4 mr-2"></i>
                {{ $globals.audioItemTimeFormat(activeRecord?.createdAt) }}
              </div>
            </div>
          </div>
          <div class="content_evaluation">
            <ScribbPlayer
              ref="scribbPalyer"
              :playlist="playlist"
              :withAttachments="true"
              :withSpeechTrace="true"
              :withWaveSurfer="false"
              :showPlaylist="true"
              @trackChanged="
                (currentRecord, parentRecord) => {
                  setActiveRecord(currentRecord);
                  setParentRecord(parentRecord);
                }
              "
              @playListItemClicked="
                (record) => {
                  setActiveRecord(record);
                }
              "
            />
            <div class="tab mt-7">
              <button
                @click="changeTab(0)"
                :class="{ active: activeTab === 0 }"
              >
                Compte-rendu
              </button>
              <button
                @click="changeTab(1)"
                :class="{ active: activeTab === 1 }"
              >
                Retranscription
              </button>
              <div class="slider"></div>
            </div>

            <div class="tabcontent">
              <transition name="fade">
                <div
                  v-show="activeTab === 0"
                  class="content_manuelle content_scribb"
                >
                  <CompteRendu
                    :calibration="calibrationData"
                    :activeRecord="activeParentRecord"
                  />
                </div>
              </transition>
              <transition name="fade">
                <div v-show="activeTab === 1" class="content_retranscription">
                  <Retranscription />
                </div>
              </transition>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Retranscription from '@/components/Records/ListTab/Retranscription.vue';
import Account from '@/components/FormWidgets/AvatarColorsRandom/AvatarColor.vue';
import CompteRendu from '@/components/Details/ListTab/CompteRendu.vue';
// import PlayList from '@/components/Records/PlayList/PlayList.vue';
import { mapActions, mapGetters } from 'vuex';
import moment from 'moment';
import ScribbPlayer from '@/components/ScribbAudioPlayer/ScribbPlayer.vue';

export default {
  components: {
    Retranscription,
    CompteRendu,
    Account,
    // PlayList,
    ScribbPlayer,
  },

  data() {
    return {
      activeSection: 1,
      activeTab: 0,
      activeRecordTab: 0,
      // activeRecord: null,
      // activeRecordId: null,
      // activeRecordName: null,
      // activeRecordCallcenter: null,
      // activeRecordCreatedAt: null,
    };
  },

  computed: {
    ...mapGetters('detailsCalibrage', ['getCompterendu']),
    ...mapGetters('record', ['getParentRecord', 'getActiveRecord']),
    calibrationData() {
      return this.getCompterendu;
    },
    playlist() {
      return this.getCompterendu.record;
    },
    activeParentRecord() {
      return this.getParentRecord;
    },
    activeRecord() {
      return this.getActiveRecord;
    },
  },

  methods: {
    ...mapActions('detailsCalibrage', ['getCompterenduDetails']),
    ...mapActions('record', ['setActiveRecord', 'setParentRecord']),
    changeTab(index) {
      this.activeTab = index;
    },
    formatDate(data) {
      return moment(data).format('MMMM YYYY');
    },
    formatFullDate(data) {
      return moment(data).format('DD/MM/YYYY');
    },
    handleSetActiveRecord(record) {
      this.setActiveRecord(record);
      this.setParentRecord(record);
      this.$refs.scribbPalyer.setCurrentRecordByRecordId(record.id);
    },
  },
  mounted() {
    if (this.$route.params.id != undefined) {
      this.getCompterenduDetails(this.$route.params.id).then((res) => {
        this.setParentRecord(res.record[0]);
        this.setActiveRecord(res.record[0]);
      });
    } else {
      this.$router.push({ name: 'dashboard' });
    }
  },
};
</script>