<template>
  <div class="scribb-form-auth">
    <div class="scribb-form-login">
      <div class="container-auth flex align-items">
        <div class="left-column box py-7">
          <div class="content-form">
            <div class="logo">
              <img src="../../assets/images/logo.svg" alt="" />
            </div>

            <div class="flex justify-center pt-9">
              <div class="card-form">
                <input-password
                  :hasError="errors != null && errors.password != null"
                  :errorMessage="
                    errors != null && errors.password != null
                      ? $t('errors.' + errors.password[0])
                      : ''
                  "
                  name="password"
                  :label="
                    $globals.capitalizeFirstLetter($t('common.newPassword'))
                  "
                  @input="
                    (e) => {
                      password = e.target.value;
                    }
                  "
                />
                <input-password
                  :hasError="
                    errors != null && errors.password_confirmation != null
                  "
                  :errorMessage="
                    errors != null && errors.password_confirmation != null
                      ? $t('errors.' + errors.password_confirmation[0])
                      : ''
                  "
                  name="password_confirmation"
                  :label="
                    $globals.capitalizeFirstLetter($t('common.confirmPassword'))
                  "
                  @input="
                    (e) => {
                      password_confirmation = e.target.value;
                    }
                  "
                />
                <div class="error" v-if="ServerError">{{ errors }}</div>
                <Button
                  class="mt-4"
                  :loading="loading"
                  @when-cliked="welcomeJoinAfterTokenVerification"
                  :text="
                    $globals.capitalizeFirstLetter(
                      $t('views.login.connectMe'),
                    )
                  " 
                />
              </div>
            </div>
          </div>
        </div>
        <div class="right-column box flex align-items justify-center">
          <AnimationScribb />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Button from '@/components/FormWidgets/Button.vue';
import InputPassword from '@/components/FormWidgets/InputPassword.vue';
import AnimationScribb from '@/components/AnimationScribb/AnimationScribb.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {
    Button,
    InputPassword,
    AnimationScribb,
  },
  data() {
    return {
      password: '',
      password_confirmation: '',
      ServerError: false,
      first_name: null,
      last_name: null,
      loading: false,
    };
  },
  mounted() {
    (this.first_name = null), (this.last_name = null);
     
    this.verifyToken({ token: this.$route.params.token })
      .then((response) => {
        this.first_name = response.first_name;
        this.last_name = response.firstlast_name_name;
      })
      .catch(() => {
        this.$router.push({ name: 'login' });
      });
  },
  computed: {
    ...mapGetters('user', ['getErrors']),
    errors() {
      return this.getErrors;
    },
  },
  methods: {
    ...mapActions('user', ['welcomeJoin', 'setErrors', 'verifyToken']),
    async welcomeJoinAfterTokenVerification() {
      const payload = {
        password: this.password,
        password_confirmation: this.password_confirmation,
        token: this.$route.params.token,
      };

      this.setErrors();

      await this.welcomeJoin(payload)
        .then(() => {
          this.passwordResetSuccess = true;
          this.loading = true;
          this.$router.push({ name: 'login' });
        })
        .catch(() => {
          this.ServerError = true;
        });

    },
  },
};
</script>
