<template>
  <transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true }" class="show">
      <div class="header_drawer">
        <h4>{{ selectedNote?.name }}</h4>
        <div class="content-button btn-add">
          <button class="close mr-5" @click="$emit('close')">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
        </div>
      </div>

      <div class="body_drawer">
        <div class="content--">
          <p class="text-justify">
            {{ selectedNote?.meaning != null ? selectedNote?.meaning  : 'aucune description' }}
          </p>
        </div>
      </div>
      <div id="overlay-" @click="$emit('close')"></div>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from 'vuex';
//import Button from '@/components/FormWidgets/Button.vue';

export default {
  components: {},
  props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {};
  },
  methods: {},
  mounted() {},
  computed: {
    ...mapGetters('record', ['getSelectedNote']),
    selectedNote() {
      return this.getSelectedNote;
    },
  },
};
</script>
