<template>
  <div>
    <div class="content__ mt-5">
      <div class="panel_header_ content">
        <div class="title">Dtails de lenregistrement</div>
        <!-- <div class="note"></div> -->
      </div>

      <div class="panel_body__">
        <div class="details">
          <div class="content">
            <label for="">Date</label>
            <p>{{ $globals.audioItemDateFormat(activeRecord.createdAt) }}</p>
          </div>

          <div class="content">
            <label for="">Heure</label>
            <p>{{ $globals.audioItemTimeFormat(activeRecord.createdAt) }}</p>
          </div>

          <div class="content">
            <label for="">Call-center</label>
            <p>
              {{
                activeRecord.agentId
                  ? activeRecord.agentId.callCenterId.name
                  : ''
              }}
            </p>
          </div>
        </div>

        <div class="details">
          <div class="content">
            <label for="">Campagne</label>
            <p>
              {{ activeRecord?.gridData?.campagne?.name }}
            </p>
          </div>

          <div class="content">
            <label for="">Agent</label>
            <p>
              {{ activeRecord.agentId ? activeRecord.agentId.firstName : '' }}
              {{ activeRecord.agentId ? activeRecord.agentId.lastName : '' }} (
              {{ activeRecord.agentId ? activeRecord.agentId.hermesId : '' }})
            </p>
          </div>

          <div class="content">
            <label for=""></label>
            <p></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';

export default {
  components: {},
  data() {
    return {
      activeSection: 1,
    };
  },
  methods: {
    toggleAccordion(section) {
      this.activeSection = this.activeSection === section ? null : section;
    },
  },
  computed: {
    ...mapGetters('record', ['getActiveRecord']),
    activeRecord() {
      return this.getActiveRecord;
    },
  },
};
</script>
