<template>
  <div>
    <div class="table scroll-x">
      <div class="table-row header" :class="data.length > 0 ? '' : 'empty'">
        <div class="table-cell" v-if="multipleSelect && multipleSelect === true">
          <div class="form-check" v-if="!selectAgents">
            <input v-model="isAllSelected" @change="handleSelectRows" type="checkbox" id="all-list" />
            <label for="all-list"></label>
          </div>
        </div>
        <div class="table-cell" v-if="hasActions && hasActions === true"></div>
        <div class="table-cell" v-for="column in columns" :key="column.key">
          <ColumnRender :column="column" :sortOrder="sortOrder" :sortKey="sortKey"
            @onSortTriggered="(key) => handleSortKeyChanged(key)" />
        </div>
      </div>
      <template v-if="!isLoading">
        <template v-if="data.length > 0">
          <div class="tbody">
            <div class="table-row" :class="selectAgents ? getRowClasses(item) : ''" v-for="(item, index) in data"
              :key="selectAgents ? 'agent' + index : index" :style="{ cursor: redirection ? 'pointer' : '' }">
              <div class="table-cell" v-if="multipleSelect && multipleSelect === true">
                <div class="form-check">
                  <input :disabled="getRowClasses(item) === 'disable-line'" @change="handleSelectedRow(item)"
                    type="checkbox" :id="selectAgents
                      ? 'item-list-agent' + index
                      : 'item-list' + index
                    " :value="item[keySelection]" v-model="selectedKeys" />
                              <label :for="selectAgents
                      ? 'item-list-agent' + index
                      : 'item-list' + index
                    "></label>
                </div>
              </div>
              <div class="table-cell" v-if="hasActions && hasActions === true">
                <div class="form-document" v-if="item.statusFinished" @click="handleeClickedRow(item)">
                  <i class="icon-document"></i>
                </div>

                <Actions v-else-if="item.status && item.status !== 'cancel' && showMoreSelection == 'calibrage'" :actions="actions" :index="index"
                  :onUpdate="() => {
                    onUpdateItem(index, item);
                  }
                  " :onArchive="() => {
                    onArchiveItem(index, item);
                  }
                  " :onDuplicate="() => {
                    onDuplicateItem(index, item);
                  }
                  " :onUnArchive="() => {
                    onUnArchiveItem(index, item);
                  }
                  " :onCancel="() => {
                    onCancelItem(index, item);
                  }
                  " :onGetVersion="() => {
                    onGetItemVersion(index, item);
                  }
                  " />
                  <Actions v-if="showMoreSelection == 'callCenter'" :actions="actions" :index="index"
                  :onUpdate="() => {
                    onUpdateItem(index, item);
                  }
                  " :onArchive="() => {
                    onArchiveItem(index, item);
                  }
                  " :onDuplicate="() => {
                    onDuplicateItem(index, item);
                  }
                  " :onUnArchive="() => {
                    onUnArchiveItem(index, item);
                  }
                  " :onCancel="() => {
                    onCancelItem(index, item);
                  }
                  " :onGetVersion="() => {
                    onGetItemVersion(index, item);
                  }
                  " />
              </div>
              <div class="table-cell" v-for="column in columns" :key="column.key">


                <div v-if="showMoreSelection == 'callCenter'">
                  <div v-if="column.key === 'id'" @click="handleRowClicked(item)"><div><span>{{ item[column.key] }}</span></div></div>
                  <div v-if="column.key === 'name'" @click="handleRowClicked(item)"><div><span>{{ item[column.key] }}</span></div></div>
                  <div v-if="column.key === 'idCallCenter'" @click="handleRowClicked(item)"><div><span>{{ item[column.key] }}</span></div></div>
                  <div v-else-if="column.key === 'acounts'">
                    <div v-if="Array.isArray(item[column.key]) && item[column.key].length > 0">
                      <div style="display: flex;flex-wrap: wrap;align-items: center;">
                        <div v-for="member in limitedMembers(item[column.key])" :key="member.key" class="mr-1">
                          <div :style="{ backgroundColor: member.avatarColor }" class="letter-avatar" :class="member.status">
                            {{ getInitials(member) }}
                          </div>
                        </div>
                        <div v-if="item[column.key].length > 3" class="more-members" @click="toggleShowMore(item.id)">
                          <span v-if="!showMoreMembers[item.id]">
                            <div class="form-actions">
                              <div class="icon rotation-h" :id="'btn-dropdown_' + index">
                                <i class="icon-more"></i>
                              </div>
                            </div>
                          </span>
                          <span v-if="showMoreMembers[item.id]">
                            <div class="form-actions">
                              <div class="icon rotation-h" :id="'btn-dropdown_' + index">
                                <i class="icon-more"></i>
                              </div>
                            </div>
                          </span>
                        </div>
                      </div>
                      <div v-if="showMoreMembers[item.id]">
                        <AccountShowMore :moreMembers="additionalMembers(item[column.key])" :index="index" />
                      </div>
                    </div>
                    <div v-else>Aucun compte</div>
                  </div>
                  <div v-else-if="column.key === 'updated_at'" @click="handleRowClicked(item)"><div>{{ $globals.formatDate(item[column.key]) }}</div></div>
                </div>
                
                <div v-if="showMoreSelection == 'calibrage'">
                  <div v-if="column.key === 'title'" @click="handleRowClicked(item)"><div><span>{{ item[column.key] }}</span></div></div>
                  <div v-else-if="column.key === 'recordCalibrations'" @click="handleRowClicked(item)">
                    <div v-if="Array.isArray(item[column.key]) && item[column.key].length > 0" style="display: flex;align-items: center;">
                      <span class="actif mr-1"></span> {{ Array.isArray(item[column.key]) && item[column.key].length > 0 ? `${item[column.key].length}` : 0 }}
                    </div>
                  </div>
                  <div v-else-if="column.key === 'memberCalibrations'">
                    <div v-if="Array.isArray(item[column.key]) && item[column.key].length > 0">
                      <div style="display: flex;flex-wrap: wrap;align-items: center;">
                        <div v-for="member in limitedMembers(item[column.key])" :key="member.key" class="mr-1">
                          <div :style="{ backgroundColor: member.avatarColor }" class="letter-avatar">
                            {{ getInitials(member) }}
                          </div>
                        </div>
                        <div v-if="item[column.key].length > 3" class="more-members" @click="toggleShowMore(item.id)">
                          <span v-if="!showMoreMembers[item.id]">
                            <div class="form-actions">
                              <div class="icon rotation-h" :id="'btn-dropdown_' + index">
                                <i class="icon-more"></i>
                              </div>
                            </div>
                          </span>
                          <span v-if="showMoreMembers[item.id]">
                            <div class="form-actions">
                              <div class="icon rotation-h" :id="'btn-dropdown_' + index">
                                <i class="icon-more"></i>
                              </div>
                            </div>
                          </span>
                        </div>
                      </div>
                      <div v-if="showMoreMembers[item.id]">
                        <AccountShowMore :moreMembers="additionalMembers(item[column.key])" :index="index" />
                      </div>
                    </div>
                    <div v-else>Aucun compte</div>
                  </div>
                  <div v-else-if="column.key === 'startDate'" @click="handleRowClicked(item)"><div>{{ $globals.formatDate(item[column.key]) }}</div></div>
                  <div v-else-if="column.key === 'status'" @click="handleRowClicked(item)"><div><span class="custom-status" :class="getStatus(item).class">{{ getStatus(item).text }}</span></div></div>
                </div>

                
              </div>
            </div>
          </div>
        </template>

        <div v-else class="table-row empty-data text-center">
          <div class="table-empty">
            <h6>
              {{ msgEmptyTable }}
            </h6>
            <button v-if="withButton" @click="handleButton()">
              {{ textButton }}
            </button>
          </div>
        </div>
      </template>

      <template v-else>
        <div class="table-row empty-data text-center">
          <div class="table-empty">
            <Loader />
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
// import TableCellRender from '../Table/TableCellRender.vue';
import ColumnRender from '../Table/ColumnRender.vue';
import Actions from '../Table/Actions.vue';
import AccountShowMore from '../Table/AccountShowMore.vue';
import Loader from '../AnimationScribb/Loader.vue';
import { mapActions, mapGetters } from 'vuex';
export default {
  props: {
    columns: Array,
    data: Array,
    multipleSelect: Boolean,
    onSelectItems: {
      type: Function,
      required: false,
      default: function () { },
    },
    onSelectItem: {
      type: Function,
      required: false,
      default: function () { },
    },
    hasActions: Boolean,
    actions: Object,
    onDeleteItem: {
      type: Function,
      required: false,
      default: function () { },
    },
    onUpdateItem: {
      type: Function,
      required: false,
      default: function () { },
    },
    onArchiveItem: {
      type: Function,
      required: false,
      default: function () { },
    },
    onUnArchiveItem: {
      type: Function,
      required: false,
      default: function () { },
    },
    onDuplicateItem: {
      type: Function,
      required: false,
      default: function () { },
    },
    onCancelItem: {
      type: Function,
      required: false,
      default: function () { },
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    onClickRow: {
      type: Function,
      default: () => { },
    },
    redirection: {
      type: Boolean,
      default: false,
    },
    selectAgents: {
      type: Boolean,
      default: false,
    },
    keySelection: {
      type: String,
      required: false,
      default: '',
    },
    msgEmptyTable: {
      type: String,
      default: function () {
        return this.$globals.capitalizeFirstLetter(this.$t('common.norecords'));
      },
    },
    withButton: {
      type: Boolean,
      default: false,
    },
    textButton: {
      type: String,
      default: '',
    },
    onGetItemVersion: {
      type: Function,
      required: false,
      default: function () { },
    },
    agentDashboard: {
      type: Boolean,
      required: false,
      default: function () { },
    },
    enabledScrollCss: {
      type: Boolean,
      required: false,
    },
    teamDashboard: {
      type: Boolean,
      required: false,
      default: function () { },
    },
    showMoreSelection: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      selectedRows: [],
      roleSelected: null,
      isAllSelected: false,
      selectedKeys: [],
      sortKey: null,
      sortOrder: 'asc',
      enabledCss: false,
      showMoreMembers: {}
    };
  },
  mounted() {
    this.$nextTick(() => {
      const btn = document.getElementById('btn-dropdown_' + this.index);
      const dropDwon = document.getElementById('actions-dropdown_' + this.index);

      if (btn && dropDwon) {
        const handleColse = () => {
          this.activeAction = null;
        };

        // Show or hide the dropdown menu when the button is clicked
        btn.addEventListener('click', function () {
          if (dropDwon.style.display === 'block') {
            dropDwon.style.display = 'none';
            handleColse();
          } else {
            dropDwon.style.display = 'block';
          }
        });

        // Hide the dropdown menu if the user clicks anywhere else on the page
        document.addEventListener('click', function (event) {
          if (!btn.contains(event.target) || dropDwon.contains(event.target)) {
            dropDwon.style.display = 'none';
            handleColse();
          }
        });
      }
    });
  },
  components: {
    // TableCellRender,
    ColumnRender,
    Loader,
    Actions,
    AccountShowMore
  },
  methods: {
    ...mapActions("agent", ["getTopFlopAgentsData"]),
    ...mapActions('team', ['getTopFlopTeamData', 'setTopFlop']),
    ...mapActions('calibrage', ['setModel']),

    handleRowClicked(item) {
      if (this.redirection && item.status !== 'cancel') this.$emit('row-clicked', item);
    },
    handleSelectRows() {
      if (this.isAllSelected) {
        this.selectedRows = this.data;
      } else {
        this.selectedRows = [];
      }
      this.$emit('onSelection', this.selectedRows);
    },
    handleeClickedRow(item) {
      this.setModel({
        id: item.id,
        recordCalibrations: item.recordCalibrations.map((item, index) => ({
          index: index,
          id: item.recordId.id,
          name: item.recordId.name,
          firstName: item.recordId != null && item.recordId.agentId ? item.recordId.agentId.firstName : '',
          lastName: item.recordId != null && item.recordId.agentId ? item.recordId.agentId.lastName : '',
          callCenter: item.recordId != null && item.recordId.agentId && item.recordId.agentId.callCenterId ? item.recordId.agentId.callCenterId.name : ''
        })),
      });
      this.$router.push({ name: "compterendu", params: { id: item.id } });
    },
    handleSelectedRow(item) {
      if (
        this.selectedRows.find(
          (x) => x[this.keySelection] == item[this.keySelection],
        )
      ) {
        this.selectedRows = this.selectedRows.filter(
          (x) => x[this.keySelection] != item[this.keySelection],
        );
        if (this.selectedRows.length == 0 && this.selectAgents)
          this.roleSelected = null;
      } else {
        this.selectedRows.push(item);
        if (this.selectAgents) this.roleSelected = item.roleId;
      }
      this.$emit('onSelection', this.selectedRows);
    },
    handleSortKeyChanged(key) {
      this.sortKey = key;
      if (this.sortOrder === 'asc') {
        this.sortOrder = 'desc';
      } else {
        this.sortOrder = 'asc';
      }
      this.$emit('onSort', {
        sortKey: this.sortKey,
        sortOrder: this.sortOrder,
      });
    },
    setSelectedRows(data) {
      this.selectedRows = data;
      this.selectedKeys = [];
    },
    handleButton() {
      this.$emit('onButtonClicked');
    },
    getRowClasses(item) {
      if (this.roleSelected && this.roleSelected != item.roleId)
        return 'disable-line';
    },
    getStatus(record) {
      let status = { text: "", class: "" };

      if (record.status === "in_progress") {
        status.text = "En cours";
        status.class = "orange";
      } else if (record.status === "finished") {
        status.text = "Termin";
        status.class = "green";
      } else if (record.status === "cancel") {
        status.text = "Annuler";
        status.class = "cancelcolor";
      }

      return status;
    },
    getInitials(member) {
      if (member.email) {
        const firstName = member.firstName ? member.firstName.charAt(0).toUpperCase() : "";
        const lastName = member.lastName ? member.lastName.charAt(0).toUpperCase() : "";
        return firstName + lastName;
      } else {
        return "";
      }
    },
    limitedMembers(members) {
      return members.slice(0, 3);
    },
    additionalMembers(members) {
      return members.slice(3);
    },
    toggleShowMore(id) {
      this.$set(this.showMoreMembers, id, !this.showMoreMembers[id]);
    }


  },
  computed: {
    ...mapGetters({ campaign: "campaign/getCampagneInfo" }),
    ...mapGetters("agent", ["getTopFlopCollection", "getPagination"]),
    agentCollection() {
      return this.getTopFlopCollection;
    }

  },
  watch: {
    selectedRows(newRows) {
      this.selectedKeys = newRows.map((row) => row[this.keySelection]);
      if (newRows.length == 0) {
        this.isAllSelected = false;
        return;
      }
      if (newRows.length == this.data.length) {
        this.isAllSelected = true;
        return;
      }
      if (newRows.length != this.data.length) {
        this.isAllSelected = false;
        return;
      }
    },
  },
};
</script>