<template>
    <div class="container-grid border-b">
        <div class="form-field search-field autocomplete" :class="{ fieldError: hasError || error.errorNoEmailFound }">
            <label class="control-label" :for="inputId">{{ label }}</label>
            <template>
                <div class="search">
                    <i class="icon-search"></i>
                </div>
                <input v-model="searchTerm" @input="filterItems" :type="type" :placeholder="placeholder" />
            </template>
            <div class="error" v-if="hasError">{{ errorMessage }}</div>
            <div class="error" v-else-if="error.errorNoEmailFound">{{ error.messageNoEmailFound }}</div>

            <div class="list-search">
                <template>
                    <span class="tag" v-for="(item, index) in itemsSelected" v-bind:key="index">
                        {{ item[keyLabel] }}
                        <i class="icon-exit-" @click="removeItem(item)"></i>
                    </span>
                </template>
            </div>
            <ul v-show="filteredItems.length > 0 && showResults">
                <li v-for="(item, index) in filteredItems" @click="selectItem(item)" v-bind:key="index">
                    <div class="flex py-1" style="align-items: center;">
                        <div>
                            <Account :firstName="item[keyLabelFirstName]" :lastName="item[keyLabelLastName]"
                                :bgColor="item[keyLabelBgColor]" />
                        </div>
                        <div>
                            <div class="content-name pl-2">
                                <div class="flex flex-column-content">
                                    <p class="search-term">{{ item[keyLabel] }}</p>
                                    <p class="access-message">{{ item[keyLabelEmail] }}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
            <ul v-show="(filteredItems.length == 0 && searchTerm.length > 0 && showNoResults)"
                :style="{ top: error.errorNoEmailFound ? '130px' : '80px' }">
                <li @click="fillSearchTerm(searchTerm)">
                    <div class="flex" style="align-items: center;">
                        <div class="mr-3 circle-avatar">
                            <i class="icon-message"></i>
                        </div>
                        <div>
                            <div class="flex flex-column-content">
                                <p class="search-term">{{ searchTerm }}</p>
                                <p class="access-message">{{ $t('views.calibrages.accesUnique') }}</p>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>

            
        </div>
    </div>
</template>
<script>
import Account from '@/components/FormWidgets/AvatarColorsRandom/AvatarColor.vue';
export default {
    components: {
        Account,
    },
    data() {
        return {
            searchTerm: '',
            filteredItems: [],
            showResults: false,
            showNoResults: true,
            noResultsMessage: 'Aucun rsultat trouv.',
            error: {
                errorNoEmailFound: false,
                messageNoEmailFound: ''
            }
        };
    },
    props: {
        type: {
            type: String,
            default: 'text',
        },
        placeholder: String,
        inputId: String,
        label: String,
        items: Array,
        itemsSelected: Array,
        keyValue: String,
        keyLabel: String,
        keyLabelFirstName: String,
        keyLabelLastName: String,
        keyLabelBgColor: String,
        keyLabelEmail: String,
        keyArray: String,
        hasError: {
            type: Boolean,
        },
        errorMessage: {
            type: String,
        },
    },
    watch: {
        searchTerm: function (newTerm) {
            if (newTerm.length > 0) {
                this.showResults = this.filteredItems.length > 0;
                this.showNoResults = !this.showResults;
                this.error.errorNoEmailFound = false;
                this.error.messageNoEmailFound = "";
            } else {
                this.showResults = true;
                this.showNoResults = true;
                this.filteredItems = [];
            }
        },
    },
    methods: {
        fillSearchTerm(email) {
            const isValidEmail = this.isValidEmail(email.trim());
            if (!isValidEmail) {
                this.error.errorNoEmailFound = true;
                this.error.messageNoEmailFound = "L'utilisateur que vous recherchez n'a pas de compte. Pour l'inviter, utilisez son adresse e-mail.";
                this.showNoResults = false;
                this.$emit('item-selected', null, this.keyArray);
            } else {
                this.error.errorNoEmailFound = false;
                this.error.messageNoEmailFound = '';

                const selectedItem = {
                    isGuest: 1,
                    email: email,
                    firstName: '',
                    lastName: '',
                    avatarColor: '#1187ad',
                };
                this.searchTerm = email;
                this.showNoResults = false;
                this.showResults = false;
                this.$emit('item-selected', selectedItem, this.keyArray);
            }
        },
        isValidEmail(email) {
            const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            return emailPattern.test(email);
        },
        filterItems() {
            this.searchTerm = this.searchTerm.trim();

            if (this.searchTerm === '') {
                this.filteredItems = [];
                this.showResults = false;
                this.error.errorNoEmailFound = false;
                this.error.messageNoEmailFound = '';
                this.$emit('item-selected', null, this.keyArray);
                return;
            }

            this.filteredItems = this.items.filter((item) => {
                const keyLabelEmailMatch = item[this.keyLabelEmail]
                    .toLowerCase()
                    .includes(this.searchTerm.toLowerCase());
                const keyLabelFirstNameMatch = item[this.keyLabelFirstName]
                    .toLowerCase()
                    .includes(this.searchTerm.toLowerCase());
                const keyLabelLastNameMatch = item[this.keyLabelLastName]
                    .toLowerCase()
                    .includes(this.searchTerm.toLowerCase());

                return keyLabelEmailMatch || keyLabelFirstNameMatch || keyLabelLastNameMatch;
            });

            this.showResults = this.filteredItems.length > 0;
        },
        selectItem(item) {
            this.searchTerm = item.email;
            this.showNoResults = false;
            this.$nextTick(() => {
                this.showResults = false;
            });
            const selectedItem = {
                isGuest: 0,
                email: item.email,
                firstName: item.firstName,
                lastName: item.lastName,
                avatarColor: item.avatarColor,
            };
            this.$emit('item-selected', selectedItem, this.keyArray);
        },

        removeItem(item) {
            this.$emit('remove-item', item[this.keyValue], this.keyArray);
        },
    },
};
</script>
