<template>
  <div
    v-if="showModalMigration"
    class="modal-dialog"
    @click="closeModalOutside"
  >
    <div class="modal-content" @click.stop>
      <div class="modal-header mb-5">
        <h4>Dplacer lagent</h4>
      </div>
      <div class="modal-body">
        <div class="form-migration">
          <div class="form-field actuelle ml-4">
            <SelectSingle
              :options="getOptions()"
              :keyLabel="'name'"
              :keyValue="'id'"
              :defaultSelected="fromTeam ? actualTeam : '0'"
              :label="
                $globals.capitalizeFirstLetter(
                  $t('components.team.formActuelle'),
                )
              "
              :isDisabled="true"
            />
          </div>
          <div class="arrow mt-10 mx-3"><i class="icon-arrow-left"></i></div>
          <div class="form-field ml-4">
            <SelectSingle
              :label="
                $globals.capitalizeFirstLetter($t('components.team.newTeam'))
              "
              :options="collectionTeam"
              :defaultSelected="'0'"
              :keyLabel="'name'"
              :keyValue="'id'"
              :hasError="SelectSingleErrors != null"
              :errorMessage="
                SelectSingleErrors && SelectSingleErrors != null
                  ? SelectSingleErrors
                  : ''
              "
              :onSelect="
                (item) => {
                  handleItemSelected(item);
                }
              "
            />
          </div>
        </div>
        <div class="modal-footer">
          <button class="btn_cancel mr-3" @click="closeModal">
            {{ cancelText }}
          </button>
          <button @click="handleOk" class="btn_remove">
            <LoaderBtn v-if="isLoading" />
            <template v-else>
              {{ okText }}
            </template>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SelectSingle from '@/components/FormWidgets/SelectSingle/SelectSingle.vue';
import LoaderBtn from '@/components/AnimationScribb/LoaderBtn.vue';
import { mapGetters } from 'vuex';

export default {
  components: {
    SelectSingle,
    LoaderBtn,
  },
  props: {
    showModalMigration: Boolean,
    actualTeam: Number,
    fromTeam: {
      type: Boolean,
      default: false,
    },
    cancelText: String,
    okText: String,
  },
  data() {
    return {
      selectedItem: null,
      SelectSingleErrors: null,
    };
  },
  methods: {
    closeModal() {
      this.$emit('close', this.item);
    },
    handleOk() {
      if (this.selectedItem == null) {
        this.SelectSingleErrors = 'Select an Item Please';
      } else {
        this.$store.commit('team/SET_IS_LOADING_BUTTON', true);
        this.$emit('onConfirm', this.selectedItem);
      }
    },
    closeModalOutside(event) {
      if (event.target.classList.contains('modal-dialog')) {
        this.closeModal();
      }
    },
    getOptions() {
      if (this.fromTeam) {
        return this.actualCollectionTeam;
      } else {
        return this.withoutTeamCollection;
      }
    },
    handleItemSelected(selectedItem) {
      this.selectedItem = selectedItem;
    },
  },
  computed: {
    ...mapGetters('team', ['getState']),
    isLoading() {
      return this.getState.loadingButton;
    },

    withoutTeamCollection() {
      return [{ id: 0, name: 'Sans quipe' }];
    },
    actualCollectionTeam() {
      const teams = this.$store.getters['team/getCollection'].map((x) => ({
        id: x.id,
        name: x.name,
      }));
      return teams;
    },
    collectionTeam() {
      const teams = this.$store.getters['team/getCollection'].map((x) => ({
        id: x.id,
        name: x.name,
      }));

      let teamsWithNoTeam;

      if (this.fromTeam) {
        const filteredTeams = teams.filter(
          (team) => team.id !== this.actualTeam,
        );
        teamsWithNoTeam = [...filteredTeams];
      } else {
        teamsWithNoTeam = [...teams];
      }

      return teamsWithNoTeam;
    },
  },
};
</script>
