<template>
  <div class="content_page detail-calibrage-record">
    <div class="header_page flex-column">
      <div class="search_audio">
        <div class="content-filter">
          <InputSearch
            @input-value="handleSearch"
            class="search-field"
            :placeholder="$t('components.listCalibrage.search')"
          />

          <div>
            <Filtres
              :items="[
                {
                  label: 'Campagnes',
                  key: 'campagnes',
                  filters: campagnes,
                },
              ]"
              @onFilterChange="handleFilterChanged"
              :getSelectedCount="selectedCount"
              @selected-names-changed="handleSelectedNamesChanged"
              :updatedFilters="this.updatedFilters"
            />
          </div>
          <ButtonSecondary
          @onClickSecondaryButton="
            () => {
              changeFilterTrash();
              setRouteName('archivedCalibrages')
              setFilter({ ...filter, ['trashed']: true });
              getData();
            }
          "
            v-if="filter.trashed === false"
            class="archive-link "
            icon="icon-archive-group"
            :text="$t('common.archiveList')"
          />
          <div
          @click="
            () => {
              changeFilterTrash();
              setRouteName('calibrations')
              setFilter({ ...filter, ['trashed']: false });
              getData();
            }
          "
            v-if="filter.trashed === true"
            class="btn-return"
          >
            <i class="icon-return mr-2"></i>
            <p>Tous les calibrages</p>
          </div>

          <div v-if="selectedRows.length > 0" class="bloc-desarchive archv">
            <p>
              <span class="pr-2">{{ selectedRows.length }}</span>
              {{ selectedRows.length > 1 ? "slectionns" : "slectionn" }}
            </p>
            <!-- <ButtonSecondary
              class="btn-archive ml-6"
              icon="icon-archiver"
              :text="filter.trashed ? $t('common.desarchiver') : $t('common.archive')"
              @onClickSecondaryButton="
                () => {
                  handleSelection();
                }
              "
            /> -->
          </div>
        </div>
        <div class="btn-add flex">
          <div class="mr-3">
            <DropDownMenu
              :disabled="selectedRows.length > 0 ? false : true"
              :items="[
                {
                  label: filter.trashed
                    ? $t('common.desarchiver')
                    : $t('common.archive'),
                  onClick: () => {
                    if (selectedRows.length > 0) {
                      handleSelection();
                    }
                  },
                  disabled: selectedRows.length > 0 ? false : true,
                },
              ]"
              icon="icon-down"
              :text="'Actions groupes'"
              :btnId="'btn_dropdown_2'"
              :dropdownId="'dropdownMenu_2'"
              :btnclass="'btn-third'"
            />
          </div>
          <div @click="toggleDrawer">
            <ButtonPrimary
              icon="icon-calendar"
              :text="$t('components.listCalibrage.planification')"
            />
          </div>
        </div>
      </div>

      <div v-if="selectedOptions.length > 0"  class="tags-">
        <div v-for="(selectedOption, index) in selectedOptions" :key="index" class="tag">
          {{ `${selectedOption.name} (${selectedOption.label})` }} 
          <span class="remove-filter" @click="removeSelectedOption(selectedOption)"> <i class="icon-exit-"></i></span>
        </div>
      </div>

    </div>
    <div class="card-table actions-tab mb-5">
      <TableComponent
        ref="calibrageTable"
        :key="switchedPage"
        :isLoading="isFetchingData"
        :multipleSelect="true"
        :hasActions="true"
        :actions="{
          archive: filter.trashed === false,
          cancel: filter.trashed === false,
          update: true,
          unarchive: filter.trashed === true,
        }"
        :onUpdateItem="
          (index, item) => {
            handleUpdate(index, item);
          }
        "
        :onArchiveItem="
          (index, item) => {
            handleArchiveAent(item);
          }
        "
        :onUnArchiveItem="
          (index, item) => {
            handleArchiveAent(item);
          }
        "
        :onCancelItem="
          (index, item) => {
            handleCancelCalibration(item);
          }
        "
        :data="data"
        :columns="columns"
        :redirection="true"
        @row-clicked="
          (item) => {
            handleeClickedRow(item);
          }
        "
        @onSort="handleSort"
        :keySelection="'id'"
        @onSelection="handleSelectedRows"
        :showMoreSelection="'calibrage'"
      />
    </div>
    <Pagination storeModuleName="calibrage" />
    <DrawerCalibrage :isOpened="showDrawer" @close="showDrawer = false" />
    <modal
      :show-modal="dispalyArchiveModal"
      :loading="isTrashLoading"
      :cancelText="$t('views.calibrages.modalBtnleft')"
      :okText="filter.trashed === true ? $t('common.desarchiver') : $t('common.archive')"
      @onConfirm="handleOk"
      @close="handleCancel"
    >
      <template v-slot:title>{{ $t("views.calibrages.modalTitle") }}</template>
      <template v-slot:content>
        <p v-if="selectedRows.length == 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t("views.calibrages.modalUnArchiveOneItemText")
              : $t("views.calibrages.modalArchiveOneItemText")
          }}
          <strong>{{ selectedRows[0].title }}</strong>
        </p>
        <p v-if="selectedRows.length == 1">
          {{
            filter.trashed == true
              ? $t("views.calibrages.modalUnArchiveOneItemQuestion")
              : $t("views.calibrages.modalArchiveOneItemQuestion")
          }}
        </p>
        <p v-if="selectedRows.length > 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t("views.calibrages.modalUnArchiveManyItemsText")
              : $t("views.calibrages.modalArchiveManyItemsText")
          }}
        </p>
        <div style="align-items: left" class="pb-5">
          <ul  v-if="selectedRows.length > 1">
            <li v-for="item of selectedRows" :key="item.id">
          {{ item.title }}
            </li>
          </ul>
        </div>

        <p v-if="selectedRows.length > 1">
          {{
            filter.trashed == true
              ? $t("views.calibrages.modalUnArchiveManyItemsQuestion")
              : $t("views.calibrages.modalArchiveManyItemsQuestion")
          }}
        </p>
      </template>
    </modal>
  </div>
</template>
<script>
import DrawerCalibrage from "@/components/DrawerForm/Calibrage/DrawerCalibrage.vue";
import InputSearch from "@/components/FormWidgets/InputSearch.vue";
import Filtres from "@/components/FormWidgets/Filter/Filter.vue";
// import DateRangePicker from 'vue2-daterange-picker';
import "vue2-daterange-picker/dist/vue2-daterange-picker.css";
import ButtonPrimary from "@/components/FormWidgets/ButtonPrimary.vue";
import TableComponent from "@/components/Table/TableCalibrageCallCenter.vue";
import ButtonSecondary from "@/components/FormWidgets/ButtonSecondary.vue";
import Pagination from "@/components/Table/Pagination.vue";
import Modal from "@/components/Modal/Modal.vue";
import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';
import { mapActions, mapGetters } from "vuex";
import moment from "moment";
export default {
  components: {
    DrawerCalibrage,
    InputSearch,
    Filtres,
    ButtonPrimary,
    ButtonSecondary,
    // DateRangePicker,
    TableComponent,
    Pagination,
    Modal,
    DropDownMenu,
  },
  name: "calibrage",
  data() {
    return {
      showDrawer: false,
      showImportMasse: false,
      dateRange: { startDate: null, endDate: null },
      dispalyArchiveModal: false,
      selectedCaibrage: null,
      archive: true,
      desarchive: false,
      BtnReturn: true,
      selectedRows: [],
      selectedOptions: [],
      updatedFilters: {},
      switchedPage: 1,
      isTrashLoading : false,
      clickedShowMore: false,
    };
  },
  methods: {
    ...mapActions('calibrage', [
      'getData',
      'setModel',
      'setFilter',
      'setPagination',
      'deleteCalibration',
      'setCollection',
      'cancelCalibration',
      "resetState",
    ]),
    ...mapActions('app', ['setRouteName']),
    toggleDrawer() {
      const callCenterId = this.authedUser.callCenterId ? this.authedUser.callCenterId.id : null;

      this.setModel({
        ...this.getModel,
        callCenterId: callCenterId,
        organizerId: this.authedUser.id,
        status: 'in_progress',
      });
      this.showDrawer = !this.showDrawer;
    },
    handleUpdate(index, item) {
       
      this.setModel({
        id: item.id,
        title: item.title,
        meetingLink: item.meetingLink,
        status: item.status,
        // campagneId: item.campagneId.campagneId,
        callCenterId: item.callCenterId ? item.callCenterId.id : null,
        organizerId: item.organizerId.id,
        // gridId: item.gridId,
        // recordId: item.recordId,
        startDate: item.startDate,
        endDate: item.endDate,
        recordCalibrations: item.recordCalibrations.map((item, index) => ({
          index: index,
          id: item.recordId.id,
          name: item.recordId.name,
          firstName: item.recordId.agentId.firstName,
          lastName: item.recordId.agentId.lastName,
          callCenter: item.recordId.agentId.callCenterId ? item.recordId.agentId.callCenterId.name : null,
        })),
        memberCalibrations: item.memberCalibrations.map((item) => ({
          isGuest: item.isQuest,
          email: item.email,
          firstName: item.firstName,
          lastName: item.lastName,
          avatarColor: item.avatarColor,
          errors: {
            email: "",
            firstName: "",
            lastName: "",
          },
        })),
        // calibrationEmails: item.calibrationEmails,
        createdAt: item.createdAt,
        updatedAt: item.updatedAt,
      });

      this.showDrawer = true;
    },
    handleCancelCalibration(item) {
      this.cancelCalibration(item.id);
    },
    getSelectedCount(keys) {
      let totalCount = 0;
      keys.forEach((key) => {
        const selectedFilters = this.getFilter[key];
        totalCount += Array.isArray(selectedFilters) ? selectedFilters.length : 0;
      });
      return totalCount;
    },
    handleSearch(value) {
      this.setPagination({
        ...this.getPagination,
        ["current_page"]: 1,
      });
      this.setFilter({
        ...this.getFilter,
        ["keyword"]: value,
      });
    },
    handleSelectedNamesChanged(selectedOptions) {
      this.selectedOptions = selectedOptions;
    },
    removeSelectedOption(selectedOption) {
      this.selectedOptions = this.selectedOptions.filter(option => option.id !== selectedOption.id || option.key !== selectedOption.key);

      const updatedFilters = {
        campagnes: this.selectedOptions.filter(item => item.key === 'campagnes').map(item => item.id)
      };

      this.handleFilterChanged(updatedFilters);
      this.updatedFilters = updatedFilters;
    },
    handleSort(options) {
      this.setFilter({
        ...this.getFilter,
        ["sortKey"]: options.sortKey,
        ["sortOrder"]: options.sortOrder,
      });
      this.getData();
    },
    ShowDrawerCalibrage() {
      this.showDrawer = !this.showDrawer;
    },
    logEvent(selected, value) {
      if (selected == "start") {
        this.setFilter({
          ...this.getFilter,
          ["startDate"]: moment(value).format("YYYY-MM-DD"),
          ["endDate"]: null,
        });
        this.getData();
      } else {
        this.setFilter({
          ...this.getFilter,
          ["endDate"]: moment(value).format("YYYY-MM-DD"),
        });
        this.getData();
      }
    },
    handleArchiveAent(calibrage) {
      this.selectedCalibrage = calibrage;
      this.selectedRows = [calibrage];
      this.$refs.calibrageTable.setSelectedRows([calibrage]);
      this.dispalyArchiveModal = true;
    },
    handleCancel() {
      // this.$refs.calibrageTable.setSelectedRows([]);
      // this.selectedRows = [];
      this.dispalyArchiveModal = false;
    },
    handleOk() {
      this.isTrashLoading = true
      this.deleteCalibration(this.selectedRows.map((x) => x.id)).then(() => {
        this.$refs.calibrageTable.setSelectedRows([]);
        this.selectedRows = [];
        this.dispalyArchiveModal = false;
        this.isTrashLoading = false;
      });
    },
    fetchMembreCalibration(membreCalibration) {
      let invited = "";

      membreCalibration.forEach((element) => {
        let firstName = "";
        let lastName = "";

        if (!element.email) {
          firstName = element.userId.firstName[0];
          lastName = element.userId.lastName[0];
        } else {
          firstName = element.firstName[0];
          lastName = element.lastName[0];
        }

        invited += firstName + "" + lastName + " ";
      });

      return invited.trim().toUpperCase();
    },
    handleeClickedRow(item) {
      this.setModel({
        id: item.id,
        recordCalibrations: item.recordCalibrations.map((item, index) => ({
          index: index,
          id: item.recordId.id,
          name: item.recordId.name,
          firstName: item.recordId.agentId.firstName,
          lastName: item.recordId.agentId.lastName,
          callCenter: item.recordId.agentId.callCenterId ? item.recordId.agentId.callCenterId.name : null,
        })),
      });
      this.$router.push({ name: "compterendu", params: { id: item.id } });
    },
    handleFilterChanged(options) {
      this.setFilter({
        ...this.getFilter,
        ["campagne"]: options.campagnes,
      });
      this.getData();
    },
    handleSelectedRows(data) {
      this.selectedRows = data;
    },
    handleSelection() {
      this.dispalyArchiveModal = true;
    },
    changeFilterTrash() {
      this.$store.commit('calibrage/RESET_PAGINATION');
      this.switchedPage++;
      this.$refs.calibrageTable.setSelectedRows([]);
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ['trashed']: !this.filter.trashed });
      this.getData();
      if (this.filter.trashed === true) this.setRouteName('archivedCalibrages');
      else this.setRouteName('calibrations');
    },
    displayArchive() {
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ["trashed"]: true });
      this.$refs.calibrageTable.setSelectedRows([]);
      this.getData();
    },
    displayActifCalibrages() {
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ["trashed"]: false });
      this.$refs.calibrageTable.setSelectedRows([]);
      this.getData();
    },
  },
  async created() {
    await this.resetState();
  },
  mounted() {
    this.getData();
    this.$store.dispatch("user/setFilter", {
      ...this.$store.getters["user"],
      ["withPagination"]: 0,
    });
    this.$store.dispatch("user/getData");
    this.setCollection([]);
  },
  watch: {
    "filter.keyword": function () {
      this.setFilter({
        ...this.getFilter,
        ["current_page"]: 1,
      });
      this.getData();
    },
  },

  computed: {
    ...mapGetters("calibrage", [
      "getCollection",
      "getModel",
      "getFilter",
      "getPagination",
      "getIsFetching",
    ]),
    ...mapGetters("user", ["getUser"]),
    isFetchingData() {
      return this.getIsFetching;
    },
    selectedCount() {
      return this.getSelectedCount(["campagne"]);
    },
    authedUser() {
      return this.getUser;
    },
    columns() {
      return [
        {
          key: "title",
          label: "Opration",
          // render: (text, record) => {
          //   return `<span>${this.$globals.capitalizeFirstLetter(record.title)}</span>`;
          // },
          sort: true,
          sortKey: "title",
        },
        {
          key: "recordCalibrations",
          label: this.$globals.capitalizeFirstLetter(this.$t("common.records")),
          // render: (text, record) => {
          //   if (
          //     Array.isArray(record.recordCalibrations) &&
          //     record.recordCalibrations.length > 0
          //   ) {
          //     let html = '<div style="display: flex;">';

          //       html += `<div>   <span class="actif mr-1"></span> ${record.recordCalibrations.length}</div>`;

          //     html += "</div>";
          //     return html;
          //   } else {
          //     return "<div>Aucun enregistrements</div>";
          //   }
          // },
        },
        {
          key: 'memberCalibrations',
          label: 'Participants',
          sort: false,
        //   render: (text, record) => {
        //     if (
        //       Array.isArray(record.memberCalibrations) &&
        //       record.memberCalibrations.length > 0
        //     ) {
        //       let html = '<div style="display: flex; flex-wrap:wrap">';

        //       for (let member of record.memberCalibrations) {
        //         let firstName = "";
        //         let lastName = "";

        //         if (member.email) {
        //           firstName = member.firstName?.[0] ?? "";
        //           lastName = member.lastName?.[0] ?? "";
        //         } else {
        //           firstName = "";
        //           lastName = "";
        //         }

        //         html += `
        //   <div
        //     style="background-color: ${member.avatarColor};"
        //     class="letter-avatar mr-1 mb-1 "
        //   >
        //     ${firstName.charAt(0).toUpperCase() + lastName.charAt(0).toUpperCase()}
        //   </div>
        // `;
        //       }

        //       html += "</div>";
        //       return html;
        //     } else {
        //       return "<div>Aucun compte</div>";
        //     }
        //   },
        },
        {
          key: "startDate",
          label: "Rendez-vous",
          // render: (text, record) => {
          //   return `<span>${this.$globals.formatDate(record.startDate)}</span>`;
          // },
          sort: true,
          sortKey: "start_date",
        },
        {
          key: "status",
          label: "tat",
          // render: (text, record) => {
          //   let statusText = "";
          //   let statusClass = "";

          //   if (record.status === "in_progress") {
          //     statusText = "En cours";
          //     statusClass = "orange";
          //   } else if (record.status === "finished") {
          //     statusText = "Termin";
          //     statusClass = "green";
          //   } else if (record.status === "cancel") {
          //     statusText = "Annuler";
          //     statusClass = "cancelcolor";
          //   }

          //   return `<div class="custom-status ${statusClass}">${statusText}</div>`;
          // },
          sort: true,
          sortKey: "status",
        },
      ];
    },
    data() {
      return this.getCollection;
    },
    filter() {
      return this.getFilter;
    },
    campagnes() {
      return this.$store.getters["campaign/getCollection"].map((element) => ({
        id: element.campagneId,
        name: element.name,
      }));
    },
  },
};
</script>
