<template>
  <div v-if="record && record.segments" class="segmentations-container">
    <div class="mb-4 agent-segments">
      <div class="avatar mr-3">
        <div class="icon_bold">
          <i class="icon-headphones-with-mic"></i>
        </div>
      </div>

      <div
        ref="speechTraceContainer"
        v-if="record.segments.length > 0"
        class="agent-progress"
      >
        <div @click="handleClick" class="progress-mask"></div>
        <div
          id="progressAgent"
          :style="{
        
            width: `3.8px`,
          }"
        ></div>
        <div v-for="(segment, index) of record.segments" :key="index">
          <div
            class="agent-animated-line"
            v-if="segment.speaker == 'agent'"
            :style="{
              background : record.insultingWords?.expressions_insulte_trouves?.find( x => segment?.speatch?.includes(x)  ) ? 'red !important' : '',
              width: getsegmentWidth(segment.period),
            }"
          ></div>
          <div
            v-else
            :style="{
              width: getsegmentWidth(segment.period),
            }"
            class="empty-line"
          ></div>
        </div>
      </div>
    </div>

    <div class="mb-4 prospect-segments">
      <div class="avatar mr-3">
        <div class="icon_bold"><i class="icon-profil"></i></div>
      </div>

      <div v-if="record.segments.length > 0" class="prospect-progress">
        <!-- <div
          ref="progress"
          id="progressProspect"
          :style="{ width: `${secondWidth}px` }"
        ></div> -->
        <div v-for="(segment, index) of record.segments" :key="index">
          <div
            class="prospect-animated-line"
            v-if="segment.speaker == 'prospect'"
            :style="{
              background : record.insultingWords?.expressions_insulte_trouves?.find( x => segment?.speatch?.includes(x)  ) ? 'red !important' : '',
              width: getsegmentWidth(segment.period),
            }"
          ></div>
          <div
            v-else
            :style="{
              width: getsegmentWidth(segment.period),
            }"
            class="empty-line"
          ></div>
        </div>
      </div>
    </div>
    <div class="content-detect mt-5" v-if="insulte">
      <h6>Insultes dtects :</h6>
      <div class="insulte-detect detect mt-3">
        <div class="mot red">Fils de chien</div>
        <div class="mot">Va te faire foutre</div>
        <div class="mot">Connard</div>
      </div>
    </div>

    <div class="content-detect mt-5" v-if="langue">
      <h6>Langues dtects :</h6>
      <div class="insulte-detect detect mt-3">
        <div class="mot red">Franais</div>
        <div class="mot">Chinois</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['record', 'playDuration'],
  components: {
  },
  data() {
    return {
      traceContainerWidth: 0,
      secondWidth: 0,
      progessLeftPosition: 0,
      insulte: false,
      langue: false,
      loadingDuration: false,
    };
  },
  mounted() {
    const container = this.$refs.speechTraceContainer;
    if (container) {
      this.traceContainerWidth = container.offsetWidth;
      this.secondWidth = container.offsetWidth / this.record.duration;
    }
    const progressElement = this.$refs.progress;
    if (progressElement) {
      this.progessLeftPosition = progressElement.offsetLeft;
    }
  },
  methods: {
    getsegmentWidth(period) {
      return `${period * this.secondWidth}px`;
    },
    handleClick(event) {
      const rect = event.target.getBoundingClientRect();
      const clickX = event.clientX - rect.left;
      const playDuration = (clickX  / this.secondWidth) ;
      this.$emit('plyaDUrationChanged', playDuration + 1);
    },
  },
  computed: {
    recordTotalDuration() {
      let total = 0;
      for (let item of this.record.segments) {
        total = total + (item.end_time - item.start_time);
      }

      return total;
    },
    period() {
      let total = 0;
      for (let item of this.record.segments) {
        total = total + Number(item.period);
      }

      return total;
    },
  },
  watch: {
    playDuration(val) {
      const pourcentage = (val / this.record.duration) * 100;
      const segmentsDurationPart = (pourcentage / 100) * this.record.duration;
      const segmentDurationWidth =
        segmentsDurationPart * this.secondWidth  - this.secondWidth ;

      const agentProgress = document.getElementById('progressAgent');
      if (agentProgress) {
        agentProgress.style.left = `${segmentDurationWidth}px`;
      }
      const prospectProgress = document.getElementById('progressProspect');
      if (prospectProgress) {
        prospectProgress.style.left = `${segmentDurationWidth}px`;
      }
    },
    traceContainerWidth(val) {
      this.secondWidth = val / this.record.duration;
    },
    'record.id'(val) {
      this.secondWidth = this.traceContainerWidth / this.record.duration;
    },
  },
};
</script>

