<template>
  <div ref="componentContainer">
    <template v-for="(categorie, index) in record.grid">
      <div
        :id="'category' + index"
        class="content_scrore content__ mt-5"
        :key="categorie.id"
      >
        <div
          @click="
            () => {
              activeCategory = index;
              scrollToRef();
            }
          "
          :ref="'catRef' + index"
          class="panel_header content"
        >
          <div class="title">{{ categorie.name }}</div>
          <div class="note">
            {{ selectedCategory === categorie.id && selectedMember ?
        formatScore(calculateMemberScore(categorie, selectedMember)) : (calculateAverageCategoryScore(categorie) ?? '-') }} / {{ categorie.totalScore }}
          </div>
        </div>
        
        
        <!-- <div class="total-score">
            Total Score: {{ calculateTotalCategoryScore(categorie) }} / {{ categorie.totalScore }}
          </div>
          <div class="average-score">
            Average Score: {{ calculateAverageCategoryScore(categorie) }} / {{ categorie.totalScore }}
          </div> -->


        <template v-if="activeCategory == index">
          <div class="content___">
            <template v-for="item in categorie.items">
              <div class="panel_body" :key="item.id">
                <div class="content_status">
                  <p class="text-regular">
                    {{ item.name }} : {{ item.description }}
                  </p>
                  <div class="flex mt-5">
                    <div class="status list_status">
                      <template v-for="note in item.notes">
                        <div class="flexbox" :key="note.id">
                          <div class="flex item">
                            <h6>{{ note.name }}</h6>
                            <div
                              @click="handleShowNote(note)"
                              class="tooltip ml-4"
                              v-if="note.meaning != null"
                            >
                              <i
                                v-if="
                                  selectedNote != null &&
                                  selectedNote?.id == note.id
                                "
                                class="icon-info-bold"
                              ></i>
                              <i v-else class="icon-info-light"></i>
                            </div>
                          </div>
                          <div class="main-account flex">
                            <!-- <template
                              v-for="(evaluation, index) in record.evaluations"
                            > -->
                            <div v-for="(evaluation, index) in record.evaluations" :key="index">
                              <!-- checkscorigvoted(note.id, item) -->
                              <div v-if="checkscorigvoted(note.id, evaluation.eval)">
                                <div @click="selectMember(categorie.id, evaluation)" class="mr-2">
                                  <Account
                                    :firstName="evaluation.user.firstName"
                                    :lastName="evaluation.user.lastName"
                                    :bgColor="evaluation.user.avatarColor"
                                    :key="index + '_avatar'"
                                    :class="{ 'selected-member': selectedMember === evaluation && selectedCategory === categorie.id }"
                                  />
                                </div>
                              </div>
                            </div>
                            <!-- </template> -->
                          </div>
                        </div>
                      </template>

                      <template
                        v-for="(evaluation, index) in record.evaluations"
                      >
                        <!-- checkscorigvoted(note.id, item) -->

                        <div
                          v-for="evalitem of evaluation.eval"
                          :key="evalitem.id + '_details_account'"
                          class="details_account mt-5"
                        >
                          <div
                            class="flexbox"
                            v-if="
                              evalitem.commentaire != null &&
                              evalitem.item_id == item.id
                            "
                          >
                            <Account
                              :firstName="evaluation.user.firstName"
                              :lastName="evaluation.user.lastName"
                              :bgColor="evaluation.user.avatarColor"
                              :key="index + '_avatar'"
                            />
                            <div
                              class="text_"
                              :style="
                                'color:' +
                                evaluation.user.avatarColor +
                                ';' +
                                'background:' +
                                evaluation.user.avatarColor +
                                '10' +
                                ';' +
                                'border:1px solid' +
                                evaluation.user.avatarColor
                              "
                              :key="'comments_members_' + index"
                            >
                              {{ evalitem.commentaire }}
                            </div>
                          </div>
                        </div>
                      </template>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
          <div class="panel_footer" :key="categorie.id + '_btn'">
            <button
              @click="
                () => {
                  activeCategory--;
                  scrollToRef();
                }
              "
              v-if="
                activeCategory > 0 && activeCategory < record.grid.length - 1
              "
              class="prev mr-3"
              id="prev2"
            >
              Prcdent
            </button>
            <button
              @click="
                () => {
                  activeCategory++;
                  scrollToRef();
                }
              "
              v-if="activeCategory < record.grid.length - 1"
              class="next"
              id="next1"
            >
              Continuer
            </button>
            <!-- <button
              @click="
                () => {
                  activeCategory--;
                  scrollToRef();
                }
              "
              v-if="activeCategory == record.grid.length - 1"
              class="next"
               id="prev3"
            >
              Prcdent
            </button> -->
            <button
              @click="
                () => {
                  activeCategory--;
                  scrollToRef();
                }
              "
              v-if="activeCategory == record.grid.length - 1 && record.grid.length > 1"
              class="prev"
              id="prev3"
            >
              Prcdent
            </button>
          </div>
        </template>
      </div>
    </template>
    <DrawerInfo
      :isOpened="showDrawer"
      @close="
        () => {
          showDrawer = false;
          setSelectedNote(null);
        }
      "
    ></DrawerInfo>
  </div>
</template>

<script>
import DrawerInfo from '@/components/DrawerForm/Records/DrawerInfo.vue';
import Account from '@/components/FormWidgets/AvatarColorsRandom/AvatarColor.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  props: ['record'],
  components: {
    Account,
    DrawerInfo,
  },
  data() {
    return {
      showDrawer: false,
      activeSection: 1,
      activeCategory: 0,
      buttonContent: '<i class="icon-info-light"></i>',
      selectedMember: null,
      selectedCategory: null,
    };
  },
  watch: {
    'record.id': function(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.activeCategory = 0;
      }
    }
  },
  methods: {
    ...mapActions('record', ['setSelectedNote']),
    toggleAccordion(section) {
      this.activeSection = this.activeSection === section ? null : section;
    },
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },
    handleShowNote(note) {
      this.setSelectedNote(note);
      this.toggleDrawer();
    },
    scrollToRef() {
      const targetElement = this.$refs['catRef' + this.activeCategory][0];
      if (targetElement) {
        targetElement.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
          inline: 'nearest',
        });
      }
    },
    checkscorigvoted(noteId, userEvaluations) {
      return userEvaluations.find((item) => item.note_id == noteId);
    },

    checkCommentairevoted(id, member) {
      if (this.record.evaluation_user != undefined) {
        let evals = this.record.evaluation_user.eval;
        const result =
          evals &&
          evals.find((item) => item.item_id === id && item.commentaire != null);
        if (result != undefined) {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },

    getCommentaireMemberVoted(id, member) {
      if (this.record.evaluation_user != undefined) {
        let evals = this.record.evaluation_user.eval;
        const result = evals && evals.find((item) => item.item_id === id);
        if (result != undefined) {
          return result.commentaire;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    getScoringUser(id) {
      if (
        this.record.evaluation_user != undefined &&
        this.record.evaluation_user.score_category != undefined
      ) {
        let score_category = this.record.evaluation_user.score_category;
        let hasKey = score_category.hasOwnProperty(id);
        if (hasKey) {
          return score_category[id];
        } else {
          return 0;
        }
      }
    },
    selectMember(categoryId, member) {
      if (this.selectedMember === member && this.selectedCategory === categoryId) {
        this.selectedMember = null;
        this.selectedCategory = null;
      } else {
        this.selectedMember = member;
        this.selectedCategory = categoryId;
      }
      this.$emit('member-selected', { categoryId, member });
    },
    formatScore(score) {
      return score >= 0 ? `${score}` : `${score}`;
    },
    calculateMemberScore(categorie, member) {
      return categorie.items.reduce((score, item) => {
        const memberEvaluations = member.eval.filter(evaluation => evaluation.item_id === item.id);
        return score + item.notes.reduce((noteScore, note) => {
          const matchingEvaluation = memberEvaluations.find(evaluation => evaluation.note_id === note.id);

          if (matchingEvaluation) {
            const noteValue = parseInt(note.score, 10);
            if (!isNaN(noteValue)) {
              return noteScore + noteValue;
            }
          }
          return noteScore;
        }, 0);
      }, 0);
    },
    calculateTotalCategoryScore(categorie) {
      const totalScore = this.record.evaluations.reduce((totalScore, evaluation) => {
        const memberScore = this.calculateMemberScore(categorie, evaluation);
        return totalScore + memberScore;
      }, 0);

      this.$emit('update-total-score', totalScore);
      return totalScore;
    },
    calculateAverageCategoryScore(categorie) {
      const totalScore = this.calculateTotalCategoryScore(categorie);
      const numberOfMembers = this.record.evaluations.length;
      const average = numberOfMembers > 0 ? (totalScore / numberOfMembers).toFixed(1) : '0.0';
      return average === '0.0' ? '-' : average;
    },
    handleClickOutside(event) {
      if (this.$refs.componentContainer && !this.$refs.componentContainer.contains(event.target)) {
        this.selectedMember = null;
        this.selectedCategory = null;
      }
    }
  },

  mounted() {
    document.addEventListener('mousedown', this.handleClickOutside);
    // $(document).ready(function () {
    //   $('.panel_header').click(function () {
    //     var target = $(this).data('toggle');
    //     // Remove the "active" class from all buttons
    //     $('.panel_header').removeClass('active');
    //     // Remove the "active" class from all toggle elements
    //     $('.panel_body').removeClass('active');
    //     // Close all other toggle elements
    //     $('.panel_body')
    //       .not('#' + target)
    //       .slideUp();
    //     // Toggle the target element
    //     $('#' + target).slideToggle();
    //     // Add the "active" class to the clicked button
    //     $(this).addClass('active');
    //     // Add the "active" class to the target element
    //     $('#' + target).toggleClass('active');
    //   });
    // });
  },
  beforeDestroy() {
    document.removeEventListener('mousedown', this.handleClickOutside);
  },
  computed: {
    ...mapGetters('user', ['getUser']),
    ...mapGetters('record', ['getSelectedNote']),
    user() {
      return this.getUser;
    },
    selectedNote() {
      return this.getSelectedNote;
    },
  },
};
</script>
