<template>
  <div class="date-range-picker date_comparision" id="date_comparision">
    <div class="compare-date flex" id="compare_date">
      <div class="flex bold pb-1">
        <p>{{ dateRange.startNewDate }} -&nbsp;</p>
        <p>{{ dateRange.endNewDate }}</p>
      </div>
      <div class="flex">
        <p>
          <span class="txt_gris">
            {{
              $globals.capitalizeFirstLetter(
                $t('components.dateRange.compared to'),
              )
            }}
          </span>
          :&nbsp;
        </p>
        <p>{{ dateRange.startOldDate }} -&nbsp;</p>
        <p @click="one">{{ dateRange.endOldDate }}</p>
      </div>
    </div>
    <date-range-picker
      ref="picker"
      :toggle="opened"
      v-model="dateRange"
      @start-selection="sDate"
      @finish-selection="eDate"
      :locale-data="getLocaleData()"
      :timePicker="withTimePicker"
    >
      <!--    header slot-->
      <div slot="header" slot-scope="header" class="slot">
        <span v-if="header.in_selection">
          <!-- {{
            $globals.capitalizeFirstLetter(
              $t('components.dateRange.selectDateEnd'),
            )
          }} -->
        </span>
      </div>
      <!--    date slot-->
      <template #date="data">
        <span class="small">{{ data.date | dateCell }}</span>
      </template>

      <!--    ranges (new slot syntax) -->
      <template #ranges="">
        <div class="ranges">
          <div class="list-select">
            <h5>
              {{
                $globals.capitalizeFirstLetter(
                  $t('components.dateRange.dateRange'),
                )
              }}
            </h5>
            <select name="" id="" @change="handleChange($event.target.value)">
              <option value="todayToYesterday">
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.dateRange.ranges.Today'),
                  )
                }}
                &#10132;
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.dateRange.ranges.Yesterday'),
                  )
                }}
              </option>
              <option value="todayToDayBeforeYesterday">
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.dateRange.ranges.Today'),
                  )
                }}
                &#10132;
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.dateRange.ranges.day_before_Yesterday'),
                  )
                }}
              </option>
              <option value="thisMonthToLastMonth">
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.dateRange.ranges.This month'),
                  )
                }}
                &#10132;
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.dateRange.ranges.Last month'),
                  )
                }}
              </option>
              <option value="thisYearToPreviousYear">
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.dateRange.ranges.This year'),
                  )
                }}
                &#10132;
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.dateRange.ranges.Previous year'),
                  )
                }}
              </option>
              <option value="" selected>
                {{
                  $globals.capitalizeFirstLetter(
                    $t('components.dateRange.customized'),
                  )
                }}
              </option>
            </select>
          </div>
          <div class="compare-group">
            <h5>
              {{
                $globals.capitalizeFirstLetter(
                  $t('components.dateRange.compare'),
                )
              }}
            </h5>
            <div class="list-compare">
              <input v-model="dateRange.startNewDate" />
              -
              <input v-model="dateRange.endNewDate" />
            </div>
            <h5 class="">
              {{
                $globals.capitalizeFirstLetter($t('components.dateRange.vs'))
              }}
            </h5>
            <div class="list-compare">
              <input v-model="dateRange.startOldDate" />
              -
              <input v-model="dateRange.endOldDate" />
            </div>
          </div>

          <div class="align__"></div>
          <div class="btn-footer">
            <button class="cancel mr-3" @click="cancelSelection()">
              {{
                $globals.capitalizeFirstLetter(
                  $t('components.dateRange.cancel'),
                )
              }}
            </button>
            <button
              id="btn_close"
              class="btn-save btn-primary"
              @click="applySelection()"
            >
              {{
                $globals.capitalizeFirstLetter($t('components.dateRange.apply'))
              }}
            </button>
          </div>

          <!-- <div class="list-container">
    <ul class="list" @click="toggleList">
  <li class="list-item">{{ selectedOption }}</li>
  <ul v-if="showOptions" class="list">
    <li class="list-item" v-for="(option, index) in options" :key="index" @click="selectOption(option)">{{ option }}</li>
  </ul>
</ul>
</div> -->
        </div>
      </template>
      <!--    footer slot-->
      <!-- <div slot="footer" slot-scope="data" class="slot drp-buttons">
        <div class="align__">
          <input v-model="dateRange.startDate" />
          -
          <input v-model="dateRange.endDate" />
        </div>
        <div class="cancelBtn">
          <button @click="cancelSelection(data)">
            {{
              $globals.capitalizeFirstLetter($t('components.dateRange.cancel'))
            }}
          </button>
        </div>
        <div
          :class="{
            'applyBtn btn btn-sm': true,
            'btn-fieldError':
              dateRange.startDate === null || dateRange.endDate === null
                ? 'btn-success'
                : dateRange.startDate !== null && dateRange.endDate !== null,
          }"
        >
          <button @click="applySelection(data)">
            {{
              $globals.capitalizeFirstLetter($t('components.dateRange.apply'))
            }}
          </button>
        </div>
      </div> -->
    </date-range-picker>
  </div>
</template>

<script>
import $ from 'jquery';
import DateRangePicker from 'vue2-daterange-picker';
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css';
import { mapActions } from 'vuex';

export default {
  name: 'SlotsDemo',
  components: { DateRangePicker },
  props: {
    withTimePicker: Boolean,
  },

  data() {
    // Get today's date
    const today = new Date();

    // Calculate the date one week ago
    const oneWeekAgo = new Date();
    oneWeekAgo.setDate(today.getDate() - 7);
    const twoWeekAgo = new Date();
    twoWeekAgo.setDate(oneWeekAgo.getDate() - 1);
    const starttwoWeekAgo = new Date();
    starttwoWeekAgo.setDate(twoWeekAgo.getDate() - 7);

    // Format the date as a string in dd/mm/yyyy format
    const formatDate = (date) => {
      const day = ('0' + date.getDate()).slice(-2);
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const year = date.getFullYear();
      return `${year}/${month}/${day}`;
    };
    const formatDateForCompare = (date) => {
      const day = ('0' + date.getDate()).slice(-2);
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const year = date.getFullYear();
      return `${day}/${month}/${year}`;
    };

    const startDate = formatDate(oneWeekAgo);
    const endDate = formatDate(today);
    const startDateForCompare = formatDateForCompare(starttwoWeekAgo);
    const endDateForCompare = formatDateForCompare(twoWeekAgo);
    return {
      startDate: startDate,
      endDate: endDate,
      startDateForCompare: startDateForCompare,
      endDateForCompare: endDateForCompare,
      showOptions: false,
      SecondSelected: false,
      dateRange: {
        startDate: startDate,
        endDate: endDate,
        startNewDate: startDateForCompare,
        endNewDate: endDateForCompare,
        startOldDate: startDateForCompare,
        endOldDate: endDateForCompare,
      },
      opened: false,
    };
  },
  filters: {
    dateCell(value) {
      return new Date(value).getDate();
    },
    // date(val) {
    //   return val ? val.toLocaleString() : '';
    // },
  },
  watch: {},
  methods: {
    formatDate(date) {
      const day = ('0' + date.getDate()).slice(-2);
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const year = date.getFullYear();
      return `${year}/${month}/${day}`;
    },
    formatDateString(dateString) {
      const date = new Date(dateString);
      const day = ('0' + date.getDate()).slice(-2);
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const year = date.getFullYear();
      return `${day}/${month}/${year}`;
    },

    updateDateRange() {
      const query = this.$route.query;
      if (
        query &&
        query.startNewDate &&
        query.endNewDate &&
        query.startOldDate &&
        query.endOldDate
      ) {
        this.dateRange = {
          startDate: this.formatDateString(query.startNewDate),
          endDate: this.formatDateString(query.endNewDate),
          startNewDate: this.formatDateString(query.startNewDate),
          endNewDate: this.formatDateString(query.endNewDate),
          startOldDate: this.formatDateString(query.startOldDate),
          endOldDate: this.formatDateString(query.endOldDate),
        };
      }
    },

    one() {
      this.$refs.picker.togglePicker();
    },
    formatDateForCompare(date) {
      const day = ('0' + date.getDate()).slice(-2);
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const year = date.getFullYear();
      return `${day}/${month}/${year}`;
    },
    handleTgolehandle(show, events) {},
    handleAgentList() {
      this.$emit('applyed', this.dateRange);
    },
    toggleList() {
      this.showOptions = !this.showOptions;
    },
    selectOption(option) {
      this.selectedOption = option;
      this.showOptions = false;
      this.toggleList();
    },

    getLocaleData() {
      return {
        direction: 'rtl',
        format: 'dd/mm/yyyy',
        separator: ' - ',
        weekLabel: this.$t('components.dateRange.weekLabel'),
        customRangeLabel: this.$t('components.dateRange.customRangeLabel'),
        daysOfWeek: this.$t('components.dateRange.daysOfWeek'),
        monthNames: this.$t('components.dateRange.monthNames'),
        firstDay: 0,
      };
    },
    applySelection() {
      // Format the dates before emitting the event
      const formatDate = (dateString) => {
        const [year, month, day] = dateString.split('/');
        return `${day}-${month}-${year}`;
      };

      const formattedDateRange = {
        startNewDate: formatDate(this.dateRange.startNewDate),
        endNewDate: formatDate(this.dateRange.endNewDate),
        startOldDate: formatDate(this.dateRange.startOldDate),
        endOldDate: formatDate(this.dateRange.endOldDate),
      };

      this.$emit('applyed', formattedDateRange);
      var click_auto = document.getElementById('date_comparision');

      click_auto.click();
    },
    cancelSelection() {
      this.SecondSelected = false;
      this.dateRange.startDate = this.startDate;
      this.dateRange.endDate = this.endDate;
      this.dateRange.startNewDate = this.startDateForCompare;
      this.dateRange.endNewDate = this.endDateForCompare;
      this.dateRange.startOldDate = this.startDateForCompare;
      this.dateRange.endOldDate = this.endDateForCompare;
      this.$emit('cancel');

      var click_auto = document.getElementById('date_comparision');
      click_auto.click();
    },

    formatDateInput(value) {
      const dateString = value;
      const date = new Date(dateString);
      const formattedDate = `${date.getDate().toString().padStart(2, '0')}/${(
        date.getMonth() + 1
      )
        .toString()
        .padStart(2, '0')}/${date.getFullYear()}`;
      return formattedDate;
    },
    sDate(value) {
      this.dateRange.endDate = null;

      this.dateRange.startDate = value;
      if (this.SecondSelected) {
        this.dateRange.startOldDate = this.formatDateInput(
          this.dateRange.startDate,
        );
      } else {
        this.dateRange.startNewDate = this.formatDateInput(
          this.dateRange.startDate,
        );
      }
    },
    eDate(value) {
      let dateSelected = value;
      this.dateRange.endDate = dateSelected;
      if (this.dateRange.endDate < this.dateRange.startDate) {
        this.dateRange.endDate = this.dateRange.startDate;

        this.dateRange.startDate = dateSelected;
      }
      if (this.SecondSelected) {
        this.dateRange.startOldDate = this.formatDateInput(
          this.dateRange.startDate,
        );
        this.dateRange.endOldDate = this.formatDateInput(
          this.dateRange.endDate,
        );
        this.SecondSelected = false;
      } else {
        this.dateRange.startNewDate = this.formatDateInput(
          this.dateRange.startDate,
        );
        this.dateRange.endNewDate = this.formatDateInput(
          this.dateRange.endDate,
        );
        this.SecondSelected = true;
      }
    },

    handleChange(selectedValue) {
      // Get today's date
      const today = new Date();

      // Perform actions based on the selected value
      switch (selectedValue) {
        case 'todayToYesterday':
          // Calculate yesterday's date
          const yesterday = new Date(today);
          yesterday.setDate(today.getDate() - 1);

          // Format the dates
          const endTodayToYesterday = this.formatDate(today);
          const startOldTodayToYesterday = this.formatDateForCompare(yesterday);
          const endOldTodayToYesterday = this.formatDateForCompare(today);

          // Update dateRange object
          this.dateRange = {
            startDate: endTodayToYesterday,
            endDate: endTodayToYesterday,
            startNewDate: endOldTodayToYesterday,
            endNewDate: endOldTodayToYesterday,
            startOldDate: startOldTodayToYesterday,
            endOldDate: startOldTodayToYesterday,
          };
          break;

        case 'todayToDayBeforeYesterday':
          // Calculate day before yesterday's date
          const dayBeforeYesterday = new Date(today);
          dayBeforeYesterday.setDate(today.getDate() - 2);

          // Format the dates
          const endTodayToDayBeforeYesterday = this.formatDate(today);
          const startOldTodayToDayBeforeYesterday =
            this.formatDateForCompare(dayBeforeYesterday);
          const endOldTodayToDayBeforeYesterday =
            this.formatDateForCompare(today);

          // Update dateRange object
          this.dateRange = {
            startDate: endTodayToDayBeforeYesterday,
            endDate: endTodayToDayBeforeYesterday,
            startNewDate: endOldTodayToDayBeforeYesterday,
            endNewDate: endOldTodayToDayBeforeYesterday,
            startOldDate: startOldTodayToDayBeforeYesterday,
            endOldDate: startOldTodayToDayBeforeYesterday,
          };
          break;

        case 'thisMonthToLastMonth':
          // Get the first day of the current month
          const firstDayThisMonth = new Date(
            today.getFullYear(),
            today.getMonth(),
            1,
          );

          // Get the last day of the current month
          const lastDayThisMonth = new Date(
            today.getFullYear(),
            today.getMonth() + 1,
            0,
          );

          // Get the first day of the last month
          const firstDayLastMonth = new Date(
            today.getFullYear(),
            today.getMonth() - 1,
            1,
          );

          // Get the last day of the last month
          const lastDayLastMonth = new Date(
            today.getFullYear(),
            today.getMonth(),
            0,
          );

          // Format the dates
          const startThisMonthToLastMonth = this.formatDate(firstDayThisMonth);
          const endThisMonthToLastMonth = this.formatDate(lastDayThisMonth);
          const starNewtThisMonthToLastMonth =
            this.formatDateForCompare(firstDayThisMonth);
          const endNewThisMonthToLastMonth =
            this.formatDateForCompare(lastDayThisMonth);
          const startOldThisMonthToLastMonth =
            this.formatDateForCompare(firstDayLastMonth);
          const endOldThisMonthToLastMonth =
            this.formatDateForCompare(lastDayLastMonth);

          // Update dateRange object
          this.dateRange = {
            startDate: startThisMonthToLastMonth,
            endDate: endThisMonthToLastMonth,
            startNewDate: starNewtThisMonthToLastMonth,
            endNewDate: endNewThisMonthToLastMonth,
            startOldDate: startOldThisMonthToLastMonth,
            endOldDate: endOldThisMonthToLastMonth,
          };
          break;

        case 'thisYearToPreviousYear':
          // Get the current year
          const currentYear = today.getFullYear();

          // Get the first day of the current year
          const firstDayThisYear = new Date(currentYear, 0, 1);

          // Get the last day of the current year
          const lastDayThisYear = new Date(currentYear, 12, 0);

          // Get the first day of the previous year
          const firstDayLastYear = new Date(currentYear - 1, 0, 1);

          // Get the last day of the previous year
          const lastDayLastYear = new Date(currentYear - 1, 12, 0);

          // Format the dates
          const startThisYearToLastYear = this.formatDate(firstDayThisYear);
          const endThisYearToLastYear = this.formatDate(lastDayThisYear);
          const startNewThisYearToLastYear =
            this.formatDateForCompare(firstDayThisYear);
          const endNewThisYearToLastYear =
            this.formatDateForCompare(lastDayThisYear);
          const startOldThisYearToLastYear =
            this.formatDateForCompare(firstDayLastYear);
          const endOldThisYearToLastYear =
            this.formatDateForCompare(lastDayLastYear);

          // Update dateRange object
          this.dateRange = {
            startDate: startThisYearToLastYear,
            endDate: endThisYearToLastYear,
            startNewDate: startNewThisYearToLastYear,
            endNewDate: endNewThisYearToLastYear,
            startOldDate: startOldThisYearToLastYear,
            endOldDate: endOldThisYearToLastYear,
          };
          break;

        default:
          // Handle default action or no action
          break;
      }
    },
  },
  mounted() {
    var click_auto_date = document.getElementById('compare_date');
    var click_date = document.getElementById('date_comparision');

    //   $(document).ready(function () {
    //     $("#compare_date").on( "click", function() {
    //     $("#date_comparision").click();
    // } );
    //   });
    // this.$refs.picker.togglePicker()
    this.updateDateRange();
  },
};
</script>
