<template>
  <div class="content_page page_utilisateurs">
    <div class="header_page">
      <div class="content-filter">
        <InputSearch
          @input-value="handleSearch"
          class="search-field"
          :placeholder="$t('components.listUser.search')"
        />
        <ButtonSecondary
          @onClickSecondaryButton="
            () => {
              changeFilterTrash();
              setRouteName('archivedUsers');
              setFilter({ ...filter, ['trashed']: true });
              getData();
            }
          "
          v-if="filter.trashed === false"
          class="archive-link"
          icon="icon-archive-group"
          :text="$t('common.archiveList')"
        />
        <div
          @click="
            () => {
              changeFilterTrash();
              setRouteName('user');
              setFilter({ ...filter, ['trashed']: false });
              getData();
            }
          "
          v-if="filter.trashed === true"
          class="btn-return"
        >
          <i class="icon-return mr-2"></i>
          <p>Toutes les utilisateurs</p>
        </div>

        <div
          v-if="selectedRows && selectedRows.length > 0"
          class="bloc-desarchive archv"
        >
          <p>
            <span class="pr-2">{{ selectedRows.length }}</span>
            {{ selectedRows.length > 1 ? 'slectionns' : 'slectionn' }}
          </p>
        </div>
      </div>
      <div class="btn-add flex">
        <div class="mr-3">
          <DropDownMenu
            :disabled="selectedRows.length > 0 ? false : true"
            :items="[
              {
                label: filter.trashed
                  ? $t('common.desarchiver')
                  : $t('common.archive'),
                onClick: () => {
                  if (selectedRows.length > 0) {
                    handleSelection();
                  }
                },
                disabled: selectedRows.length > 0 ? false : true,
              },
            ]"
            icon="icon-down"
            :text="'Actions groupes'"
            :btnId="'btn_dropdown_2'"
            :dropdownId="'dropdownMenu_2'"
            :btnclass="'btn-third'"
          />
        </div>
        <div class="btn-add" @click="toggleDrawer">
          <ButtonPrimary
            icon="icon-add"
            :text="$t('components.listUser.addUser')"
          />
        </div>
      </div>
    </div>

    <div class="card-table actions-tab mb-5">
      <TableComponent
        ref="utilisateurTable"
        :key="switchedPage"
        :isLoading="isFetching"
        :multipleSelect="true"
        :hasActions="true"
        :keySelection="'id'"
        :redirection="true"
        :data="data"
        :columns="columns"
        @onSort="handleSort"
        :actions="{
          archive: filter.trashed === false,
          update: true,
          unarchive: filter.trashed === true,
        }"

        :onUpdateItem="
          (index, item) => {
            handleUpdate(index, item);
          }
        "

        :onArchiveItem="
          (index, item) => {
            handleDeleteRow(index, item);
          }
        "
        :onUnArchiveItem="
          (index, item) => {
            handleArchiveUser(item);
          }
        "
        @onSelection="handleSelectedRows"
      />
    </div>
    <modal
      :cancelText="$t('components.listUser.modalBtnleft')"
      :loading="isTrashLoading"
      :show-modal="dispalyArchiveModal"
      :title="'Attention'"
      :btnleft="'Annuler'"
      :okText="
        filter.trashed === true
          ? $t('common.desarchiver')
          : $t('common.archive')
      "
      :btnright="'Supprimer'"
      :text="'La suppression de cette utilisateur sera dfinitive et irrversible'"
      :question="'tes-vous sr de vouloir supprimer cette utilisateur ?'"
      @close="handleCancel"
      @onConfirm="handleOk"
    >
      <template v-slot:title>
        {{ $t('components.listUser.modalTitle') }}
      </template>

      <template v-slot:content>
        <p v-if="selectedRows.length == 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('components.listUser.modalUnArchiveOneItemText')
              : $t('components.listUser.modalArchiveOneItemText')
          }}
          <strong>{{ selectedRows[0].name }}</strong>
        </p>
        <p v-if="selectedRows.length == 1">
          {{
            filter.trashed == true
              ? $t('components.listUser.modalUnArchiveOneItemQuestion')
              : $t('components.listUser.modalArchiveOneItemQuestion')
          }}
        </p>
        <p v-if="selectedRows.length > 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('components.listUser.modalUnArchiveManyItemsText')
              : $t('components.listUser.modalArchiveManyItemsText')
          }}
        </p>
        <div class="pb-5">
          <ul class="bold" v-if="selectedRows.length > 1">
            <li v-for="item of selectedRows" :key="item.id">
              <strong>{{ item.name }}</strong>
            </li>
          </ul>
        </div>
        <p v-if="selectedRows.length > 1">
          {{
            filter.trashed == true
              ? $t('components.listUser.modalUnArchiveManyItemsQuestion')
              : $t('components.listUser.modalArchiveManyItemsQuestion')
          }}
        </p>
      </template>
    </modal>

    <Pagination storeModuleName="user" />

    <DrawerUser
     :isOpened="showDrawer"
      @close="showDrawer = false"
    />
  </div>
</template>

<script>
import DrawerUser from '@/components/DrawerForm/Utilisateur/DrawerUser.vue';
import InputSearch from '@/components/FormWidgets/InputSearch.vue';
import ButtonSecondary from '@/components/FormWidgets/ButtonSecondary.vue';
import ButtonPrimary from '@/components/FormWidgets/ButtonPrimary.vue';
import TableComponent from '@/components/Table/Table.vue';
import Pagination from '@/components/Table/Pagination.vue';
import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';
import moment from 'moment';
import { mapActions, mapGetters } from 'vuex';
import Modal from '@/components/Modal/Modal.vue';

export default {
  components: {
    DrawerUser,
    InputSearch,
    ButtonSecondary,
    ButtonPrimary,
    TableComponent,
    Pagination,
    Modal,
    DropDownMenu,
  },
  data() {
    return {
      showDrawer: false,
      dispalyArchiveModal: false,
      switchedPage: 1,
      isTrashLoading: false,
      selectedUser: null,
      selectedRows: [],
      model: {}, // Ajoutez cette ligne pour initialiser le modle
    };
  },
  methods: {
    ...mapActions('user', [
      'getData',
      'setPagination',
      'setFilter',
      'deletedUser',
      'setModel',
    ]),
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },

    handleSort(options) {
      this.setFilter({
        ...this.getFilter,
        ['sortKey']: options.sortKey,
        ['sortOrder']: options.sortOrder,
      });
      this.getData();
    },

    handleSearch(value) {
      this.setPagination({
        ...this.getPagination,
        ['current_page']: 1,
      });
      this.setFilter({
        ...this.getFilter,
        ['keyword']: value,
      });
      this.getData();
    },

    handleSelectedRows(data) {
      this.selectedRows = data;
    },
    handleDeleteRow(index, item) {
      this.selectedRows = [item];
      this.dispalyArchiveModal = true;
    },
    handleSelection() {
      this.dispalyArchiveModal = true;
    },
    handleCancel() {
      this.dispalyArchiveModal = false;
    },

    handleOk() {
      this.isTrashLoading = true;
      this.deletedUser(this.selectedRows.map((x) => x.id)).then(() => {
        this.$refs.utilisateurTable.setSelectedRows([]);
        this.selectedRows = [];
        this.dispalyArchiveModal = false;
        this.isTrashLoading = false;
      });
    },

    handleArchiveUser(user) {
      this.selectedUser = user;
      this.selectedRows = [user];
      this.dispalyArchiveModal = true;
    },

    changeFilterTrash() {
      this.$store.commit('user/RESET_PAGINATION');
      this.switchedPage++;
      this.$refs.utilisateurTable.setSelectedRows([]);
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ['trashed']: !this.filter.trashed });
      this.getData();
      if (this.filter.trashed === true) this.setRouteName('archivedUsers');
      else this.setRouteName('user');
    },

    ShowDrawerUser() {
      this.showDrawer = !this.showDrawer;
    },

    handleUpdate(index, item) {
       
      this.setModel({
        id: item.id,
        firstName: item.firstName,
        lastName: item.lastName,
        email: item.email,
        phone: item.phone,
        role: item.role,
      });
      this.showDrawer = true;
    },

  },
  mounted() {
    this.setFilter({
      ...this.getFilter,
      ['withPagination']: 1,
      ['current_page']: 1,
    });

    this.getData();

  },

  watch: {
    keyword() {
      this.getData();
    },
  },

  computed: {
    ...mapGetters('user', [
      'getCollection',
      'getPagination',
      'getFilter',
      'getState',
    ]),

    isFetching() {
      return this.getState.isFetchingDataLoading;
    },
    data() {
      return this.getCollection;
    },


    columns() {
      return [
        {
          key: 'id',
          label: '#',
        },
        {
          key: 'firstName',
          label: this.$t('components.addUserDrawer.utilisateur'),
          render: (text, record) => {
            return `<span>${record.firstName} ${record.lastName}</span>`;
          },
          sort: true,
          sortKey: 'first_name',
        },
        {
          key: 'email',
          label: this.$t('components.addUserDrawer.userEmail'),
          render: (text, record) => {
            return `<span>${record.email}</span>`;
          },
          sort: true,
          sortKey: 'email',
        },
        {
          key: 'role',
          label: this.$t('components.addUserDrawer.roleUser'),
          render: (text, record) => {
            let roleClass = '';
            let roleText = '';

            switch (record.role) {
              case 'agent-qualite':
                roleClass = 'sup';
                roleText = 'Agent-qualite';
                break;
              case 'supervisor-qualite':
                roleClass = 'sup';
                roleText = 'Supervisor-qualite';
                break;
              case 'super-admin':
                roleClass = 'sup';
                roleText = 'Super-admin';
                break;
              case 'admin':
                roleClass = 'admin';
                roleText = 'Admin';
                break;
              default:
                roleText = record.role;
            }

            return `<span class="${roleClass}">${roleText}</span>`;
          },
          sort: true,
          sortKey: 'role',
        },
        {
          key: 'createdAt',
          label: this.$t('components.addUserDrawer.creationDate'),
          render: (text, record) => {
            const formattedDate = moment(record.createdAt).format(
              'DD/MM/YYYY - HH[h]mm',
            );
            return `<span>${formattedDate}</span>`;
          },
          sort: true,
          sortKey: 'created_at',
        },
        {
          key: 'updatedAt',
          label: this.$t('components.addUserDrawer.updateDate'),
          render: (text, record) => {
            const formattedDate = moment(record.updatedAt).format(
              'DD/MM/YYYY - HH[h]mm',
            );
            return `<span>${formattedDate}</span>`;
          },
          sort: true,
          sortKey: 'updated_at',
        },
      ];
    },
    keyword() {
      return this.getFilter.keyword;
    },

    filter() {
      return this.getFilter;
    },
  },
};
</script>