<template>
  <div v-if="activeRecord" class="header_">
    <div class="content-info">
      <div class="title">
        <h4>Audio #{{ activeRecord.id }}</h4>
        <i
          @click="addOrRemoveFavory(activeRecord.id)"
          :class="
            activeRecord.is_favory == true
              ? 'icon-bookmark-bold ml-4'
              : 'record-white icon-favoris ml-4'
          "
        ></i>

        <a
          :href="`${activeRecord.url}?token=${accesToken}`"
          download
          class="ml-4"
        >
          <i class="icon-download"></i>
        </a>
      </div>
      <div clas="content-description">
        <div class="info">
          <p class="mr-4" v-if="
              $globals.hasAuthorities('records', 'anonymisation', connectedUser?.role)
            ">
            <i class="icon-home"></i>
            {{
              activeRecord.agentId ? activeRecord.agentId.callCenterId.name : ''
            }}
          </p>
          <p>
            <i class="icon-calendar"></i>
            {{ $globals.audioItemDateFormat(activeRecord.createdAt) }}
          </p>
          <p>
            <i class="icon-time-circle ml-4"></i>
            {{ $globals.audioItemTimeFormat(activeRecord.createdAt) }}
          </p>
          <p v-if="
              $globals.hasAuthorities('records', 'anonymisation', connectedUser?.role)
            ">
            <i class="icon-headphones-with-mic ml-4"></i>
            {{ activeRecord.agentId ? activeRecord.agentId.firstName : '' }}
            {{ activeRecord.agentId ? activeRecord.agentId.lastName : '' }}
          </p>
        </div>
      </div>
      <div class="info">
        <p>
          <i class="icon-megaphone"></i>
          {{ activeRecord?.gridData?.campagne?.name }}
        </p>

        <!-- <p>
          <i class="ml-4"></i>
          {{ activeRecord.qualification }}
        </p> -->

        <p>
          <i class="icon-badge ml-4"></i>
          {{ activeRecord.gridData.name }} (V4)
        </p>
        <p v-if="activeRecord && activeRecord.originalName">
          <i class="icon-volume-up ml-4" style="font-size: unset;"></i>
          {{ activeRecord.originalName }}
        </p>
      </div>
    </div>
    <div class="content-score">
      <div class="mr-3 flexbox">
        <template v-for="evaluation of evaluationMembers">
          <Acount
            :key="evaluation.id"
            :lastName="evaluation.user.lastName"
            :firstName="evaluation.user.firstName"
            :bgColor="evaluation.user.avatarColor"
          />
        </template>
      </div>
      <div class="scribb content mr-4">
        <h4>Total Score</h4>
        <!-- <h5>{{ totalScore === 0 ? '-' : totalScore }}</h5> -->
        <h5>{{ totalScore }}</h5>
      </div>
      <div class="manuelle content">
        <h4>Votre Score</h4>
        <h5>{{ activeRecord.pourcentage_scrib }}%</h5>
      </div>
    </div>
  </div>
</template>
<script>
import Acount from '@/components/FormWidgets/AvatarColorsRandom/AvatarColor.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  props: ['totalScore', 'connectedUser'],
  components: {
    Acount,
  },
  data() {
    return {
      activeSection: 1,
      accesToken: localStorage.getItem('auth'),
    };
  },
  methods: {
    addOrRemoveFavory(id) {
      this.AddOrRemoveFavory({ recordId: id });
    },
    ...mapActions('record', ['AddOrRemoveFavory']),
  },
  computed: {
    ...mapGetters('record', ['getActiveRecord']),
    ...mapGetters('user', ['getUser']),
    activeRecord() {
      return this.getActiveRecord;
    },
    evaluationMembers() {
      let members = [];
      if (this.getUser != null) {
        for (let item of this.getActiveRecord.evaluations) {
          if (!members.find((x) => x.user.id == item.user.id)) {
            members.push(item);
          }
        }
        return members.filter((item) => item.user.id != this.getUser.id);
      } else {
        return [];
      }
    },
  },
};
</script>
