<template>
  <div class="card-table-outer mb-5">
    <div class="action-table">
      <div class="content-text">
        <h3 class="title-table">
          {{ $globals.capitalizeFirstLetter($t('common.team')) }}
        </h3>
        <div class="switch-btn-top-flop">
          <div
            class="top"
            @click="changeTab(0)"
            :class="{ active: activeTab === 0 }"
          >
            <i class="icon-arrow-up pr-2"></i>
            Top
          </div>
          <div
            class="flop"
            @click="changeTab(1)"
            :class="{ active: activeTab === 1 }"
          >
            <i class="icon-arrow-down pr-2"></i>
            Flop
          </div>
          <div class="slider"></div>
        </div>
      </div>
      <div class="content-action">
        <div class="more-details">
          <router-link to="/teams/statistique">
            {{ $globals.capitalizeFirstLetter($t('common.moreDetails')) }}
          </router-link>
          <i class="icon-arrow-tab-right"></i>
        </div>
      </div>
    </div>
    <div class="card-table">
      <TableComponent
        :isLoading="isFetchingTeam"
        :data="data"
        :columns="columns"
        :AgentDashboard="true"
        :enabledScrollCss="true"
      />
      <!-- <TableScroll :columns="tableColumns" :rows="tableRows"/> -->
      <!-- <Pagination
        storeModuleName="team"
        storeFunctionName="getTopFlopTeamData"
      /> -->
    </div>
  </div>
</template>

<script>
import TableComponent from '@/components/Table/Table.vue';
//import TableScroll from '@/components/Table/TableScroll.vue';
import { mapGetters, mapActions } from 'vuex';
import Pagination from '@/components/Table/Pagination.vue';

export default {
  components: {
    TableComponent,
    // TableScroll,
    //Pagination,
  },
  data() {
    return {
      activeTab: 0,
      isFetching: false,

      tableColumns: ['Agent', 'Call_center', 'Appels', 'Scoring'],
      tableRows: [],
      tableTeamKey:0
    };
  },
  mounted() {
    if (this.user) {
      const payload = {
        campagneId: this.user.selectedCampagneId,
        gridSelected: {
          id: this.user.selectedGridId,
        },
      };
      this.getTopFlopTeamData(payload).then(() => {
        this.isFetching = false;
        this.tableTeamKey++
      });
    }
  },
  async created() {
    await this.resetState();
  },
  methods: {
    ...mapActions('team', ['getTopFlopTeamData', 'setTopFlop', 'resetState']),
    changeTab(index) {
      if (index == 1) {
        this.isFetching = true;
        this.setTopFlop('flop');
        this.getTopFlopTeamData(this.campaign).then(() => {
          this.isFetching = false;
        });
      } else {
        this.isFetching = true;
        this.setTopFlop('top');
        this.getTopFlopTeamData(this.campaign).then(() => {
          this.isFetching = false;
        });
      }
      this.activeTab = index;
    },
  },

  computed: {
    ...mapGetters('team', ['getTopFlopCollection', 'getIsFetching']),
    ...mapGetters({ campaign: 'campaign/getCampagneInfo' }),
    ...mapGetters('user', ['getUser']),
    user() {
      return this.getUser;
    },
    columns() {
      return [
        {
          key: 'name',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.agents.table.columns.name'),
          ),
          render: (text, record) => {
            return `<div>   <i class="icon-teams mr-1"></i> ${record.teamName}<span> #</span>${record.teamId} </div> `; // Custom rendering
          },
        },
        {
          key: 'callcenter',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('common.callCenter'),
          ),
          render: (text, record) => {
            return `<div>   <span class="actif mr-1"></span> ${record.callCenterName}</div> `;
          },
        },

        {
          key: 'Appels',
          label: this.$globals.capitalizeFirstLetter(this.$t('common.calls')),
          render: (text, record) => {
            return `<div>   <span class="actif mr-1"></span> ${record.numCalls}</div> `;
          },
        },
        {
          key: 'Scoring',
          label: this.$globals.capitalizeFirstLetter(this.$t('common.scoring')),
          render: (text, record) => {
            let scoreDiff = parseFloat(record.scoreDifference).toFixed(2);

            if (scoreDiff < 0) {
              return `<div class="bold">${record.avgScoreNewDate}   
              <span class="taux red">${record.scoreDifference}%</span></div>`;
            } else if (scoreDiff > 0) {
              return `<div class="bold">${record.avgScoreNewDate}   
              <span class="taux green">+${record.scoreDifference}%</span></div>`;
            } else {
              return `<div class="bold">${record.avgScoreNewDate}   
              <span class="taux">${record.scoreDifference}%</span></div>`;
            }
          },
        },
      ];
    },
    data() {
      return this.getTopFlopCollection;
    },
    isFetchingTeam() {
      return this.getIsFetching;
    },
  },

  watch: {
    user: {
      handler(newval) {
        this.isFetching = true;
        const payload = {
          campagneId: newval.selectedCampagneId,
          gridSelected: {
            id: newval.selectedGridId,
          },
        };
        if (
          newval?.selectedCampagneId != undefined &&
          newval?.selectedCampagneId != null
        ) {
          this.getTopFlopTeamData(payload).then(() => {
            this.isFetching = false;
          });
        }
      },
      deep: true,
    },
  },
};
</script>
