<template>
  <div
    class="form-field search-field autocomplete"
    :class="{ fieldError: hasError }"
  >
    <label class="control-label" :for="inputId">{{ label }}</label>
    <template v-if="WithIconUser && itemsSelected.length == 0">
      <div class="search">
        <i class="icon-search"></i>
      </div>
      <input
        v-model="searchTerm"
        @input="filterItems"
        :type="type"
        :placeholder="placeholder"
        @click="showResults = true"
      />
    </template>
    <template v-if="!WithIconUser">
      <div class="search">
        <i class="icon-search"></i>
      </div>
      <input
        v-model="searchTerm"
        @input="filterItems"
        :type="type"
        :placeholder="placeholder"
        @click="showResults = true"
      />
    </template>
    <div class="error" v-if="hasError">{{ errorMessage }}</div>
    <div class="list-search">
      <template v-if="WithIconUser">
        <div class="superviseur pt-5">
          <div
            class="supr"
            v-for="(item, index) in itemsSelected"
            v-bind:key="index"
          >
            <div class="icon">
              <i class="icon-profile pr-3"></i>
            </div>
            <p>
              {{ item[keyLabel] }}
              <span class="regular">#{{ item[keyLabelhermesId] }}</span>
              <i
                v-if="WithIconUser"
                class="icon-remove"
                @click="removeItem(item)"
              ></i>
            </p>
          </div>
        </div>
        <!-- <span
          class="tag"
          v-for="(item, index) in itemsSelected"
          v-bind:key="index"
        >
          {{ item[keyLabel] }}
          <i class="icon-remove" @click="removeItem(item)"></i>
        </span> -->
      </template>
      <template v-else-if="!isAgent">
        <span
          class="tag"
          v-for="(item, index) in itemsSelected"
          v-bind:key="index"
        >
          {{ item[keyLabel] }}
          <i class="icon-exit-" @click="removeItem(item)"></i>
        </span>
      </template>
    </div>
    <ul v-show="showResults && showNoResults === false">
      <li
        v-for="(item, index) in filteredItems"
        @click="selectItem(item)"
        v-bind:key="index"
      >
        {{ item[keyLabel] }}
        {{ isAgent || WithIconUser ? '(' + item[keyLabelhermesId] + ')' : '' }}
      </li>
    </ul>
    <ul v-show="theResult">
      <li>{{ noResultsMessage }}</li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      searchTerm: '',
      filteredItems: this.items,
      showResults: false,
      showNoResults: false,
      noResultsMessage: 'Aucun rsultat trouv.',
    };
  },
  props: {
    type: {
      type: String,
      default: 'text', // Input type (e.g., text, password, email, etc.)
    },
    placeholder: String,
    inputId: String, // Input ID
    label: String, // Label text
    items: {
      type: Array,
      default: () => [],
    },
    itemsSelected: Array,
    keyValue: String,
    keyLabel: String,
    keyLabelhermesId: String,
    keyArray: String,
    WithIconUser: Boolean,
    isAgent: Boolean,
    hasError: {
      type: Boolean,
    },
    errorMessage: {
      type: String,
    },
  },
  computed: {
    theResult() {
      return (
        this.showResults &&
        (this.items.length === 0 ||
          (this.showNoResults && this.searchTerm.length > 0))
      );
    },
  },
  watch: {
    items(newItems) {
      this.filteredItems = newItems;
    },
    searchTerm(newSearchTerm) {
      this.filterItems();
    },
  },
  mounted() {
    this.filteredItems = this.items;
  },
  methods: {
    // theResult() {
    //   if (this.items.length === 0) return true;
    //   if (this.showNoResults && this.searchTerm.length > 0) {
    //     return true;
    //   } else return false;
    // },
    filterItems() {
      this.searchTerm = this.searchTerm.trim();

      this.filteredItems = this.items.filter((item) => {
        const keyLabelMatch = item[this.keyLabel]
          .toLowerCase()
          .includes(this.searchTerm.toLowerCase());
        if (this.WithIconUser || this.isAgent) {
          const keyLabelHermesIdMatch = item[this.keyLabelhermesId]
            .toLowerCase()
            .includes(this.searchTerm.toLowerCase());

          return keyLabelMatch || keyLabelHermesIdMatch;
        }
        return keyLabelMatch;
      });
      this.showNoResults =
        this.searchTerm.length > 0 && this.filteredItems.length === 0;

      if (this.searchTerm.length === 0) {
        this.filteredItems = this.items;
      }
    },
    // filterItems() {
    //   this.showResults = false;
    //   this.filteredItems = this.items.filter((item) =>
    //     item[this.keyLabel]
    //       .toLowerCase()
    //       .includes(this.searchTerm.toLowerCase()),
    //   );
    //   if (this.filteredItems.length > 0) {
    //     this.showResults = true;
    //   }
    // },
    selectItem(item) {
      const selectedValue = item[this.keyLabel];
      this.searchTerm = selectedValue;
      this.showResults = false;
      // Emit the custom event to the parent
      this.$emit('item-selected', item, this.keyArray);
      this.searchTerm = '';
    },
    removeItem(item) {
      this.$emit('remove-item', item[this.keyValue], this.keyArray);
    },
  },
};
</script>
