<template>
  <transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true, show: isOpened }" class="show versioning">
      <div class="header_drawer">
        <div class="title">
          <h4>
            {{ $globals.capitalizeFirstLetter($t("views.grids.versions")) }}
          </h4>
        </div>
        <div class="">
          <button class="close mr-5" @click="handleClose()">
            {{ $globals.capitalizeFirstLetter($t("views.grids.close")) }}
            <i class="icon-exit"></i>
          </button>
        </div>
      </div>

      <div class="body_drawer">
        <div class="card">
       
            <div class="call">{{ $globals.capitalizeFirstLetter($t("views.grids.outgoingCalls")) }}</div>
          
          
            <div class="version" v-if="
              getVersion && getVersion[0] && getVersion[0].lastVersion !== undefined
            ">Version Actuelle : {{ getVersion[0].lastVersion }}</div>
          
      
            <div class="share"  v-if="
              getVersion && getVersion[0] && getVersion[0] !== undefined
            ">{{ $globals.capitalizeFirstLetter($t("views.grids.publishedOn")) }}  : {{ getVersion[0].createdAt }} {{ $globals.capitalizeFirstLetter($t("views.grids.by")) }}   : {{ getVersion[0].publishBy }}</div>
          
        </div>
  
          <h5> {{ $globals.capitalizeFirstLetter($t("views.grids.historical")) }}  </h5>
            <div class="content-table mb-7">
             
                <TableComponent
                  :isLoading="isFetchingData"
                  :data="getVersion"
                  :columns="columns"
                />
              
            <Pagination storeModuleName="grid"  :idParam="this.idGridVersioning" storeFunctionName="getGridVersioning" getPaginationName="getPaginationVersioning" setPaginationName="setPaginationVersioning" />
            </div>
          
  
      </div>
      <div id="overlay-" @click="handleClose()"></div>
    </div>
  </transition>
</template>

<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination from "@/components/Table/Pagination.vue";
import { mapGetters } from 'vuex';

export default {
  components: {
    TableComponent,
    Pagination
  },
  data() {
    return {
      showDrawerVersioning: false,
      activeBlock: 0,
      itemError: false,
      dispalyCommentModal: false,
      isFetching: false,
      index: 0,
    };
  },

  props: {
    isOpened: Boolean,
    versionsData: Array,
    idGridVersioning:Number
  },

  computed: {
    ...mapGetters("grid", ["getVersion","getIsFetchingVersioning"]),

    columns() {
      return [
        {
          key: "index",
          label: this.$globals.capitalizeFirstLetter(this.$t("views.grids.version")),
        },
        {
          key: "comment",
          label: this.$globals.capitalizeFirstLetter(this.$t("views.grids.description")),
        },

        {
          key: "createdAt",
          label: this.$globals.capitalizeFirstLetter(this.$t("views.grids.createdAt")),
          
        },
        {
          key: "publishBy",
          label: this.$globals.capitalizeFirstLetter(this.$t("views.grids.publishedBy")),
        },
      ];
    },
    isFetchingData() {
      return this.getIsFetchingVersioning;
    },
  },

  methods: {
    handleClose() {
      this.showDrawerVersioning = false;
      this.activeBlock = 0;
      this.$emit("close");
    },

    mounted() {
      // Show the initial block
      this.$set(this.blocks, this.activeBlock, {
        ...this.blocks[this.activeBlock],
        active: true,
      });
    },
  },
};
</script>
