<template>
  <div v-if="!loading" class="content_page page_details compte_rendu calibrg">
    <div class="flexbox__">
      <div class="content_page_o">
        <div class="header_details">
          <div class="main-title">
            <h1>
              {{ calibrationData.calibration?.title }} -
              {{ formatDate(calibrationData.calibration?.created_at) }}
            </h1>
            <div class="rdv">
              Rendez-vous :
              {{ formatFullDate(calibrationData.calibration?.start_date) }}
            </div>
          </div>
          <div class="main-account">
            <template
              v-for="(member, index) of calibrationData?.member_calibration"
            >
              <Account
                :key="index"
                :firstName="member.first_name"
                :lastName="member.last_name"
                :bgColor="member.avatar_color"
              />
            </template>
          </div>
        </div>
        <div class="tabs mt-4 mb-2">
          <button
            v-for="item in playlist"
            :key="item.id"
            @click="
              () => {
                handleSetActiveRecord(item);
              }
            "
            :class="{
              active:
                activeParentRecord != undefined &&
                activeParentRecord.id === item.id,
            }"
          >
            {{ item.name }}
          </button>
        </div>
        <div class="content_page__">
          <div class="title__">
            <div class="main-title">{{ activeRecord?.name }}</div>
            <div class="date">
              <div class="flex align-items">
                <i class="icon-home mr-2"></i>
                {{ activeRecord?.agentId?.callCenterId?.name }}
              </div>
              <div class="flex align-items">
                <i class="icon-calendar ml-4 mr-2"></i>
                {{ $globals.formatDateWithoutTime(activeRecord?.createdAt) }}
              </div>
              <div class="flex align-items">
                <i class="icon-time-circle ml-4 mr-2"></i>
                {{ $globals.audioItemTimeFormat(activeRecord?.createdAt) }}
              </div>
            </div>
          </div>
          <div class="content_evaluation">
            <ScribbPlayer
              ref="scribbPalyer"
              :playlist="playlist"
              :withAttachments="true"
              :withSpeechTrace="true"
              :showPlaylist="true"
              :withWaveSurfer="false"
              @trackChanged="
                (currentRecord, parentRecord) => {
                  setActiveRecord(currentRecord);
                  setParentRecord(parentRecord);
                }
              "
              @playListItemClicked="
                (record) => {
                  setActiveRecord(record);
                }
              "
            />
            <div class="tabcontent">
              <!-- <transition name="fade">
                                <div v-show="activeTab === 0" class="content_manuelle content_scribb">
                                   
                                    <EvaluationCalibrage />
                                </div>
                            </transition>
                            <transition name="fade">
                                <div v-show="activeTab === 1" class="content_retranscription">
                                    <Retranscription />
                                </div>
                            </transition> -->
              <div class="tab">
                <button
                  @click="changeTab(0)"
                  :class="{ active: activeTab === 0 }"
                >
                  Votre valuation
                </button>
                <button
                  @click="changeTab(1)"
                  :class="{ active: activeTab === 1 }"
                >
                  Retranscription
                </button>
                <div class="slider"></div>
              </div>
              <div class="tabcontent">
                <transition name="fade">
                  <div
                    v-if="activeTab === 0"
                    class="content_manuelle content_scribb"
                  >
                    <EvaluationCalibrage :calibration="calibrationData" />
                    <!-- <EvaluationFinishedDetails
                      :record="activeRecord"
                      v-if="calibrationData?.calibration?.status == 'finished'"
                    />
                    <EvaluationCalibrage
                      v-else
                      :calibration="calibrationData"
                      :activeRecord="activeRecord"
                    /> -->
                  </div>
                </transition>
                <transition name="fade">
                  <div v-show="activeTab === 1" class="content_retranscription">
                    <Retranscription />
                  </div>
                </transition>
              </div>

              <!-- <EvaluationCalibrage :calibration="calibrationData" />
                            <Retranscription /> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div
    v-else
    class="content_page page_details compte_rendu calibrg text-center"
  >
    <Loader />
  </div>
</template>

<script>
import Retranscription from '@/components/Records/ListTab/Retranscription.vue';
import Account from '@/components/FormWidgets/AvatarColorsRandom/AvatarColor.vue';
import EvaluationCalibrage from '@/components/Records/ListTab/EvaluationCalibrage.vue';
import { mapActions, mapGetters } from 'vuex';
import moment from 'moment';
import ScribbPlayer from '@/components/ScribbAudioPlayer/ScribbPlayer.vue';
import Loader from '@/components/AnimationScribb/Loader.vue';

// import EvaluationFinishedDetails from '@/components/Records/ListTab/EvaluationFinishedDetails.vue';
export default {
  components: {
    Retranscription,
    EvaluationCalibrage,
    Account,
    ScribbPlayer,
    Loader,
    // EvaluationFinishedDetails,
  },

  data() {
    return {
      activeSection: 1,
      activeTab: 0,
      activeRecordTab: 0,
      activeRecordId: null,
      activeRecordName: null,
      activeRecordCallcenter: null,
      activeRecordCreatedAt: null,
      loading: false,
    };
  },

  computed: {
    ...mapGetters('detailsCalibrage', ['getCollection']),
    ...mapGetters('record', ['getParentRecord', 'getActiveRecord']),
    calibrationData() {
      return this.getCollection;
    },
    playlist() {
      return this.getCollection?.record_calibration?.map(
        (item) => item.record[0],
      );
    },
    activeParentRecord() {
      return this.getParentRecord;
    },
    activeRecord() {
      return this.getActiveRecord;
    },
  },
  methods: {
    ...mapActions('record', ['setActiveRecord', 'setParentRecord']),
    ...mapActions('detailsCalibrage', ['getData']),
    changeTab(index) {
      this.activeTab = index;
    },
    formatDate(data) {
      return moment(data).format('MMMM YYYY');
    },
    formatFullDate(data) {
      return moment(data).format('DD/MM/YYYY');
    },
    handleSetActiveRecord(record) {
      this.setActiveRecord(record);
      this.setParentRecord(record);
      this.$refs.scribbPalyer.setCurrentRecordByRecordId(record.id);
    },
  },
  mounted() {
    let codeMember = this.$route.params.code;
    this.loading = true;
    this.getData(codeMember)
      .then((res) => {
        this.setParentRecord(res.record_calibration[0].record[0]);
        this.setActiveRecord(res.record_calibration[0].record[0]);
        this.loading = false;
      })
      .catch(() => {
        this.loading = false;
        this.$router.push('/calibrations');
      });
  },
};
</script>
