<template>
  <div>
    <div class="content__ mt-5">
      <div class="panel_header_ content">
        <div class="title">Tableau comparatif</div>
        <!-- <div class="note"></div> -->
      </div>

      <div class="record-table">
        <table cellpadding="0" cellspacing="0">
          <thead>
            <tr>
              <th :colspan="uniqueUsers.length + 3" style="text-align: center">
                Tableau comparatif
              </th>
            </tr>
            <tr v-if="uniqueUsers.length != 0">
              <th></th>
              <th>Total Score</th>
              <th>Moyene</th>
              <th v-for="user in uniqueUsers" :key="user.id">
                {{ user.user.firstName }} {{ user.user.lastName }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="category in categories" :key="category.id">
              <td>{{ category.name }}</td>
              <td>{{ category.totalScore }}</td>
              <td>{{ calculateAveragePercentage(category) }}%</td>
              <td v-for="user in uniqueUsers" :key="user.id">
                <span v-if="getUserScore(category, user) !== null">
                  {{ getUserScore(category, user) }}%
                </span>
                <span v-else>-</span>
              </td>
            </tr>
            <tr v-if="categories.length == 0">
              <td colspan="2" style="text-align: center">
                Pas d'valuation pour le moment
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['activeRecord'],
  components: {},
  data() {
    return {
      totalScore: 0,
    };
  },
  methods: {
    getUserScore(category, user) {
      const userScore = category.items.reduce((score, item) => {
        const userEvaluations = user.eval.filter(
          (evaluation) => evaluation.item_id === item.id,
        );
        return (
          score +
          item.notes.reduce((noteScore, note) => {
            const matchingEvaluation = userEvaluations.find(
              (evaluation) => evaluation.note_id === note.id,
            );
            if (matchingEvaluation) {
              const noteValue = parseInt(note.score, 10);
              if (!isNaN(noteValue)) {
                return noteScore + noteValue;
              }
            }
            return noteScore;
          }, 0)
        );
      }, 0);
      if (category.totalScore > 0) {
        const percentage = (userScore / category.totalScore) * 100;
        return Math.round(percentage);
      }
      return 0;
    },
    calculateAveragePercentage(category) {
      if (this.uniqueUsers.length === 0) {
        return '-';
      }
      const totalPercentage = this.uniqueUsers.reduce((total, user) => {
        return total + this.getUserScore(category, user);
      }, 0);
      const averagePercentage = totalPercentage / this.uniqueUsers.length;
      return Math.round(averagePercentage);
    },
  },
  computed: {
    uniqueUsers() {
      const evaluations = this.activeRecord.evaluations || [];
      const users = evaluations.flatMap((evaluation) =>
        evaluation.user ? [evaluation] : [],
      );
      return [...new Map(users.map((user) => [user.id, user])).values()];
    },
    categories() {
       return this.activeRecord.grid || [];
    },
  },
};
</script>
<style scoped>
.record-table {
  width: 100%;
  border: 1px solid #d5d5d5;
  padding: 1rem;
  background-color: #fff;
  margin-top: 2rem;
  border-radius: 8px;
}
.record-table table {
  width: 100%;
  background-color: white;
}
.record-table th,
.record-table td {
  border-bottom: 1px solid #d5d5d5;
  padding: 0.7rem 1rem;
  font-size: 0.8666666667rem;
}
.record-table th {
  background-color: #f4f6f5;
  border: none;
  border-bottom: 1px solid #d5d5d5;
}
</style>
