<template>
  <div class="content_page page-dashboard">
    <div class="header_page">
      <div class="content-filter">
        <div class="filter_dropdown">
          <SelectCampgnes @onCreate="addCampaign" @onCreateGrille="addGrille" @onUpdate="updateCampaign"
            @onUpdateGrille="updateGrille" @onSelect="selectCampaign" @onReset="resetCampaign"
            :selectedByDefault="true" />
        </div>
        <DateComparision
          :withTimePicker="false"
          @cancel="cancelDatePicker"
          @applyed="applyedDatePicker"
        />
        <Filtres
          :items="filterItems"
          @onFilterChange="handleFilterChanged"
          @selected-names-changed="handleSelectedNamesChanged"
          :updatedFilters="this.updatedFilters"
        />
      </div>
      <div v-if="selectedOptions.length > 0" class="tags-">
        <div
          v-for="(selectedOption, index) in selectedOptions"
          :key="index"
          class="tag"
        >
          <span
            class="remove-filter"
            @click="removeSelectedOption(selectedOption)"
          >
            <i class="icon-exit-"></i>
          </span>
        </div>
      </div>
    </div>
    <ListeAppel :key="keyAppelListData" />
    <div class="stat__">
      <div class="content_callcenter_">
        <ListCallCenter :key="keyCallcenterData" />
      </div>
      <div class="content_statuts_appels">
        <StatutsAppel :key="keyQualificationData" />
      </div>
    </div>

    <ListEquipe :key="keyTeamData" />
    <ListAgents :key="keyAgentData" :dateRange="dateRange" />
    <DrawerCampagne
      :isOpened="showDrawerCampagne"
      :drawer="showDrawerCampagne"
      @close="closeDrawerCampagne"
    />
    <DrawerEvaluation
      :isOpened="showDrawerGrille"
      @close="showDrawerGrille = false"
      :dashboardPosition="true"
      :key="keyDrawer"
    />
  </div>
</template>

<script>
import SelectCampgnes from '@/components/SelectCampagne/SelectCampagnes.vue';
import ListeAppel from '@/components/Dashboard/ListAppel.vue';
import ListCallCenter from '@/components/Dashboard/ListCallCenter.vue';
import StatutsAppel from '@/components/Dashboard/StatutAppel/StatusAppel.vue';
import ListEquipe from '@/components/Dashboard/ListEquipe.vue';
import ListAgents from '@/components/Dashboard/ListAgents.vue';
import DateComparision from '@/components/FormWidgets/DateRange/DateComparison.vue';
import { mapGetters, mapActions } from 'vuex';
import DrawerCampagne from '@/components/DrawerForm/Campagnes/DrawerCampagnes.vue';
import DrawerEvaluation from '@/components/DrawerForm/Evaluation/DrawerEvaluation.vue';
import Filtres from '@/components/FormWidgets/Filter/Filter.vue';

export default {
  components: {
    SelectCampgnes,
    ListCallCenter,
    StatutsAppel,
    ListeAppel,
    ListEquipe,
    ListAgents,
    DateComparision,
    DrawerCampagne,
    DrawerEvaluation,
    Filtres,
  },
  props: { selectedByDefault: Boolean },
  data() {
    return {
      keyDrawer: 1,
      showDrawerCampagne: false,
      showDrawerGrille: false,
      dateRange: {
        startDate: null,
        endDate: null,
        startNewDate: null,
        endNewDate: null,
        startOldDate: null,
        endOldDate: null,
      },
      updatedFilters: {},
      selectedOptions: [],
      options: {},
      keyCallcenterData: 0,
      keyAgentData: 2,
      keyTeamData: 3,
      keyAppelListData: 4,
      keyQualificationData: 5,
    };
  },
  computed: {
    ...mapGetters('agent', { agentFilter: 'getFilter' }),
    ...mapGetters('team', { teamFilter: 'getFilter' }),
    ...mapGetters({ campaignRes: 'campaign/getCampagneInfo' }),
    ...mapGetters({ user: 'user/getUser' }),
    ...mapGetters('callCenter', { callCenterFilter: 'getFilter' }),
    ...mapGetters('qualification', { qualificationFilter: 'getFilter' }),
    CampagneInfo() {
      return this.getCampagneInfo;
    },
    filterItems() {
      let items = [
        {
          label: 'Call-center',
          key: 'callCenters',
          filters: this.$store.getters['callCenter/getCollection'].map((x) => ({
            id: x.id,
            name: x.name,
          })),
        },
        {
          label: 'Equipe',
          key: 'teams',
          filters: this.$store.getters['team/getCollection'].map((x) => ({
            id: x.id,
            name: x.name,
          })),
        },
        {
          label: 'Agents',
          key: 'agents',
          filters: this.$store.getters['agent/getCollection'].map((x) => ({
            id: x.id,
            name: x.firstName + ' ' + x.lastName,
          })),
        },
      ];

      if (
        ['responsable-activite', 'service-qualite'].includes(this.getUser?.role)
      ) {
        items = items.filter((x) => x.key != 'callCenters');
      }

      return items;
    },
  },

  created() {
    this.resetCampagneSelected();
  },

  methods: {
    ...mapActions('agent', {
      fetchAgentData: 'getTopFlopAgentsData',
      setAgentFilter: 'setFilter',
    }),
    ...mapActions('campaign', {
      setModel: 'setModel',
      resetCampagneSelected: 'resetCampagneSelected',
      fetchCampagneData: 'addCampgneSelectedToUserWithStat',
      setCampagneFilter: 'setFilter',
    }),
    ...mapActions('team', {
      fetchTeamData: 'getTopFlopTeamData',
      setTeamFilter: 'setFilter',
    }),
    ...mapActions('qualification', {
      fetchQualificationData: 'getQualificationsStatistics',
      setQualificationFilter: 'setFilter',
    }),
    ...mapActions('callCenter', {
      fetchCallCenterData: 'getCallCenterStatistics',
      setCallCenterFilter: 'setFilter',
    }),
    ...mapActions('grid', ['setGridModel']),
    cancelDatePicker() {
      this.setAgentFilter({
        ...this.agentFilter,
        ['startNewDate']: null,
        ['endNewDate']: null,
        ['startOldDate']: null,
        ['endOldDate']: null,
      });
      this.fetchAgentData(this.campaignRes);
      this.setTeamFilter({
        ...this.agentFilter,
        ['startNewDate']: null,
        ['endNewDate']: null,
        ['startOldDate']: null,
        ['endOldDate']: null,
      });
      this.fetchTeamData(this.campaignRes);
      this.setCallCenterFilter({
        ...this.callCenterFilter,
        ['startNewDate']: null,
        ['endNewDate']: null,
      });
      this.fetchCallCenterData(this.campaignRes);
      this.setCampagneFilter({
        ...this.CampagneFilter,
        ['startNewDate']: null,
        ['endNewDate']: null,
      });
      //this.fetchCampagneData(this.CampagneInfo);
      this.setAgentFilter({
        ...this.agentFilter,
        ['startNewDate']: null,
        ['endNewDate']: null,
        ['startOldDate']: null,
        ['endOldDate']: null,
      });
      this.dateRange = {
        startDate: null,
        endDate: null,
        startNewDate: null,
        endNewDate: null,
        startOldDate: null,
      };
    },

    applyedDatePicker(dateRange) {
      this.dateRange = dateRange;
      this.setAgentFilter({
        ...this.agentFilter,
        ['startNewDate']: dateRange.startNewDate,
        ['endNewDate']: dateRange.endNewDate,
        ['startOldDate']: dateRange.startOldDate,
        ['endOldDate']: dateRange.endOldDate,
      });
      this.fetchAgentData(this.campaignRes).then(() => { });
      this.setTeamFilter({
        ...this.agentFilter,
        ['startNewDate']: dateRange.startNewDate,
        ['endNewDate']: dateRange.endNewDate,
        ['startOldDate']: dateRange.startOldDate,
        ['endOldDate']: dateRange.endOldDate,
      });
      this.fetchTeamData(this.campaignRes);
      this.setCallCenterFilter({
        ...this.callCenterFilter,
        ['startNewDate']: dateRange.startNewDate,
        ['endNewDate']: dateRange.endNewDate,
      });
      this.fetchCallCenterData(this.campaignRes);

      this.setCampagneFilter({
        ...this.CampagneFilter,
        ['startNewDate']: dateRange.startNewDate,
        ['endNewDate']: dateRange.endNewDate,
      });

      this.setQualificationFilter({
        ...this.qualificationFilter,
        ['startNewDate']: dateRange.startNewDate,
        ['endNewDate']: dateRange.endNewDate,
        ['startOldDate']: dateRange.startOldDate,
        ['endOldDate']: dateRange.endOldDate,
      });
      this.fetchQualificationData(this.campaignRes);
    },

    addCampaign() {
      this.showDrawerCampagne = true;
    },
    addGrille() {
      this.showDrawerGrille = true;
      this.keyDrawer++;
    },
    selectCampaign(item) {
      //selected Campagne
    },
    updateCampaign(item) {
      this.showDrawerCampagne = true;
      this.setModel({
        id: item.campagneId,
        name: item.name,
        createdAt: item.createdAt,
        updatedAt: item.updatedAt,
      });
    },
    updateGrille(item) {
      this.showDrawerGrille = true;
      this.keyDrawer++;
      this.setGridModel({
        id: item.id,
        name: item.name,
        campagne: item.campagne,
        statuts: item.statuts,
        updatedAt: item.updatedAt,
        categories: item.categories,
        selectedCategorie: item.selectedCategorie,
        draft: item.draft,
      });
    },
    resetCampaign() {
      //reset the filter
    },
    closeDrawerCampagne() {
      this.showDrawerCampagne = false;
    },
    handleFilterChanged(options) {
      this.setCallCenterFilter({
        ...this.callCenterFilter,
        ['callCenters']: options.callCenters,
        ['agents']: options.agents,
        ['teams']: options.teams,
      });
      this.keyCallcenterData++;
      this.keyAppelListData++;
      this.setTeamFilter({
        ...this.teamFilter,
        ['callCenters']: options.callCenters,
        ['agents']: options.agents,
        ['teams']: options.teams,
      });
      this.keyTeamData++;

      this.setAgentFilter({
        ...this.agentFilter,
        ['callCenters']: options.callCenters,
        ['agents']: options.agents,
        ['teams']: options.teams,
      });

      this.keyAgentData++;
      this.setQualificationFilter({
        ...this.qualificationFilter,
        ['callCenters']: options.callCenters,
        ['agents']: options.agents,
        ['teams']: options.teams,
      });
      this.keyQualificationData++;
    },
    handleSelectedNamesChanged(selectedOptions) {
      this.selectedOptions = selectedOptions;
    },
    removeSelectedOption(selectedOption) {
      this.selectedOptions = this.selectedOptions.filter(
        (option) =>
          option.id !== selectedOption.id || option.key !== selectedOption.key,
      );

      const updatedFilters = {
        callCenters: this.selectedOptions
          .filter((item) => item.key === 'callCenters')
          .map((item) => item.id),
        agents: this.selectedOptions
          .filter((item) => item.key === 'agents')
          .map((item) => item.id),
        teams: this.selectedOptions
          .filter((item) => item.key === 'teams')
          .map((item) => item.id),
      };
      this.handleFilterChanged(updatedFilters);
      this.updatedFilters = updatedFilters;
    },
  },
  mounted() {
    this.$store.dispatch('team/setIsInDashboard', true);
    this.$store.dispatch('callCenter/setFilter', {
      ...this.$store.getters['callCenter/getFilter'],
      ['withPagination']: 0,
    });
    this.$store.dispatch('callCenter/getData');
    this.$store.dispatch('team/setFilter', {
      ...this.$store.getters['team/getFilter'],
      ['withPagination']: 1,
    });
    this.$store.dispatch('team/getData');
    this.$store.dispatch('agent/setIsInDashboard', true);
    this.$store.dispatch('agent/setFilter', {
      ...this.$store.getters['agent/getFilter'],
      ['withPagination']: 1,
    });
    this.$store.dispatch('agent/getData');
  },
  watch: {
    user() {
      let $grid = {
        name: this.user.selectedGridName ?? null,
        id: this.user.selectedGridId ?? null,
        categories: this.user.categories ?? null,
        draft: this.user.gridDraft ?? null,
      };
      this.$store.commit('campaign/SET_CAMPAGNE_INFO', {
        campagneId: this.user.selectedCampagneId,
        campagneName: this.user.selectedCampagneName,
        gridSelected: $grid,
      });
    },
  },
};
</script>
