<template>
  <div>
    <template v-for="(categorie, index) in grid.categories">
      <div
        :id="'category' + index"
        class="content_scrore content__ mt-5"
        :key="categorie.id"
      >
        <div
          @click="
            () => {
              activeCategory = index;
              scrollToRef();
            }
          "
          :ref="'catRef' + index"
          class="panel_header content"
        >
          <div class="title">{{ categorie.name }}</div>
          <div class="note">
            {{ getScoringUser(categorie.id) }} / {{ categorie.totalScore }}
          </div>
        </div>
        <template v-if="activeCategory == index">
          <div class="content___">
            <template v-for="(item, index) in categorie.items">
              <div class="panel_body" :key="item.id">
                <div
                  class="content_status"
                  :class="categorie.items.length < index ? 'note__' : ''"
                >
                  <p class="text-regular">
                    {{ item.name }} : {{ item.description }}
                  </p>
                  <div class="list-form">
                    <div class="list-radio">
                      <template v-for="note in item.notes">
                        <div class="form-radio" :key="note.id">
                          <input
                            type="radio"
                            class="form-check-input m-0"
                            :name="item.id"
                            :id="note.id"
                            :disabled="!shouldShowFinishButton"
                            :value="note.id"
                            :checked="checkscorigvoted(note.id, item)"
                            @change="
                              updateOrCreateNoteEval(
                                activeRecord.id,
                                item.id,
                                note.id,
                              )
                            "
                          />
                          <label class="form-check-label" :for="note.id">
                            {{ note.name }}
                          </label>
                          <div
                            class="tooltip ml-2"
                            @click="handleShowNote(note)"
                          >
                            <i
                              v-if="
                                selectedNote != null &&
                                selectedNote?.id == note.id
                              "
                              class="icon-info-bold"
                            ></i>
                            <i v-else class="icon-info-light"></i>
                          </div>
                        </div>
                      </template>
                    </div>
                    <div class="zone-texte">
                      <textarea
                        cols="50"
                        :disabled="!shouldShowFinishButton"
                        rows="8"
                        v-model="item.commentaire"
                        @blur="updateCommentaire(item, item.commentaire)"
                      ></textarea>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
          <div class="panel_footer" :key="categorie.id + '_btn'">
            <button
              @click="
                () => {
                  activeCategory--;
                  scrollToRef();
                }
              "
              v-if="
                activeCategory > 0 &&
                activeCategory <= grid?.categories.length - 1
              "
              class="prev mr-3"
              id="prev2"
            >
              Prcdent
            </button>
            <button
              @click="
                () => {
                  activeCategory++;
                  scrollToRef();
                }
              "
              v-if="activeCategory < grid.categories.length - 1"
              class="next"
              id="next1"
            >
              Continuer
            </button>
            <!-- <button
              @click="
                () => {
                  handleFinishEvaluation();
                }
              "
              v-if="
                activeCategory == grid.categories.length - 1 &&
                activeRecord.evaluationFinished == 0
              "
              class="next"
              id="next1"
            >
              Terminer
            </button> -->
            
            <button
              @click="handleFinishEvaluation()"
              v-if="shouldShowFinishButton"
              class="next"
              id="next1"
            >
            <p v-if="shouldShowFinishText &&  activeRecord.evaluationFinished === 1 "> Il vous reste 2 heures pour effectuer les dernires modifications. </p>
            <p v-else> Terminer </p>
              
            </button>
          </div>
        </template>
      </div>
    </template>
    <DrawerInfo
      :isOpened="showDrawer"
      @close="
        () => {
          showDrawer = false;
          setSelectedNote(null);
        }
      "
    ></DrawerInfo>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
import DrawerInfo from '@/components/DrawerForm/Records/DrawerInfo.vue';

export default {
  props: ['calibration'],
  components: {
    DrawerInfo,
  },
  data() {
    return {
      //  activeSection: 2,
      activeCategory: 0,
      gridId: [],
      showDrawer: false,
    };
  },
  watch: {
    'activeRecord.id': function(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.activeCategory = 0;
      }
    }
  },
  methods: {
    ...mapActions('detailsCalibrage', [
      'noteEvalCalibrage',
      'updateCommentaireNoteCalibration',
      'updateRecordCalibration',
      'updateRecordCalibrationGuest',
      'setCollection',
    ]),
    ...mapActions('record', ['setParentRecord', 'setSelectedNote']),
    handleShowNote(note) {
      this.setSelectedNote(note);
      this.toggleDrawer();
    },
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },
    // toggleAccordion(section) {
    //   this.activeSection = this.activeSection === section ? null : section;
    // },
    getCommentairescorigvoted(id) {
      if (
        this.activeRecord.evaluation_user != undefined &&
        this.activeRecord.evaluation_user.eval != undefined
      ) {
        let evals = this.activeRecord.evaluation_user.eval;
        const result = evals.find(({ note_id }) => note_id === id);
        if (result != undefined) {
          return result.commentaire;
        } else {
          return '';
        }
      } else {
        return '';
      }
    },
    checkscorigvoted(id, item) {
      let evaluation;

      for (let item of this.calibration.record_calibration) {
        for (let record of item.record) {
          if (record.id == this.activeRecord.id) {
            evaluation = item;
            break;
          }
        }
      }
      if (evaluation != undefined) {
        let evals = evaluation.evaluation_user;
        const result = evals.find(({ note_id }) => note_id === id);
        if (result != undefined) {
          item.commentaire = result.commentaire;
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    getScoringUser(id) {
      let evaluation;

      for (let item of this.calibration.record_calibration) {
        for (let record of item.record) {
          if (record.id == this.activeRecord.id) {
            evaluation = item;
            break;
          }
        }
      }
      if (
        evaluation &&
        evaluation.evaluation_user != undefined &&
        evaluation.scoring_categ != undefined
      ) {
        let score_category = evaluation.scoring_categ;
        let hasKey = score_category.hasOwnProperty(id);
        if (hasKey) {
          return score_category[id];
        } else {
          return '-';
        }
      }
    },
    updateOrCreateNoteEval(record_id, item_id, note_id) {
    
      let codeMember = this.$route.params.code;
      if (codeMember) {
        let data = {
          recordId: record_id,
          itemId: item_id,
          noteId: note_id,
          calibrationId:
            this.calibration.calibration != undefined &&
            this.calibration.calibration.id != undefined
              ? this.calibration.calibration.id
              : null,
          is_manuel: true,
          code_calibration: codeMember,
        };
        this.noteEvalCalibrage(data).then((res) => {
        
          if (res.grid != undefined && res.grid.categories != undefined) {
            // this.setParentRecord({
            //   ...this.getParentRecord,
            //   ['grid']: res.grid,
            // });
            // this.gridId = res.gridId.categories;
          }
        });
      }
    },
    updateCommentaire(item, commentaire) {
      let codeMember = this.$route.params.code;
      let evaluation;

      for (let item of this.calibration.record_calibration) {
        for (let record of item.record) {
          if (record.id == this.activeRecord.id) {
            evaluation = item;
            break;
          }
        }
      }
      if (evaluation != undefined) {
        let evals = evaluation.evaluation_user;
        const result = evals.find(({ item_id }) => item_id === item.id);

        if (result != undefined) {
          result.commentaire = commentaire;
          let data = {
            commentaire: commentaire,
            id: result.id,
            code_calibration: codeMember,
          };
          this.updateCommentaireNoteCalibration(data);
        }
      } else {
        return false;
      }
    },
    scrollToRef() {
      // Access the referenced element using $refs
      const targetElement = this.$refs['catRef' + this.activeCategory][0];
      const ti = document.getElementById('content_page');

      // Check if the element exists
      if (targetElement) {
        // Scroll to the referenced element
        targetElement.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
          inline: 'nearest',
          offsetTop: targetElement.scrollTop + 50,
        });
      }
      /*  targetElement.scrollTo({
          top:  300,
          behavior: 'smooth'
        }); */
    },
    handleFinishEvaluation() {
      const payload = {
        id: this.activeRecord.recordCalibrationId,
        data: {
          finished: true,
          recordId: this.activeRecord.id,
        },
      };
      if (this.getIsGuest == true) {
        this.updateRecordCalibrationGuest(payload).then((res) => {
          this.setParentRecord({
            ...this.activeRecord,
            ['evaluationFinished']: 1,
          });
          let records = [];
          for (let item of this.calibration.record_calibration) {
            if (item.record[0].id == this.activeRecord.id) {
              records.push([
                {
                  ...item,
                  record: [{ ...item.record[0], ['evaluationFinished']: 1 }],
                },
              ]);
            } else {
              records.push(item);
            }
          }
        });
      } else {
        this.updateRecordCalibration(payload).then((res) => {
          this.setParentRecord({
            ...this.activeRecord,
            ['evaluationFinished']: 1,
          });
          let records = [];
          for (let item of this.calibration.record_calibration) {
            if (item.record[0].id == this.activeRecord.id) {
              records.push([
                {
                  ...item,
                  record: [{ ...item.record[0], ['evaluationFinished']: 1 }],
                },
              ]);
            } else {
              records.push(item);
            }
          }
        });
      }
    },
  },
  computed: {
    ...mapGetters('record', ['getParentRecord', 'getSelectedNote']),
    ...mapGetters('user', ['getUser']),
    ...mapGetters('detailsCalibrage', ['getCollection', 'getIsGuest']),
    selectedNote() {
      return this.getSelectedNote;
    },
    activeRecord() {
      
      return this.getParentRecord;
    },
    grid() {
      return this.getParentRecord?.grid ?? [];
    },
    user() {
      return this.getUser;
    },
    shouldShowFinishButton() {
      if (
        this.activeCategory === this.grid?.categories?.length - 1 &&
        this.activeRecord
      ) {
        if (this.activeRecord.evaluationFinished === 0) {
          return true;
        }
        
        if (this.activeRecord.evaluationFinished === 1 && this.activeRecord.recordCalibrationUpdatedAt) {
          const updatedAt = new Date(this.activeRecord.recordCalibrationUpdatedAt);
          const currentTime = new Date();
          const tenMinutesInMs = 2 * 60 * 60 * 1000;

          const diffInMs = currentTime - updatedAt;

          return diffInMs < tenMinutesInMs;
        }
      }
      return false;
    },

    shouldShowFinishText() {
      if (
        this.activeCategory === this.grid?.categories?.length - 1 &&
        this.activeRecord
      ) {
        if (this.activeRecord.evaluationFinished === 0) {
          return true;
        }
        
        if (this.activeRecord.evaluationFinished === 1 && this.activeRecord.recordCalibrationUpdatedAt) {
          const updatedAt = new Date(this.activeRecord.recordCalibrationUpdatedAt);
          const currentTime = new Date();
          const tenMinutesInMs = 2 * 60 * 60 * 1000;

          const diffInMs = currentTime - updatedAt;

          return diffInMs < tenMinutesInMs;
        }
      }
      return false;
    },
    
  },
};
</script>
