<template>
  <div class="list_card_stat">
    <template v-if="data.stat != undefined">
      <template v-for="(item, key) in data.stat">
        <div class="card_stat" :key="key">
          <div class="status">
            <div>
              <span>Titre appel</span>
              <i class="icon-right-circle" @click="goToCampagne()"></i>
            </div>
            <div class="title">{{ key }}</div>
          </div>
          <vue-ellipse-progress
            class="progress__"
            :progress="item"
            line-mode="normal 0"
            :empty-thickness="15"
            :angle="0"
            emptyColor="transparent"
            :thickness="0"
            :size="224"
            :legendValue="item"
            animation="rs 700 1000"
            :gap="10"
            half
            :dot="{
              size: 25,
              transitionDuration: '1s',
              border: '6px #140727 solid',
            }"
          >
            <span class="vl_" slot="legend-value">/100</span>
          </vue-ellipse-progress>
          <!----Greeen---->
          <div class="icon-stat">
            <i class="icon-stat-up">
              <div class="path1"></div>
              <div class="path2"></div>
            </i>
          </div>
          <!----Greeen---->

          <!------red----->
          <!-- <div class="icon-stat">
            <i class="icon-stat-down"> 
              <div class="path1"></div> 
              <div class="path2"></div></i>
          </div> -->
          <!------red----->

          <!------jaune----->
          <!-- <div class="icon-stat">
            <i class="icon-stat-equals"> 
              <div class="path1"></div> 
              <div class="path2"></div>
              <div class="path3"></div>
            </i>
          </div> -->
          <!------jaune----->
        </div>
      </template>
    </template>

    
    
    <div
      class="card_stat card_category"
      @click="toggleDrawer"
      v-if="!this.$globals.isRoleAllowed('roleAllowed', this.role)"
    >
      <div class="content_card">
        <i class="icon-insert pb-3"></i>
        <h5>Ajouter une catgorie</h5>
      </div>
    </div>
    <div class="card_stat call-center list_call_center">
      <ListUnacceptableSituationsCallCenter :key="keyListUnacceptableSituationsCallCenter"/>
    </div>
    <DrawerEvaluation
      :isOpened="showDrawer"
      @close="showDrawer = false"
      :dashboardPosition="true"
      :key="keyDrawer"
    />
  </div>

</template>

<script>
import DrawerEvaluation from '@/components/DrawerForm/Evaluation/DrawerEvaluation.vue';
import ListUnacceptableSituationsCallCenter from './ListUnacceptableSituationsCallCenter.vue';
import { VueEllipseProgress } from 'vue-ellipse-progress';
import { mapGetters, mapActions } from 'vuex';
export default {
  components: {
    DrawerEvaluation,
    VueEllipseProgress,
    ListUnacceptableSituationsCallCenter,
  },
  data() {
    return {
      showDrawer: false,
      keyDrawer: 1,
      role: null,
      keyListUnacceptableSituationsCallCenter:0
    };
  },
  methods: {
    ...mapActions('grid', ['setGridModel']),
    ...mapActions('campaign', ['addCampgneSelectedToUserWithStat']),
    toggleDrawer() {
      this.keyDrawer++;
      this.showDrawer = !this.showDrawer;
      if (
        this.getCampagneInfo != undefined &&
        this.getCampagneInfo.gridSelected != undefined &&
        this.getCampagneInfo.gridSelected.name !== null
      ) {
        this.setGridModel(this.getCampagneInfo.gridSelected);
        return this.getGridModel;
      }
    },
    goToCampagne() {
      this.$router.push({ name: 'campaigns' });
    },
  },
  computed: {
    ...mapGetters('campaign', ['getstatByCampagne', 'getCampagneInfo']),
    ...mapGetters('grid', ['getGridModel']),
    ...mapGetters('user', ['getUser']),
    user() {
      return this.getUser;
    },
    data() {
      return this.getstatByCampagne;
    },
  },

  mounted() {
    this.keyListUnacceptableSituationsCallCenter++;
    this.role = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';
    if (this.user) {
      const getCampagneInfo = {
        campagneId: this.user.selectedCampagneId,
        gridSelected: {
          id: this.user.selectedGridId,
        },
      };
      this.addCampgneSelectedToUserWithStat(getCampagneInfo);
    }
  },
  watch: {
    user: {
      handler(newval) {
        const payload = {
          campagneId: newval.selectedCampagneId,
          gridSelected: {
            id: newval.selectedGridId,
          },
        };

        this.addCampgneSelectedToUserWithStat(payload).then(() => {});
      },
    },
  },
};
</script>
