<template>
  <div class="filter">
    <ButtonSecondary
      icon="icon-filter"
      :text="$globals.capitalizeFirstLetter($t('common.filters'))"
      id="dropdownFilter"
    >
      <template v-if="countOptions > 0" v-slot:rightContent>
        <div class="filter-items-count">{{ countOptions }}</div>
      </template>
    </ButtonSecondary>

    <div id="dropdownMenuFilter">
      <div
        v-for="(item, index) of items"
        :key="item.key"
        class="content_callcenter content"
      >
        <div
          class="accordion-header"
          :data-toggle="index == 0 ? 'ElementCallcenter' : 'ElementCallcenter' + index"
        >
          <i class="icon-arrow-tab-right mr-3"></i>
          {{ item.label }}
        </div>

        <div
          class="accordion-content"
          :id="index == 0 ? 'ElementCallcenter' : 'ElementCallcenter' + index"
        >
          <InputSearch
            @input-value="
              (value) => {
                handleSearch(value, item.key);
              }
            "
            :inputId="item.key"
            class="search-field"
            placeholder="Recherche"
          />
          <div class="form-checkbox flexbox mt-7">
            <div class="scroll">
              <!-- eslint-disable -->
              <div
                v-for="option of item.filters"
                :key="option.id + item.key"
                class="form-check"
                v-if="
                  option.name?.toLowerCase().includes(searchKeys[item.key]?.toLowerCase())
                "
              >
                <input
                  :value="option.id"
                  type="checkbox"
                  :id="option.id + item.key + item.label"
                  v-model="selectedOptions[item.key]"
                  @change="handleCheckboxChange"
                />
                <label :for="option.id + item.key + item.label">
                  {{ option.name }}
                </label>
              </div>
              <!-- eslint-enable -->
              <span
                v-if="selectedOptions[item.key]?.length > 0"
                @click="handleReset(item.key)"
                class="show-more mt-4"
              >
                Rintialiser
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ButtonSecondary from "@/components/FormWidgets/ButtonSecondary.vue";
import InputSearch from "@/components/FormWidgets/InputSearch.vue";
import $ from "jquery";
export default {
  components: {
    ButtonSecondary,
    InputSearch,
  },
  props: {
    items: {
      type: Array,
    },
    onFilterChange: Function,
    getSelectedCount: Number,
    updatedFilters: {
      type: Object,
      required: false
    }
  },
  data() {
    return {
      openSection: null,
      selectedOptions: {},
      searchKeys: {},
      countOptions: 0,
    };
  },
  methods: {
    toggleAccordion(index) {
      // If the clicked section is already open, close it. Otherwise, open it.
      this.openSection = this.openSection === index ? null : index;
    },
    handleCheckboxChange() {
      this.$emit("onFilterChange", this.selectedOptions);
    },
    handleSearch(value, key) {
      this.searchKeys[key] = value;
    },
    updateCountOptions() {
      let count = 0;
      for (const key in this.selectedOptions) {
        if (this.selectedOptions.hasOwnProperty(key)) {
          count += this.selectedOptions[key].length;
        }
      }
      this.countOptions = count;
    },
    getSelectedOptionNames() {
      const selectedOptions = [];
      for (const key in this.selectedOptions) {
        if (this.selectedOptions.hasOwnProperty(key)) {
          const selectedIds = this.selectedOptions[key];
          const selectedItem = this.items.find(item => item.key === key);
          const selectedOptionsData = selectedItem?.filters || [];
          selectedIds.forEach(id => {
            const selectedOption = selectedOptionsData.find(option => option.id === id);
            if (selectedOption) {
              selectedOptions.push({ label: selectedItem.label, key: selectedItem.key, name: selectedOption.name, id: selectedOption.id });
            }
          });
        }
      }
      return selectedOptions;
    },
    emitSelectedNames() {
      this.$emit('selected-names-changed', this.getSelectedOptionNames());
    },
    handleReset(keySearch) {
      this.selectedOptions[keySearch] = [];
      this.$emit("onFilterChange", this.selectedOptions);
    },
    updateSelectedOptions() {
      for (const key in this.updatedFilters) {
        if (this.updatedFilters.hasOwnProperty(key)) {
          const filterIds = this.updatedFilters[key];
          this.selectedOptions[key] = filterIds;
        }
      }
    }
  },
  watch: {
    selectedOptions: {
      handler() {
        this.updateCountOptions();
        this.emitSelectedNames();
      },
      deep: true,
    },
    updatedFilters: {
      immediate: true,
      handler() {
        this.updateSelectedOptions();
      },
      deep: true
    }
  },
  mounted() {
    for (let item of this.items) {
      this.$set(this.selectedOptions, item.key, []);
      this.$set(this.searchKeys, item.key, "");
    }

    const dropdownFilter = document.getElementById("dropdownFilter");
    const dropdownMenuFilter = document.getElementById("dropdownMenuFilter");

    // Show or hide the dropdown menu when the button is clicked
    dropdownFilter.addEventListener("click", function () {
      if (dropdownMenuFilter.style.display === "block") {
        dropdownMenuFilter.style.display = "none";
      } else {
        dropdownMenuFilter.style.display = "block";
      }
    });

    // Hide the dropdown menu if the user clicks anywhere else on the page
    document.addEventListener("click", function (event) {
      if (
        !dropdownFilter.contains(event.target) &&
        !dropdownMenuFilter.contains(event.target)
      ) {
        dropdownMenuFilter.style.display = "none";
      }
    });

    $(document).ready(function () {
      $(".accordion-header").click(function () {
        var target = $(this).data("toggle");

        // Remove the "active" class from all buttons
       // $(".accordion-header").removeClass("active");

        // Remove the "active" class from all toggle elements
       // $(".accordion-content").removeClass("active");

        // Close all other toggle elements
        $(".accordion-content")
          .not("#" + target)
          .slideUp().prev().removeClass('active');

        // Toggle the target element
        $("#" + target).slideToggle();

        // Add the "active" class to the clicked button
      //  $(this).addClass("active");

        // Add the "active" class to the target element
      //  $("#" + target).toggleClass("active");
      });
      $('#dropdownMenuFilter .content_callcenter:first-child .accordion-header').addClass('active');
      //$('#dropdownMenuFilter .content_callcenter .openadr').prev().removeClass('active');
      $(".accordion-header").click(function () {
      $(this).toggleClass('active');
      });

    
      
    });
  },
};
</script>
