<template>
  <transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true }" class="show">
      <div class="header_drawer">
        <div class="title">
          <!-- <h4>{{ $t('components.addCampaignDrawer.addCampaign') }}</h4> -->
          {{
            model.id == null
              ? $t('components.addCampaignDrawer.addCampaign')
              : $t('components.addCampaignDrawer.updateCampaign')
          }}
        </div>
        <div class="content-button btn-add">
          <button class="close mr-5" @click="handleClose">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <Button
            :loading="loading"
            @when-cliked="handleOnSubmit"
            :text="
              $globals.capitalizeFirstLetter(
                model.id == null ? $t('common.save') : $t('common.save'),
              )
            "
          />
        </div>
      </div>

      <div class="body_drawer">
        <div class="form-group border-b mb-5">
          <input-field
            class="mb-7"
            :defaultValue="model.name"
            :hasError="errors != null && errors.name != null"
            :errorMessage="
              errors && errors.name != null
                ? $t('errors.' + errors.name[0])
                : ''
            "
            @input="
              (e) => {
                this.setModel({
                  ...this.getModel,
                  ['name']: e.target.value,
                });
              }
            "
            name="name"
            :label="$t('components.addCampaignDrawer.campaignName')"
            id="1"
          />
        </div>

        <template v-if="model.id != null">
          <div class="form-field mb-2">
            <label>Grille d'valuation</label>
          </div>
          <transition-group>
            <div
              class="item-drag"
              v-for="element in model.grids"
              :key="element.id"
              @click="updateGrille(element)"
            >
              <span
                :class="{
                  actif: element.statuts === 1 && element.draft === 0,
                  inactif: element.statuts === 0 && element.draft === 0,
                  yellow: element.draft === 1,
                }"
                class="mr-1"
              ></span>
              <span></span>
              {{ element.name }}
              <span>#{{ element.id }}</span>
            </div>
          </transition-group>
          <DrawerEvaluation
            :isOpened="showDrawerGrille"
            @close="showDrawerGrille = false"
            :dashboardPosition="false"
            :campagnePosition="true"
            :key="keyDrawer"
          />
        </template>
      </div>
      <div id="overlay-" @click="handleClose"></div>
    </div>
  </transition>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import draggable from 'vuedraggable';
import DrawerEvaluation from '@/components/DrawerForm/Evaluation/DrawerEvaluation.vue';

// import SelectSingle from '@/components/FormWidgets/Select2/SelectSingle.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {
    InputField,
    Button,
    // SelectSingle,
    DrawerEvaluation,
  },
  data() {
    return {
      myArray: [],
      isNameValid: true,
      showDrawerGrille: false,
      keyDrawer: 1,
      loading: false,
    };
  },
  props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
  },
  watch: {
    'model.name': function (newValue) {
      let errors = [];
      if (newValue != null) {
        if (newValue == '') {
          errors.push('CAMPAIGN_NAME_REQUIRED');
        } else if (!/^[----A-Za-z0-9 -]+$/.test(newValue)) {
          errors.push('CAMPAIGN_NAME_REGEX');
        }
        if (errors.length > 0) {
          this.setErrors({ ...this.getErrors, ['name']: errors });
        } else {
          let copy = this.errors;
          if (copy != null) {
            if (copy.name != undefined) {
              delete copy.name;
              this.setErrors(copy);
            }
          }
        }
      }
    },
  },
  methods: {
    ...mapActions('campaign', [
      'setModel',
      'createCampaign',
      'setErrors',
      'updateCampagne',
      'setPagination',
      'resetModel',
    ]),
    ...mapActions('grid', ['setGridModel']),
    validateName(e) {
      // Expression rgulire pour le caractre "-"
      const regex = /^[a-zA-Z-]*$/;
      // Testez la valeur par rapport  l'expression rgulire
      const isValid = regex.test(e.target.value);

      this.isNameValid = isValid;

      // Mettez  jour le modle si la valeur est valide
      if (isValid) {
        this.setModel({
          ...this.getModel,
          ['name']: e.target.value,
        });
      } else {
        this.setErrors({
          ...this.getErrors,
          name: [this.$t('components.addCampaignDrawer.validName')],
        });
      }
    },

    handleOnSubmit() {

     

      if (this.model.id == null) {
        this.loading = true;

        this.createCampaign()
          .then(() => {
            this.loading = false;
           

            this.setPagination({
              ...this.getPagination,
              ['current_page']: 1,
            });
            this.handleClose();
          })
          .catch((err) => {
          
            this.loading = false;           
          });
      } else {
        this.loading = true;

        this.updateCampagne()
          .then(() => {
            this.loading = false;
            this.handleClose();
          })
          .catch(() => {
            this.loading = false;
          });
      }
    },
    handleClose() {
      this.resetModel();
      this.setErrors(null);
      this.$emit('close');
    },
    updateGrille(element) {
      this.showDrawerGrille = true;
      this.keyDrawer++;
      this.setGridModel({
        id: element.id,
        name: element.name,
        campagne: element.campagne,
        statuts: element.statuts,
        updatedAt: element.updatedAt,
        categories: element.categories,
        selectedCategorie: element.selectedCategorie,
        draft: element.draft,
      });
    },
  },

  computed: {
    ...mapGetters('campaign', ['getModel', 'getErrors', 'getPagination']),
    model() {
      return this.getModel;
    },
    errors() {
      return this.getErrors;
    },
  },
};
</script>
