<template>
  <div class="scribb-form-auth">
    <div class="scribb-form-login">
      <div class="container-auth flex align-items">
        <div class="left-column box py-7">
          <!-- <select v-model="$i18n.locale">
            <option v-for="(lang, i) in langs" :key="`lang-${i}`" :value="lang">
              {{ lang }}
            </option>
          </select> -->
          <div class="content-form">
            <div class="logo">
              <img src="../../assets/images/logo.svg" alt="" />
            </div>
            <h1 class="">{{ $t('views.login.morning') }} </h1>
            <h2>
              <strong>{{ $t('views.login.welcomeToScribb') }}</strong>
            </h2>
            <h3>{{ $t('views.login.loginOrRegister') }}</h3>
        
            <div class="forbiden-errors mt-4"  v-if="errors && errors.forbidden">
          
              <span>{{ $t('errors.' + errors.forbidden[0]) }}</span>
            </div>
            <div class="flex justify-center pt-9">
              <div class="card-form">
                <input-field
                  :hasError="errors != null && errors.email != null"
                  :errorMessage="
                    errors != null && errors.email != null
                      ? $t('errors.' + errors.email[0])
                      : ''
                  "
                  name="email"
                  label="E-mail*"
                  input-id="1"
                  @input="
                    (e) => {
                      email = e.target.value;
                    }
                  "
                  @keypress="handleKeyPress"
                />
                    
                <input-password
                  :hasError="errors != null && errors.password != null"
                  name="password"
                  :label="$globals.capitalizeFirstLetter($t('common.password'))"
                  :errorMessage="
                    errors != null && errors.password != null
                      ? $t('errors.' + errors.password[0])
                      : ''
                  "
                  @input="
                    (e) => {
                      password = e.target.value;
                      // Rinitialiser les erreurs du mot de passe lors de chaque saisie
                      errors = null;
                    }
                  "
                  @keypress="handleKeyPress"
                />
                <div class="link-forgot-password">
                  <div class="content-checkbox">
                    <div class="form-check">
                      <input type="checkbox" id="check-mdp" v-model="rememberMe" />
                      <label for="check-mdp">
                        {{ $t('views.login.rememberMe') }}
                      </label>
                    </div>
                  </div>

                  <router-link to="/forgot-password">
                    {{ $t('components.password.forgetPassword') }}
                  </router-link>
                </div>

                <p v-if="ServerError">{{ ServerError }}</p>
                <Button
                  class="mt-4"
                  :loading="loading"
                  @when-cliked="onSubmit"
                  :text="$globals.capitalizeFirstLetter($t('common.login'))"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="right-column box flex align-items justify-center">
          <AnimationScribb />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import { mapActions } from 'vuex';
import InputPassword from '@/components/FormWidgets/InputPassword.vue';
import AnimationScribb from '@/components/AnimationScribb/AnimationScribb.vue';

export default {
  components: {
    InputField,
    Button,
    InputPassword,
    AnimationScribb,
  },
  data() {
    return {
      email: null,
      password: null,
      rememberMe: false,
      errors: null,
      ServerError: null,
      loading: false,
    };
  },
  methods: {
    ...mapActions('user', ['logIn']),
    ...mapActions('notifications', ['getNotifications']),

    resetMessageError() {
      this.Error = [];
      this.ServerError = null;
    },

    handleKeyPress(event) {
      if (event.key === 'Enter') {
        this.onSubmit();
      }
    },

    // async onSubmit() {
    //   this.resetMessageError();
    //   this.errors = null;
    //   this.loading = true;
    //   await this.logIn({ email: this.email, password: this.password })
    //     .then(() => {
    //       // Get the current language from the route
    //       this.getNotifications();
    //       this.loading = false;
    //       this.$router.push({ name: 'dashboard' }); // Redirect to the dashboard page with the current language
    //     })
    //     .catch((error) => {
    //       this.loading = false;
    //       if (
    //         error.response.status === 422 ||
    //         error.response.status === 401 ||
    //         error.response.status === 403
    //       )
    //         this.errors = error.response.data.errors;
    //     });
    // },

    async onSubmit() {
      this.resetMessageError();
      this.errors = null;
      this.loading = true;
      await this.logIn({ email: this.email, password: this.password, remember_me: this.rememberMe })
        .then(() => {
          this.getNotifications();
          this.loading = false;
          this.$router.push({ name: 'dashboard' });
        })
        .catch((error) => {
       
          this.loading = false;
          if (
            error.response.status === 422 ||
            error.response.status === 401 ||
            error.response.status === 403
          )
            this.errors = error.response.data.errors;
        });
    },
    checkIfErrorExist(/* keySearched */) {
      let message = null;
      return message;
    },
  },
  mounted() {
    document.addEventListener('keydown', this.handleKeyPress);
  },

  beforeDestroy() {
    document.removeEventListener('keydown', this.handleKeyPress);
  },

};
</script>


