<template>
  <div class="content_page page_agents" :class="(role=='agent-qualite' )?'tab-agents':''">
    <div class="header_page flex-column">
      <div class="search_audio">
        <div class="content-filter">
          <InputSearch
            @input-value="handleSearch"
            class="search-field"
            :inputId="'searchAgents'"
            :placeholder="$globals.capitalizeFirstLetter($t('common.search'))"
          />
          <!-- <DateRangePicker
            :withTimePicker="false"
            @cancel="cancelDatePicker"
            @applyed="applyedDatePicker"
          /> -->
          <Filtres
            :items="filteredItems"
            @onFilterChange="handleFilterChanged"
            @selected-names-changed="handleSelectedNamesChanged"
            :updatedFilters="this.updatedFilters"
          />
          <ButtonSecondary
            @onClickSecondaryButton="
              () => {
                changeFilterTrash();
                setRouteName('archivedAgents');
                setFilter({ ...filter, ['trashed']: true });
                getData();
              }
            "
            v-if="filter.trashed === false"
            class="archive-link"
            icon="icon-archive-group"
            :text="$t('common.archiveList')"
          />
          <div
            @click="
              () => {
                changeFilterTrash();
                setRouteName('agents');
                setFilter({ ...filter, ['trashed']: false });
                getData();
              }
            "
            v-if="filter.trashed === true"
            class="btn-return"
          >
            <i class="icon-return mr-2"></i>
            <p>Tous les Agents</p>
          </div>

          <div v-if="selectedRows.length > 0" class="bloc-desarchive archv">
            <p>
              <span class="pr-2">{{ selectedRows.length }}</span>
              {{ selectedRows.length > 1 ? 'slectionns' : 'slectionn' }}
            </p>
            <!-- <ButtonSecondary
              class="btn-archive ml-6"
              icon="icon-archiver"
              :text="filter.trashed ? $t('common.desarchiver') : $t('common.archive')"
              @onClickSecondaryButton="
                () => {
                  handleSelection();
                }
              "
            /> -->
          </div>
        </div>
        <div class="btn-add flex">
          <div class="mr-3">
            <DropDownMenu
              :disabled="selectedRows.length > 0 ? false : true"
              :items="[
                {
                  label: filter.trashed
                    ? $t('common.desarchiver')
                    : $t('common.archive'),
                  onClick: () => {
                    if (selectedRows.length > 0) {
                      handleSelection();
                    }
                  },
                  disabled: selectedRows.length > 0 ? false : true,
                },
              ]"
              icon="icon-down"
              :text="'Actions groupes'"
              :btnId="'btn_dropdown_2'"
              :dropdownId="'dropdownMenu_2'"
              :btnclass="'btn-third'"
            />
          </div>
          <div>
            <DropDownMenu
              :disabled="!$globals.hasAuthorities('agents', 'create', connectedUser?.role)"
              :items="[
                {
                  label: $t('views.agents.addAgent'),
                  onClick: ShowDrawerAgent,
                },
                {
                  label: $t('views.agents.massImport'),
                  onClick: ShowDrawerImport,
                },
                {
                  label: $t('views.agents.massArchive'),
                  onClick: ShowDrawerArchive,
                },
              ]"
              icon="icon-down"
              :text="$t('views.agents.manageAgents')"
              :btnId="'btn_dropdown'"
              :dropdownId="'dropdownMenu'"
              :btnclass="'btn-primary'"
            />
          </div>

          <!-- <DropDownMenu
            :items="[
              {
                label: $t('views.agents.addAgent'),
                onClick: ShowDrawerAgent,
              },
              {
                label: $t('views.agents.massImport'),
                onClick: ShowDrawerImport,
              },
              {
                label: $t('views.agents.massArchive'),
                onClick: ShowDrawerArchive,
              },
            ]"
            icon="icon-down"
            :text="$t('views.agents.manageAgents')"
          /> -->
        </div>
      </div>

      <div v-if="selectedOptions.length > 0" class="tags-">
        <div
          v-for="(selectedOption, index) in selectedOptions"
          :key="index"
          class="tag"
        >
          {{ `${selectedOption.name} (${selectedOption.label})` }}
          <span
            class="remove-filter"
            @click="removeSelectedOption(selectedOption)"
          >
            <i class="icon-exit-"></i>
          </span>
        </div>
      </div>
    </div>

    <div class="card-table actions-tab mb-5">
      <TableComponent
        ref="agentTable"
        :key="switchedPage"
        :isLoading="isFetchingData"
        :multipleSelect="hasActions"
        :hasActions="hasActions"
        :actions="{
          archive: filter.trashed === false,
          update: true,
          unarchive: filter.trashed === true,
        }"
        :onUpdateItem="
          (index, item) => {
            handleUpdate(index, item);
          }
        "
        :onArchiveItem="
          (index, item) => {
            handleArchiveAent(item);
          }
        "
        :onUnArchiveItem="
          (index, item) => {
            handleArchiveAent(item);
          }
        "
        :data="data"
        :columns="columns"
        :redirection="true"
        @row-clicked="
          (item) => {
            handleeClickedRow(item);
          }
        "
        @onSort="handleSort"
        :keySelection="'id'"
        @onSelection="handleSelectedRows"
      />
    </div>
    <Pagination storeModuleName="agent" />
    <DrawerAgent
      :isOpened="showDrawer"
      :collectionTeam="collectionTeam"
      :collectionCallCenters="collectionCallCenters"
      @close="showDrawer = false"
    />
    <ImportMasse :show="showImportMasse" @close="showImportMasse = false" />
    <ArchiveMasse :show="showArchiveMasse" @close="showArchiveMasse = false" />
    <modal
      :show-modal="dispalyArchiveModal"
      :loading="isTrashLoading"
      :cancelText="$t('views.agents.modalBtnleft')"
      :okText="
        filter.trashed === true
          ? $t('common.desarchiver')
          : $t('common.archive')
      "
      @onConfirm="handleOk"
      @close="handleCancel"
    >
      <template v-slot:title>{{ $t('views.agents.modalTitle') }}</template>
      <template v-slot:content>
        <p v-if="selectedRows.length == 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('views.agents.modalUnArchiveOneItemText')
              : $t('views.agents.modalArchiveOneItemText')
          }}
          <strong>
            {{ selectedRows[0].firstName }} {{ selectedRows[0].lastName }}
          </strong>
        </p>
        <p v-if="selectedRows.length == 1">
          {{
            filter.trashed == true
              ? $t('views.agents.modalUnArchiveOneItemQuestion')
              : $t('views.agents.modalArchiveOneItemQuestion')
          }}
        </p>
        <p v-if="selectedRows.length > 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('views.agents.modalUnArchiveManyItemsText')
              : $t('views.agents.modalArchiveManyItemsText')
          }}
        </p>
        <div style="align-items: left" class="pb-5">
          <ul v-if="selectedRows.length > 1">
            <li v-for="item of selectedRows" :key="item.id">
              {{ item.firstName + ' ' + item.lastName }}
            </li>
          </ul>
        </div>

        <p v-if="selectedRows.length > 1">
          {{
            filter.trashed == true
              ? $t('views.agents.modalUnArchiveManyItemsQuestion')
              : $t('views.agents.modalArchiveManyItemsQuestion')
          }}
        </p>
      </template>
    </modal>
  </div>
</template>

<script>
import DrawerAgent from '@/components/DrawerForm/Agent/DrawerAgent.vue';
import ImportMasse from '@/components/DrawerForm/Agent/ImportMasse.vue';
import ArchiveMasse from '@/components/DrawerForm/Agent/ArchiveMasse.vue';
import InputSearch from '@/components/FormWidgets/InputSearch.vue';
import Filtres from '@/components/FormWidgets/Filter/Filter.vue';
// import DateRangePicker from '@/components/FormWidgets/DateRange/DateRangePicker.vue';
// import 'vue2-daterange-picker/dist/vue2-daterange-picker.css';
import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';
import TableComponent from '@/components/Table/Table.vue';
import Pagination from '@/components/Table/Pagination.vue';
import { mapActions, mapGetters } from 'vuex';
import moment from 'moment';
import Modal from '@/components/Modal/Modal.vue';
import ButtonSecondary from '@/components/FormWidgets/ButtonSecondary.vue';
export default {
  components: {
    DrawerAgent,
    ImportMasse,
    InputSearch,
    DropDownMenu,
    TableComponent,
    Pagination,
    Filtres,
    // DateRangePicker,
    ArchiveMasse,
    Modal,
    ButtonSecondary,
  },
  name: 'agents',
  data() {
    return {
      dateRange: { startDate: null, endDate: null },
      showDrawer: false,
      showImportMasse: false,
      showArchiveMasse: false,
      dispalyArchiveModal: false,
      selectedAgent: null,
      selectedRows: [],
      selectedOptions: [],
      updatedFilters: {},
      switchedPage: 1,
      isTrashLoading: false,
      role: 'visitor',
    };
  },

  methods: {
    ...mapActions('agent', [
      'getData',
      'setModel',
      'setFilter',
      'setErrors',
      'setPagination',
      'deleteAgent',
      'resetState',
    ]),
    ...mapActions('app', ['setRouteName']),
    handleUpdate(index, item) {
      this.setModel({
        id: item.id,
        roleId: parseInt(item.roleId),
        firstName: item.firstName,
        lastName: item.lastName,
        hermesId: item.hermesId,
        callCenterId: item.callCenterId ? item.callCenterId.id : null,
      });
      this.showDrawer = true;
    },
    handleSearch(value) {
      this.$store.dispatch('agent/setFilter', {
        ...this.getFilter,
        ['keyword']: value,
      });
    },
    handleSelectedNamesChanged(selectedOptions) {
      this.selectedOptions = selectedOptions;
    },
    removeSelectedOption(selectedOption) {
      this.selectedOptions = this.selectedOptions.filter(
        (option) =>
          option.id !== selectedOption.id || option.key !== selectedOption.key,
      );

      const updatedFilters = {
        collectionCallCenters: this.selectedOptions
          .filter((item) => item.key === 'collectionCallCenters')
          .map((item) => item.id),
        collectionTeam: this.selectedOptions
          .filter((item) => item.key === 'collectionTeam')
          .map((item) => item.id),
      };

      this.handleFilterChanged(updatedFilters);
      this.updatedFilters = updatedFilters;
    },
    ShowDrawerImport() {
      this.showImportMasse = !this.showImportMasse;
    },

    ShowDrawerArchive() {
      this.showArchiveMasse = !this.showArchiveMasse;
    },

    handleeClickedRow(item) {
      this.setModel({
        id: item.id,
        roleId: parseInt(item.roleId),
        firstName: item.firstName,
        lastName: item.lastName,
        hermesId: item.hermesId,
        callCenterId: item.callCenterId ? item.callCenterId.id : null,
      });
      this.showDrawer = true;
    },
    ShowDrawerAgent() {
      this.showDrawer = !this.showDrawer;

      if (!this.$globals.isRoleAllowed('roleAllowed', this.role)) {
        const lastCallCenter =
          this.collectionCallCenters[this.collectionCallCenters.length - 1];
        if (lastCallCenter) {
          this.setModel({
            ...this.model,
            callCenterId: lastCallCenter.id,
            roleId: 1,
          });
        }
      } else {
        this.setModel({
          ...this.model,
          callCenterId: this.accessCallCenterId,
          roleId: 1,
        });
      }
    },
    handleArchiveAent(agent) {
      this.selectedAgent = agent;
      this.selectedRows = [agent];
      this.$refs.agentTable.setSelectedRows([agent]);
      this.dispalyArchiveModal = true;
    },
    handleCancel() {
      // this.selectedRows = [];
      // this.$refs.agentTable.setSelectedRows([]);
      this.dispalyArchiveModal = false;
    },
    handleOk() {
      // const agentId = this.selectedAgent.id;
      // this.dispalyArchiveModal = false;
      // this.setModel({
      //   id: agentId,
      // });
      this.isTrashLoading = true;
      this.$refs.agentTable.setSelectedRows([]);
      this.deleteAgent(this.selectedRows.map((x) => x.id)).then(() => {
        this.selectedRows = [];
        this.dispalyArchiveModal = false;
        this.isTrashLoading = false;
      });
    },
    handleFilterChanged(options) {
      // this.setFilter({
      //   ...this.getFilter,
      //   ['callcenter']: options.collectionCallCenters,
      //   ['team']: options.collectionTeam,
      // });

      this.setFilter({
        ...this.getFilter,
        ['callcenter']: options.collectionCallCenters,
        ['team']: options.collectionTeam,
      });
      this.getData();

      // this.$store.dispatch('agent/setFilter', {
      //   ...this.getFilter,
      //   ['callcenter']: options.collectionCallCenters,
      //   ['team']: options.collectionTeam,
      // });
      // console.log(this.getFilter);
      // this.$store.dispatch('agent/getData');
    },

    applyedDatePicker(date) {
      this.dateRange.startDate = date.startDate;
      this.dateRange.endDate = date.endDate;
    },
    cancelDatePicker() {
      this.dateRange.startDate = null;
      this.dateRange.endDate = null;
    },
    handleSort(options) {
      this.setFilter({
        ...this.getFilter,
        ['sortKey']: options.sortKey,
        ['sortOrder']: options.sortOrder,
      });
      this.getData();
    },
    handleSelectedRows(data) {
      this.selectedRows = data;
    },
    handleSelection() {
      this.dispalyArchiveModal = true;
    },
    changeFilterTrash() {
      this.$store.commit('agent/RESET_PAGINATION');
      this.switchedPage++;
      this.$refs.agentTable.setSelectedRows([]);
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ['trashed']: !this.filter.trashed });
      this.getData();
      if (this.filter.trashed === true) this.setRouteName('archivedAgents');
      else this.setRouteName('agents');
    },
    displayArchive() {
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ['trashed']: true });
      this.$refs.agentTable.setSelectedRows([]);
      this.getData();
    },
    displayActifAgents() {
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ['trashed']: false });
      this.$refs.agentTable.setSelectedRows([]);
      this.getData();
    },
  },
  async created() {
    await this.resetState();
  },
  mounted() {
    this.$store.dispatch('agent/setIsInDashboard', false);
    this.role = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';

    const userString = localStorage.getItem('user');
    const userObject = JSON.parse(userString);

    if (userObject && userObject.callCenterId) {
      const callCenterId = userObject.callCenterId;
      this.accessCallCenterOptions = [callCenterId];
      this.accessCallCenterId = callCenterId.id;
    }

    this.getData();
    if (!this.$globals.isRoleAllowed('roleAllowed', this.role)) {
      this.$store.dispatch('callCenter/setFilter', {
        ...this.$store.getters['callCenter/getFilter'],
        withPagination: 0,
        onlyIdName: 1,
      });
      this.$store.dispatch('callCenter/getData');
    }
    this.$store.dispatch('team/setFilter', {
      ...this.$store.getters['team/getFilter'],
      withPagination: 0,
      onlyIdName: 1,
    });
    this.$store.dispatch('team/getData');
  },
  watch: {
    'filter.keyword': function () {
      this.$store.dispatch('agent/setFilter', {
        ...this.getFilter,
        ['current_page']: 1,
      });
      this.$store.dispatch('agent/getData');
    },
    dateRange: {
      handler() {
        this.$store.dispatch('agent/setFilter', {
          ...this.getFilter,
          ['startDate']: this.dateRange.startDate
            ? moment(this.dateRange.startDate).format('YYYY-MM-DD')
            : this.dateRange.startDate,
          ['endDate']: this.dateRange.endDate
            ? moment(this.dateRange.endDate).format('YYYY-MM-DD')
            : this.dateRange.endDate,
        });
        this.$store.dispatch('agent/getData');
      },
      deep: true,
    },
  },
  computed: {
    ...mapGetters('agent', [
      'getCollection',
      'getFilter',
      'getPagination',
      'getIsFetching',
      'getModel',
    ]),
    ...mapGetters('user', ['getUser']),
    model() {
      return this.getModel;
    },
    isFetchingData() {
      return this.getIsFetching;
    },
    connectedUser() {
      return this.getUser;
    },
    collectionCallCenters() {
      if (!this.$globals.isRoleAllowed('roleAllowed', this.role)) {
        return this.$store.getters['callCenter/getCollection'].map((x) => ({
          id: x.id,
          name: x.name,
        }));
      } else {
        return [];
      }
    },
    collectionTeam() {
      const teams = this.$store.getters['team/getCollection'].map((x) => ({
        id: x.id,
        name: x.name,
      }));

      const teamsWithNoTeam = [{ id: 0, name: 'Sans quipe' }, ...teams];

      return teamsWithNoTeam;
    },
    columns() {
      const baseColumns = [
        {
          key: 'id',
          label: '#',
        },
        {
          key: 'name',
          label: this.$t('views.agents.table.columns.name'),
          render: (text, record) => {
            return `<span>${
              this.$globals.capitalizeFirstLetter(record.firstName) +
              ' ' +
              this.$globals.capitalizeFirstLetter(record.lastName) +
              ' <br><span style="font-size: 13px;">' + record.hermesId + '</span>'
            }</span>`;
          },
          sort: true,
          sortKey: 'first_name',
        },
        {
          key: 'role',
          label: this.$globals.capitalizeFirstLetter(this.$t('common.profile')),
          render: (text, record) => {
            return `${
              record.roleId == 1
                ? '<span class="agent">Agent</span>'
                : '<span class="sup">Superviseur</span>' /* ici test agent */
            } `;
          },
          sort: true,
          sortKey: 'role_id',
        },
        {
          key: 'equipe',
          label: this.$globals.capitalizeFirstLetter(this.$t('common.team')),
          render: (text, record) => {
            if (record.teams && record.teams.length > 0) {
              const teamNames = record.teams
                .map((team) => team.name)
                .join(', ');
              return `<div>${teamNames}</div>`;
            } else {
              return `<div><span class="empty-teams"> <i class="icon-alerte pr-1"></i> Sans quipe</span></div>`;
            }
          },
          sort: true,
          sortKey: 'team_id',
        },
        {
          key: 'created_at',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('common.createdAt'),
          ),
          render: (text, record) => {
            return `<span>${this.$globals.formatDateWithoutTime(record.created_at)}</span>`;
          },
          sort: true,
          sortKey: 'created_at',
        },
      ];

      if (
        !this.$globals.isRoleAllowed(
          'roleAllowed',
          localStorage.getItem('user')
            ? JSON.parse(localStorage.getItem('user')).role
            : 'visitor',
        )
      ) {
        baseColumns.splice(2, 0, {
          key: 'call_center',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('common.callCenter'),
          ),
          render: (text, record) => {
            const teamName =
              record.callCenterId !== null &&
              record.callCenterId.name !== null &&
              record.callCenterId.name !== undefined
                ? record.callCenterId.name
                : '-';
            return `<div>${this.$globals.capitalizeFirstLetter(teamName)}</div>`;
          },
          sort: true,
          sortKey: 'call_center_id',
        });
      }

      return baseColumns;
    },
    data() {
      return this.getCollection;
    },
    filter() {
      return this.getFilter;
    },
    filteredItems() {
      if (!this.$globals.isRoleAllowed('roleAllowed', this.role)) {
        return [
          {
            label: 'Call-center',
            key: 'collectionCallCenters',
            filters: this.collectionCallCenters,
          },
          {
            label: 'quipe',
            key: 'collectionTeam',
            filters: this.collectionTeam,
          },
        ];
      } else {
        return [
          {
            label: 'quipe',
            key: 'collectionTeam',
            filters: this.collectionTeam,
          },
        ];
      }
    },
    hasActions() {
      return this.$globals.hasAuthorities('agents', 'archive', this.getUser?.role);
    }
  },
};
</script>
