<template>
  <transition name="slide">
    <div v-if="show" :class="{ drawer: true, show: show }" class="show">
      <div class="header_drawer">
        <div class="title">
          <h4>{{ $t('views.agents.massImport') }}</h4>
        </div>
        <div
          class="content-button btn-add"
          style="justify-content: flex-end"
          v-if="
            (uploading === false && uploadingSuccess === false) || importError
          "
        >
          <button class="close mr-5" @click="close()">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <Button
            @when-cliked="uploadFile()"
            :text="$t('views.agents.import')"
          />
        </div>
        <div
          v-if="uploading && file !== null && importError === false"
          class="content-button btn-add"
          style="justify-content: flex-end"
        >
          <button
            class="close mr-5"
            style="color: #f5ff63; text-decoration: none"
          >
            {{ $t('views.agents.VeuillezPatienter') }}
          </button>
        </div>
        <div
          v-if="
            uploading === false &&
            uploadingSuccess === true &&
            importError === false
          "
          class="content-button btn-add"
          style="justify-content: flex-end"
        >
          <button
            class="close mr-5"
            @click="newUpload()"
            style="color: #f5ff63; text-wrap: nowrap"
          >
            {{ $t('views.agents.NouvelImport') }}
          </button>
          <Button
            @when-cliked="close()"
            :text="$t('views.agents.ArchivageTerminer')"
          />
        </div>
      </div>

      <div class="body_drawer importFile">
        <form id="import-masse">
          <div
            class="form-group-flex"
            v-show="
              (uploading === false && uploadingSuccess === false) || importError
            "
          >
            <div class="form-field">
              <label for="">{{ $t('views.agents.labelFichierCSV') }}</label>

              <div class="custom-file-upload">
                <input
                  type="file"
                  id="file"
                  ref="fileInput"
                  @change="handleFileUpload"
                />
                <div class="file-upload-buttons" @click.prevent="selectFile">
                  <span class="file-name">{{ fileNameInput }}</span>
                  <button>Parcourir</button>
                </div>
              </div>
              <p v-if="importError === false" class="pt-3 mb-7">
                {{ $t('views.agents.dessinCSV') }}
                <a :href="csvFileLik" :download="'scb_import_grids.json'">
                  {{ $t('views.agents.lienTelechargerGabarit') }}
                </a>
                <br />
                <a class="notice-button" @click="showDrawerNotice()">
                  {{ $t('views.agents.lienNotice') }}
                </a>
              </p>
              <div class="error mb-4 pt-2" v-if="importError">
                {{ importErrorMessage }}
              </div>
            </div>
          </div>
          <div v-if="importError === false">
            <div
              v-if="uploading && file !== null && importError === false"
              class="patienter py-7 text-center"
            >
              <p class="mb-3">
                {{ $t('views.agents.loadingMsgP1') }}
                <br />
                {{ $t('views.agents.loadingMsgP2') }}
              </p>
              <LoaderImport />
            </div>

            <div
              class="recapitalitf"
              v-if="uploading === false && uploadingSuccess === true"
            >
              <div class="importation-finaliser mb-5">
                <div class="py-4">
                  <h3 class="pb-3">
                    <i class="icon-checked_green pr-3"></i>
                    <strong>{{ $t('views.agents.finalImportation') }}</strong>
                  </h3>
                  <p>{{ $t('views.agents.recapImportation') }}</p>
                  <p>{{ $t('views.agents.recapline2') }}</p>
                </div>
              </div>
              <ul class="mb-5">
                <li>{{ successfulRows }} succs</li>
                <li>
                  {{
                    failedRows.length === 0
                      ? failedRows + ' erreur'
                      : failedRows + ' erreurs'
                  }}
                  <a href="#" @click.prevent="downloadFailedRowsCSV">
                    Tlcharger
                  </a>
                </li>
              </ul>

              <TableComponent :columns="columns" :data="data" class="mt-2" />
            </div>
          </div>
        </form>
        <Notice :isOpened="showNotice" @close="showNotice = false" />
      </div>
      <div id="overlay-" @click="close()"></div>
    </div>
  </transition>
</template>

<script>
import Button from '@/components/FormWidgets/Button.vue';
import TableComponent from '@/components/Table/Table.vue';
import LoaderImport from '@/components/AnimationScribb/LoaderBtn.vue';
import { mapActions, mapGetters } from 'vuex';
import Notice from "@/components/DrawerForm/Evaluation/Notice.vue";
export default {
  components: {
    TableComponent,
    Button,
    LoaderImport,
    Notice
  },
  data() {
    return {
      csvFileLik:
        process.env.VUE_APP_SERVER_URL +
        '/fichier-scribb/scb_import_grids.json',
      showImportMasse: false,
      file: null,
      uploading: false,
      uploadingSuccess: false,
      data: [],
      dataFailedRows: [],

      importError: false,
      importErrorMessage: '',
      failedRows: 0,
      successfulRows: 0,
      fileName: '',
      fileNameInput: 'Aucun fichier choisi',
      showNotice: false,

    };
  },
  props: ['show'],
  computed: {
    ...mapGetters('grid', ['getPagination', 'getErrors']),
    errors() {
      return this.getErrors;
    },
  },
  methods: {
    ...mapActions('grid', ['getData', 'setPagination', 'uploadGrids']),
    close() {
      this.$emit('close');
      this.newUpload();
    },
    newUpload() {
      this.showImportMasse = false;
      this.importError = false;
      this.importErrorMessage = '';
      this.resetFileUpload();
      const form = document.getElementById('import-masse');
      form.reset();
      this.fileNameInput = 'Aucun fichier choisi';
    },
    selectFile() {
      this.$refs.fileInput.click();
    },

    async handleFileUpload(event) {
      const file = event.target.files[0];
      if (file) {
        this.file = file;
        this.fileNameInput = file.name;
      }
    },

    async uploadFile() {
       if (this.file) {
        this.importError = false;
        this.importErrorMessage = '';
        this.uploadingSuccess = false;
        this.uploading = true;
        const response = this.uploadGrids(this.file)
          .then(() => {
            this.setPagination({
              ...this.getPagination,
              ['current_page']: 1,
            });
            this.uploadingSuccess = false;
            this.uploading = false;
            if (this.errors < 1) {

              this.close();
              this.resetFileUpload();
            } else {
              this.importError = true;
              this.importErrorMessage = this.errors;
            }
          })
          .catch(() => {
            this.importError = true;
            this.importErrorMessage = this.errors;
          });
      

      }
    },
    resetFileUpload() {
      this.file = null;
      document.getElementById('file').value = '';
      this.uploadingSuccess = false;
      this.uploading = false;

      this.fileNameInput = 'Aucun fichier choisi';
      const form = document.getElementById('import-masse');
      form.reset();
    },
    showDrawerNotice(){
      this.showNotice = !this.showNotice;
    },
  },
};
</script>
