<template>
  <transition name="slide">
  <div v-if="isOpened" :class="{ drawer: true }" class="show">
    <div class="header_drawer">
      <div class="title">
        <h4>
          {{
            model.id == null
              ? $t('views.agents.addAgent')
              : $t('views.agents.editAgent')
          }}
        </h4>
      </div>
      <div class="content-button btn-add">
        <button class="close mr-5" @click="handleClose">
          {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
          <i class="icon-exit"></i>
        </button>
        <Button
          :loading="loading"
          @when-cliked="handleOnSubmit"
          :text="$globals.capitalizeFirstLetter($t('common.save'))"
        />
      </div>
    </div>
    <div class="body_drawer">
      <div class="form-group-flex">
        <input-field
          class="mb-7"
          :defaultValue="model.firstName"
          :hasError="errors != null && errors.firstName != null"
          :errorMessage="
            errors && errors.firstName != null
              ? $t('errors.' + errors.firstName[0])
              : ''
          "
          @input="
            (e) => {
              this.setModel({
                ...this.getModel,
                ['firstName']: e.target.value,
              });
            }
          "
          name="firstName"
          :label="$globals.capitalizeFirstLetter($t('common.firstName'))"
          id="1"
        />
        <input-field
          :defaultValue="model.lastName"
          :hasError="errors != null && errors.lastName != null"
          :errorMessage="
            errors && errors.lastName != null
              ? $t('errors.' + errors.lastName[0])
              : ''
          "
          @input="
            (e) => {
              this.setModel({
                ...this.getModel,
                ['lastName']: e.target.value,
              });
            }
          "
          class="mb-7 ml-4"
          name="lastName"
          :label="$globals.capitalizeFirstLetter($t('common.lastName'))"
          id="2"
        />
      </div>
      <!-- style="display: flex; width: 100%"  -->
      <div class="container-grid">
        <SelectSingle
          :class="{ 'disable-select': this.$globals.isRoleAllowed('roleAllowed', this.role) }"
          :isDisabled="this.$globals.isRoleAllowed('roleAllowed', this.role) == true"
          :options="!this.$globals.isRoleAllowed('roleAllowed', this.role) ? collectionCallCenters : this.accessCallCenterOptions"
          :keyLabel="'name'"
          :keyValue="'id'"
          :hasError="errors != null && errors.callCenterId != null"
          :errorMessage="
            errors && errors.callCenterId != null
              ? $t('errors.' + errors.callCenterId[0])
              : ''
          "
          :defaultSelected="model.callCenterId"
          :label="
            $globals.capitalizeFirstLetter($t('components.team.callcenter'))
          "
          :onSelect="
            (item) => {
              setModel({ ...model, ['callCenterId']: item.id });
            }
          "
        />
        <SelectSingle
          :id="2"
          :options="options"
          :keyLabel="'label'"
          :keyValue="'value'"
          label="Profil"
          :hasError="errors != null && errors.roleId != null"
          :errorMessage="
            errors && errors.roleId != null
              ? $t('errors.' + errors.roleId[0])
              : ''
          "
          :defaultSelected="model.roleId"
          :onSelect="
            (item) => {
              setModel({ ...model, ['roleId']: item.value });
            }
          "
        />
      </div>
      <input-field
        :value="model.hermesId"
        :defaultValue="model.hermesId"
        @input="
          (e) => {
            this.setModel({
              ...this.getModel,
              ['hermesId']: e.target.value,
            });
          }
        "
        :hasError="errors != null && errors.hermesId != null"
        :errorMessage="
          errors && errors.hermesId != null
            ? $t('errors.' + errors.hermesId[0])
            : ''
        "
        class="mb-7"
        name="hermesId"
        :label="$globals.capitalizeFirstLetter($t('common.id'))"
        input-id="4"
      />
    </div>

    <div id="overlay-" @click="handleClose()"></div>
  </div>
</transition>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import SelectSingle from '@/components/FormWidgets/SelectSingle/SelectSingle.vue';
import { mapGetters, mapActions } from 'vuex';
export default {
  components: {
    InputField,
    Button,
    SelectSingle,
  },
  data() {
    return {
      showDrawer: false,
      options: [
        {
          label: 'Agent',
          value: 1,
        },
        {
          label: 'Superviseur',
          value: 2,
        },
      ],
      loading: false,
    };
  },
  props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
    collectionTeam: {
      type: Array,
      required: true,
    },
    collectionCallCenters: {
      type: Array,
      required: true,
    },
  },
  methods: {
    ...mapActions('agent', [
      'setModel',
      'createAgent',
      'setErrors',
      'resetModel',
      'updateAgent',
    ]),
    close() {
      this.showDrawer = false;
      this.resetModel();
      this.setErrors(null);
    },
    handleFirstName(e) {
      this.setModel({ ...this.getModel, ['firstName']: e.target.value });
    },
    handleClose() {
      this.$emit('close');
      this.resetModel();
      this.setErrors(null);
    },
    handleOnSubmit() {
      this.loading = true;
      if (this.model.id == null) {
        this.createAgent()
          .then(() => {
            this.loading = false;
            this.handleClose();
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        this.updateAgent()
          .then(() => {
            this.loading = false;
            this.handleClose();
          })
          .catch(() => {
            this.loading = false;
          });
      }
    },
    displayToKey() {},
  },
  computed: {
    ...mapGetters('agent', ['getModel', 'getErrors']),
    model() {
      return this.getModel;
    },
    errors() {
      return this.getErrors;
    },
  },
  watch: {
    'model.firstName': function (newValue) {
      if (newValue !== null) {
        let errors = [];
        if (newValue === '') {
          errors.push('FIRST_NAME_REQUIRED');
        } else if (newValue.length < 2) {
          errors.push('FIRSTNAME_MIN');
        } else if (!/^[A-Za-z0-9 -]+$/.test(newValue)) {
          errors.push('FIRSTNAME_REGEX');
        }
        if (this.getErrors) {
          if (errors.length > 0) {
            this.setErrors({ ...this.getErrors, ['firstName']: errors });
          } else {
            let copy = this.errors;
            delete copy.firstName;
            this.setErrors(copy);
          }
        }
      }
    },
    'model.lastName': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('LAST_NAME_REQUIRED');
      } else if (newValue && newValue.length < 2) {
        errors.push('LASTNAME_MIN');
      } else if (!/^[A-Za-z0-9 -]+$/.test(newValue)) {
        errors.push('LASTNAME_REGEX');
      }
      if (this.getErrors) {
        if (errors.length > 0) {
          this.setErrors({ ...this.getErrors, ['lastName']: errors });
        } else {
          let copy = this.errors;
          delete copy.lastName;
          this.setErrors(copy);
        }
      }
    },
    'model.hermesId': function (newValue) {
      let errors = [];
      if (newValue == null || newValue === '') {
        errors.push('HERMES_ID_REQUIRED');
      } else {
        if (!/^[a-zA-Z0-9]*$/.test(newValue)) {
          errors.push('HERMES_ID_REGEX');
        }
        if (newValue.length > 80) {
          errors.push('HERMES_ID_LENGTH_EXCEEDED');
        }
      }
      if (this.getErrors) {
        if (errors.length > 0) {
          this.setErrors({ ...this.getErrors, ['hermesId']: errors });
        } else {
          let copy = this.errors;
          delete copy.hermesId;
          this.setErrors(copy);
        }
      }
    }
  },
  mounted() {
    this.role = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';

    const userString = localStorage.getItem('user');
    const userObject = JSON.parse(userString);
    const callCenterId = userObject.callCenterId;
    this.accessCallCenterOptions = [callCenterId];
    
  },
};
</script>
