<template>
  <transition name="slide">
  <div v-if="show" :class="{ drawer: true, show: show }" class="show">
    <div class="header_drawer">
      <div class="title">
        <h4>{{ $t('views.agents.massArchive') }}</h4>
      </div>
      <div class="content-button btn-add" style="justify-content: flex-end;"
        v-if="(uploading === false && uploadingSuccess === false) || importError">
        <button class="close mr-5" @click="close()">
          {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
          <i class="icon-exit"></i>
        </button>
        <Button @when-cliked="uploadFile()" :text="$t('views.agents.archive')" />
      </div>
      <div v-if="uploading && file !== null && importError === false" class="content-button btn-add"
        style="justify-content: flex-end;">
        <button class="close mr-5" style="color: #F5FF63;">
          {{ $t('views.agents.VeuillezPatienter') }}
        </button>
      </div>
      <div v-if="uploading === false && uploadingSuccess === true && importError === false" class="content-button btn-add"
        style="justify-content: flex-end;">
        <button class="close mr-5" @click="close()" style="color: #F5FF63;">
          {{ $t('views.agents.ArchivageTerminer') }}
        </button>
        <Button @when-cliked="newUpload()" :text="$t('views.agents.NouvelArchive')" />
      </div>
    </div>

    <div class="body_drawer importFile">
      <form id="archive-masse">
        <div class="form-group-flex" v-if="(uploading === false && uploadingSuccess === false) || importError">
          <div class="form-field">
            <label for="">{{ $t('views.agents.labelFichierCSV') }}</label>
            <!-- <input type="file" accept=".csv" class="import" id="file" @change="handleFileUpload" @input="resetFile" /> -->  

            <div class="custom-file-upload">
              <input type="file" id="fileInput" ref="fileInput" @change="handleFileUpload" />
              <div class="file-upload-buttons" @click.prevent="selectFile">
                <span class="file-name">{{ fileNameInput }}</span>
                <button>Parcourir</button>
              </div>
            </div>

            <p v-if="importError === false" class="pt-3 mb-7">
              {{ $t('views.agents.dessinCSV') }}
              <a :href="csvFileLik">{{ $t('views.agents.lienTelechargerGabarit') }}</a>
            </p>
            <div class="error mb-4 pt-2" v-if="importError">
              {{ importErrorMessage }}
            </div>
          </div>
        </div>
      </form>

      <div v-if="importedAgents.length !== 0 && uploadingSuccess === false && uploading === false
        ">
        <div class="list-search">
          <span class="tag" v-for="(user, index) in importedAgents" :key="index">
            {{ user.join(' - ') }}
            <i class="icon-remove" @click="removeUser(user)"></i>
          </span>
        </div>

        <p class="mt-5">
          {{ this.importedAgents.length > 1 ? $t('views.agents.noticeArchivagePlural') : $t('views.agents.noticeArchivageSingle') }}
        </p>
        <div class="content-form-checkbox mt-5">
          <div class="form-check">
            <input v-model="isChecked" type="checkbox" id="l_1" />
            <label class="ml-2" for="l_1">
              {{ this.importedAgents.length > 1 ? $t('views.agents.confirmationArchivagePlural') : $t('views.agents.confirmationArchivageSingle') }}
            </label>
          </div>
        </div>
      </div>

      <div v-if="importError === false">
        <div v-if="uploading && file !== null && importError === false" class="patienter py-7 text-center">
          <p class="mb-3">
            {{ $t("views.agents.loadingMsgP1") }} <br>{{ $t("views.agents.loadingMsgP2") }}
          </p>
          <LoaderImport />
        </div>

        <div class="recapitalitf" v-if="uploading === false && uploadingSuccess === true">
          <div class="importation-finaliser mb-5">
            <div class="py-4">
              <h3>
                <i class="icon-checked_green pr-3"></i>
                <strong>{{ $t('views.agents.finalArchivage') }}</strong>
              </h3>
              <p>{{ $t('views.agents.recapArchivage') }}</p>
            </div>
          </div>
          <ul class="mb-5">
            <li>{{ successfulRows }} succs</li>
            <li>{{ failedRows }} erreurs <a href="#" @click.prevent="downloadFailedRowsCSV">Tlcharger</a></li>
          </ul>

          <TableComponent :columns="columns" :data="data" class="mt-2" />
        </div>

      </div>
    </div>
    <div id="overlay-" @click="close()"></div>
  </div>
  </transition>
</template>

<script>
//import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import TableComponent from '@/components/Table/Table.vue';
import { axios } from '../../../store/services/axios.js';
import LoaderImport from '@/components/AnimationScribb/LoaderBtn.vue';
import { mapActions, mapGetters } from "vuex";
export default {
  components: {
    TableComponent,
    Button,
    LoaderImport,
  },
  data() {
    return {
      csvFileLik:
        process.env.VUE_APP_SERVER_URL + "/fichier-scribb/scb_archive_agents.csv",
      showImportMasse: false,
      file: null,
      uploading: false,
      uploadingSuccess: false,
      data: [],
      dataFailedRows: [],
      columns: [
        {
          key: 'fullName',
          label: 'Agent',
          render: (text, record) => {
            return `<span>${record.hermesId}</span>`;
          },
        },
        {
          key: 'message',
          label: 'Statut',
          render: (text, record) => {
            const parsedobj = JSON.parse(JSON.stringify(record));
            if (parsedobj.errors) {
              let html = '<span>';
              Object.keys(parsedobj.errors).forEach((key) => {
                if (Array.isArray(parsedobj.errors[key])) {
                  parsedobj.errors[key].forEach((error) => {
                    const replacedError = this.$t(`errors.${error}`).replace(/ - /g, '<br/>');
                    html += `${replacedError}</br>`;
                  });
                } else {
                  const replacedError = this.$t(`errors.${parsedobj.errors[key]}`).replace(/ - /g, '<br/>');
                  html += `${replacedError}</br>`;
                }
              });
              html += '</span>';
              return html;
            } else {
              return `<span>${this.$t(`success.${parsedobj.status}`)}</span>`;
            }
          },
        },
      ],
      importError: false,
      importErrorMessage: '',
      failedRows: 0,
      successfulRows: 0,
      importedAgents: [],
      isChecked: false,
      fileName: "",
      fileNameInput: 'Aucun fichier choisi',
    };
  },
  props: ['show'],
  computed: {
    ...mapGetters("agent", [
      "getPagination",
    ]),
  },
  methods: {
    ...mapActions("agent", [
      "getData",
      "setPagination",
    ]),
    close() {
      this.$emit('close');
      this.newUpload();
    },
    newUpload() {
      this.showImportMasse = false;
      this.importError = false;
      this.importErrorMessage = '';
      this.resetFileUpload();

      const form = document.getElementById('archive-masse');
      form.reset();
      this.fileNameInput = 'Aucun fichier choisi';
      this.importedAgents = [];
      this.isChecked = false;
    },
    selectFile() {
      this.$refs.fileInput.click();
    },
    removeUser(userId) {

      this.importedAgents = this.importedAgents.filter((user) => user !== userId);

      const header = 'identifiant';
      const csvContent = `${header}\n${this.importedAgents.map(user => Object.values(user).join(',')).join('\n')}`;
      const blob = new Blob([csvContent], { type: 'text/csv' });
      const transformedFile = new File([blob], 'importedAgents.csv', { type: 'text/csv' });
      this.file = transformedFile;


      if (this.importedAgents.length === 0) {
        this.close();
        return;
      }
    },
    async handleFileUpload(event) {
      try {
        this.file = null;
        this.importError = false;
        this.importErrorMessage = '';
        this.uploadingSuccess = false;

        const file = event.target.files[0];
        if (file) {

          this.fileNameInput = file.name;
          const reader = new FileReader();

          reader.onload = async (e) => {
            try {
              this.file = file;

              const content = e.target.result;
              const lines = content.split('\n');

              const firstLine = lines[0].trim();
              if (firstLine === '' || firstLine !== 'identifiant') {
                this.uploading = false;
                this.uploadingSuccess = false;
                this.importError = true;
                this.importErrorMessage = this.$t('errors.VERIFY_CSV_HEADER_COLUMN_NAMES');
                return;
              } else {
                if (
                  lines.length <= 1 ||
                  (lines.length === 2 && lines[1].trim() === '')
                ) {
                  this.uploading = false;
                  this.uploadingSuccess = false;
                  this.importError = true;
                  this.importErrorMessage = this.$t('errors.EMPTY_FILE_CSV');
                  return;
                } else {
                  const importedAgents = [];

                  for (let i = 1; i < lines.length; i++) {
                    const values = lines[i].split(',');
                    if (values.some((value) => value.trim() !== '')) {
                      importedAgents.push(values);
                    }
                  }

                  this.importedAgents = importedAgents;

                }

              }

              this.uploading = false;
              this.uploadingSuccess = false;
            } catch (err) {
              this.uploading = false;
              this.uploadingSuccess = false;
            }
          };

          reader.readAsText(file);

        }
      } catch (err) {

        if (err.response && err.response.data && err.response.data.message) {
          this.importError = true;
          this.importErrorMessage = err.response.data.message;
        } else {
          this.importError = true;
          this.importErrorMessage =
            "Une erreur s'est produite lors du tlchargement du fichier.";
        }
      }
    },

    async uploadFile() {
      if (this.file && this.isChecked) {
        try {
          this.uploading = true;
          const formData = new FormData();
          formData.append('file', this.file);

          const config = {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('auth')}`,
            },
          };

          const response = await axios.post('/agent/massDeletion', formData, config);

          if (response && response.data.data) {
            const { data } = response.data.data;
            this.uploading = false;
            this.uploadingSuccess = true;
            this.data = [...data.failed_rows, ...data.successful_rows];
            this.failedRows = data.failed_rows.length;
            this.successfulRows = data.successful_rows.length;


            this.dataFailedRows = data.failed_rows;

            if (this.data.length === 0) {
              this.importError = true;
              this.importErrorMessage = this.$t('errors.EMPTY_FILE_CSV');
              this.resetFileUpload();
            }
            if (this.successfulRows > 0) {
              this.setPagination({
                ...this.getPagination,
                ['current_page']: 1,
              });
              this.$store.dispatch("agent/getData");
            }
          }
        } catch (err) {

          if (err.response && err.response.data && err.response.data.message) {
            this.importError = true;
            this.importErrorMessage = this.$t(`errors.${err.response.data.message}`);
          } else {
            this.importError = true;
            this.importErrorMessage =
              "Une erreur s'est produite lors du tlchargement du fichier.";
          }

        }
      }
    },
    resetFileUpload() {
      this.uploadingSuccess = false;
      this.uploading = false;
      const form = document.getElementById('archive-masse');
      form.reset();
      this.fileNameInput = 'Aucun fichier choisi';
    },
    resetFile() {
      this.file = null;
    },
    downloadFailedRowsCSV() {
      if (this.dataFailedRows.length > 0) {
        const filteredData = this.dataFailedRows.map(row => {
          const filteredRow = { ...row };

          if (filteredRow.hasOwnProperty('hermesId')) {
            filteredRow['identifiant'] = filteredRow['hermesId'];
            delete filteredRow['hermesId'];
          }

          delete filteredRow.roleName;
          delete filteredRow.callCenterName;
          delete filteredRow.errors;
          delete filteredRow.id;

          return filteredRow;
        });

        const csvContent = this.convertToCSV(filteredData);
        const blob = new Blob([csvContent], { type: 'text/csv' });
        const blobURL = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = blobURL;
        a.download = 'failed_rows.csv';
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        window.URL.revokeObjectURL(blobURL);
      }
    },
    convertToCSV(data) {
      const header = Object.keys(data[0]).join(',');
      const rows = data.map(row => Object.values(row).join(','));
      return `${header}\n${rows.join('\n')}`;
    },
  },
};
</script>
