<template>
  <div class="form-actions">
    <div :id="'actions-dropdown_' + index" style="display: block;">
      <div style="display: flex; flex-wrap: wrap">
        <div style="display: flex; flex-wrap: wrap;">
          <div v-for="member in moreMembers" :key="member.key" class="mr-1">
            <div :style="{ backgroundColor: member.avatarColor }" class="letter-avatar">
              {{ getInitials(member) }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['moreMembers', 'index'],
  data() {
    return {
      activeAction: null,
    };
  },
  mounted() {
    const btn = document.getElementById('btn-dropdown_' + this.index);
    const dropDwon = document.getElementById('actions-dropdown_' + this.index);
    const handleColse = () => {
      this.activeAction = null;
    };

    btn.addEventListener('click', function () {
      if (dropDwon.style.display === 'block') {
        dropDwon.style.display = 'none';
        handleColse();
      } else {
        dropDwon.style.display = 'block';
      }
    });

    document.addEventListener('click', function (event) {
      if (!btn.contains(event.target) || dropDwon.contains(event.target)) {
        dropDwon.style.display = 'none';
        handleColse();
      }
    });
  },
  components: {},
  methods: {
    getInitials(member) {
      if (member.email) {
        const firstName = member.firstName ? member.firstName.charAt(0).toUpperCase() : '';
        const lastName = member.lastName ? member.lastName.charAt(0).toUpperCase() : '';
        return firstName + lastName;
      } else {
        return '';
      }
    },
  },
};
</script>
