<template>
  <div
    class="list-audio scroll"
    ref="audioScrollContainer"
    @scroll="handleScroll"
  >

  
  <template v-if="recordsFavory.length > 0">
    <h6 class="favoris mb-3 "> {{$globals.capitalizeFirstLetter($t('common.favory'))  }} </h6>
      <template v-for="(record, index) of recordsFavory">
        <AudioItem
          :ref="`audioCard${index}`"
          :record="record"
          :key="'favory_' + record.id"
          :favory="true"
        />
      </template>
      <!-- <hr class="mb-3 "> -->
    </template>

    <template v-if="records.length > 0">
      <template v-for="(record, index) of records">
        <AudioItem
          :ref="`audioCard${index}`"
          :record="record"
          :key="'record_' + index + record.id"
          :favory="false"
          @openToggleDrawer="openToggleDrawer"
        />
      </template>
        <NotifCalibrage
          @openToggleDrawer="openToggleDrawer"
          ref="notificationRef"
        />
    </template>
    <template v-if="isfetchingRecords">
      <Loader />
    </template>
  </div>
</template>

<script>
import Loader from '@/components/AnimationScribb/Loader.vue';
import NotifCalibrage from '@/components/Records/NotifCalibrage/NotifCalibrage.vue';
import { mapActions, mapGetters } from 'vuex';
import AudioItem from './AudioItem.vue';
export default {
  components: { AudioItem, Loader, NotifCalibrage, },
  data() {
    return {
      recordListHeight: 640,
      loading: false,
    };
  },
  methods: {
    ...mapActions('record', ['setPagination', 'getData']),
    handleScroll() {
      const container = this.$refs.audioScrollContainer;
      if (
        container.scrollHeight >= 640 &&
        !this.isfetchingRecords &&
        this.pagination.last_page &&
        this.pagination.current_page < this.pagination.last_page
      ) {
        this.setPagination({
          ...this.getPagination,
          ['current_page']: this.pagination.current_page + 1,
        });
        this.getData();
      }
    },
    generateListAudioHeight() {
     
      if (this.records.length > 0 && this.isfetchingRecords == false) {
        const elements = this.$refs.audioCard0[0].$refs.audioItem;
        this.recordListHeight = elements.getBoundingClientRect().height * 5;
      }
    },
    openToggleDrawer() {
      this.$emit('openToggleDrawer');
    },
  },
  computed: {
    ...mapGetters('record', [
      'getCollection',
      'getPagination',
      'getIsFetchingRecords',
    ]),
    records() {
      return this.getCollection.filter((record) => record.is_favory == false);
    },
    recordsFavory() {
      return this.getCollection.filter((record) => record.is_favory == true);
    },
    pagination() {
      return this.getPagination;
    },
    isfetchingRecords() {
      return this.getIsFetchingRecords;
    },
  },
  mounted() {},
  watch: {
    records() {
      this.generateListAudioHeight();
    },
    isfetchingRecords() {
      this.generateListAudioHeight();
    },
  },
};
</script>
