<template>
  <div class="acount-item pt-3 pb-3 border-b">
    <div class="details details-overlay">
      <Account
        :firstName="acount.firstName"
        :lastName="acount.lastName"
        :bgColor="acount.avatarColor"
        :disabled="['inactive', 'invitation-canceled'].includes(acount.status)"
      />
      <div class="infos">
        <span
          :class="{ 'disbaled-infos': acount.status == 'inactive' }"
          class="email"
        >
          {{ acount.email }}
        </span>
        <span
          :class="{ 'disbaled-infos': acount.status == 'inactive' }"
          class="role"
        >
          {{ acount.role == 'service-qualite' ? 'Service qualit' : '' }}
          {{
            acount.role == 'responsable-activite' ? 'Responsable activit' : ''
          }}
        </span>
      </div>
    </div>
    <div class="status">
      <div
        v-if="['active', 'inactive'].includes(acount.status)"
        class="dropdown"
        @click="ShowDropdownMenu"
      >
        <button class="dropbtn">
          <div :class="acount.status"></div>
          <span class="label">{{ acount.status }}</span>
          <i class="ml-3 icon-down"></i>
        </button>
        <div class="dropdown-content" v-if="ShowDropdown">
          <div
            @click="handleChangeStatus('active')"
            class="dropdown-item"
            v-if="acount.status == 'inactive'"
          >
            <div class="active"></div>
            <span class="label">active</span>
          </div>
          <div
            @click="handleChangeStatus('inactive')"
            class="dropdown-item"
            v-if="acount.status == 'active'"
          >
            <div class="inactive"></div>
            <span class="label">inactive</span>
          </div>
        </div>
      </div>
      <div
        class="close-outside"
        v-if="ShowDropdown"
        @click="CloseOutside"
      ></div>

      <div
        v-if="acount.status == 'send-an-invitation'"
        class="send-an-invitation"
      >
        <div class="progress"></div>
        <span class="label">Invitation envoye</span>
      </div>
      <div
        v-if="acount.status == 'invitation-canceled'"
        class="send-an-invitation"
      >
        <div class="inactive"></div>
        <span class="label">Invitation annule</span>
      </div>
    </div>

    <div class="acount-actions">
      <i
        @click="handleEditAcount()"
        v-if="acount.status == 'active'"
        class="icon-edit"
      ></i>
      <div>
        <i
          v-if="
            ['send-an-invitation', 'invitation-canceled'].includes(
              acount.status,
            )
          "
          @click="handleInviteUser"
          class="icon-message"
        ></i>
        <i
          v-if="acount.status == 'send-an-invitation'"
          @click="handleBlock"
          class="icon-forbidden"
        ></i>
      </div>
      <i
        style="margin-bottom: 5px"
        @click="handleDeleteAcount()"
        v-if="['inactive', 'invitation-canceled'].includes(acount.status)"
        class="icon-delete"
      ></i>
    </div>
  </div>
</template>

<script>
import Account from '@/components/FormWidgets/AvatarColorsRandom/AvatarColor.vue';

export default {
  props: ['acount'],
  components: {
    Account,
  },
  data() {
    return {
      ShowDropdown: false,
      options: [
        {
          label: 'Active',
          value: 1,
          leftContent: `<div class="actif" ></div>`,
        },
        {
          label: 'Inactif',
          value: 0,
          leftContent: `<div class="inactif" ></div>`,
        },
      ],
    };
  },
  methods: {
    handleChangeStatus(status) {
      this.$emit('onStatusChange', {
        id: this.acount.id,
        data: { status: status },
      });
    },
    ShowDropdownMenu() {
      this.ShowDropdown = !this.ShowDropdown;
    },
    CloseOutside() {
      this.ShowDropdown = false;
    },
    handleEditAcount() {
      this.$emit('onEditAcount', this.acount);
    },
    handleDeleteAcount() {
      this.$emit('onDeleteAcount', this.acount);
    },
    handleInviteUser() {
      this.$emit('onInviteUser', this.acount);
    },
    handleBlock() {
      this.$emit('onBlock', this.acount);
    },
  },
};
</script>
