    <div class="panel_body_" v-if="activeRecord?.status == 'completed'">
      <template v-for="(message, index) of activeRecord.segments">
        <div
          :key="message.id"
          v-if="message.speaker == 'agent'"
          class="content_left"
        >
          <div
            class="user_content"
            v-if="
              index == 0 ||
              (index != 0 &&
                message.speaker != activeRecord.segments[index - 1].speaker)
            "
          >
            <div class="avatar">
              <div class="icon_bold">
                <i class="icon-headphones-with-mic"></i>
              </div>
            </div>
            <div class="user">
              <h3>
                {{
                  activeRecord.agentId.firstName +
                  ' ' +
                  activeRecord.agentId.lastName
                }}
              </h3>
            </div>
            <div class="time">{{ formatSegmentTime(message.start_time) }}</div>
          </div>
          <p>
            {{ message.speatch == null ? '...' : message.speatch }}
          </p>
        </div>
        <div
          :key="message.id"
          v-if="message.speaker == 'prospect'"
          class="content_right"
        >
          <div class="">
            <div
              class="user_content"
              v-if="
                index == 0 ||
                (index != 0 &&
                  message.speaker != activeRecord.segments[index - 1].speaker)
              "
            >
              <div class="avatar">
                <div class="icon_bold"><i class="icon-profil"></i></div>
              </div>
              <div class="time mr-4">
                {{ formatSegmentTime(message.start_time) }}
              </div>
              <div class="user">
                <h3>Prospect</h3>
              </div>
            </div>
            <p>
              {{ message.speatch == null ? '...' : message.speatch }}
            </p>
          </div>
        </div>
      </template>
    </div>
    <div v-else class="text-center">
      <span>Transcription en cours</span>
      <div class="center-loading">
        <Loader />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import Loader from '@/components/AnimationScribb/Loader.vue';
import moment from 'moment';

export default {
  components: {
    Loader,
  },
  data() {
    return {
      current_speaker: '',
    };
  },
  computed: {
    ...mapGetters('record', ['getActiveRecord']),
    activeRecord() {
      return this.getActiveRecord;
    },
  },
  methods: {
    // checkSpeaker(speaker) {
    //   if (speaker == this.current_speaker) {
    //     this.current_speaker = speaker;
    //     return false;
    //   } else {
    //     this.current_speaker = speaker;
    //     return true;
    //   }
    // },
    formatSegmentTime(seconds) {
      return moment.utc(seconds * 1000).format('mm:ss');
    },
  },
};
</script>
