<template>
<transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true, show: show }" class="show">
        <div class="header_drawer">
            <div class="title">
                <h4>{{ $t('views.agents.notice') }}</h4>
            </div>
            <div class="content-button btn-add" style="justify-content: flex-end">
                <button class="close mr-5" @click="handleClose">
                {{ $globals.capitalizeFirstLetter($t("common.cancel")) }}
                <i class="icon-exit"></i>
                </button>
            </div>
        </div>

        <div class="body_drawer importFile">
            <div>
                <div class="recapitalitf">

                    <h4 class="mt-4 mb-5"><strong> Listes des rles </strong></h4>
                    <TableComponent :columns="columnsRole" :data="dataRole" class="mt-2 mb-5" />
                    <div v-if="!this.$globals.isRoleAllowed('roleAllowed', this.role)">
                        <h4 class="mt-4 mb-5"><strong> Listes des call-centers </strong></h4>
                        <TableComponent :columns="columnsCallCenter" :data="dataCallCenter" class="mt-2" />
                    </div>
                    
                </div>
            </div>
        </div>
        <div id="overlay-" v-show="show" @click="handleClose()"></div>
    </div>
</transition>
</template>
  
<script>
//import InputField from '@/components/FormWidgets/InputField.vue';
// import Button from "@/components/FormWidgets/Button.vue";
import TableComponent from "@/components/Table/Table.vue";
import { axios } from "../../../store/services/axios.js";
import { mapActions, mapGetters } from "vuex";
export default {
    components: {
        // Button,
        TableComponent,
    },
    data() {
        return {
            show: false,
            dataRole: [
                {id: '1', lib: 'Agent'},
                {id: '2', lib: 'Superviseur'},
            ],
            columnsRole: [
                {
                key: "id",
                label: "ID de rle",
                },
                {
                key: "lib",
                label: "tiquette de rle",
                },
            ],
            columnsCallCenter: [
                {
                key: "id",
                label: "ID du call-center",
                },
                {
                key: "name",
                label: "Nom du call-center",
                },
            ],
        };
    },
    props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
  },
  
    computed: {
    ...mapGetters('callCenter', ['getCollection', 'getFilter', 'getState']),
    dataCallCenter() {
      return this.getCollection;
    },
    },
    methods: {
    ...mapActions('callCenter', [
      'setModel',
      'getData',
      'resetModel',
      'setFilter',
    ]),
    handleClose() {
      this.$emit("close");
    },
        
},
  mounted() {
    this.role = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';
    
  },
};
</script>
  