<template>
  <div class="progress-content">
    <div class="main-title">
      <h5>{{ title }}</h5>
      <h6>{{ progress }}%</h6>
    </div>
    <div class="progress-bar">
      <div
        class="progress-bar-inner"
        :style="{ width: progress + '%', backgroundColor: color }"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: ['target', 'color', 'title',],
  data() {
    return {
      progress: 0,
    };
  },

  methods: {},

  computed: {},
  mounted() {
   // this.progress= Math.round(this.target)
    // Simulate progress increase over time
    let step= (this.target.toFixed(2)) / 5
 
    setInterval(() => {
      if (this.progress < this.target.toFixed(2)) {
        this.progress += step;
      }
      if (this.progress > this.target.toFixed(2)) {
        this.progress  = this.target.toFixed(2) ;
      }
    }, 100);
  },
};
</script>
