<template>
  <div class="content_page page_settings">
    <div
      v-if="
        data.length == 0 && $globals.hasAuthorities('settings', 'create', role)
      "
      class="header_page"
    >
      <div class="btn-add" @click="toggleDrawer">
        <ButtonPrimary icon="icon-add" text="Ajouter ...." />
      </div>
    </div>
    <div class="card-table  mb-5">
      <TableComponent
        :isLoading="isFetching"
        :hasActions="
          data.length > 0 && $globals.hasAuthorities('settings', 'update', role)
        "
        :actions="{ archive: true, update: true }"
        :onUpdateItem="
          (index, item) => {
            editRow(index, item);
          }
        "
        :onArchiveItem="
          (index, item) => {
            deleteRow(index, item);
          }
        "
        :data="data"
        :columns="columns"
      />
    </div>
    <DrawerSettings
      :isOpened="showDrawer"
      @close="
        showDrawer = false;
        setErrors([]);
      "
    />
  </div>
</template>
<script>
import DrawerSettings from '@/components/DrawerForm/Settings/DrawerSettings.vue';
import ButtonPrimary from '@/components/FormWidgets/ButtonPrimary.vue';
import TableComponent from '@/components/Table/Table.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  components: {
    DrawerSettings,
    ButtonPrimary,
    TableComponent,
  },
  data() {
    return {
      showDrawer: false,
      role: JSON.parse(localStorage.getItem('user')).role,
      tableData: [],
      isFetching: false,
    };
  },
  methods: {
    ...mapActions('settings', [
      'getSettingsData',
      'setModel',
      'setErrors',
      'deleteSetting',
    ]),

    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },

    getTableHeaders() {
      // Extract the keys from the first object in tableData
      if (this.tableData.length > 0) {
        return Object.keys(this.tableData[0]);
      }
      return [];
    },

    deleteRow(index, item) {
      this.deleteSetting(item.configurationId);
    },
    editRow(index, item) {
      this.setModel({
        id: item.configurationId,
        adresseSftp: item.adresseSftp,
        userNameSftp: item.userNameSftp,
        portSftp: item.portSftp,
        loginSftp: item.loginSftp,
        pathSftp: item.pathSftp,
        // datePosition: item.datePosition,
        // dateFormat: item.dateFormat,
        // qualificationPosition: item.qualificationPosition,
        // separatorCharacter: item.separatorCharacter,
        // identifiantPosition: item.identifiantPosition,
      });
      this.showDrawer = true;
    },

    handleInputValue(value) {
      this.keyword = value;
      this.fetchedData();
    },
  },
  watch: {
    keyword() {
      this.getSettingsData();
    },
  },
  computed: {
    ...mapGetters('settings', ['getSettingsCollection']),
    columns() {
      return [
        {
          key: 'adresseSftp',
          label: this.$t('views.settings.adresseSftp'),
        },
        {
          key: 'userNameSftp',
          label: this.$t('views.settings.userName'),
        },

        {
          key: 'portSftp',
          label: this.$t('views.settings.port'),
        },
        {
          key: 'loginSftp',
          label: this.$t('views.settings.login'),
        },
        {
          key: 'pathSftp',
          label: this.$t('views.settings.url'),
        },
        // {
        //   key: 'separatorCharacter',
        //   label: this.$t('views.settings.separator'),
        // },
        // {
        //   key: 'datePosition',
        //   label: this.$t('views.settings.positionDate'),
        // },
        // {
        //   key: 'identifiantPosition',
        //   label: this.$t('views.settings.positionIdentifiant'),
        // },
        // {
        //   key: 'qualificationPosition',
        //   label: this.$t('views.settings.positionQualification'),
        // },
        // {
        //   key: 'dateFormat',
        //   label: this.$t('views.settings.dateFormat'),
        // },
      ];
    },
    data() {
      return this.getSettingsCollection;
    },
  },
  name: 'parameter',
  mounted() {
    this.isFetching = true;
    this.getSettingsData().then(() => {
      this.isFetching = false;
    });
  },
};
</script>
