<template>
  <div class="content_page page_qualification">
    <div class="header_page">
      <div class="content-filter">
        <InputSearch
          class="search-field"
          :placeholder="
            $globals.capitalizeFirstLetter($t('common.search')) + '...'
          "
          @input-value="handleInputValue"
        />
        <ButtonSecondary
          @onClickSecondaryButton="changeFilterTrash()"
          v-if="filter.trashed === false"
          class="archive-link"
          icon="icon-archive-group"
          :text="$t('common.archiveList')"
        />
        <div
          @click="changeFilterTrash()"
          v-if="filter.trashed === true"
          class="btn-return"
        >
          <i class="icon-return mr-2"></i>
          <p>{{ $t('common.allQualification') }}</p>
        </div>

        <div v-if="selectedRows.length > 0" class="bloc-desarchive archv">
          <p>
            <span class="pr-2">{{ selectedRows.length }}</span>
            {{ selectedRows.length > 1 ? 'slectionns' : 'slectionn' }}
          </p>
          <!-- <ButtonSecondary
            class="btn-archive ml-6"
            icon="icon-archiver"
            :text="
              filter.trashed ? $t('common.desarchiver') : $t('common.archive')
            "
            @onClickSecondaryButton="
              () => {
                handleSelection();
              }
            "
          /> -->
        </div>
      </div>
      <div class="btn-add flex">
        <div class="mr-3">
          <DropDownMenu
            :disabled="selectedRows.length > 0 ? false : true"
            :items="[
              {
                label: filter.trashed
                  ? $t('common.desarchiver')
                  : $t('common.archive'),
                onClick: () => {
                  if (selectedRows.length > 0) {
                    handleSelection();
                  }
                },
                disabled: selectedRows.length > 0 ? false : true,
              },
            ]"
            icon="icon-down"
            :text="'Actions groupes'"
            :btnId="'btn_dropdown_2'"
            :dropdownId="'dropdownMenu_2'"
            :btnclass="'btn-third'"
          />
        </div>

        <div class="btn-add" @click="toggleDrawer">
          <ButtonPrimary
            icon="icon-add"
            :text="
              $globals.capitalizeFirstLetter($t('components.qualification.add'))
            "
          />
        </div>
      </div>
    </div>
    <div class="card-table actions-tab mb-5">
      <TableComponent
        ref="qualificationTable"
        :isLoading="isFetching"
        :multipleSelect="true"
        :onSelectItems="() => {}"
        :onSelectItem="(index, item) => {}"
        :hasActions="true"
        :actions="{
          archive: filter.trashed === false,
          update: true,
          unarchive: filter.trashed === true,
        }"
        :onUpdateItem="
          (index, item) => {
            handleUpdateClicked(item);
          }
        "
        :onArchiveItem="
          (index, item) => {
            handleArchive(item);
          }
        "
        :onUnArchiveItem="
          (index, item) => {
            handleArchive(item);
          }
        "
        :data="data"
        :columns="columns"
        @onSort="handleSort"
        :keySelection="'id'"
        @onSelection="handleSelectedRows"
        :key="switchedPage"
      />
    </div>
    <Pagination storeModuleName="qualification" />
    <DrawerQualification :isOpened="showDrawer" @close="closeDrawer" />
    <modal
      :show-modal="dispalyArchiveModal"
      :loading="isTrashLoading"
      :cancelText="$t('views.qualification.modalBtnleft')"
      :okText="
        filter.trashed === true
          ? $t('common.desarchiver')
          : $t('common.archive')
      "
      @onConfirm="handleOk"
      @close="handleCancel"
    >
      <template v-slot:title>
        {{ $t('views.qualification.modalTitle') }}
      </template>
      <template v-slot:content>
        <p v-if="selectedRows.length == 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('views.qualification.modalUnArchiveOneItemText')
              : $t('views.qualification.modalArchiveOneItemText')
          }}
          <strong>{{ selectedRows[0].name }}</strong>
        </p>
        <p v-if="selectedRows.length == 1">
          {{
            filter.trashed == true
              ? $t('views.qualification.modalUnArchiveOneItemQuestion')
              : $t('views.qualification.modalArchiveOneItemQuestion')
          }}
        </p>
        <p v-if="selectedRows.length > 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('views.qualification.modalUnArchiveManyItemsText')
              : $t('views.qualification.modalArchiveManyItemsText')
          }}
        </p>
        <div class="pb-5">
          <ul class="bold" v-if="selectedRows.length > 1">
            <li v-for="item of selectedRows" :key="item.id">
              {{ item.name }}
            </li>
          </ul>
        </div>
        <p v-if="selectedRows.length > 1">
          {{
            filter.trashed == true
              ? $t('views.qualification.modalUnArchiveManyItemsQuestion')
              : $t('views.qualification.modalArchiveManyItemsQuestion')
          }}
        </p>
      </template>
    </modal>
  </div>
</template>
<script>
import DrawerQualification from '@/components/DrawerForm/Qualification/DrawerQualification.vue';
import InputSearch from '@/components/FormWidgets/InputSearch.vue';
import ButtonPrimary from '@/components/FormWidgets/ButtonPrimary.vue';
import TableComponent from '@/components/Table/Table.vue';
import { mapGetters, mapActions } from 'vuex';
import Pagination from '@/components/Table/Pagination.vue';
import Modal from '@/components/Modal/Modal.vue';
import ButtonSecondary from '@/components/FormWidgets/ButtonSecondary.vue';
import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';

export default {
  components: {
    DrawerQualification,
    InputSearch,
    ButtonPrimary,
    TableComponent,
    Pagination,
    Modal,
    ButtonSecondary,
    DropDownMenu,
  },
  data() {
    return {
      showDrawer: false,
      dispalyArchiveModal: false,
      selectedQualification: null,
      selectedRows: [],
      switchedPage: 1,
      isTrashLoading : false
    };
  },
  created() {
    this.resetModel();
    this.resetFilter();
    this.resetPagination();
    this.setFilter({ ...this.filter, ['trashed']: false });
    this.getData();
  },

  computed: {
    ...mapGetters('qualification', ['getCollection', 'getFilter', 'getState']),
    isFetching() {
      return this.getState.isFetchingDataLoading;
    },
    columns() {
      return [
        {
          key: 'id',
          label: '#',
        },
        {
          key: 'name',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.qualification.columns.title'),
          ),
          sort: true,
          sortKey: 'name',
        },
        {
          key: 'date',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.qualification.columns.date'),
          ),
          render: (text, record) => {
            return `<span>${this.$globals.formatDate(
              record.updated_at,
            )}</span>`;
          },
          sort: true,
          sortKey: 'updated_at',
        },
      ];
    },
    data() {
      return this.getCollection;
    },
    keyword() {
      return this.getFilter.keyword;
    },
    filter() {
      return this.getFilter;
    },
  },
  methods: {
    ...mapActions('qualification', [
      'setModel',
      'getData',
      'resetModel',
      'resetFilter',
      'resetPagination',
      'setFilter',
      'deleteQualification',
    ]),
    ...mapActions('app', ['setRouteName']),

    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },
    handleInputValue(value) {
      this.setFilter({
        ...this.getFilter,
        ['keyword']: value,
      });
    },
    handleUpdateClicked(dataFromAction) {
      this.$store.commit('qualification/SET_MODEL', dataFromAction);
      this.toggleDrawer();
    },
    closeDrawer() {
      this.resetModel();
      this.showDrawer = false;
    },
    handleArchive(qualification) {
      this.selectedQualification = qualification;
      this.selectedRows = [qualification];
      this.dispalyArchiveModal = true;
    },
    handleCancel() {
      this.dispalyArchiveModal = false;
    },
    handleOk() {
      this.isTrashLoading = true;
      this.deleteQualification(this.selectedRows.map((x) => x.id)).then(() => {
        this.$refs.qualificationTable.setSelectedRows([]);
        this.selectedRows = [];
        this.dispalyArchiveModal = false;
        this.isTrashLoading = false;
      });

      // const qualificationId = this.selectedQualification.id;
      // this.dispalyArchiveModal = false;
      // this.setModel({
      //   id: qualificationId,
      // });
      // this.deleteQualification();
    },
    handleSort(options) {
      this.setFilter({
        ...this.getFilter,
        ['sortKey']: options.sortKey,
        ['sortOrder']: options.sortOrder,
      });
      this.getData();
    },
    handleSelection() {
      this.dispalyArchiveModal = true;
    },
    handleSelectedRows(data) {
      this.selectedRows = data;
    },

    changeFilterTrash() {
      this.switchedPage++;
      this.$store.commit('qualification/RESET_PAGINATION');
      this.$refs.qualificationTable.setSelectedRows([]);
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ['trashed']: !this.filter.trashed });
      this.getData();
      if (this.filter.trashed === true)
        this.setRouteName('archivedQualifications');
      else this.setRouteName('qualifications');
    },
  },
  watch: {
    'filter.keyword': function () {
      this.setFilter({
        ...this.getFilter,
        ['current_page']: 1,
      });
      this.getData();
    },
  },

  name: 'equipe',
  mounted() {
    this.setFilter({ ...this.getFilter, ['withPagination']: 1 });
    this.getData();
  },
};
</script>
