<template>
    <div>
        <div class="form-field mb-7">
            <label id="select-label" v-if="label">
                {{ label }}
            </label>
            <div ref="items" class="custom-select" :class="{ fieldError: hasError }" @blur="open = false">
                <div class="selected" :class="{ open: open }" @click="openSelected" :id="'custom-select' + index">
                    <i class="icon-down"></i>
                    <div style="display: flex; align-items: center; flex-wrap: wrap;">
                        <div class="list-search">
                            <template>
                                <span v-for="item in model.recordCalibrations" :key="item.id"
                                class="tag"
                                style="height: 35px;" @click.stop="removeSelected(item)"
                                >
                                {{ truncateText(item.name) }}
                                <i class="icon-exit-"></i>
                                </span>
                            </template>
                        </div>

                        <!-- <div v-for="item in model.recordCalibrations" :key="item.id">
                            <span class="selected-item" @click.stop="removeSelected(item)">{{ truncateText(item.name) }}</span>
                        </div> -->
                    </div>
                </div>
                <div class="items" :class="{ selectHide: !open }" :id="'items_' + index">
                    <div ref="search" class="select-search">
                        <input type="text" v-model="keySearch" @input="handleKeySearch" />
                    </div>
                    <div class="space" style="margin-top: 55px">
                        <template v-if="filteredOptions.length > 0">
                            <template v-for="option of formatedOptions">
                                <div :key="option.id" :class="{ 'item': true, 'selected-single': isSelected(option) }"
                                    v-if="option.text.toLowerCase().includes(keySearch.toLowerCase()) && !option.hidden"
                                    @click="toggleSelect(option)" class="item"
                                    style="display: flex; align-items: center">
                                    <template v-if="option.leftContent != null">
                                        <div v-html="option.leftContent"></div>
                                    </template>
                                    <span>{{ option.text }}</span>
                                </div>
                            </template>
                        </template>
                        <template v-else>
                            <div class="no-results">Aucun rsultat</div>
                        </template>
                    </div>
                </div>
            </div>
            <div class="error mb-4 pt-2" v-if="hasError">{{ errorMessage }}</div>
        </div>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
export default {
    props: {
        options: {
            type: Array,
            required: true,
        },
        defaultSelected: {
            type: [String, Number, Object, Array],
            required: false,
            default: () => [],
        },
        hasError: {
            type: Boolean,
        },
        errorMessage: {
            type: String,
        },
        onSelect: {
            type: Function,
            default: () => { },
        },
        label: {
            type: String,
            required: false,
        },
        keyValue: String,
        keyLabel: String,
        isDisabled: Boolean,
        id: Number,
    },
    data() {
        return {
            selected: [],
            open: false,
            formatedOptions: [],
            keySearch: '',
            index: null
        };
    },
    created() {
        this.formatOptions();
        if (Array.isArray(this.defaultSelected)) {
            this.selected = this.defaultSelected;
        } else if (this.defaultSelected) {
            this.selected = [this.defaultSelected];
        }
    },
    mounted() {
        this.setWidth();
        window.addEventListener('resize', this.setWidth); // Recalculate width on window resize
    },
    destroyed() {
        window.removeEventListener('resize', this.setWidth); // Remove event listener on component destroy
    },
    methods: {
        ...mapActions('calibrage', [
            'setModel',
        ]),
        handleKeySearch(event) {
            const inputValue = this.keySearch.trim();
            // this.$emit('lengthInputValue', inputValue);
        },
        formatOptions() {
            this.formatedOptions = [];
            for (let index in this.options) {
                let formatedItem = { ...this.options[index] };
                if (!formatedItem.id) {
                    formatedItem.id = formatedItem[this.keyValue];
                }
                if (!formatedItem.text) {
                    formatedItem.text = formatedItem[this.keyLabel];
                }
                this.formatedOptions.push(formatedItem);
                if (Array.isArray(this.defaultSelected) && this.defaultSelected.includes(formatedItem.id)) {
                    this.selected.push(formatedItem);
                } else if (formatedItem.id == this.defaultSelected) {
                    this.selected.push(formatedItem);
                }
            }
        },
        openSelected() {
            if (!this.isDisabled) this.open = !this.open;
        },
        setWidth() {
            const itemsWidth = this.$refs.items.offsetWidth;
            this.$refs.search.style.width =
                Number(itemsWidth) -
                8 /* - (this.options.length > 4 ? 6 : 3)  */ +
                'px'; // Apply the width to the .search div
        },
        isSelected(option) {
            return this.model.recordCalibrations.some(item => item.id === option.id);
        },
        toggleSelect(option) {
            if (this.isSelected(option)) {
                this.model.recordCalibrations = this.model.recordCalibrations.filter(item => item.id !== option.id);
            } else {
                this.model.recordCalibrations.push({
                    id: option.id,
                    index: this.model.recordCalibrations.length,
                    name: option.name,
                    firstName: option.agentId.firstName,
                    lastName: option.agentId.lastName,
                    callCenter: option.agentId.callCenterId ? option.agentId.callCenterId.name : null,
                });
            }
            this.onSelect(this.model.recordCalibrations);

            this.setModel({
                ...this.model,
                recordCalibrations: [...this.model.recordCalibrations],
            });
        },
        removeSelected(item) {
            this.model.recordCalibrations = this.model.recordCalibrations.filter(selectedItem => selectedItem.id !== item.id);
            this.onSelect(this.model.recordCalibrations);
            this.setModel({
                ...this.model,
                recordCalibrations: [...this.model.recordCalibrations],
            });
        },
        truncateText(text) {
            return text.length > 14 ? text.substring(0, 14) + '...' : text;
        },
        Close() {
            const togleClose = () => {
                this.open = false;
            }
            const btn = document.getElementById("custom-select" + this.index);
            const items = document.getElementById("items_" + this.index);
            // Hide the dropdown menu if the user clicks anywhere else on the page
            document.addEventListener("click", function (event) {
                if (!btn.contains(event.target) && (!items.contains(event.target))) {
                    togleClose();
                }
            });
        }
    },
    computed: {
        ...mapGetters('calibrage', ['getModel']),
        model() {
            return this.getModel;
        },
        filteredOptions() {
            return this.formatedOptions.filter((option) =>
                option.text.toLowerCase().includes(this.keySearch.toLowerCase()),
            );
        },
    },
    watch: {
        options() {
            this.formatOptions();
        },
        selected() {
            // Trigger some action when selected changes if needed
        },
    },
    mounted() {
        this.Close();
    },
};
</script>

<style>
/* Ajouter quelques styles pour amliorer l'apparence */
.selected-item {
    background: #e0e0e0;
    border-radius: 3px;
    margin: 2px;
    padding: 2px 6px;
    cursor: pointer;
}

.custom-select .selected {
    min-height: 45px;
    height: auto !important;
    padding-right: 2.6em;
}

.custom-select {
    line-height: 45px !important;
}

.list-search {
    display: -moz-flex;
    display: flex;
    -moz-align-items: center;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 0.7rem;
    margin-top: 0.7rem;
}
</style>
