<template>
  <div v-if="showModal" class="modal-dialog" @click="closeModalOutside">
    <div class="modal-content" @click.stop>
      <div class="modal-header">
        <h4><slot name="title"></slot></h4>
      </div>
      <div class="modal-body">
        <!-- Your modal content goes here -->
        <!-- <p class="pb-5">
          {{ text }}
        </p>
        <p>{{ question }}</p> -->
        <slot name="content"></slot>
      </div>
      <div class="modal-footer">
        <button class="btn_cancel mr-3" @click="closeModal">
          {{ cancelText }}
        </button>
        <button @click="handleOk" class="btn_remove">
          <LoaderBtn v-if="loading" />
          <template v-else>
            {{ okText }}
          </template>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import LoaderBtn from '@/components/AnimationScribb/LoaderBtn.vue';

export default {
  components: {
    LoaderBtn,
  },
  props: {
    showModal: Boolean,
    title: String,
    cancelText: String,
    okText: String,
    text: String,
    question: String,
    item: Object,
    loading: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    closeModal() {
      this.$emit('close', this.item);

    var modifEnreg=document.querySelector('.modif-enreg');
      if (modifEnreg) {
   modifEnreg.classList.remove('non-enregistr')
}




    },
    handleOk() {
      this.$emit('onConfirm', this.item);
    },
    closeModalOutside(event) {
      if (event.target.classList.contains('modal-dialog')) {
        this.closeModal();
      }
    },
  },
};
</script>
