<template>
  <div class="list_statut list_call_center">
    <div class="title mb-7">
      <div class="main-title">
        {{ $globals.capitalizeFirstLetter($t('common.statutsAppel')) }}
      </div>
      <div class="more-details">
        <router-link to="/callcenters">
          {{ $globals.capitalizeFirstLetter($t('common.moreDetails')) }}
        </router-link>

        <i class="icon-arrow-tab-right"></i>
      </div>
    </div>
    <div class="list" v-if="data">
      <div class="item" v-for="(item, key) in data" :key="key">
        <h6>{{ item.qualificationName }}</h6>
        <div class="icon-condition">
          <div
            v-if="
              item?.percentageDifference >= 0 &&
              item?.percentageDifference <= 30
            "
          >
            <img src="../../../assets/icon/data-positive1.svg" alt="" />
          </div>

          <div
            v-if="
              item?.percentageDifference >= 31 &&
              item?.percentageDifference <= 60
            "
          >
            <img src="../../../assets/icon/data-positive2.svg" alt="" />
          </div>

          <div
            v-if="
              item?.percentageDifference >= 61 &&
              item?.percentageDifference <= 100
            "
          >
            <img src="../../../assets/icon/data-positive3.svg" alt="" />
          </div>

          <div
            v-if="
              item?.percentageDifference < 0 &&
              item?.percentageDifference >= -30
            "
          >
            <img src="../../../assets/icon/data-negative1.svg" alt="" />
          </div>

          <div
            v-if="
              item?.percentageDifference <= -31 &&
              item?.percentageDifference >= -60
            "
          >
            <img src="../../../assets/icon/data-negative2.svg" alt="" />
          </div>

          <div
            v-if="
              item?.percentageDifference <= -61 &&
              item?.percentageDifference >= -100
            "
          >
            <img src="../../../assets/icon/data-negative3.svg" alt="" />
          </div>
        </div>

        <div class="nbrstat">{{ item.TotalCalls }}</div>
        <div class="taux green" v-if="item.percentageDifference >= 0">
          {{ item.percentageDifference }}%
        </div>
        <div class="taux red" v-if="item.percentageDifference < 0">
          {{ item.percentageDifference }}%
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {},
  data() {
    return {
      isFetching: false,
      //currentValue: parseFloat(document.getElementById('value').innerText),
      currentValue1: 10,
      currentValue2: -20,
      currentValue3: 70,
      currentValue4: -40,
    };
  },
  mounted() {
    if (this.user) {
      const payload = {
        campagneId: this.user.selectedCampagneId,
        gridSelected: {
          id: this.user.selectedGridId,
        },
      };
      this.getQualificationsStatistics(payload).then(() => {
        this.isFetching = false;
      });
    }
  },

  methods: {
    ...mapActions('qualification', ['getQualificationsStatistics']),
  },
  computed: {
    ...mapGetters('qualification', ['getCollection']),
    ...mapGetters({ campaign: 'campaign/getCampagneInfo' }),
    ...mapGetters('user', ['getUser']),
    user() {
      return this.getUser;
    },
    data() {
      return this.getCollection;
    },
  },
  watch: {
    user: {
      handler(newval) {
        this.isFetching = true;
        const payload = {
          campagneId: newval.selectedCampagneId,
          gridSelected: {
            id: newval.selectedGridId,
          },
        };
        if (
          newval?.selectedCampagneId != undefined &&
          newval?.selectedCampagneId != null
        ) {
          this.getQualificationsStatistics(payload).then((res) => {
            this.isFetching = false;
          });
        }
      },
      deep: true,
    },
  },
};
</script>
