<template>
    <div v-if="showModalPassword" class="modal-dialog p2_ update-password" @click="closeModalOutside">
      <div class="modal-content" @click.stop>
        <div class="modal-header ">
          <h4>Modifier le mot de passe</h4>
        </div>
        <div class="modal-body container-auth">
         
          <div class="form-password">
            <div class="user">
              <div class="icon">
                <i class="icon-headphones-with-mic"></i>
              </div>
              <div class="content">
                <div class="name">Gabriel Martineau</div>
                <div class="id">27462</div>
              </div>
            </div>
            <FieldPassword
            label="Nouveau mot de passe"
            />

            <FieldPassword
            label="Confirmer le nouveau mot de passe"
            />
         

    
        </div>
        <div class="modal-footer">
          <button class="btn_cancel mr-3" @click="closeModal">
            {{ cancelText }}
          </button>
          <button @click="handleOk" class="btn_remove">{{ okText }}</button>
        </div>
      </div>
      </div>
    </div>
  </template>
  <script>
  import FieldPassword from '@/components/FormWidgets/InputPassword.vue';
  export default {
    components: {
        FieldPassword,
  },
    props: {
      showModalPassword: Boolean,
      cancelText: String,
      okText: String,

    },
    methods: {
      closeModal() {
        this.$emit("close", this.item);
      },
      handleOk() {
        this.$emit("onConfirm", this.item);
      },
      closeModalOutside(event) {
        if (event.target.classList.contains("modal-dialog")) {
          this.closeModal();
        }
      },
    },
  };
  </script>
  