export const dateStringToRecordDateFormat = (dateString) => {
    const parsedDate = new Date(dateString);
    const year = parsedDate.getFullYear();
    const month = (parsedDate.getMonth() + 1).toString().padStart(2, '0');
    const day = parsedDate.getDate().toString().padStart(2, '0');
    const hours = parsedDate.getHours().toString().padStart(2, '0');
    const minutes = parsedDate.getMinutes().toString().padStart(2, '0');
    const seconds = parsedDate.getSeconds().toString().padStart(2, '0');
    return `${day}${month}${year}${hours}${minutes}${seconds}`
}
export const RecordDateFormatToDateString = (recordDate) => {
    const day = recordDate.substr(0, 2);
    const month = recordDate.substr(2, 2);
    const year = recordDate.substr(4, 4);
    const hours = recordDate.substr(8, 2);
    const minutes = recordDate.substr(10, 2);
    const seconds = recordDate.substr(12, 2);
    return {
        day,
        month,
        year,
        hours,
        minutes,
        seconds
    }

}

export function isValidDateTime(dateObject) {
    return !isNaN(dateObject.getTime()) && dateObject.toString() !== 'Invalid Date';
}