<template>
  <div class="content_page page_campagnes">
    <div class="header_page">
      <div class="content-filter">
        <InputSearch
          @input-value="handleSearch"
          class="search-field"
          :placeholder="$t('components.listCampaign.search')"
        />

        <!-- <Filtres
          :items="[
            {
              label: 'Call-center',
              key: 'callCenters',
              filters: callCenters,
              onCheck: () => {},
            },
          ]"
        /> -->

        <ButtonSecondary
          @onClickSecondaryButton="
            () => {
              changeFilterTrash();
              setRouteName('archivedCampaigns');
              setFilter({ ...filter, ['trashed']: true });
              getData();
            }
          "
          v-if="filter.trashed === false"
          class="archive-link"
          icon="icon-archive-group"
          :text="$t('common.archiveList')"
        />
        <div
          @click="
            () => {
              changeFilterTrash();
              setRouteName('campaigns');
              setFilter({ ...filter, ['trashed']: false });
              getData();
            }
          "
          v-if="filter.trashed === true"
          class="btn-return"
        >
          <i class="icon-return mr-2"></i>
          <p>Toutes les campagnes</p>
        </div>
        <div v-if="selectedRows.length > 0" class="bloc-desarchive archv">
          <p>
            <span class="pr-2">{{ selectedRows.length }}</span>
            {{ selectedRows.length > 1 ? 'slectionns' : 'slectionn' }}
          </p>
        </div>
      </div>

      <div class="btn-add flex">
        <div class="mr-3">
          <DropDownMenu
            :disabled="selectedRows.length > 0 ? false : true"
            :items="[
              {
                label: filter.trashed
                  ? $t('common.desarchiver')
                  : $t('common.archive'),
                onClick: () => {
                  if (selectedRows.length > 0) {
                    handleSelection();
                  }
                },
                disabled: selectedRows.length > 0 ? false : true,
              },
            ]"
            icon="icon-down"
            :text="'Actions groupes'"
            :btnId="'btn_dropdown_2'"
            :dropdownId="'dropdownMenu_2'"
            :btnclass="'btn-third'"
          />
        </div>
        <div class="btn-add" @click="toggleDrawer">
          <ButtonPrimary
            icon="icon-add"
            :text="$t('components.listCampaign.addCampaign')"
          />
        </div>
      </div>
      <div v-if="selectedOptions.length > 0" class="tags-">
        <div
          v-for="(selectedOption, index) in selectedOptions"
          :key="index"
          class="tag"
        >
          {{ `${selectedOption.name} (${selectedOption.label})` }}
          <span
            class="remove-filter"
            @click="removeSelectedOption(selectedOption)"
          >
            <i class="icon-exit-"></i>
          </span>
        </div>
      </div>
    </div>

    <div class="card-table actions-tab mb-5">
      <TableComponent
        ref="campagneTable"
        :key="switchedPage"
        :isLoading="isFetchingData"
        :multipleSelect="true"
        :hasActions="true"
        :actions="{
          archive: filter.trashed === false,
          update: true,
          duplicate: true,
          unarchive: filter.trashed === true,
        }"
        :onDeleteItem="(index, item) => {}"
        :onUpdateItem="
          (index, item) => {
            handleUpdate(index, item);
          }
        "
        :onArchiveItem="
          (index, item) => {
            handleDeleteRow(index, item);
          }
        "
        :onUnArchiveItem="
          (index, item) => {
            handleArchiveCampagne(item);
          }
        "
        :onDuplicateItem="
          (index, item) => {
            handleDuplicate(item);
          }
        "
        :redirection="true"
        :data="getCollection"
        :columns="columns"
        @onSort="handleSort"
        :keySelection="'campagneId'"
        @onSelection="handleSelectedRows"
      />
    </div>
    <modal
      :cancelText="$t('components.listCampaign.modalBtnleft')"
      :loading="isTrashLoading"
      :show-modal="dispalyArchiveModal"
      :title="'Attention'"
      :btnleft="'Annuler'"
      :okText="
        filter.trashed === true
          ? $t('common.desarchiver')
          : $t('common.archive')
      "
      :btnright="'Supprimer'"
      :text="'La suppression de cette campagne sera dfinitive et irrversible'"
      :question="'tes-vous sr de vouloir supprimer cette campagne ?'"
      @close="handleCancel"
      @onConfirm="handleOk"
    >
      <template v-slot:title>
        {{ $t('components.listCampaign.modalTitle') }}
      </template>

      <template v-slot:content>
        <p v-if="selectedRows.length == 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('components.listCampaign.modalUnArchiveOneItemText')
              : $t('components.listCampaign.modalArchiveOneItemText')
          }}
          <strong>{{ selectedRows[0].name }}</strong>
        </p>
        <p v-if="selectedRows.length == 1">
          {{
            filter.trashed == true
              ? $t('components.listCampaign.modalUnArchiveOneItemQuestion')
              : $t('components.listCampaign.modalArchiveOneItemQuestion')
          }}
        </p>
        <p v-if="selectedRows.length > 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('components.listCampaign.modalUnArchiveManyItemsText')
              : $t('components.listCampaign.modalArchiveManyItemsText')
          }}
        </p>
        <div class="pb-5">
          <ul class="bold" v-if="selectedRows.length > 1">
            <li v-for="item of selectedRows" :key="item.id">
              {{ item.name }}
            </li>
          </ul>
        </div>
        <p v-if="selectedRows.length > 1">
          {{
            filter.trashed == true
              ? $t('components.listCampaign.modalUnArchiveManyItemsQuestion')
              : $t('components.listCampaign.modalArchiveManyItemsQuestion')
          }}
        </p>

        <!-- <p class="pb-5">
          {{ $t('components.listCampaign.modalText') }}
        </p>
        <p>{{ $t('components.listCampaign.modalQuestion') }}</p> -->
      </template>
    </modal>

    <Pagination storeModuleName="campaign" />

    <DrawerCampagnes :isOpened="showDrawer" @close="showDrawer = false" />
  </div>
</template>

<script>
import DrawerCampagnes from '@/components/DrawerForm/Campagnes/DrawerCampagnes.vue';
import InputSearch from '@/components/FormWidgets/InputSearch.vue';
// import Filtres from '@/components/FormWidgets/Filter/Filter.vue';
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css';
import ButtonPrimary from '@/components/FormWidgets/ButtonPrimary.vue';
import TableComponent from '@/components/Table/Table.vue';
import Pagination from '@/components/Table/Pagination.vue';
import moment from 'moment';
import Modal from '@/components/Modal/Modal.vue';
import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';
import ButtonSecondary from '@/components/FormWidgets/ButtonSecondary.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {
    DrawerCampagnes,
    InputSearch,
    // Filtres,
    ButtonPrimary,
    TableComponent,
    Pagination,
    Modal,
    ButtonSecondary,
    DropDownMenu,
  },
  data() {
    return {
      showSidebar: false,
      showDrawer: false,
      showImportMasse: false,
      dateRange: { startDate: null, endDate: null },
      dispalyArchiveModal: false,
      selectedCampaign: null,
      selectedRows: [],
      selectedOptions: [],
      switchedPage: 1,
      isTrashLoading : false,
    };
  },

  methods: {
    ...mapActions('campaign', [
      'getData',
      'setModel',
      'setFilter',
      'setPagination',
      'deleteCampaign',
      'setCollection',
      'duplicateCampaign',
      'getNumberGrid',
    ]),
    ...mapActions('app', ['setRouteName']),

    toggleSidebar() {
      this.showSidebar = !this.showSidebar;
    },

    cancelDatePicker() {
      this.setFilter({
        ...this.getFilter,
        ['startDate']: null,
        ['endDate']: null,
      });
      this.setPagination({
        ...this.getPagination,
        ['current_page']: 1,
      });
      this.setCollection([]);
      this.getData()
        .then(() => {})
        .catch(() => {});
    },
    applyedDatePicker(date) {
      this.setFilter({
        ...this.getFilter,
        ['startDate']: date.startDate,
        ['endDate']: date.endDate,
      });
      this.setPagination({
        ...this.getPagination,
        ['current_page']: 1,
      });
      this.setCollection([]);
      this.getData()
        .then(() => {})
        .catch(() => {});
    },
    handleOk() {
      this.isTrashLoading = true
      this.deleteCampaign(this.selectedRows.map((x) => x.campagneId)).then(
        () => {
          this.$refs.campagneTable.setSelectedRows([]);
          this.selectedRows = [];
          this.dispalyArchiveModal = false;
          this.isTrashLoading = false;
        
        },
      );
    },
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },

    handleUpdate(index, item) {
      this.setModel({
        id: item.campagneId,
        name: item.name,
        numberOfGrids: item.numberOfGrids,
        createdAt: item.createdAt,
        updatedAt: item.updatedAt,
        grids: item.grids,
      });
      this.showDrawer = true;
    },
    handleSearch(value) {
      this.setPagination({
        ...this.getPagination,
        ['current_page']: 1,
      });
      this.setFilter({
        ...this.getFilter,
        ['keyword']: value,
      });
    },
    removeSelectedOption(selectedOption) {
      this.selectedOptions = this.selectedOptions.filter(
        (option) =>
          option.id !== selectedOption.id || option.key !== selectedOption.key,
      );

      const updatedFilters = {
        collectionCallCenters: this.selectedOptions
          .filter((item) => item.key === 'collectionCallCenters')
          .map((item) => item.id),
        collectionTeam: this.selectedOptions
          .filter((item) => item.key === 'collectionTeam')
          .map((item) => item.id),
      };

      this.handleFilterChanged(updatedFilters);
      this.updatedFilters = updatedFilters;
    },
    handleDeleteCampaign(id) {
      this.deleteCampaign(id).then(() => {
        this.dispalyArchiveModal = false;
        this.selectedCampaign = null;
      });
    },
    logEvent(selected, value) {
      if (selected == 'start') {
        this.setFilter({
          ...this.getFilter,
          ['startDate']: moment(value).format('YYYY-MM-DD'),
          ['endDate']: null,
        });
        this.getData();
      } else {
        this.setFilter({
          ...this.getFilter,
          ['endDate']: moment(value).format('YYYY-MM-DD'),
        });
        this.getData();
      }
    },
    handleCancel() {
      this.dispalyArchiveModal = false;
    },
    handleSort(options) {
      this.setFilter({
        ...this.getFilter,
        ['sortKey']: options.sortKey,
        ['sortOrder']: options.sortOrder,
      });
      this.getData();
    },
    handleSelectedRows(data) {
      this.selectedRows = data;
    },
    handleDeleteRow(index, item) {
      this.selectedRows = [item];
      this.dispalyArchiveModal = true;
    },
    handleArchiveCampagne(campaign) {
      this.selectedCampaign = campaign;
      this.selectedRows = [campaign];
      this.dispalyArchiveModal = true;
    },
    handleSelection() {
      this.dispalyArchiveModal = true;
    },
    changeFilterTrash() {
      this.$store.commit('campaign/RESET_PAGINATION');
      this.switchedPage++;
      this.$refs.campagneTable.setSelectedRows([]);
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ['trashed']: !this.filter.trashed });
      this.getData();
      if (this.filter.trashed === true) this.setRouteName('archivedCampaigns');
      else this.setRouteName('campaigns');
    },
    handleDuplicate(item) {
      this.duplicateCampaign(item.campagneId).then(() => {
        this.dispalyArchiveModal = false;
        this.selectedCampaign = null;
      });
    },
  },
  mounted() {
    this.setFilter({
      ...this.getFilter,
      ['withPagination']: 1,
      ['current_page']: 1
    });
    this.getData();
  },

  watch: {
    keyword() {
      this.getData();
    },
  },

  computed: {
    ...mapGetters('campaign', [
      'getCollection',
      'getFilter',
      'gfetPagination',
      'getIsFetching',
    ]),
    isFetchingData() {
      return this.getIsFetching;
    },
    columns() {
      return [
        {
          key: 'campagneId',
          label: '#',
        },
        {
          key: 'name',
          label: this.$t('components.listCampaign.fullName'),
          render: (text, record) => {
            return `<span>${record.name}</span>`;
          },
          sort: true,
          sortKey: 'name',
        },
        {
          key: 'numberOfGrids',
          label: this.$t('components.listCampaign.grille'),
          render: (text, record) => {
            return `<span>${record.numberOfGrids}</span>`;
          },
          sort: true,
          sortKey: 'numberOfGrids',
        },
        {
          key: 'createdAt',
          label: this.$t('components.listCampaign.creationDate'),
          render: (text, record) => {
            const formattedDate = moment(record.createdAt).format(
              'DD/MM/YYYY - HH[h]mm',
            );
            return `<span>${formattedDate}</span>`;
          },
          sort: true,
          sortKey: 'created_at',
        },
        {
          key: 'updatedAt',
          label: this.$t('components.listCampaign.updateDate'),
          render: (text, record) => {
            const formattedDate = moment(record.updatedAt).format(
              'DD/MM/YYYY - HH[h]mm',
            );

            return `<span>${formattedDate}</span>`;
          },
          sort: true,
          sortKey: 'updated_at',
        },
      ];
    },
    data() {
      return this.getCollection;
    },
    keyword() {
      return this.getFilter.keyword;
    },
    callCenters() {
      return this.$store.getters['callCenter/getCollection'].map((x) => ({
        id: x.id,
        name: x.name,
      }));
    },
    filter() {
      return this.getFilter;
    },
  },

  name: 'campagne',
};
</script>
