<template>
  <div>
    <div class="table scroll-x">
      <div class="table-row header" :class="data.length > 0 ? '' : 'empty'">
        <div
          class="table-cell"
          v-if="multipleSelect && multipleSelect === true"
        >
          <div class="form-check">
            <input
              v-model="isAllSelected[indexTeam]"
              @change="handleSelectRows"
              type="checkbox"
              :id="'item-list' + indexTeam + 'checkbox'"
            />
            <label :for="'item-list' + indexTeam + 'checkbox'"></label>
          </div>
        </div>
        <div class="table-cell" v-for="column in columns" :key="column.key">
          <ColumnRender
            :column="column"
            :sortOrder="sortOrder"
            :sortKey="sortKey"
            @onSortTriggered="(key) => handleSortKeyChanged(key)"
          />
        </div>
      </div>
      <template v-if="!isLoading">
        <template v-if="data.length > 0">
          <div
            :style="{ cursor: redirection ? 'pointer' : '' }"
            class="table-row"
            v-for="(item, index) in data"
            :key="index + indexTeam"
          >
            <div
              class="table-cell"
              v-if="multipleSelect && multipleSelect === true"
            >
              <div class="form-check">
                <input
                  @change="handleSelectedRow(item)"
                  type="checkbox"
                  :id="'item-list' + indexTeam + index + 'checkbox'"
                  :value="item[keySelection]"
                  v-model="selectedKeys"
                />
                <label
                  :for="'item-list' + indexTeam + index + 'checkbox'"
                ></label>
              </div>
            </div>
            <div class="table-cell" v-for="column in columns" :key="column.key">
              <TableCellRender
                :column="column"
                :data="item"
                @row-clicked="
                  (item) => {
                    handleRowClicked(item);
                  }
                "
              />
            </div>
          </div>
        </template>

        <div v-else class="table-row empty-data text-center">
          <div class="table-empty">
            <h6>
              {{ msgEmptyTable }}
            </h6>
            <button v-if="withButton" @click="handleButton()">
              {{ textButton }}
            </button>
          </div>
        </div>
      </template>

      <template v-else>
        <div class="table-row empty-data text-center">
          <div class="table-empty">
            <Loader />
          </div>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import TableCellRender from '../Table/TableCellRender.vue';
import ColumnRender from '../Table/ColumnRender.vue';
import Loader from '../AnimationScribb/Loader.vue';
import { mapGetters } from 'vuex';

export default {
  props: {
    columns: Array,
    data: Array,
    multipleSelect: {
      type: Boolean,
      default: false,
    },
    onSelectItems: {
      type: Function,
      required: false,
      default: function () {},
    },
    onSelectItem: {
      type: Function,
      required: false,
      default: function () {},
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    onClickRow: {
      type: Function,
      default: () => {},
    },
    redirection: {
      type: Boolean,
      default: false,
    },
    keySelection: {
      type: String,
      required: false,
      default: '',
    },
    indexTeam: {
      indexTeam: { type: Number, required: false, default: 0 },
    },
    msgEmptyTable: {
      type: String,
      default: function () {
        return this.$globals.capitalizeFirstLetter(this.$t('common.norecords'));
      },
    },
    withButton: {
      type: Boolean,
      default: false,
    },
    textButton: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      selectedRows: [],
      isAllSelected: {},
      selectedKeys: [],
      sortKey: null,
      sortOrder: 'asc',
    };
  },
  created() {
    if (!this.isAllSelected.hasOwnProperty(this.indexTeam)) {
      this.$set(this.isAllSelected, this.indexTeam, false);
    }
  },
  mounted() {},
  components: {
    TableCellRender,
    ColumnRender,
    Loader,
  },
  methods: {
    handleRowClicked(item) {
      if (this.redirection) this.$emit('row-clicked', item);
    },
    handleSelectRows() {
      if (this.isAllSelected[this.indexTeam]) {
        this.selectedRows = this.data;
      } else {
        this.selectedRows = [];
      }
      this.$emit('onSelection', this.selectedRows);
    },
    handleSelectedRow(item) {
      if (
        this.selectedRows.find(
          (x) => x[this.keySelection] == item[this.keySelection],
        )
      ) {
        this.selectedRows = this.selectedRows.filter(
          (x) => x[this.keySelection] != item[this.keySelection],
        );
      } else {
        this.selectedRows.push(item);
      }
      this.$emit('onSelection', this.selectedRows);
    },
    handleSortKeyChanged(key) {
      this.sortKey = key;
      if (this.sortOrder === 'asc') {
        this.sortOrder = 'desc';
      } else {
        this.sortOrder = 'asc';
      }
      this.$emit('onSort', {
        sortKey: this.sortKey,
        sortOrder: this.sortOrder,
      });
    },
    setSelectedRows(data) {
      this.selectedRows = data;
      this.selectedKeys = [];
    },
    handleButton() {
      this.$emit('onButtonClicked');
    },
  },
  computed: {
    ...mapGetters('team', ['getCollection']),
  },
  watch: {
    getCollection() {
      if (!this.isAllSelected.hasOwnProperty(this.indexTeam)) {
        this.$set(this.isAllSelected, this.indexTeam, false);
      }
      this.selectedRows = [];
      this.selectedKeys = [];
    },
    selectedRows(newRows) {
      this.selectedKeys = newRows.map((row) => row[this.keySelection]);
      if (newRows.length == 0) {
        this.isAllSelected[this.indexTeam] = false;
        return;
      }

      if (newRows.length == this.data.length) {
        this.isAllSelected[this.indexTeam] = true;
        return;
      }
      if (newRows.length != this.data.length) {
        this.isAllSelected[this.indexTeam] = false;
        return;
      }
    },
  },
};
</script>
