<template>
  <div class="content_page page_call_center">
    <div class="header_page">
      <div class="content-filter">
        <InputSearch
          class="search-field"
          :placeholder="
            $globals.capitalizeFirstLetter($t('common.search')) + '...'
          "
          @input-value="handleInputValue"
        />
        <!-- <DateRangePicker
          :withTimePicker="false"
          @cancel="cancelDatePicker"
          @applyed="applyedDatePicker"
        /> -->

        <ButtonSecondary
          @onClickSecondaryButton="changeFilterTrash()"
          v-if="filter.trashed === false"
          class="archive-link ml-6"
          icon="icon-archive-group"
          :text="$t('common.archiveList')"
        />
        <div
          @click="changeFilterTrash()"
          v-if="filter.trashed === true"
          class="btn-return ml-5"
        >
          <i class="icon-return mr-2"></i>
          <p>{{ $t('common.allCallcenter') }}</p>
        </div>

        <div v-if="selectedRows.length > 0" class="bloc-desarchive archv">
          <p>
            <span class="pr-2">{{ selectedRows.length }}</span>
            {{ selectedRows.length > 1 ? 'slectionns' : 'slectionn' }}
          </p>
          <!-- <ButtonSecondary
            class="btn-archive ml-6"
            icon="icon-archiver"
            :text="
              filter.trashed ? $t('common.desarchiver') : $t('common.archive')
            "
            @onClickSecondaryButton="
              () => {
                handleSelection();
              }
            "
          /> -->
        </div>
      </div>
      <div class="btn-add flex">
        <div class="mr-3">
          <DropDownMenu
            :disabled="selectedRows.length > 0 ? false : true"
            :items="[
              {
                label: filter.trashed
                  ? $t('common.desarchiver')
                  : $t('common.archive'),
                onClick: () => {
                  if (selectedRows.length > 0) {
                    handleSelection();
                  }
                },
                disabled: selectedRows.length > 0 ? false : true,
              },
            ]"
            icon="icon-down"
            :text="'Actions groupes'"
            :btnId="'btn_dropdown_2'"
            :dropdownId="'dropdownMenu_2'"
            :btnclass="'btn-third'"
          />
        </div>
        <div class="btn-add" @click="toggleDrawer">
          <ButtonPrimary
            icon="icon-add"
            :text="
              $globals.capitalizeFirstLetter($t('components.callCenter.add'))
            "
          />
        </div>
      </div>
    </div>
    <div class="card-table actions-tab mb-5">
      <TableComponent
        ref="callCenterTable"
        :key="swtchedPage"
        :isLoading="isFetching"
        :multipleSelect="true"
        :hasActions="true"
        :actions="{
          archive: filter.trashed === false,
          update: filter.trashed === false,
          unarchive: filter.trashed === true,
        }"
        :data="data"
        :columns="columns"
        :onUpdateItem="
          (index, item) => {
            handleUpdateClicked(item);
          }
        "
        :onArchiveItem="
          (index, item) => {
            handleArchiveCallCenter(item);
          }
        "
        :onUnArchiveItem="
          (index, item) => {
            handleArchiveCallCenter(item);
          }
        "
        :redirection="true"
        @onSort="handleSort"
        :keySelection="'id'"
        @onSelection="handleSelectedRows"
        :showMoreSelection="'callCenter'"
      />
    </div>
    <Pagination storeModuleName="callCenter" />
    <DrawerCallCenter :isOpened="showDrawer" @close="closeDrawer" />
    <modal
      :show-modal="dispalyArchiveModal"
      :loading="isTrashLoading"
      :cancelText="$t('views.callcenter.modalBtnleft')"
      :okText="
        filter.trashed === true
          ? $t('common.desarchiver')
          : $t('common.archive')
      "
      @onConfirm="handleOk"
      @close="handleCancel"
    >
      <template v-slot:title>{{ $t('views.callcenter.modalTitle') }}</template>
      <template v-slot:content>
        <p v-if="selectedRows.length == 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('views.callcenter.modalUnArchiveOneItemText')
              : $t('views.callcenter.modalArchiveOneItemText')
          }}
          <strong>{{ selectedRows[0].name }}</strong>
        </p>
        <p v-if="selectedRows.length == 1">
          {{
            filter.trashed == true
              ? $t('views.callcenter.modalUnArchiveOneItemQuestion')
              : $t('views.callcenter.modalArchiveOneItemQuestion')
          }}
        </p>
        <p v-if="selectedRows.length > 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('views.callcenter.modalUnArchiveManyItemsText')
              : $t('views.callcenter.modalArchiveManyItemsText')
          }}
        </p>
        <div style="align-items: left" class="pb-5">
          <ul  v-if="selectedRows.length > 1">
            <li v-for="item of selectedRows" :key="item.id">
             {{ item.name }}
            </li>
          </ul>
        </div>
        <p v-if="selectedRows.length > 1">
          {{
            filter.trashed == true
              ? $t('views.callcenter.modalUnArchiveManyItemsQuestion')
              : $t('views.callcenter.modalArchiveManyItemsQuestion')
          }}
        </p>

        <!-- <p class="pb-5">
          {{ $t('views.callcenter.modalText') }}
        </p>
        <p>{{ $t('views.callcenter.modalQuestion') }}</p> -->
      </template>
    </modal>
  </div>
</template>
<script>
import DrawerCallCenter from '@/components/DrawerForm/CallCenter/DrawerCallCenter.vue';
import InputSearch from '@/components/FormWidgets/InputSearch.vue';
//import DateRangePicker from "@/components/FormWidgets/DateRange/DateRangePicker.vue";
import TableComponent from '@/components/Table/TableCalibrageCallCenter.vue';
import ButtonPrimary from '@/components/FormWidgets/ButtonPrimary.vue';
import { mapGetters, mapActions } from 'vuex';
import Modal from '@/components/Modal/Modal.vue';
import Pagination from '@/components/Table/Pagination.vue';
import moment from 'moment';
import ButtonSecondary from '@/components/FormWidgets/ButtonSecondary.vue';
import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';

export default {
  components: {
    DrawerCallCenter,
    InputSearch,
    ButtonPrimary,
    TableComponent,
    Pagination,
    Modal,
    ButtonSecondary,
    DropDownMenu,
    //DateRangePicker
  },
  data() {
    return {
      showDrawer: false,
      dateRange: { startDate: null, endDate: null },
      dispalyArchiveModal: false,
      selectedCallCenter: null,
      selectedRows: [],
      swtchedPage: 1,
      isTrashLoading : false
    };
  },
  created() {
    this.resetModel();
    this.resetFilter();
    this.resetPagination();
    this.setFilter({ ...this.filter, ['trashed']: false });
    this.getData();
  },
  methods: {
    ...mapActions('callCenter', [
      'setModel',
      'getData',
      'resetModel',
      'resetFilter',
      'resetPagination',
      'setFilter',
      'deleteCallCenter',
    ]),
    ...mapActions('app', ['setRouteName']),

    closeDrawer() {
      this.resetModel();
      this.showDrawer = false;
    },
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },
    handleInputValue(value) {
      this.setFilter({
        ...this.getFilter,
        ['keyword']: value,
      });
    },
    handleUpdateClicked(dataFromAction) {
      this.$store.commit('callCenter/SET_MODEL', dataFromAction);
      this.toggleDrawer();
    },
    applyedDatePicker(date) {
      this.dateRange.startDate = date.startDate;
      this.dateRange.endDate = date.endDate;
    },
    cancelDatePicker() {
      this.dateRange.startDate = null;
      this.dateRange.endDate = null;
    },
    handleArchiveCallCenter(callcenter) {
      this.selectedCallCenter = callcenter;
      this.selectedRows = [callcenter];
      this.dispalyArchiveModal = true;
    },
    handleCancel() {
      // this.$refs.callCenterTable.setSelectedRows([]);
      // this.selectedRows = [];
      this.dispalyArchiveModal = false;
    },
    handleOk() {
      this.isTrashLoading = true
      this.deleteCallCenter(this.selectedRows.map((x) => x.id)).then(() => {
        this.$refs.callCenterTable.setSelectedRows([]);
        this.selectedRows = [];
        this.dispalyArchiveModal = false;
        this.isTrashLoading = false
      });

      // const callcenterId = this.selectedCallCenter.id;
      // this.dispalyArchiveModal = false;
      // this.setModel({
      //   id: callcenterId,
      // });
      // this.deleteCallCenter();
    },
    handleSort(options) {
      this.setFilter({
        ...this.getFilter,
        ['sortKey']: options.sortKey,
        ['sortOrder']: options.sortOrder,
      });
      this.getData();
    },
    handleSelectedRows(data) {
      this.selectedRows = data;
    },
    handleSelection() {
      this.dispalyArchiveModal = true;
    },
    changeFilterTrash() {
      this.swtchedPage++;
      this.$store.commit('callCenter/RESET_PAGINATION');

      this.$refs.callCenterTable.setSelectedRows([]);
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ['trashed']: !this.filter.trashed });
      this.getData();
      if (this.filter.trashed === true)
        this.setRouteName('archivedCallcenters');
      else this.setRouteName('callcenters');
    },
  },
  watch: {
    'filter.keyword': function () {
      this.setFilter({
        ...this.getFilter,
        ['current_page']: 1,
      });
      this.getData();
    },

    dateRange: {
      handler() {
        this.setFilter({
          ...this.getFilter,
          ['startDate']: this.dateRange.startDate
            ? moment(this.dateRange.startDate).format('YYYY-MM-DD')
            : this.dateRange.startDate,
          ['endDate']: this.dateRange.endDate
            ? moment(this.dateRange.endDate).format('YYYY-MM-DD')
            : this.dateRange.endDate,
        });
        this.getData();
      },
      deep: true,
    },
  },
  computed: {
    ...mapGetters('callCenter', ['getCollection', 'getFilter', 'getState']),
    isFetching() {
      return this.getState.isFetchingDataLoading;
    },
    columns() {
      return [
        {
          key: 'id',
          label: '#',
        },
        {
          key: 'name',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.callCenter.columns.name'),
          ),
          sort: true,
          sortKey: 'name',
        },
        {
          key: 'idCallCenter',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.callCenter.columns.idCallCenter'),
          ),
          sort: true,
          sortKey: 'id_call_center',
        },
        {
          key: 'acounts',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.callCenter.columns.profile'),
          ),
          // render: (text, record) => {
          //   if (Array.isArray(record.acounts) && record.acounts.length > 0) {
          //     let html = '<div style="display : flex;" >';

          //     for (let item of record.acounts) {
          //       html =
          //         html +
          //         `
          //               <div
          //               style="background-color: ${item.avatarColor};"
          //               class="letter-avatar mr-1 ${item.status}"
          //               >
          //                   ${
          //                     item.firstName.charAt(0).toUpperCase() +
          //                     item.lastName.charAt(0).toUpperCase()
          //                   }
          //               </div>
          //       `;
          //     }
          //     html = html + '</div>';
          //     return html;
          //   } else {
          //     return `<div>${this.$globals.capitalizeFirstLetter(
          //       this.$t('components.callCenter.columns.noaccounts'),
          //     )}</div>`;
          //   }
          // },
        },
        {
          key: 'updated_at',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.callCenter.columns.date'),
          ),
          // render: (text, record) => {
          //   return `<span>${this.$globals.formatDate(
          //     record.updated_at,
          //   )}</span>`;
          // },
          sort: true,
          sortKey: 'updated_at',
        },
      ];
    },
    data() {
      return this.getCollection;
    },
    keyword() {
      return this.getFilter.keyword;
    },
    filter() {
      return this.getFilter;
    },
    callCenters() {
      return this.$store.getters['callCenter/getCollection'].map((x) => ({
        id: x.id,
        name: x.name,
      }));
    },
  },
  name: 'equipe',
  mounted() {
    this.setFilter({ ...this.getFilter, ['withPagination']: 1 });
    this.getData();
    this.role = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';

    const userString = localStorage.getItem('user');
    const userObject = JSON.parse(userString);
    const callCenterId = userObject.callCenterId;
    this.accessCallCenterOptions = [callCenterId];
  },
};
</script>
