<template>
    <transition name="slide">
        <div v-if="isOpened" :class="{ drawer: true, show: isOpened }" class="show">
            <div class="header_drawer">
                <div class="title">
                    <h4>{{ $t('views.calibrages.inviterParticipant') }}</h4>
                </div>
                <div class="content-button btn-add">
                    <button class="close mr-5" @click="handleClose">
                        {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
                        <i class="icon-exit"></i>
                    </button>
                    <Button :text="$globals.capitalizeFirstLetter($t('common.invite'))" :loading="loading"
                        @when-cliked="handleOnSubmit" />
                </div>
            </div>

            <div class="body_drawer">
                <div class="container-grid mb-4 invt_partcp">
                    
                    <Autocomplete 
                    type="text" 
                    :keyLabel="'fullName'" 
                    :keyLabelFirstName="'firstName'"
                    :keyLabelLastName="'lastName'"
                    :keyLabelEmail="'email'"
                    :keyLabelBgColor="'avatarColor'"
                    :keyValue="'id'"
                    :label="'Adresse e-mail'"
                    :keyArray="'users'" 
                    class="search-field"
                    placeholder="Rechercher un utilisateur" 
                    :items="users" 
                    storeModuleName="agent" 
                    @item-selected="handleUserSelectedItem"
                    @remove-item="removeUserItem" 
                    :hasError="errors != null && errors.selectedItem != null"
                    :errorMessage="errors && errors.selectedItem != null
                    ? $t('errors.' + errors.selectedItem[0])
                    : ''" />

                </div>

                <div class="form-group-flex mt-4"
                    v-if="this.selectedItem && this.selectedItem.isGuest && this.selectedItem.isGuest == 1">


                    <input-field
                    class="ml-2"
                    :name="`firstName`"
                    :id="`firstName`"
                    :label="$globals.capitalizeFirstLetter($t('common.firstName'))"
                    :hasError="errors != null && errors.firstName != null" :errorMessage="errors && errors.firstName != null ? $t('errors.' + errors.firstName) : ''"
                    :defaultValue="firstName"
                    @input="updateFirstName"
                    />

                    <input-field class="ml-2" :name="`lastName`" :id="`lastName`"
                        :label="$globals.capitalizeFirstLetter($t('common.lastName'))"
                        :hasError="errors != null && errors.lastName != null"
                        :errorMessage="errors && errors.lastName != null ? $t('errors.' + errors.lastName) : ''"
                        :defaultValue="lastName" @input="updateLastName" />
                </div>

            </div>
            <div id="overlay-" v-show="isOpened" @click="handleClose()"></div>
        </div>
    </transition>
</template>

<script>
import { axios } from "../../../store/services/axios.js";
import Button from '@/components/FormWidgets/Button.vue';
import Autocomplete from '@/components/FormWidgets/Calibration/Autocomplete.vue';
// import Autocomplete from '@/components/FormWidgets/AutocompleteRecords.vue';
import InputField from '@/components/FormWidgets/InputField.vue';
import { mapActions, mapGetters } from "vuex";
export default {
    components: {
        Button,
        Autocomplete,
        InputField,
    },
    data() {
        return {
            loading: false,
            selectedItem: null,
            firstName: '',
            lastName: '',
            formSubmitted: false,
        };
    },
    props: {
        isOpened: {
            type: Boolean,
            required: true,
        },
    },

    computed: {
        ...mapGetters('calibrage', ['getModel', 'getErrors', 'getPagination']),
        ...mapGetters('user', ['getUser']),
        model() {
            return this.getModel;
        },
        errors() {
            return this.getErrors;
        },
        users() {
            const authedUserId = this.getUser.id;

            const excludedEmails = this.model.memberCalibrations ? this.model.memberCalibrations.map(calibration => calibration.email) : [];

            return this.$store.getters['user/getCollection']
                .filter(user => user.id !== authedUserId && !excludedEmails.includes(user.email))
                .map((x) => ({
                    ...x,
                    ['id']: x.id,
                    ['fullName']: x.firstName + ' ' + x.lastName,
                    ['email']: x.email,
                    ['avatarColor']: x.avatarColor,
                }));
        },
    },
    methods: {
        ...mapActions('calibrage', [
            'setModel',
            'createCalibration',
            'updateCalibration',
            'setErrors',
            'resetModel',
            'setPagination',
        ]),
        handleClose() {
            this.$emit("close");
            // this.setErrors(null);
            this.selectedItem = null;
            this.firstName = '';
            this.lastName = '';
            delete this.errors.firstName;
            delete this.errors.lastName;
        },
        updateFirstName(event) {
            this.firstName = event.target.value;
            this.validateForm();
        },

        updateLastName(event) {
            this.lastName = event.target.value;
            this.validateForm();
        },
        validateForm() {
            const errors = { ...this.getErrors };

            if (this.formSubmitted && this.selectedItem) {
                const emailExists = this.model.memberCalibrations.some(calibration => calibration.email === this.selectedItem.email);

                if (emailExists) {
                    errors.selectedItem = ['EMAIL_ALREADY_EXIST'];
                } else {
                    delete errors.selectedItem;
                } 

                if (!this.firstName) {
                    errors.firstName = ['FIRST_NAME_REQUIRED'];
                } else if (!/^[A-Za-z-\s-]+$/.test(this.firstName)) {
                    errors.firstName = ['FIRST_NAME_ALPHABETICAL_ERROR'];
                } else if (this.firstName.length < 2) {
                    errors.firstName = ['FIRSTNAME_MIN'];
                } else if (this.firstName.length > 80) {
                    errors.firstName = ['FIRST_NAME_LENGTH_EXCEEDED'];
                } else {
                    delete errors.firstName;
                }

                if (!this.lastName) {
                    errors.lastName = ['LAST_NAME_REQUIRED'];
                } else if (!/^[A-Za-z-\s-]+$/.test(this.lastName)) {
                    errors.lastName = ['LAST_NAME_ALPHABETICAL_ERROR'];
                } else if (this.lastName.length < 2) {
                    errors.lastName = ['LASTNAME_MIN'];
                } else if (this.lastName.length > 80) {
                    errors.lastName = ['LAST_NAME_LENGTH_EXCEEDED'];
                } else {
                    delete errors.lastName;
                } 
            }

            this.setErrors(errors);
        },
        handleOnSubmit() {
            this.formSubmitted = true;
            this.validateForm();
            if (!this.selectedItem || !this.selectedItem.email) {
                this.setErrors({ ...this.getErrors, selectedItem: ['EMAIL_CALIBRAGE_REQUIRED'] });
            } else {
                if (this.selectedItem.isGuest && this.selectedItem.isGuest === 1) {
                    if (!this.errors.firstName && !this.errors.lastName && !this.errors.selectedItem) {
                        this.setModel({
                                ...this.model,
                                memberCalibrations: [
                                    ...this.model.memberCalibrations,
                                    {
                                        isGuest: this.selectedItem.isGuest,
                                        email: this.selectedItem.email,
                                        firstName: this.firstName,
                                        lastName: this.lastName,
                                        avatarColor: this.selectedItem.avatarColor,
                                        errors: {
                                            email: '',
                                            firstName: '',
                                            lastName: '',
                                        },
                                    },
                                ],
                            });
                            this.handleClose();
                    } 
                } else {
                    this.setModel({
                        ...this.model,
                        memberCalibrations: [
                            ...this.model.memberCalibrations,
                            {
                                isGuest: this.selectedItem.isGuest,
                                email: this.selectedItem.email,
                                firstName: this.selectedItem.firstName,
                                lastName: this.selectedItem.lastName,
                                avatarColor: this.selectedItem.avatarColor,
                                errors: {
                                    email: '',
                                    firstName: '',
                                    lastName: '',
                                },
                            },
                        ],
                    });
                    this.handleClose();
                }
            }
        },
        removeUserItem() { },
        handleUserSelectedItem(item) {
            this.selectedItem = item;
            this.validateForm();
            delete this.errors.firstName;
            delete this.errors.lastName;
            // this.setErrors(null);

        },

    },
    watch: {
        firstName: {
            handler(newVal) {
                this.validateForm();
            },
            immediate: true,
        },
        lastName: {
            handler(newVal) {
                this.validateForm();
            },
            immediate: true,
        },
    },
};
</script>