<template>
  <transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true }" class="show">
      <div class="header_drawer">
        <div class="title">
          <h4>{{ activeRecord.recordName }}</h4>
        </div>
        <div class="content-button">
          <button class="close mr-5" @click="handleClose">
            {{ $globals.capitalizeFirstLetter($t('views.grids.close')) }}
            <i class="icon-exit"></i>
          </button>

          <Button
            class="btn-primary"
            @when-cliked="handleClose"
            :text="$globals.capitalizeFirstLetter($t('common.btnSave'))"
          />
        </div>
      </div>
      <div class="body_drawer">
        <ScribbPlayer
          :playlist="records"
          :withSpeechTrace="true"
          :withWaveSurfer="true"
          :withAttachments="false"
            @trackChanged="
                (currentRecord, parentRecord) => {
                  setModel(currentRecord)
                }
              "
        />

        <TrainingRetranscription />
      </div>
      <div id="overlay-" @click="handleClose"></div>
    </div>
  </transition>
</template>

<script>
import ScribbPlayer from '@/components/ScribbAudioPlayer/ScribbPlayer.vue';
import Button from '@/components/FormWidgets/Button.vue';

import { mapGetters, mapActions } from 'vuex';
import TrainingRetranscription from '@/components/Records/ListTab/TrainingRetranscription.vue';

export default {
  components: {
    ScribbPlayer,
    TrainingRetranscription,
    Button,
  },
  data() {
    return {
      showDrawer: false,
      isLoading: false,
      keyAudioTraining: 1,
    };
  },
  props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
  },

  computed: {
    ...mapGetters('training', ['getPagination', 'getModel', 'getCollection']),
    activeRecordIndex() {
      for (let index in this.records) {
        if (this.records[index].id == this.activeRecord.id) {
          return index;
        }
      }
      return null;
    },
    activeRecord() {
      return this.getModel;
    },
    records() {
      return this.getCollection;
    },
  },
  methods: {
    ...mapActions('training', ['setModel']),

    handleClose() {
      this.$emit('close');
    },
  
  },
  mounted() {
    this.isLoading = true;
  },
  created() {},
};
</script>
