<template>
  <div>
    <!-- {{ this.activeRecord.id }} -->
    <!-- {{ this.calibration.evaluation_user }} -->
    <!-- {{ grid.categories }} -->
    <!-- <ul>
      <p>Total: {{ selectedMember ? formatScore(calculateTotalMemberScore(selectedMember)) : '-' }}</p>
      <li v-for="(categorie, index) in grid.categories" :key="categorie.id" style="margin-bottom: 20px;">
        <span :id="'category' + index">{{ categorie?.name }}: <span class="ml-5"><b>{{ selectedCategory === categorie.id
        && selectedMember ? formatScore(calculateMemberScore(categorie, selectedMember)) : '-' }} / {{
