        <ul v-for="item in categorie.items" :key="item.id">
          <li>{{ item.name }} : {{ item.description }}
            <ul>
              <li v-for="note in item.notes" :key="note.id" style="display: flex;">{{ note.name }}
                <ul>
                  <li v-for="(member, memberIndex) in calibration.member_calibration" :key="memberIndex">
                    <ul>
                      <li v-if="checkscorigvoted(note.id, item, member.id, item.record_id)"
                        @click="selectMember(categorie.id, member)"
                        :class="{ 'selected-member': selectedMember === member && selectedCategory === categorie.id }">
                        {{ member.first_name }} | <b>{{ note.score }}</b> | {{ item.commentaire }}
                      </li>
                    </ul>
                  </li>
                </ul>
              </li>
            </ul>
          </li>
        </ul>
      </li>
    </ul> -->


    <template v-for="(categorie, index) in grid.categories">
      <div :id="'category' + index" class="content_scrore content__ mt-5" :key="categorie.id">
        <div @click="() => {
        activeCategory = index;
        scrollToRef();
      }
        " :ref="'catRef' + index" class="panel_header content">
          <div class="title">{{ categorie?.name }}</div>
          <div class="note">{{ selectedCategory === categorie.id && selectedMember ?
        formatScore(calculateMemberScore(categorie, selectedMember)) : '-' }} / {{ categorie.totalScore }}
          </div>
        </div>
        <template v-if="activeCategory == index">
          <div class="content___">
            <template v-for="item in categorie.items">
              <div class="panel_body" :key="item.id">
                <div class="content_status">
                  <p>{{ item.name }} : {{ item.description }}</p>
                  <div class="flex">
                    <div class="status">
                      <template v-for="note in item.notes">
                        <div class="flexbox" :key="note.id">
                          <div class="flex">
                            {{ note.name }}
                            <div class="tooltip ml-2" @click="handleShowNote(note)">
                              <i v-if="selectedNote != null &&
        selectedNote?.id == note.id
        " class="icon-info-bold"></i>
                              <i v-else class="icon-info-light"></i>
                            </div>
                          </div>
                          <div class="main-account flex">
                            <div v-for="(member, index) in calibration.member_calibration" :key="index">
                              <div v-if="checkscorigvoted(note.id, item, member.id, item.record_id)">
                                <div @click="selectMember(categorie.id, member)" class="mr-2">
                                  <Account :firstName="member.first_name" :lastName="member.last_name"
                                    :bgColor="member.avatar_color" :key="'account_' + index" :cursor="'pointer'"
                                    :class="{ 'selected-member': selectedMember === member && selectedCategory === categorie.id }" />
                                </div>
                              </div>
                            </div>

                          </div>
                        </div>
                      </template>

                      <div v-for="(
                          member, index
                        ) in calibration.member_calibration" :key="index">
                        <template v-if="checkCommentairevoted(item.id, member.id)">
                          <div class="details_account mt-5" :key="index + '_details_account'">
                            <div class="flexbox">
                              <Account :firstName="member.first_name" :lastName="member.last_name"
                                :bgColor="member.avatar_color" :key="index + '_avatar'" />
                              <div class="text_" :style="'color:' +
        member.avatar_color +
        ';' +
        'background:' +
        member.avatar_color +
        '10' +
        ';' +
        'border:1px solid' +
        member.avatar_color
        " :key="'comments_members_' + index">
                                {{
        getCommentaireMemberVoted(item.id, member.id)
      }}
                              </div>
                            </div>
                          </div>
                        </template>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
          <div class="panel_footer" :key="categorie.id + '_btn'">
            <button @click="() => {
        activeCategory--;
        scrollToRef();
      }
        " v-if="grid.categories.length > 1 &&
        ((activeCategory > 0 &&
          activeCategory < grid.categories.length - 1) ||
          activeCategory == grid.categories.length - 1)
        " class="prev mr-3" id="prev2">
              Prcdent
            </button>
            <button @click="() => {
        activeCategory++;
        scrollToRef();
      }
        " v-if="grid.categories.length > 1 &&
        activeCategory < grid.categories.length - 1
        " class="next" id="next1">
              Continuer
            </button>
          </div>
        </template>
      </div>
    </template>
    <DrawerInfo :isOpened="showDrawer" @close="() => {
        showDrawer = false;
        setSelectedNote(null);
      }
      "></DrawerInfo>
  </div>
</template>

<script>
import $ from 'jquery';
import Account from '@/components/FormWidgets/AvatarColorsRandom/AvatarColor.vue';
import { mapActions, mapGetters } from 'vuex';
import DrawerInfo from '@/components/DrawerForm/Records/DrawerInfo.vue';
export default {
  props: ['calibration', 'activeRecord'],
  components: {
    Account,
    DrawerInfo,
  },
  data() {
    return {
      activeSection: 1,
      activeCategory: 0,
      showDrawer: false,
      // memberScore: [],
      selectedMember: null,
      selectedCategory: null,
      // noteId: [],
    };
  },
  methods: {
    ...mapActions('record', ['setSelectedNote']),
    handleShowNote(note) {
      this.setSelectedNote(note);
      this.toggleDrawer();
    },
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },
    toggleAccordion(section) {
      this.activeSection = this.activeSection === section ? null : section;
    },
    scrollToRef() {
      // Access the referenced element using $refs
      const targetElement = this.$refs['catRef' + this.activeCategory][0];
      const ti = document.getElementById('content_page');

      // Check if the element exists
      if (targetElement) {
        // Scroll to the referenced element
        targetElement.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
          inline: 'nearest',
          offsetTop: targetElement.scrollTop + 50,
        });
      }
      /*  targetElement.scrollTo({
          top:  300,
          behavior: 'smooth'
        }); */
    },
    checkscorigvoted(id, item, member, recordId) {
      if (this.calibration.evaluation_user != undefined) {
        let evals = this.calibration.evaluation_user;
        const result = evals.find(
          (item) =>
            item.note_id === id &&
            item.member_calibration_id === member &&
            item.record_id == this.activeRecord.id,
        );
        if (result != undefined) {
          item.commentaire = result.commentaire;
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },

    checkCommentairevoted(id, member) {
      if (this.calibration.evaluation_user != undefined) {
        let evals = this.calibration.evaluation_user;
        const result = evals.find(
          (item) =>
            item.item_id === id &&
            item.member_calibration_id === member &&
            item.commentaire != null &&
            item.record_id == this.activeRecord.id
        );
        if (result != undefined) {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    getCommentaireMemberVoted(id, member) {
      if (this.calibration.evaluation_user != undefined) {
        let evals = this.calibration.evaluation_user;
        const result = evals.find(
          (item) =>
            item.item_id === id && item.member_calibration_id === member &&
            item.record_id == this.activeRecord.id,
        );
        if (result != undefined) {
          return result.commentaire;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    // calculateCategoryScore(categorie) {
    //   let totalScore = 0;
    //   categorie.items.forEach(item => {
    //     item.notes.forEach(note => {
    //       this.calibration.member_calibration.forEach(member => {
    //         if (this.checkscorigvoted(note.id, item, member.id, item.record_id)) {
    //           totalScore += parseInt(note.score.replace('+', ''), 10);
    //         }
    //       });
    //     });
    //   });
    //   return totalScore;
    // },
    // showMemberScore(member, score, recordId, categorieId, noteId) {
    // 
    //   this.noteId = noteId;
    //   if (!this.memberScore[categorieId]) {
    //     this.memberScore[categorieId] = {};
    //   }
    //   this.memberScore[categorieId][this.noteId] = score;
    //   this.selectedMember = member;

    //  
    // },


    calculateMemberScore(categorie, member) {
      return categorie.items.reduce((score, item) => {
        return score + item.notes.reduce((noteScore, note) => {
          if (this.checkscorigvoted(note.id, item, member.id, item.record_id)) {
            return noteScore + parseInt(note.score, 10);
          }
          return noteScore;
        }, 0);
      }, 0);
    },
    calculateTotalMemberScore(member) {
      return this.grid.categories.reduce((totalScore, categorie) => {
        return totalScore + this.calculateMemberScore(categorie, member);
      }, 0);
    },
    calculateTotalCategoryScore(categorie) {
      return categorie.items.reduce((totalScore, item) => {
        return totalScore + item.notes.reduce((noteScore, note) => {
          return noteScore + this.calibration.member_calibration.reduce((memberScore, member) => {
            if (this.checkscorigvoted(note.id, item, member.id, item.record_id)) {
              return memberScore + parseInt(note.score, 10);
            }
            return memberScore;
          }, 0);
        }, 0);
      }, 0);
    },
    calculateOverallTotalScore() {
      return this.grid.categories.reduce((overallTotal, categorie) => {
        return overallTotal + this.calculateTotalCategoryScore(categorie);
      }, 0);
    },
    selectMember(categoryId, member) {
      if (this.selectedMember === member && this.selectedCategory === categoryId) {
        this.selectedMember = null;
        this.selectedCategory = null;
      } else {
        this.selectedMember = member;
        this.selectedCategory = categoryId;
      }
    },
    formatScore(score) {
      return score >= 0 ? `${score}` : `${score}`;
    },
    initializeSelectedMemberAndCategory() {
      if (
        this.calibration &&
        this.grid &&
        this.grid.categories &&
        this.calibration.member_calibration.length > 0 &&
        this.grid.categories.length > 0
      ) {
        let highestScore = -Infinity;
        let memberWithHighestScore = null;

        this.calibration.member_calibration.forEach(member => {
          const memberScore = this.calculateTotalMemberScore(member);
          if (memberScore > highestScore) {
            highestScore = memberScore;
            memberWithHighestScore = member;
          }
        });

        this.selectedMember = memberWithHighestScore;
        this.selectedCategory = this.grid.categories[0].id;
      }
    },

  },
  watch: {
    activeRecord(record) {
      this.grid.categories = record ? record.grid?.categories : [];
    },
    'activeRecord.id': function(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.activeCategory = 0;
      }
    }
  },
  mounted() {
    this.$watch(
      () => [this.calibration, this.grid],
      () => {
        this.initializeSelectedMemberAndCategory();
      },
      { immediate: true }
    );
    $(document).ready(function () {
      $('.panel_header').click(function () {
        var target = $(this).data('toggle');

        // Remove the "active" class from all buttons
        $('.panel_header').removeClass('active');

        // Remove the "active" class from all toggle elements
        $('.panel_body').removeClass('active');

        // Close all other toggle elements
        $('.panel_body')
          .not('#' + target)
          .slideUp();

        // Toggle the target element
        $('#' + target).slideToggle();

        // Add the "active" class to the clicked button
        $(this).addClass('active');

        // Add the "active" class to the target element
        $('#' + target).toggleClass('active');
      });
    });
  },
  computed: {
    ...mapGetters('record', [
      'getActiveRecord',
      'getSelectedNote',
      'getParentRecord',
    ]),
    selectedNote() {
      return this.getSelectedNote;
    },
    grid() {
      return this.getParentRecord?.grid ?? [];
    },
  },
};
</script>
