<template>
  <div ref="audioItem" class="card_list">
    <div
      class="card"
      :class="{
        active: getParentRecord && record && getParentRecord.id === record.id,
      }"  @click="handleSetActiveRecord(record)"
    >
      <div class="header_card">
        <h3>
          Audio #{{ record ? record.id : '' }}
          <!-- <i class="icon-duplicate1 ml-3"></i> -->
        </h3>
        <div class="content-actions">
          <button>
            <i
              :class="
                getParentRecord &&
                record &&
                getParentRecord.id === record.id &&
                record.is_favory == true
                  ? 'icon-bookmark-bold active-record-favory'
                  : getParentRecord &&
                    record &&
                    getParentRecord.id === record.id
                  ? 'icon-bookmark-bold active-record'
                  : record.is_favory == true
                  ? 'record-favory icon-bookmark-bold'
                  : ' record-white icon-favoris'
              "
              @click="addOrRemoveFavory(record.id)"
            ></i>
          </button>
          <div class="form-check"
            v-if="
              $globals.hasAuthorities('enregistrements', 'create', role)
            ">
            <input
              type="checkbox"
              :id="record.id"
              :checked="
                getSelectedRecords.some(
                  (selectedRecord) => selectedRecord.id === record.id,
                )
              "
              @change="handleCheckboxChange(record)"
            />
            <label :for="record.id"></label>
          </div>
          <!-- <button><i class="icon-tick-square"></i></button> -->
        </div>
      </div>
      <div class="body_card" @click="handleSetActiveRecord(record)">
        <p>
          <i class="icon-home mr-1"></i>
          {{
            record && record.agentId && record.agentId.callCenterId
              ? record.agentId.callCenterId.name
              : ''
          }}
        </p>
        <div class="date">
          <p>
            <i class="icon-calendar mr-1"></i>
            {{ record ? $globals.audioItemDateFormat(record.createdAt) : '' }}
          </p>
          <p>
            <i class="icon-time-circle mr-1"></i>
            {{ record ? $globals.audioItemTimeFormat(record.createdAt) : '' }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
export default {
  data() {
    return {
      // NotifValue: false,
      role: null,
    };
  },
  props: {
    record: {
      type: Object,
      required: true,
    },
    favory: {
      type: Boolean,
    },
  },
  methods: {
    recordItemClicked() {
      this.setActiveRecord(this.record);
      // this.setParentRecord(this.record);
      this.$emit('changePlayerTrackByRecordId', this.record.id);
      // this.$refs.scribbPalyer.setCurrentRecordByRecordId(this.record.id);
    },
    handleCheckboxChange(record) {
      if (this.isCheckboxChecked(record)) {
        const updatedRecords = [
          ...this.getSelectedRecords,
          {
            ['id']: record.id,
            ['duration']: record.duration,
            ['name']: record.name,
            ['evaluations']: record.evaluations,
            ['grid']: record.grid,
            ['agent']: record.agentId,
            ['gridData']: record.gridData ? record.gridData : null,
          },
        ];
         
        this.setSelectedRecords(updatedRecords);
      } else {
        this.removeRecordFromSelected(record);
      }
    },
    isCheckboxChecked(record) {
      const checkbox = document.getElementById(record.id);
      return checkbox.checked;
    },
    removeRecordFromSelected(record) {
      const updatedRecords = this.getSelectedRecords.filter(
        (selectedRecord) => selectedRecord.id !== record.id,
      );
      this.setSelectedRecords(updatedRecords);
    },
    addOrRemoveFavory(id) {
      this.AddOrRemoveFavory({ recordId: id });
    },
    ...mapActions('record', [
      'setActiveRecord',
      'AddOrRemoveFavory',
      'setSelectedRecords',
      'setParentRecord'
    ]),
    handleSetActiveRecord(record) {
      this.setActiveRecord(record);
      this.setParentRecord(this.record);
      this.$parent.$parent.$refs.scribbPalyer.setCurrentRecordByRecordId(
        record.id,
      );
    },
  },
  computed: {
    ...mapGetters('record', ['getActiveRecord', 'getSelectedRecords' , 'getParentRecord']),
  },
  mounted() {
    this.role = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';
  },
};
</script>
