<template>
  <div class="form-field">
    <div class="search">
      <i class="icon-search"></i>
    </div>
    <label class="control-label" :for="inputId">{{ label }}</label>
    <input
      :type="type"
      :placeholder="placeholder"
      @input="handleSearch"
      v-model="searchQuery"
    />
  </div>
</template>

<script>
export default {
  data() {
    return { searchQuery: '' };
  },
  props: {
    type: {
      type: String,
      default: 'text', // Input type (e.g., text, password, email, etc.)
    },
    placeholder: String,
    inputId: String, // Input ID
    label: String, // Label text
  },
  methods: {
    handleSearch() {
      this.$emit('input-value', this.searchQuery);
    },
  },
};
</script>
