<template>
  <div>
    <!-- <template
      v-if="activeRecord.id != null && activeRecord.linkRegistration != null"
    >
    </template> -->
      <ScribbPlayer
        ref="scribbPalyer"
        :playlist="playlist"
        :withSpeechTrace="true"
        :withWaveSurfer="true"
        :withAttachments="true"
        @trackChanged="handleTrackChanged"
      />

    <div class="audioplayer">
      <div class="list-audio2">
        <table>
          <tr>
            <th>
              <p>#</p>
            </th>
            <th>
              <p>Nom</p>
            </th>
            <th>
              <p>Date</p>
            </th>
            <th>
              <p>Dure</p>
            </th>
          </tr>
          <tr
            v-for="(track, index) in playlist"
            :key="index"
            @click="selectTrack(index, track)"
            :class="{ active: index === currentTrackIndex }"
          >
            <td v-if="index === currentTrackIndex">
              <p><i class="icon-volume-up"></i></p>
            </td>
            <td v-else>
              <p>{{ track.id }}</p>
            </td>
            <td>
              <p>{{ track.name }}</p>
            </td>
            <td>
              <p>{{ track.Date }}</p>
            </td>
            <td>
              <p>{{ track.duree }}</p>
            </td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>


<script>
import { mapActions, mapGetters } from 'vuex';
import ScribbPlayer from '@/components/ScribbAudioPlayer/ScribbPlayer.vue';

export default {
  props: {
    playlist: Array,
  },
  components: {
    ScribbPlayer,
  },
  data() {
    return {
      activeSection: 1,
      currentTrackIndex: 0,
      isPlaying: false,
      progress: 0,
      currentTime: 0,
      totalDuration: 0,
      selectedSpeed: 1,
      speeds: [0.5, 0.75, 1, 1.25, 1.5, 2],
      wavesurfer: null,
    };
  },
  methods: {
    ...mapActions('record', ['setActiveRecord']),
    selectTrack(index, record) {
      this.setActiveRecord(record);
      this.currentTrackIndex = index;
      this.$refs.scribbPalyer.setCurrentRecordByRecordId(record.id);
    },
    handleTrackChanged(trackIndex, record) {
      this.setActiveRecord(record);
      this.currentTrackIndex = trackIndex;
    },
  },
  created() {
    this.currentTrack = this.playlist[this.currentTrackIndex] ?? [];
  },
};
</script>
