<template>
  <div class="card-table-outer mb-5">
    <div class="action-table" v-if="moredetails">
      <div class="content-text">
        <h3 class="title-table">
          {{ $globals.capitalizeFirstLetter($t('common.agents')) }}
        </h3>
        <div class="switch-btn-top-flop">
          <div
            class="top"
            @click="changeTab(0)"
            :class="{ active: activeTab === 0 }"
          >
            <i class="icon-arrow-up pr-2"></i>
            Top
          </div>
          <div
            class="flop"
            @click="changeTab(1)"
            :class="{ active: activeTab === 1 }"
          >
            <i class="icon-arrow-down pr-2"></i>
            Flop
          </div>
          <div class="slider"></div>
        </div>
      </div>
      <div class="content-action">
        <div class="more-details">
          <router-link
            :to="{
              path: '/agents/statistique',
              query: createQueryParams(dateRange),
            }"
          >
            {{ $globals.capitalizeFirstLetter($t('common.moreDetails')) }}
          </router-link>
          <i class="icon-arrow-tab-right"></i>
        </div>
      </div>
    </div>
    <div class="card-table mb-5">
      <TableComponent
        :isLoading="moredetails ? isFetchingAgent : isFetchingAgent"
        :data="data"
        :columns="columns"
        :AgentDashboard="moredetails ? true : false"
        :enabledScrollCss="true"
        :key="keyAgentTable"
        :redirection="true"
        @row-clicked="
          (item) => {
            toggleDrawer(item);
          }
        "
      />
      <Pagination
        v-if="!moredetails"
        storeModuleName="agent"
        storeFunctionName="getStatAgentsData"
        :idParam="{
          campagneId: user?.selectedCampagneId || campaign.campagneId,
          gridSelected: user?.selectedGridId
            ? {
                id: user?.selectedGridId,
              }
            : campaign.gridSelected,
        }"
      />
    </div>
    <ModalAgentStat
      v-if="showModalMigrer"
      :showModalMigration="showModalMigrer"
      :item="selectedItem"
      :cancelText="$t('views.equipe.modalBtnleft')"
      :okText="$t('views.agents.modalBtnOk')"
      @onConfirm="closeModal"
      @close="closeModal()"
    />
  </div>
</template>

<script>
import TableComponent from '@/components/Table/Table.vue';
import ModalAgentStat from '@/views/Equipe/Modal/ModalAgentStat.vue';
import { mapGetters, mapActions } from 'vuex';
import Pagination from '@/components/Table/Pagination.vue';

export default {
  components: {
    TableComponent,
    ModalAgentStat,
    Pagination,
  },
  data() {
    return {
      showModalMigrer: false,
      activeTab: 0,
      isFetching: false,
      throttled: false,
      gridId: null,
      keyAgentTable: 0,
      selectedItem: [],
    };
  },
  props: {
    moredetails: {
      type: Boolean,
      required: false,
      default: true,
    },
    dateRange: {
      type: Object,
      required: false,
      default: () => ({
        startDate: null,
        endDate: null,
        startNewDate: null,
        endNewDate: null,
        startOldDate: null,
        endOldDate: null,
      }),
    },
  },
  mounted() {
    if (this.user) {
      const payload = {
        campagneId: this.user.selectedCampagneId,
        gridSelected: {
          id: this.user.selectedGridId,
        },
      };
      if (this.moredetails) {
        this.getTopFlopAgentsData(payload).then(() => {
          this.isFetching = false;
          this.keyAgentTable++;
        });
      } else {
        this.getStatAgentsData(payload).then(() => {
          this.keyAgentTable++;
        });
      }
    }
  },
  async created() {
    if (!this.moredetails) await this.resetState();
  },
  methods: {
    ...mapActions('agent', [
      'getTopFlopAgentsData',
      'getStatAgentsData',
      'setTopFlop',
      'resetState',
    ]),
    changeTab(index) {
      if (index == 1) {
        this.isFetching = true;
        this.setTopFlop('flop');
        this.getTopFlopAgentsData(this.campaign).then(() => {
          this.isFetching = false;
        });
      } else {
        this.isFetching = true;
        this.setTopFlop('top');
        this.getTopFlopAgentsData(this.campaign).then(() => {
          this.isFetching = false;
        });
      }
      this.activeTab = index;
    },
    toggleDrawer(item) {
      this.selectedItem = item;
      this.showModalMigrer = !this.showModalMigrer;
    },
    closeModal() {
      this.showModalMigrer = false;
    },
    createQueryParams(dateRange) {
      const params = {};

      // Only add properties that are not null or undefined
      if (dateRange.startDate) params.startDate = dateRange.startDate;
      if (dateRange.endDate) params.endDate = dateRange.endDate;
      if (dateRange.startNewDate) params.startNewDate = dateRange.startNewDate;
      if (dateRange.endNewDate) params.endNewDate = dateRange.endNewDate;
      if (dateRange.startOldDate) params.startOldDate = dateRange.startOldDate;
      if (dateRange.endOldDate) params.endOldDate = dateRange.endOldDate;

      return params;
    },
  },
  computed: {
    ...mapGetters('agent', [
      'getTopFlopCollection',
      'getIsFetching',
      'getIsFetchingDetails',
      'getStatCollection',
    ]),

    ...mapGetters({ campaign: 'campaign/getCampagneInfo' }),
    ...mapGetters('user', ['getUser']),
    user() {
      return this.getUser;
    },
    isFetchingAgent() {
      return this.getIsFetching;
    },
    getIsFetchingDetails() {
      return this.getIsFetchingDetails;
    },

    columns() {
      return [
        {
          key: 'name',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.agents.table.columns.name'),
          ),
          render: (text, record) => {
            return `<div><i class="icon-profil mr-1"></i>${
              record.firstName + ' ' + record.lastName
            }</div>`;
          },
        },
        {
          key: 'Identifiant',
          label: this.$globals.capitalizeFirstLetter(this.$t('common.id')),
          render: (text, record) => {
            return `<span>${record.agentId}</span>`;
          },
        },

        {
          key: 'Call center',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('common.callCenter'),
          ),
          render: (text, record) => {
            return `<div>   <span class="actif mr-1"></span> ${record.callCenterName}</div> `;
          },
        },
        {
          key: 'Appels',
          label: this.$globals.capitalizeFirstLetter(this.$t('common.calls')),
          render: (text, record) => {
            return `<div>   <span class="actif mr-1"></span> ${record.numCalls}</div> `;
          },
        },
        {
          key: 'Scoring',
          label: this.$globals.capitalizeFirstLetter(this.$t('common.scoring')),
          render: (text, record) => {
            let scoreDiff = parseFloat(record.scoreDifference).toFixed(2);

            if (scoreDiff < 0) {
              return `<div class="bold">${record.avgScoreNewDate}
              <span class="taux red">${record.scoreDifference}%</span></div>`;
            } else if (scoreDiff > 0) {
              return `<div class="bold">${record.avgScoreNewDate}
              <span class="taux green">+${record.scoreDifference}%</span></div>`;
            } else {
              return `<div class="bold">${record.avgScoreNewDate}
              <span class="taux">${record.scoreDifference}%</span></div>`;
            }
          },
        },
      ];
    },
    data() {
      if (this.moredetails) return this.getTopFlopCollection;
      return this.getStatCollection;
    },
  },

  watch: {
    user: {
      handler(newval) {
        this.isFetching = true;
        const payload = {
          campagneId: newval.selectedCampagneId,
          gridSelected: {
            id: newval.selectedGridId,
          },
        };
        if (
          newval?.selectedCampagneId != undefined &&
          newval?.selectedCampagneId != null
        ) {
          if (this.moredetails) {
            this.getTopFlopAgentsData(payload).then(() => {
              this.isFetching = false;
            });
          } else {
            this.getStatAgentsData(payload).then(() => {
              this.isFetching = false;
            });
          }
        }
      },
      deep: true,
    },
  },
};
</script>
