<template>
  <div class="scribb-form-auth">
    <div class="scribb-form-login">
      <div class="container-auth flex align-items">
        <div class="left-column box py-7">
          <div class="content-form">
            <div class="logo">
              <img src="../../assets/images/logo.svg" alt="" />
            </div>

            <div class="flex justify-center pt-9">
              <h2>
                <strong>{{ $t('views.joinUs.bonjour') }} {{ firstName }} {{ lastName }} {{ $t('views.joinUs.text') }}</strong>
              </h2>
            </div>
            <div class="flex justify-center pt-9">
              <Button
                class="mt-4 mr-2"
                :loading="false"
                @when-cliked="handlejoinUs(payload)"
                :text="`${$t('common.confirm')} `"

              />
              <button class="btn-secondary ml-2 mt-4">
                {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
              </button>
            </div>
          </div>
        </div>
        <div class="right-column box flex align-items justify-center">
          <AnimationScribb />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Button from '@/components/FormWidgets/Button.vue';
import AnimationScribb from '@/components/AnimationScribb/AnimationScribb.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {
    Button,
    AnimationScribb,
  },

  data() {
    return {
      ServerError: false,
      firstName: '',
      lastName: '',
    };
  },

  computed: {
    ...mapGetters('user', ['getErrors']),
    errors() {
      return this.getErrors;
    },
  },

  methods: {
    ...mapActions('user', ['joinUs', 'setErrors']),
    async handlejoinUs() {
      this.setErrors(null);
      const token = this.$route.params.token;
      await this.joinUs(token).then(()=>{
        this.$router.push({ name: 'login' });
      }).catch(()=>{
        this.ServerError = true;
      })
    },
  },
  created() {
    // // Rcuprer le nom et le prnom de l'utilisateur de la session Laravel
    // this.firstName = this.$route.query.firstName;
    // this.lastName = this.$route.query.lastName;

    
  },
};
</script>
