<template>
  <div>
    <div v-if="column.render" @click="handleRowClicked(data)">
      <VRuntimeTemplate
        :template="column.render(data[column.key], data)"
      ></VRuntimeTemplate>
    </div>
    <div v-else @click="handleRowClicked(data)">
      {{ data[column.key] }}
    </div>
  </div>
</template>

<script>
import VRuntimeTemplate from 'v-runtime-template';
export default {
  props: ['column', 'data'],
  components: { VRuntimeTemplate },
 methods: { 
    handleRowClicked(item) {
      this.$emit('row-clicked', item);
    },
  }
};
</script>
