<template>
  <div class="form-actions">
    <div class="icon" :id="'btn-dropdown' + index">
      <i class="icon-more"></i>
    </div>
    <div :id="'actions-dropdown' + index">
      <div
        v-if="actions.update === true"
        :class="{ active_action: activeAction == 'update' }"
      > 
        <button
          @click="
            () => {
              activeAction = 'update';
              onUpdate();
            }
          "
        >
          <i class="icon-edit mr-3"></i>
          Modifier
        </button>
      </div>
      <div
        v-if="actions.duplicate === true"
        :class="{ active_action: activeAction == 'duplicate' }"
      >
        <button
          @click="
            () => {
              activeAction = 'duplicate';
              handleClickOutside();
              onDuplicate();
            }
          "
        >
          <i class="icon-duplicate mr-3"></i>
          Dupliquer
        </button>
      </div>

      <div
        v-if="actions.archive === true"
        :class="{ active_action: activeAction == 'archive' }"
      >
        <button
          @click="
            () => {
              activeAction = 'archive';
              handleClickOutside();
              onArchive();
            }
          "
        >
          <i class="icon-archive mr-3"></i>
          Archiver
        </button>
      </div>
      <div
        v-if="actions.unarchive === true"
        :class="{ active_action: activeAction == 'unarchive' }"
      >
        <button
          @click="
            () => {
              activeAction = 'unarchive';
              handleClickOutside();
              onUnArchive();
            }
          "
        >
          <i class="icon-download mr-3"></i>
          Dsarchiver
        </button>
      </div>
      <div
        v-if="actions.cancel === true"
        :class="{ active_action: activeAction == 'cancel' }"
      >
        <button
          @click="
            () => {
              activeAction = 'cancel';
              handleClickOutside();
              onCancel();
            }
          "
        >
          <i class="icon-download mr-3"></i>
          Annuler
        </button>
      </div>
      <div
        v-if="actions.version === true && draftDisabled==1"
        :class="{ active_action: activeAction == 'version' }"
      >
        <button
          @click="
            () => {
              activeAction = 'version';
              handleClickOutside();
              onGetVersion();
            }
          "
        >
          <i class="icon-version mr-3"></i>
          Versions
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    actions: Object,
    index: Number,
    draftDisabled: {
      type: Number,
    },
    onDelete: {
      type: Function,
      required: false,
      default: function () {},
    },
    onUpdate: {
      type: Function,
      required: false,
      default: function () {},
    },
    onArchive: {
      type: Function,
      required: false,
      default: function () {},
    },
    onDuplicate: {
      type: Function,
      required: false,
      default: function () {},
    },
    onUnArchive: {
      type: Function,
      required: false,
      default: function () {},
    },
    onCancel: {
      type: Function,
      required: false,
      default: function () {},
    },
    onGetVersion: {
      type: Function,
      required: false,
      default: function () {},
    },
  },
  methods: {
    handleClickOutside() {
      const dropDwon = document.getElementById("actions-dropdown" + this.index);
      dropDwon.style.display = "none";
    },
  },
  data() {
    return {
      activeAction: "",
    };
  },
  mounted() {
    const btn = document.getElementById("btn-dropdown" + this.index);
    const dropDwon = document.getElementById("actions-dropdown" + this.index);
    const handleColse = () => {
      this.activeAction = null;
    };
    // Show or hide the dropdown menu when the button is clicked
    btn.addEventListener("click", function () {
      if (dropDwon.style.display === "block") {
        dropDwon.style.display = "none";
        handleColse();
      } else {
        dropDwon.style.display = "block";
      }
    });

    // Hide the dropdown menu if the user clicks anywhere else on the page
    document.addEventListener("click", function (event) {
      if (!btn.contains(event.target) || dropDwon.contains(event.target)) {
        dropDwon.style.display = "none";
        handleColse();
      }
    });
  },
};
</script>
