<template>
  <div>
    <template v-for="(categorie, index) in activeParentRecord?.grid ?? []">
      <div
        :id="'category' + index"
        class="content_scrore content__ mt-5"
        :key="categorie.id"
      >
        <div
          @click="
            () => {
              activeCategory = index;
              setActiveCategory(index);
            }
          "
          :ref="'catRef' + index"
          class="panel_header content"
        >
          <div class="title">{{ categorie.name }}</div>
          <div class="note">
            {{ getScoringUser(categorie.id) }} / {{ categorie.totalScore }}
          </div>
        </div>
        <template v-if="activeCategory === index">
          <div class="content___">
            <template v-for="(item, index) in categorie.items" >
              <div  class="panel_body" :key="item.id">
                <div
                  class="content_status"
                  :class="categorie.items.length < index ? 'note__' : ''"
                >
                  <p class="text-regular">
                    {{ item.name }} : {{ item.description }}
                  </p>
                  <div class="list-form">
                    <div class="list-radio">
                      <template v-for="note in item.notes">
                        <div class="form-radio" :key="note.id">
                          <input
                            type="radio"
                            class="form-check-input m-0"
                            :disabled="user.role == 'responsable-activite'"
                            :name="item.id"
                            :id="'note_' + note.id"
                            :value="note.id"
                            :checked="checkscorigvoted(note.id, item)"
                            @change="
                              updateOrCreateNoteEval(
                                activeRecord.id,
                                item.id,
                                note.id,
                              )
                            "
                          />
                          <label
                            class="form-check-label"
                            :for="'note_' + note.id"
                          >
                            {{ note.name }}
                          </label>
                          <!--  <div class="tooltip ml-4" v-if="note.meaning != null">
                            <i class="icon-info-light"></i>
                          </div> -->

                          <div
                            class="tooltip ml-2"
                            @click="handleShowNote(note)"
                          >
                            <i
                              v-if="
                                selectedNote != null &&
                                selectedNote?.id == note.id
                              "
                              class="icon-info-bold"
                            ></i>
                            <i v-else class="icon-info-light"></i>
                          </div>
                        </div>
                      </template>
                    </div>
                    <div class="zone-texte">
                      <textarea
                        cols="50"
                        rows="8"
                        :disabled="user.role == 'responsable-activite' || !isAnyNoteChecked(item)"
                        v-model="item.commentaire"
                        @blur="updateCommentaire(item, item.commentaire)"
                      ></textarea>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
          <div class="panel_footer" :key="categorie.id + '_btn'">
            <button
              @click="
                () => {
                  activeCategory--;
                  scrollToRef();
                }
              "
              v-if="
                activeCategory > 0 &&
                activeCategory < activeRecord.grid?.length - 1
              "
              class="prev mr-3"
              id="prev2"
            >
              Prcdent
            </button>
            <button
              @click="
                () => {
                  activeCategory++;
                  scrollToRef();
                }
              "
              v-if="activeCategory < activeRecord.grid?.length - 1"
              class="next"
              id="next1"
            >
              Continuer
            </button>
            <!-- <button
              @click="
                () => {
                  activeCategory--;
                  scrollToRef();
                }
              "
              v-if="activeCategory == activeRecord.grid?.length - 1"
              class="next"
              id="next1"
            >
              Prcdent
            </button> -->
            <button
              @click="
                () => {
                  activeCategory--;
                  scrollToRef();
                }
              "
              v-if="activeCategory == activeRecord.grid?.length - 1 && activeRecord.grid?.length > 1"
              class="prev"
              id="prev3"
            >
              Prcdent
            </button>
            <DrawerInfo
              :isOpened="showDrawer"
              @close="
                () => {
                  showDrawer = false;
                  setSelectedNote(null);
                }
              "
            ></DrawerInfo>
          </div>
        </template>
      </div>
    </template>
  </div>
</template>
<script>
import DrawerInfo from '@/components/DrawerForm/Records/DrawerInfo.vue';
import { mapGetters, mapActions } from 'vuex';
export default {
  props: ['record'],
  components: {
    DrawerInfo,
  },
  data() {
    return {
      activeCategory: 0,
      showDrawer: false,
      isIconClicked: false,
    };
  },
  watch: {
    'record.id': function(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.activeCategory = 0;
      }
    }
  },
  methods: {
    ...mapActions('record', [
      'noteEvalManual',
      'updateCommentaireNote',
      'updateRecordEvaluation',
      'setSelectedNote',
      'setActiveRecord',
      'setParentRecord',
    ]),
    ...mapActions('evaluation', ['updateEvaluation']),

    // toggleAccordion(section) {
    //   this.activeSection = this.activeSection === section ? null : section;
    // },
    handleShowNote(note) {
      this.setSelectedNote(note);
      this.toggleDrawer();
    },
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },
    getCommentairescorigvoted(id) {
      if (
        this.activeRecord.evaluation_user != undefined &&
        this.activeRecord.evaluation_user.eval != undefined
      ) {
        let evals = this.activeRecord.evaluation_user.eval;
        const result = evals.find(({ note_id }) => note_id === id);
        if (result != undefined) {
          return result.commentaire;
        } else {
          return '';
        }
      } else {
        return '';
      }
    },
    checkscorigvoted(id, item) {
      if (
        this.activeRecord.evaluation_user != undefined &&
        this.activeRecord.evaluation_user.eval != undefined
      ) {
        let evals = this.activeRecord.evaluation_user.eval;
        const result = evals.find(({ note_id }) => note_id === id);
        if (result != undefined) {
          item.commentaire = result.commentaire;
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    getScoringUser(id) {
      if (
        this.activeRecord.evaluation_user != undefined &&
        this.activeRecord.evaluation_user.score_category != undefined
      ) {
        let score_category = this.activeRecord.evaluation_user.score_category;
        let hasKey = score_category.hasOwnProperty(id);
        if (hasKey) {
          return score_category[id];
        } else {
          return 0;
        }
      }
    },
    updateOrCreateNoteEval(record_id, item_id, note_id) {
      let data = {
        recordId: record_id,
        itemId: item_id,
        noteId: note_id,
        evaluationId:
          this.activeRecord.evaluation_user != undefined &&
          this.activeRecord.evaluation_user.id != undefined
            ? this.activeRecord.evaluation_user.id
            : null,
        is_manuel: true,
      };
      this.noteEvalManual(data);
    },
    updateCommentaire(item, commentaire) {
   
      if (this.activeParentRecord.evaluations != undefined) {
        let evals = this.activeParentRecord.evaluations.find(
          (e) => e.user.id == this.user.id,
        )?.eval;
        const result = evals.find(({ item_id }) => item_id === item.id);
 
        if (result != undefined) {
          result.commentaire = commentaire;
          let data = {
            commentaire: commentaire,
            id: result.id,
            userId: this.user.id,
          };
          this.updateCommentaireNote(data).then(() => {
            let evaluations = [];
            for (let item of this.activeParentRecord.evaluations) {

          
              if (item.user.id != this.user.id) {
                evaluations.push(item);
              } else {
            
          
                evaluations.push({
                  ...item,
                  ['eval']: item.eval.map((item) =>
                    item.item_id == result.item_id
                      ? { ...item, ['commentaire']: commentaire }
                      : item,
                  ),
                });
              }
            }

           /*  this.setActiveRecord({
              ...this.activeRecord,
              ['evaluations']: evaluations,
            });
            this.setParentRecord({
              ...this.activeParentRecord,
              ['evaluations']: evaluations,
            }); */
          });
        }
      } else {
        return false;
      }
    },
    scrollToRef() {
      // Access the referenced element using $refs
      const targetElement = this.$refs['catRef' + this.activeCategory][0];
      // const ti = document.getElementById('content_page');

      // Check if the element exists
      if (targetElement) {
        // Scroll to the referenced element
        targetElement.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
          inline: 'nearest',
          offsetTop: targetElement.scrollTop + 50,
        });
      }
      /*  targetElement.scrollTo({
        top:  300,
        behavior: 'smooth'
      }); */
    },
    setActiveCategory(index) {
      this.activeCategory = Number(index);
      this.scrollToRef();
    },
    handleFinishEvaluation() {
      this.updateEvaluation({
        id: this.activeRecord.evaluation_user.id,
        data: { finished: true },
      }).then((res) => {
        this.updateRecordEvaluation({
          recordId: this.activeRecord.id,
          evaluationData: res.data.data.data,
        });
      });
    },
    isAnyNoteChecked(item) {
      const evaluations = this.activeRecord?.evaluation_user?.eval ?? [];
      return item.notes.some(note => 
        evaluations.some(evalItem => evalItem.note_id === note.id)
      );
    },
  },
  computed: {
    ...mapGetters('record', [
      'getActiveRecord',
      'getSelectedNote',
      'getParentRecord',
    ]),
    ...mapGetters('user', ['getUser']),
    activeRecord() {
      return this.getActiveRecord;
    },
    selectedNote() {
      return this.getSelectedNote;
    },
    user() {
      return this.getUser;
    },
    activeParentRecord() {
      return this.getParentRecord;
    },
  },
  mounted() {},
};
</script>
