<template>
  <button @click="handleClick" class="btn-secondary" :id="id">
    <i :class="icon" class="mr-3"></i>
    {{ text }}
    <slot name="rightContent"></slot>
  </button>
</template>

<script>
export default {
  props: {
    text: String, // Label text
    icon: String,
    id: String,
  },
  methods: {
    handleClick() {
      this.$emit("onClickSecondaryButton");
    },
  },
};
</script>
