<template>
  <div class="content_page page_evaluation">
    <div class="header_page">
      <div class="content-filter">
        <InputSearch
          class="search-field"
          placeholder="Recherche"
          @input-value="handleSearch"
        />
        <Filtres
          :items="[
            {
              label: 'Campagnes',
              key: 'campagnes',
              filters: campagnes,
            },
          ]"
          @onFilterChange="handleFilterChanged"
        />
        <ButtonSecondary
          @onClickSecondaryButton="
            () => {
              changeFilterTrash();
              setRouteName('archivedGrids');
              setFilter({ ...filter, ['trashed']: true });
              getData();
            }
          "
          v-if="filter.trashed === false"
          class="archive-link"
          icon="icon-archive-group"
          :text="$t('common.archiveList')"
        />
        <div
          @click="
            () => {
              changeFilterTrash();
              setRouteName('evaluations');
              setFilter({ ...filter, ['trashed']: false });
              getData();
            }
          "
          v-if="filter.trashed === true"
          class="btn-return"
        >
          <i class="icon-return mr-2"></i>
          <p>Toutes les grilles</p>
        </div>
        <div v-if="selectedRows.length > 0" class="bloc-desarchive archv">
          <p>
            <span class="pr-2">{{ selectedRows.length }}</span>
            {{ selectedRows.length > 1 ? 'slectionns' : 'slectionn' }}
          </p>
        </div>
      </div>
      <div class="btn-add flex">
        <div class="mr-3">
          <DropDownMenu
            :disabled="selectedRows.length > 0 ? false : true"
            :items="[
              {
                label: filter.trashed
                  ? $t('common.desarchiver')
                  : $t('common.archive'),
                onClick: () => {
                  if (selectedRows.length > 0) {
                    handleSelection();
                  }
                },
                disabled: selectedRows.length > 0 ? false : true,
              },
            ]"
            icon="icon-down"
            :text="'Actions groupes'"
            :btnId="'btn_dropdown_2'"
            :dropdownId="'dropdownMenu_2'"
            :btnclass="'btn-third'"
          />
        </div>
        <DropDownMenu
          :items="[
            {
              label: $globals.capitalizeFirstLetter(
                $t('common.add') + ' ' + $t('views.grids.grid'),
              ),
              onClick: toggleDrawer,
            },
            {
              label: $t('views.agents.massImport'),
              onClick: ShowDrawerImport,
            },
            {
              label: 'Export',
              onClick: exportSelectedGrids,
              disabled: this.selectedRows.length < 1,
            },
          ]"
          icon="icon-down"
          :text="$t('views.grids.manageGrid')"
          :btnId="'btn_dropdown'"
          :dropdownId="'dropdownMenu'"
          :btnclass="'btn-primary'"
        />
        <!-- </div> -->
      </div>

      <div v-if="selectedOptions.length > 0" class="tags-">
        <div
          v-for="(selectedOption, index) in selectedOptions"
          :key="index"
          class="tag"
        >
          {{ `${selectedOption.name} (${selectedOption.label})` }}
          <span
            class="remove-filter"
            @click="removeSelectedOption(selectedOption)"
          >
            <i class="icon-exit-"></i>
          </span>
        </div>
      </div>
    </div>
    <div class="card-table actions-tab mb-5">
      <TableComponent
        ref="gridTable"
        :key="switchedPage"
        :isLoading="isFetchingData"
        :multipleSelect="true"
        :hasActions="true"
        :actions="{
          archive: filter.trashed === false,
          unarchive: filter.trashed === true,
          duplicate: true,
          version: true,
        }"
        :onArchiveItem="
          (index, item) => {
            handleArchiveEvent(item);
          }
        "
        :onUnArchiveItem="
          (index, item) => {
            handleArchiveEvent(item);
          }
        "
        :onDuplicateItem="
          (index, item) => {
            handleDuplicate(item);
          }
        "
        :onGetItemVersion="
          (index, item) => {
            handleGetItemVersion(item);
          }
        "
        :redirection="true"
        :data="data"
        :columns="columns"
        @onSort="handleSort"
        :keySelection="'id'"
        @onSelection="handleSelectedRows"
        @row-clicked="
          (item) => {
            editRow(item);
          }
        "
      />
    </div>

    <Pagination storeModuleName="grid" />
    <DrawerEvaluation
      :isOpened="showDrawer"
      @close="showDrawer = false"
      :dashboardPosition="false"
    />
    <DrawerEvaluationVersioningVue
      :isOpened="showDrawerVersioning"
      @close="showDrawerVersioning = false"
      :versionsData="versionsResult"
      :idGridVersioning="idVersioning"
    />
    <ImportGridMasseVue
      :show="showImportMasse"
      @close="showImportMasse = false"
    />

    <modal
      :show-modal="dispalyArchiveModal"
      :loading="isTrashLoading"
      :cancelText="$t('views.agents.modalBtnleft')"
      :okText="
        filter.trashed === true
          ? $t('common.desarchiver')
          : $t('common.archive')
      "
      @onConfirm="handleOk"
      @close="handleCancel"
    >
      <template v-slot:title>{{ $t('views.agents.modalTitle') }}</template>
      <template v-slot:content>
        <p v-if="selectedRows.length == 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('views.grids.modalUnArchiveOneItemText')
              : $t('views.grids.modalArchiveOneItemText')
          }}
          <strong>{{ selectedRows[0]?.name }}</strong>
        </p>
        <p v-if="selectedRows.length == 1">
          {{
            filter.trashed == true
              ? $t('views.grids.modalUnArchiveOneItemQuestion')
              : $t('views.grids.modalArchiveOneItemQuestion')
          }}
        </p>
        <p v-if="selectedRows.length > 1" class="pb-5">
          {{
            filter.trashed == true
              ? $t('views.grids.modalUnArchiveManyItemsText')
              : $t('views.grids.modalArchiveManyItemsText')
          }}
        </p>
        <div class="pb-5">
          <ul class="bold" v-if="selectedRows.length > 1">
            <li v-for="item of selectedRows" :key="item.id">
              {{ item.name }}
            </li>
          </ul>
        </div>

        <p v-if="selectedRows.length > 1">
          {{
            filter.trashed == true
              ? $t('views.grids.modalUnArchiveManyItemsQuestion')
              : $t('views.grids.modalArchiveManyItemsQuestion')
          }}
        </p>
      </template>
    </modal>
  </div>
</template>
<script>
import DrawerEvaluation from '@/components/DrawerForm/Evaluation/DrawerEvaluation.vue';
import InputSearch from '@/components/FormWidgets/InputSearch.vue';
import Filtres from '@/components/FormWidgets/Filter/Filter.vue';
import ButtonSecondary from '@/components/FormWidgets/ButtonSecondary.vue';
import TableComponent from '@/components/Table/Table.vue';
import Pagination from '@/components/Table/Pagination.vue';
import moment from 'moment';
import Modal from '@/components/Modal/Modal.vue';
import { mapGetters, mapActions } from 'vuex';
import DrawerEvaluationVersioningVue from '@/components/DrawerForm/Evaluation/DrawerEvaluationVersioning.vue';
import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';
import ImportGridMasseVue from '@/components/DrawerForm/Evaluation/ImportGridMasse.vue';
import download from 'downloadjs';
export default {
  components: {
    DrawerEvaluation,
    InputSearch,
    Filtres,
    TableComponent,
    Pagination,
    Modal,
    ButtonSecondary,
    DrawerEvaluationVersioningVue,
    DropDownMenu,
    ImportGridMasseVue,
  },
  name: 'grids',
  data() {
    return {
      showDrawer: false,
      dispalyArchiveModal: false,
      selectedGrid: null,
      dateRange: {
        startDate: null,
        endDate: null,
      },
      archive: true,
      desarchive: false,
      BtnReturn: true,
      selectedRows: [],
      keyDrawer: 1,
      showDrawerVersioning: false,
      keyDrawerVersioning: 1,
      versionsResult: [],
      idVersioning: null,
      selectedOptions: [],
      switchedPage: 1,
      isTrashLoading: false,
      showImportMasse: false,
    };
  },
  watch: {
    keyword() {
      this.getData();
    },
    'filter.keyword': function () {
      this.setFilter({
        ...this.getFilter,
        ['current_page']: 1,
      });
      this.getData();
    },
    dateRange: {
      handler() {
        this.$store.dispatch('grid/setFilter', {
          ...this.getFilter,
          ['startDate']: this.dateRange.startDate
            ? moment(this.dateRange.startDate).format('YYYY-MM-DD')
            : this.dateRange.startDate,
          ['endDate']: this.dateRange.endDate
            ? moment(this.dateRange.endDate).format('YYYY-MM-DD')
            : this.dateRange.endDate,
        });
        this.$store.dispatch('grid/getData');
      },
      deep: true,
    },
  },
  async created() {
    await this.resetState();
  },

  computed: {
    ...mapGetters('grid', ['getGridCollection', 'getFilter', 'getIsFetching']),
    isFetchingData() {
      return this.getIsFetching;
    },

    columns() {
      return [
        {
          key: 'id',
          label: '#',
        },
        {
          key: 'name',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.grids.title'),
          ),
          sort: true,
          sortKey: 'name',
        },

        {
          key: 'campagne',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.grids.campagne'),
          ),
          render: (text, record) => {
            return record?.campagne
              ? `<span>${record?.campagne?.name}</span>`
              : `<span></span>`;
          },
          sort: true,
          sortKey: 'campagne_id',
        },
        {
          key: 'statuts',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.grids.status'),
          ),
          render: (text, record) => {
            let sortKey = '';
            if (record.draft == 0) {
              return `<div><span class="yellow mr-2"></span>${this.$globals.capitalizeFirstLetter(
                this.$t('views.grids.draft'),
              )}</div>`;
            } else if (record.statuts == 1) {
              return `<div><span class="actif mr-2"></span>${this.$globals.capitalizeFirstLetter(
                this.$t('views.grids.active'),
              )}</div>`;
            } else if (record.statuts == 0) {
              return `<div><span class="inactif mr-2"></span>${this.$globals.capitalizeFirstLetter(
                this.$t('views.grids.inactive'),
              )}</div>`;
            }
          },
          sort: true,
          sortKey: 'statuts',
        },
       /* {
          key: 'isLocked',
          label: 'EnCours de Modification',
          render: (text, record) => {
            if (record.isLocked != 0) {
              return `<span>EnCours de Modification par ${record.lockedBy}</span>`;
            } else {
              return `<span>pas de mise  jour en cours</span>`;
            }
          },
        },*/

        {
          key: 'updatedAt',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.grids.UpdateDate'),
          ),
          render: (text, record) => {
            return `<div>${this.$globals.formatDate(record.updated_at)}</div>`; // Custom rendering
          },
          sort: true,
          sortKey: 'updated_at',
        },
      ];
    },
    data() {
      return this.getGridCollection;
    },
    filter() {
      return this.getFilter;
    },
    keyword() {
      return this.getFilter.keyword;
    },
    campagnes() {
      return this.$store.getters['campaign/getCollection'].map((element) => ({
        id: element.campagneId,
        name: element.name,
      }));
    },
  },

  methods: {
    ...mapActions('app', ['setRouteName']),
    ...mapActions('campaign', ['setGridSelectedToNull']),
    ...mapActions('grid', [
      'getData',
      'setFilter',
      'deleteGrid',
      'setGridModel',
      'duplicateGrid',
      'getGridVersioning',
      'resetGridVersioning',
      'resetState',
      'lockGrid',
      'exportGrids',
    ]),
    ShowDrawerImport() {
      this.showImportMasse = !this.showImportMasse;
    },
    exportSelectedGrids() {
      this.exportGrids(this.selectedRows).then((response) => {
        download(JSON.stringify(response), 'grid_scb.json', 'text/plain');
      });
    },
    handleFilterChanged(options) {
      this.setFilter({
        ...this.getFilter,
        ['campagne']: options.campagnes,
      });
      this.getData();
    },
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },
    handleSearch(value) {
      this.setFilter({
        ...this.getFilter,
        ['keyword']: value,
      });
    },
    editRow(item) {
      if (item.isLocked == 0) {
        this.lockGrid(item.id).then((resp) => {
          if (resp) {
            this.showDrawer = !this.showDrawer;
            this.keyDrawer++;
            this.setGridModel({
              id: item.id,
              name: item.name,
              campagne: item.campagne,
              statuts: item.statuts,
              updatedAt: item.updatedAt,
              categories: item.categories,
              selectedCategorie: item.selectedCategorie,
              draft: item.draft,
            });
          }
        });
      }
    },

    handleArchiveEvent(grid) {
      this.selectedGrid = grid;
      this.selectedRows = [grid];
      this.dispalyArchiveModal = true;
    },
    handleCancel() {
      this.dispalyArchiveModal = false;
    },
    handleOk() {
      let selectedgridIds = [...this.selectedRows.map((row) => row.id)];
      this.isTrashLoading = true;
      this.$refs.gridTable.setSelectedRows([]);
      this.deleteGrid(this.selectedRows.map((x) => x.id)).then(() => {
        this.selectedRows = [];
        this.dispalyArchiveModal = false;
        this.isTrashLoading = false;
      });
      this.setGridSelectedToNull(selectedgridIds);
    },
    handleSelectedRows(data) {
      this.selectedRows = data;
    },
    handleSelection() {
      this.dispalyArchiveModal = true;
    },
    handleSort(options) {
      this.setFilter({
        ...this.getFilter,
        ['sortKey']: options.sortKey,
        ['sortOrder']: options.sortOrder,
      });
      this.getData();
    },
    handleDuplicate(item) {
      this.duplicateGrid(item.id).then(() => {
        this.dispalyArchiveModal = false;
      });
    },
    handleGetItemVersion(item) {
      this.resetGridVersioning();
      this.idVersioning = item.id;
      this.getGridVersioning(item.id).then(() => {
        this.isFetching = false;
        this.showDrawerVersioning = true;
      });
    },
    removeSelectedOption(selectedOption) {
      this.selectedOptions = this.selectedOptions.filter(
        (option) =>
          option.id !== selectedOption.id || option.key !== selectedOption.key,
      );

      const updatedFilters = {
        collectionCallCenters: this.selectedOptions
          .filter((item) => item.key === 'collectionCallCenters')
          .map((item) => item.id),
        collectionTeam: this.selectedOptions
          .filter((item) => item.key === 'collectionTeam')
          .map((item) => item.id),
      };

      this.handleFilterChanged(updatedFilters);
      this.updatedFilters = updatedFilters;
    },
    changeFilterTrash() {
      this.$store.commit('grid/RESET_PAGINATION');
      this.switchedPage++;
      this.$refs.gridTable.setSelectedRows([]);
      this.selectedRows = [];
      this.setFilter({ ...this.filter, ['trashed']: !this.filter.trashed });
      this.getData();
      if (this.filter.trashed === true) this.setRouteName('archivedGrids');
      else this.setRouteName('grids');
    },
  },
  mounted() {
    this.setFilter({
      ...this.getFilter,
      ['withPagination']: 1,
      ['draft']: null,
      ['trashed']: false,
    });
    this.resetState();
    this.getData().then(() => {});
    this.$store.dispatch('campaign/getData');
  },
};
</script>
