<template>
  <div>
    <div class="form-field mb-7">
      <label id="select-label" v-if="label">
        {{ label }}
      </label>
      <div
        ref="items"
        class="custom-select"
        :class="{ fieldError: hasError }"
        @blur="open = false"
      >
        <div
          class="selected"
          :class="{ open: open }"
          @click="openSelected"
          :id="'custom-select' + index"
        >
          <i class="icon-down"></i>
          <div style="display: flex; align-items: center">
            <template v-if="selected && selected.leftContent != null">
              <div v-html="selected?.leftContent"></div>
            </template>
            <span>
              {{ selected?.text || '' }}
            </span>
          </div>
        </div>
        <div
          class="items"
          :class="{ selectHide: !open }"
          :id="'items_' + index"
        >
          <div ref="search" class="select-search">
            <input type="text" v-model="keySearch" @input="handleKeySearch" />
          </div>
          <div class="space" style="margin-top: 55px">
            <template v-if="filteredOptions.length > 0">
              <div
                v-for="option in filteredOptions"
                :key="option.id"
                :class="{
                  item: true,
                  'selected-single': defaultSelected === option.id,
                }"
                @click="
                  selected = option;
                  open = false;
                  onSelect(option);
                "
                class="item"
                style="display: flex; align-items: center"
              >
                <template v-if="option.leftContent != null">
                  <div v-html="option.leftContent"></div>
                </template>
                <span>{{ option.text }}</span>
              </div>
            </template>
            <template v-else>
              <div class="no-results">Aucun rsultat</div>
            </template>
          </div>
        </div>
      </div>
      <div class="error mb-4 pt-2" v-if="hasError">{{ errorMessage }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    options: {
      type: Array,
      required: true,
    },
    defaultSelected: {
      type: [String, Number, Object],
      required: false,
      default: null,
    },
    hasError: {
      type: Boolean,
    },
    errorMessage: {
      type: String,
    },
    onSelect: {
      type: Function,
      default: () => {},
    },
    label: {
      type: String,
      required: false,
    },
    keyValue: String,
    keyLabel: String,
    isDisabled: Boolean,
    id: Number,
  },
  data() {
    return {
      selected: null,
      open: false,
      formatedOptions: [],
      keySearch: '',
      index: null,
    };
  },
  created() {
    this.formatOptions();
    this.generateRandomString();
  },
  mounted() {
    this.setWidth();
    window.addEventListener('resize', this.setWidth);
  },
  destroyed() {
    window.removeEventListener('resize', this.setWidth);
  },
  methods: {
    handleKeySearch(event) {
      const inputValue = this.keySearch.trim();
      this.$emit('lengthInputValue', inputValue);
    },
    generateRandomString() {
      const characters =
        'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let result = '';
      const charactersLength = characters.length;
      for (let i = 0; i < 10; i++) {
        result += characters.charAt(
          Math.floor(Math.random() * charactersLength),
        );
      }
      this.index = result;
    },
    formatOptions() {
      this.formatedOptions = [];
      for (let index in this.options) {
        let formatedItem = { ...this.options[index] };
        if (!formatedItem.id) {
          formatedItem.id = formatedItem[this.keyValue];
        }
        if (!formatedItem.text) {
          formatedItem.text = formatedItem[this.keyLabel];
        }
        this.formatedOptions.push(formatedItem);
        if (
          this.defaultSelected !== null &&
          this.defaultSelected !== undefined &&
          formatedItem.id == this.defaultSelected
        ) {
          this.selected = formatedItem;
        }
      }
    },
    openSelected() {
      if (!this.isDisabled) this.open = !this.open;
    },
    setWidth() {
      const itemsWidth = this.$refs.items.offsetWidth;
      this.$refs.search.style.width = itemsWidth - 8 + 'px';
    },
    setSelected(value) {
      const selectedOption = this.formatedOptions.find(
        (option) => option.id == value,
      );
      if (selectedOption) {
        this.selected = selectedOption;
      } else {
        this.selected = { id: value, text: '' };
      }
    },
    Close() {
      const togleClose = () => {
        this.open = false;
      };
      const btn = document.getElementById('custom-select' + this.index);
      const items = document.getElementById('items_' + this.index);
      document.addEventListener('click', function (event) {
        if (!btn.contains(event.target) && !items.contains(event.target)) {
          togleClose();
        }
      });
    },
  },
  computed: {
    filteredOptions() {
      return this.formatedOptions.filter(
        (option) =>
          typeof option.text === 'string' &&
          typeof this.keySearch === 'string' &&
          !option.hidden &&
          option.text.toLowerCase().includes(this.keySearch.toLowerCase()),
      );
    },
  },
  watch: {
    options() {
      this.formatOptions();
    },
    selected() {},
  },
  mounted() {
    this.Close();
  },
};
</script>
