<template>
  <div class="audioplayer">
    <audio
      v-if="currentTrack.url"
      ref="audioPlayer"
      @loadedmetadata="getAudioDuration"
      @timeupdate="handlePlayDuration"
      controls
      style="display: none"
    >
      <source :src="`${currentTrack.url}?token=${accesToken}`" type="audio/wav" />
    </audio>
    <!-- wavesurfer begin -->
    <!-- <div v-if="withWaveSurfer == true" class="controls mb-3">
      <div class="player___">

        <div class="wave-container">
          <div id="waveform" ref="waveform" class="wave-bar"></div>
        </div>
      </div>
    </div> -->

    <!-- wavesurfer end -->

    <!-- speech trace begin  -->
    <div
      v-if="
        withSpeechTrace &&
        currentRecord &&
        (currentRecord.status == 'completed' ||
          currentRecord.status == 'to_be_treated')
      "
      class="slide-x mt-7"
    >
      <SpeechTrace
        @plyaDUrationChanged="
          (duration) => {
            plyaDUrationChanged(duration);
          }
        "
        :playDuration="currentTrack.playDuration"
        v-if="currentRecord"
        :record="currentRecord"
      />
    </div>
    <!-- speech trace end -->

    <!-- audio player begin -->
    <div class="playlist_">
      <div class="controls_">
        <div class="time">
          <div class="">
            {{ formatTime(currentTrack.playDuration) }}/{{
              formatTime(currentTrack.duration)
            }}
          </div>
        </div>
        <div class="actions-playlist">
          <button
            :disabled="
              previousDisabledForRecordId &&
              currentRecord?.id == previousDisabledForRecordId
            "
            @click="playPrevious()"
          >
            <i class="icon-prev"></i>
          </button>
          <button @click="seek_f(-10)"><i class="icon-replay"></i></button>
          <button class="play_" v-if="currentTrack.paused" @click="togglePlay">
            <i class="icon-play"></i>
          </button>
          <button class="play_" v-else @click="togglePlay">
            <i class="icon-pause"></i>
          </button>
          <button @click="seek_f(10)"><i class="icon-forward"></i></button>
          <button
            :disabled="
              nextDisabledForRecordId &&
              currentRecord?.id == nextDisabledForRecordId
            "
            @click="playNext"
          >
            <i class="icon-next"></i>
          </button>
        </div>
        <div class="speed-selector">
          <label for="speed">Vitesse</label>
          <select id="speed" v-model="selectedPlaySpeed" @change="changeSpeed">
            <option v-for="speed in speeds" :key="speed" :value="speed">
              {{ speed }}x
            </option>
          </select>
        </div>
      </div>
    </div>

    <!-- audio player end -->

    <div v-if="currentRecord && showPlaylist">
      <div class="list-audio2">
        <table>
          <tr>
            <th>
              <p>#</p>
            </th>
            <th>
              <p>Nom</p>
            </th>
            <th>
              <p>Date</p>
            </th>
            <th>
              <p>Dure</p>
            </th>
            <th>
              <p>Qualification</p>
            </th>
          </tr>
          <tr
            v-for="(track, index) of parentRecord !== null &&
            parentRecord.attachments
              ? [parentRecord, ...parentRecord.attachments]
              : [parentRecord]"
            :key="index"
            @click="
              () => {
                handlePlaylistItemClicked(track);
              }
            "
            :class="{ active: track.id === currentRecord?.id }"
            style="cursor: pointer"
          >
            <td v-if="track.id === currentRecord?.id">
              <p><i class="icon-volume-up"></i></p>
            </td>
            <td v-else>
              <p>{{ track.id }}</p>
            </td>
            <td>
              <p>{{ track?.name }}</p>
            </td>
            <td>
              <p>{{ formatRecordDate(track.createdAt) }}</p>
            </td>
            <td>
              <p>
                {{
                  track.duration
                    ? formatRecordDuration(track.duration)
                    : '--:--'
                }}
              </p>
            </td>
            <td>
              <p>{{ track.qualification?.name }}</p>
            </td>
            <td>
              <a :href="`${track.url}?token=${accesToken}`"  download class="mr-2">
                <i class="icon-download"></i>
              </a>
            </td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment';
// import WaveSurfer from 'wavesurfer.js';
import SpeechTrace from '../AnimationScribb/AnimationRecords.vue';

export default {
  props: [
    'playlist',
    'withWaveSurfer',
    'withSpeechTrace',
    'showPlaylist',
    'withAttachments',
  ],
  components: {
    SpeechTrace,
  },
  data() {
    return {
      accesToken : localStorage.getItem('auth'),
      currentTrackId: null,
      tracks: [],
      currentRecord: null,
      parentRecord: null,
      currentTrack: {
        url: null,
        duration: 0,
        paused: true,
        playDuration: null,
        loaded: false,
      },
      activeTrackIndex: 0,
      selectedPlaySpeed: 1,
      speeds: [0.5, 0.75, 1, 1.25, 1.5, 2],
      // wavesurfer: null,
      previousDisabledForRecordId: null,
      nextDisabledForRecordId: null,
      playlistNestedRecords: [],
      currentTrackPlaylist: [],
    };
  },
  created() {
    this.$on('changePlayerTrackByRecordId', this.setCurrentRecordByRecordId);
  },
  methods: {
    getAudioDuration() {
      const audioElement = this.$refs.audioPlayer;
      if (audioElement) {
        /* if (this.withWaveSurfer) {
          this.wavesurfer.load(this.currentTrack.url);
        } */
        this.currentTrack.duration = audioElement.duration;
        this.currentTrack.loaded = true;
      }
    },
    handlePlayDuration() {
      const audioElement = this.$refs.audioPlayer;
      if (audioElement) {
        this.currentTrack.playDuration = audioElement.currentTime;
      }
    },
    formatTime(time) {
      const minutes = Math.floor(time / 60);
      const seconds = Math.floor(time % 60);
      return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
    },
    togglePlay() {
      const audio = this.$refs.audioPlayer;
      if (this.currentTrack.loaded == true) {
        if (this.currentTrack.paused == true) {
          audio.play();
          // this.wavesurfer.play();
          this.currentTrack.paused = false;
        } else {
          audio.pause();
          // this.wavesurfer.pause();
          this.currentTrack.paused = true;
        }
      }
    },
    async playNext() {
      let index = this.playlistNestedRecords.findIndex(
        (obj) => obj.id === this.currentRecord.id,
      );
      this.currentRecord = this.playlistNestedRecords[index + 1];
      for (let item of this.tracks) {
        if (item.id == this.currentRecord.id) {
          this.parentRecord = item;
          break;
        }
        if (item.attachments) {
          for (let child of item.attachments) {
            if (child.id == this.currentRecord.id) {
              this.parentRecord = item;
              break;
            }
          }
        }
      }
      this.currentTrack = {
        url: this.currentRecord.url,
        duration: 0,
        paused: true,
        playDuration: 0,
        loaded: false,
      };
      this.$refs.audioPlayer.load();
      /* if (this.withWaveSurfer) {
        this.wavesurfer.load(this.currentTrack.url);
      } */
      this.$emit('trackChanged', this.currentRecord, this.parentRecord);
    },
    playPrevious() {
      let index = this.playlistNestedRecords.findIndex(
        (obj) => obj.id === this.currentRecord.id,
      );
      this.currentRecord = this.playlistNestedRecords[index - 1];
      for (let item of this.tracks) {
        if (item.id == this.currentRecord.id) {
          this.parentRecord = item;
          break;
        }
        if (item.attachments) {
          for (let child of item.attachments) {
            if (child.id == this.currentRecord.id) {
              this.parentRecord = item;
              break;
            }
          }
        }
      }
      this.currentTrack = {
        url: this.currentRecord.url,
        duration: 0,
        paused: true,
        playDuration: 0,
        loaded: false,
      };
      this.$refs.audioPlayer.load();
      /* if (this.withWaveSurfer) {
        this.wavesurfer.load(this.currentTrack.url);
      } */
      this.$emit('trackChanged', this.currentRecord, this.parentRecord);
    },
    changeSpeed() {
      this.$refs.audioPlayer.playbackRate = this.selectedPlaySpeed;
      // this.wavesurfer.setPlaybackRate(this.selectedPlaySpeed);
    },
    setCurrentRecordByRecordId(id) {
      const index = this.playlistNestedRecords.findIndex(
        (item) => item.id == id,
      );
      if (index !== -1) {
        this.activeTrackIndex = index;
        this.currentRecord = this.playlistNestedRecords[index];
        this.parentRecord = this.playlistNestedRecords[index];
        this.currentTrack = {
          url: this.playlistNestedRecords[index].url,
          duration: 0,
          paused: true,
          playDuration: 0,
          loaded: false,
        };
        this.$refs.audioPlayer.load();
        /* if (this.withWaveSurfer) {
          this.wavesurfer.load(this.currentTrack.url);
        } */
      }
    },
    loadPlaylist() {
      if (this.tracks != undefined) {
        this.currentTrack.url = this.tracks[0] ? this.tracks[0].url : '';
        this.currentRecord = this.tracks[0] ? this.tracks[0] : '';
        this.parentRecord = this.tracks[0] ? this.tracks[0] : '';
        this.currentTrackId = this.tracks[0] ? this.tracks[0]?.id : null;
        this.activeTrackIndex = 0;
      }
      /* if (this.withWaveSurfer != null) {
        this.wavesurfer.setVolume(0);
        this.wavesurfer.on('click', (event) => {
          this.currentTrack.playDuration =
            this.currentTrack.duration * Number(event);
          const audioElement = this.$refs.audioPlayer;
          audioElement.currentTime = this.currentTrack.playDuration;
        });
      } */
    },
    formatRecordDate(date) {
      return moment(date).format('DD/MM/YYYY');
    },
    handlePlaylistItemClicked(record) {
      this.currentRecord = record;
      this.currentTrack = {
        url: record.url,
        duration: 0,
        paused: true,
        playDuration: 0,
        loaded: false,
      };
      this.$refs.audioPlayer.load();
      /* if (this.withWaveSurfer) {
        this.wavesurfer.load(record.url);
      } */
      this.$emit('playListItemClicked', record);
    },
    managePalyNextButtons() {
      if (this.withAttachments == true) {
        this.previousDisabledForRecordId = this.parentRecord?.id;
        this.nextDisabledForRecordId =
          this.parentRecord?.attachments?.length > 0
            ? this.parentRecord?.attachments[
                this.parentRecord?.attachments?.length - 1
              ]?.id
            : this.parentRecord?.id;
      } else {
        this.previousDisabledForRecordId = this.tracks[0]?.id;
        this.nextDisabledForRecordId = this.tracks[this.tracks.length - 1]?.id;
      }
    },
    formatRecordDuration(seconds) {
      return moment.utc(seconds * 1000).format('mm:ss');
    },

    plyaDUrationChanged(duration) {
      this.currentTrack.playDuration = duration;
      this.$refs.audioPlayer.currentTime = duration;
      if (this.currentTrack.paused == false) {
        this.$refs.audioPlayer.play();
      }
    },
    seek_f(duration) {
      const audio = this.$refs.audioPlayer;
      if (audio) {
        audio.currentTime = Math.min(audio.currentTime + duration, audio.duration) ;
      }
    },

  },
  mounted() {
    /* this.wavesurfer = WaveSurfer.create({
      container: this.$refs.waveform,
      waveColor: '#DDDDDD',
      progressColor: '#140727',
      height: 60,
      barHeight: 20,
      barGap: 2,
      cursorWidth: 0,
      responsive: true,
      barWidth: 2,
      barRadius: 10,
      autoCenter: true,
      sampleRate: 22050,
    }); */
    this.tracks = this.playlist;
    this.loadPlaylist();
  },
  watch: {
    playlist(value) {
      this.tracks = value;
      this.loadPlaylist();
    },
    currentTrack(value) {
      this.managePalyNextButtons();
    },
    tracks(value) {
      this.managePalyNextButtons();
      let items = [];
      if (value) {
        for (let track of value) {
          items.push(track);
          if (track.attachments) {
            for (let child of track.attachments) {
              items.push(child);
            }
          }
        }

        this.playlistNestedRecords = items;
      }
    },
    'currentTrack.playDuration'(value) {
      this.$emit('playDurationChanged', value);
    },
  },
};
</script>

