<template>
  <div class="list_call_center">
    <div class="title mb-7">
      <div class="main-title">
        {{ $globals.capitalizeFirstLetter($t('common.callCenter')) }}
      </div>
      <div class="more-details">
        <router-link to="/callcenters">
          {{ $globals.capitalizeFirstLetter($t('common.moreDetails')) }}
        </router-link>

        <i class="icon-arrow-tab-right"></i>
      </div>
    </div>

    <div class="content-stat">
      <div class="content-left align-center">
        <div>
          <vue-ellipse-progress
            class="progress__c"
            :progress="this.avg"
            line-mode="normal 0"
            :empty-thickness="15"
            :angle="0"
            emptyColor="transparent"
            :thickness="0"
            :size="205"
            :legendValue="this.avg"
            animation="rs 700 1000"
            :gap="10"
            :key="keyEclipse"
          >
            <p slot="legend-caption">Note moyenne</p>
            <span slot="legend-value">%</span>
          </vue-ellipse-progress>
        </div>
        <div class="list-note flex justify-around align-items mt-5">
          <div class="red round flex">{{ redPercent }}%</div>
          <div class="orange round flex">{{ orangePercent }}%</div>
          <div class="yellow- round flex">{{ yellowPercent }}%</div>
          <div class="green round flex">{{ greenPercent }}%</div>
        </div>
      </div>
      <div class="content-right">
        <Listprogressbar :data="data" :key="renderKey" />
      </div>
    </div>
  </div>
</template>

<script>
import Listprogressbar from '@/components/Dashboard/ListProgressbarCallcenter.vue';
import { mapGetters, mapActions } from 'vuex';
export default {
  components: {
    Listprogressbar,
  },
  data() {
    return {
      data: [],
      avg: 0,
      renderKey: 0,
      progressBarColors: ['#4caf50', '#2196f3', '#ff9800', '#e91e63'],
      redPercent: 0,
      orangePercent: 0,
      yellowPercent: 0,
      greenPercent: 0,
      numOfRed: 0,
      numOfYellow: 0,
      numOfOrange: 0,
      numOfGreen: 0,
      keyEclipse:0
    };
  },
  mounted() {
    if (this.user) {
      const payload = {
        campagneId: this.user.selectedCampagneId,
        gridSelected: {
          id: this.user.selectedGridId,
        },
      };

      this.getCallCenterStatistics(payload).then((res) => {
        this.data = res.data.scoreByCallCenter;
        this.avg = res.data.average;
        this.data.forEach((element) => {
          if (element.avgScore > 0 && element.avgScore <= 49) {
            this.numOfRed++;
          }
          if (element.avgScore >= 50 && element.avgScore <= 59) {
            this.numOfOrange++;
          }
          if (element.avgScore >= 60 && element.avgScore <= 79) {
            this.numOfYellow++;
          }
          if (element.avgScore >= 80 && element.avgScore <= 100) {
            this.numOfGreen++;
          }
        });
        this.redPercent = (this.numOfRed * 100) / 4;
        this.orangePercent = (this.numOfOrange * 100) / 4;
        this.yellowPercent = (this.numOfYellow * 100) / 4;
        this.greenPercent = (this.numOfGreen * 100) / 4;
        this.keyEclipse++;
        this.renderKey++;
      });
    }
  },
  methods: {
    ...mapActions('callCenter', ['getCallCenterStatistics']),
  },
  computed: {
    ...mapGetters({ campaign: 'campaign/getCampagneInfo' }),
    ...mapGetters('user', ['getUser']),
    user() {
      return this.getUser;
    },
  },

  watch: {
    user: {
      handler(newval) {
        this.isFetching = true;
        const payload = {
          campagneId: newval.selectedCampagneId,
          gridSelected: {
            id: newval.selectedGridId,
          },
        };
        if (
          newval?.selectedCampagneId != undefined &&
          newval?.selectedCampagneId != null
        ) {
          this.getCallCenterStatistics(payload).then((res) => {
            this.data = res.data.scoreByCallCenter;
            this.avg = res.data.average;
            this.isFetching = false;
            this.data.forEach((element) => {
              if (element.avgScore > 0 && element.avgScore <= 49) {
                this.numOfRed++;
              }
              if (element.avgScore >= 50 && element.avgScore <= 59) {
                this.numOfOrange++;
              }
              if (element.avgScore >= 60 && element.avgScore <= 79) {
                this.numOfYellow++;
              }
              if (element.avgScore >= 80 && element.avgScore <= 100) {
                this.numOfGreen++;
              }
            });
            this.redPercent = (this.numOfRed * 100) / 4;
            this.orangePercent = (this.numOfOrange * 100) / 4;
            this.yellowPercent = (this.numOfYellow * 100) / 4;
            this.greenPercent = (this.numOfGreen * 100) / 4;
          });
        }
      },
      deep: true,
    },
  },
};
</script>
