<template>
    <div  v-if="this.getSelectedRecords.length > 0" class="notif_calibrage">
        <div> 
            <div class="content_left">
              <h3>
                <i class="icon-checked"></i>
                {{ getSelectedRecords.length }} {{ getSelectedRecords.length > 1 ? 'audios slectionns' : 'audio slectionn' }}
              </h3>
        </div>
        <div @click="openToggleDrawer" class="content_right">  <ButtonPrimary icon="icon-calendar" :text="$t('components.listCalibrage.planification')" /></div>
    </div>
    </div>
</template>


<script>
import ButtonPrimary from '@/components/FormWidgets/ButtonPrimary.vue'
import { mapGetters, mapActions } from 'vuex';
export default {
    components: {  
        ButtonPrimary 
    },

    data() {
    return {
      message: "",
    };
  },
  props: {

  },
  methods: {
    ...mapActions('calibrage', [
      'setModel',
    ]),
    // showNotification() {
    //   setTimeout(() => {
    //     this.hideNotification();
    //   }, 5000); // Hide the notification after 3 seconds (adjust as needed)
    // },
    // hideNotification() {
    //   this.show = false;
    // },
    openToggleDrawer() {
      this.$emit('openToggleDrawer');
      const callCenterId = this.authedUser.callCenterId ? this.authedUser.callCenterId.id : null;
      this.setModel({
          ...this.getModel,
          callCenterId: callCenterId,
          organizerId: this.authedUser.id,
          status: 'in_progress',
          ['recordCalibrations']: this.getSelectedRecords,
        });
    }

  },
  computed: {
    ...mapGetters('record', ['getSelectedRecords']),
    ...mapGetters('calibrage', ['getModel']),
    ...mapGetters("user", ["getUser"]),
    authedUser() {
      return this.getUser;
    },
  },
};
</script>

