<template>
  <div
    :style="{
      backgroundColor: bgColor
        ? bgColor
        : getColorForLetter(FirstName.charAt(0).toUpperCase()),
        cursor: cursor,
    }"
    class="letter-avatar"
    :class="{ disabled: disabled }"
  >
    {{ FirstName.charAt(0).toUpperCase()
    }}{{ LastName.charAt(0).toUpperCase() }}
  </div>
</template>

<script>
export default {
  props: {
    firstName: String,
    lastName: String,
    bgColor: String,
    disabled: Boolean,
    cursor: String,
  },
  components: {},
  data() {
    return {
      FirstName: this.firstName ? this.firstName : 'Nicolas ',
      LastName: this.lastName ? this.lastName : 'Vasseur',
    };
  },
  watch: {
    firstName(newVal, oldVal) {
      this.FirstName = newVal;
    },
    lastName(newVal, oldVal) {
      this.LastName = newVal;
    }
  },
  methods: {
    getColorForLetter(letter) {
      // Mapping of letters to colors
      const colorMap = {
        A: '#2DA7FF',
        B: '#CB8419',
        C: '#D22CC1',
        D: '#39D6B5',
        E: '#2B3AC6',
        F: '#47BC4A',
        G: '#F5B95F',
        H: '#AA196E',
        I: '#F02C4D',
        J: '#B6BB29',
        K: '#603C23',
        L: '#5BB4EF',
        M: '#7B41ED',
        N: '#FF9900',
        O: '#21B925',
        P: '#065342',
        Q: '#0F0677',
        R: '#DE972D',
        S: '#DEBE90',
        T: '#E1445E',
        U: '#F2702D',
        V: '#18DB1E',
        W: '#8CD0D6',
        X: '#DE3598',
        Y: '#EF14C3',
        Z: '#F397E1',

        // Add more mappings as needed
      };

      // Default color if the letter is not in the mapping
      const defaultColor = '#808080';

      return colorMap[letter] || defaultColor;
    },
  },
};
</script>

<style>
.selected-member {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
</style>