<template>
  <transition name="slide">
    <div
      v-if="isOpened"
      :class="{ drawer: true, show: show }"
      class="show calibrage"
    >
      <div class="header_drawer">
        <div class="title">
          <!-- {{ authedUser.id }} -->
          <!-- {{ campaigns }} -->
          <!-- {{ model.campagneId }} -->
          <!-- {{ records }} -->
          <!-- {{ model.title }} -->
          <h4>
            {{
              model.id == null
                ? $t('views.calibrages.planifierCalibrage')
                : $t('views.calibrages.editCalibrage')
            }}
          </h4>
        </div>
        <div class="content-button btn-add">
          <button class="close mr-5" @click="handleClose">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <Button
            :text="$globals.capitalizeFirstLetter($t('common.save'))"
            :loading="loading"
            @when-cliked="handleOnSubmit"
          />
        </div>
      </div>

      <div class="body_drawer">
        <!-- {{ getSelectedRecords }}<br><br> -->
        <!-- {{ campaigns }} -->
        <!-- {{ model }} -->
        <!-- {{ model.memberCalibrations }} -->
        <!-- {{ model.recordCalibrations }} -->
        <!-- {{ newParticipants }} -->
        <div class="container-grid border-b">
          <input-field
            class="mb-7"
            id="1"
            :label="
              $globals.capitalizeFirstLetter(
                $t('views.calibrages.titleDrawerPlanifierCalibrage'),
              )
            "
            :defaultValue="model.title"
            :hasError="errors != null && errors.title != null"
            :errorMessage="
              errors && errors.title != null
                ? $t('errors.' + errors.title[0])
                : ''
            "
            @input="updateTitle"
            name="title"
          />
        </div>

        <h4 class="mb-4 py-6 border-b"><strong>Participants</strong></h4>
        <div class="edit-comptes">
          <div class="content-user form-group-flex pb-5 border-b">
            <div class="flex">
              <div>
                <Account
                  :firstName="authedUser.firstName"
                  :lastName="authedUser.lastName"
                  :bgColor="authedUser.avatarColor"
                />
              </div>
              <div>
                <div class="content-name pl-2">
                  {{ authedUser.firstName }}
                  {{ authedUser.lastName }}
                </div>
                <div class="content-email pl-2">
                  {{ authedUser.email }}
                </div>
              </div>
            </div>
            <div class="action">
              <p>Vous tes lorganisateur du calibrage</p>
            </div>
          </div>
          <div class="">
            <div v-for="(user, index) of model.memberCalibrations" :key="index">
              <div v-if="user.email !== authedUser.email">
                <div class="content-user form-group-flex pt-4 pb-5 border-b">
                  <div class="flex">
                    <div>
                      <Account
                        :key="user.uuid"
                        :firstName="user.firstName"
                        :lastName="user.lastName"
                        :bgColor="user.avatarColor"
                      />
                    </div>
                    <div>
                      <div class="content-name pl-2">
                        {{ user.firstName + ' ' + user.lastName }}
                      </div>
                      <div class="content-email pl-2">{{ user.email }}</div>
                    </div>
                  </div>
                  <div class="action">
                    <button
                      @click="editMemberCalibrations(index, user)"
                      v-if="user.isGuest == 1"
                    >
                      <i class="icon-edit"></i>
                    </button>
                    <button><i class="icon-message"></i></button>
                    <button @click="deleteMemberCalibrations(index)">
                      <i class="icon-delete"></i>
                    </button>
                  </div>
                </div>

                <div
                  class="form-group-flex comptes mt-4"
                  v-if="isEditing(index)"
                >
                  <input-field
                    class="mb-4"
                    :label="$globals.capitalizeFirstLetter($t('common.email'))"
                    :defaultValue="toModify[index].email"
                    :name="`email_${index}`"
                    :id="`email_${index}`"
                    :hasError="toModify[index].errors.email != ''"
                    :errorMessage="
                      toModify[index].errors.email != ''
                        ? $t('errors.' + toModify[index].errors.email)
                        : ''
                    "
                    @blur="validateEmailItems(toModify[index])"
                    @input="
                      (e) =>
                        updateEditedUser(
                          'email',
                          e.target.value.toLowerCase(),
                          index,
                        )
                    "
                  />
                  <input-field
                    class="ml-2"
                    :label="
                      $globals.capitalizeFirstLetter($t('common.firstName'))
                    "
                    :defaultValue="toModify[index].firstName"
                    :name="`firstName_${index}`"
                    :id="`firstName_${index}`"
                    :hasError="toModify[index].errors.firstName != ''"
                    :errorMessage="
                      toModify[index].errors.firstName != ''
                        ? $t('errors.' + toModify[index].errors.firstName)
                        : ''
                    "
                    @blur="validateFirstNameItems(toModify[index])"
                    @input="
                      (e) =>
                        updateEditedUser(
                          'firstName',
                          e.target.value.toLowerCase(),
                          index,
                        )
                    "
                  />
                  <input-field
                    class="ml-2"
                    :label="
                      $globals.capitalizeFirstLetter($t('common.lastName'))
                    "
                    :defaultValue="toModify[index].lastName"
                    :name="`lastName_${index}`"
                    :id="`lastName_${index}`"
                    :hasError="toModify[index].errors.lastName != ''"
                    :errorMessage="
                      toModify[index].errors.lastName != ''
                        ? $t('errors.' + toModify[index].errors.lastName)
                        : ''
                    "
                    @blur="validateLastNameItems(toModify[index])"
                    @input="
                      (e) =>
                        updateEditedUser(
                          'lastName',
                          e.target.value.toLowerCase(),
                          index,
                        )
                    "
                  />
                  <div class="remove" style="display: flex">
                    <button
                      @click.prevent="
                        saveEditedParticipant(toModify[index], index)
                      "
                      class="btn-remove pl-3"
                    >
                      <i class="icon-tick-square"></i>
                    </button>
                    <button
                      class="btn-remove pl-1"
                      @click="cancelEditParticipant(index)"
                    >
                      <i class="icon-remove"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="add-new-participants">
            <div
              class="error mt-2"
              v-if="this.model.memberCalibrations.length === 0"
            >
              {{
                errors && errors.memberCalibrations != null
                  ? $t('errors.MEMBRECALIBRATION_REQUIRED')
                  : ''
              }}
            </div>
            <div class="add-participants py-6">
              <button @click="showDrawerNotice()">
                <span class="mr-2"><i class="icon-plus"></i></span>
                Ajouter un participant
              </button>
            </div>
            <h4 class="mt-6"><strong>Enregistrement audio</strong></h4>
            <div class="form-group mt-2">
              <SelectMultiple
                :options="records"
                :keyLabel="'name'"
                :keyValue="'id'"
                :hasError="
                  errorsExistAudio ||
                  (errors != null && errors.recordCalibrations != null)
                "
                :errorMessage="
                  messageExistAudio != null
                    ? messageExistAudio
                    : errors && errors.recordCalibrations != null
                    ? $t('errors.' + errors.recordCalibrations[0])
                    : ''
                "
                :defaultSelected="model.callCenterId"
                :label="''"
              />

              <!-- <Autocomplete
                type="text"
                :keyLabel="'name'"
                :keyArray="'records'"
                :keyValue="'id'"
                class="search-field searchAudio"
                placeholder="Rechercher un audio"
                storeModuleName="record"
                @item-selected="handleRecordSelectedItem"
                :hasError="
                  errorsExistAudio ||
                  (errors != null && errors.recordCalibrations != null)
                "
                :errorMessage="
                  messageExistAudio != null
                    ? messageExistAudio
                    : errors && errors.recordCalibrations != null
                    ? $t('errors.' + errors.recordCalibrations[0])
                    : ''
                "
              /> -->
            </div>
            <div class="table_audio border-b">
              <TableComponent
                :data="model.recordCalibrations"
                :columns="columns"
              />
            </div>

            <!-- <Pagination storeModuleName="exemple" /> -->
            <h4 class="my-9"><strong>Rendez-vous</strong></h4>

            <div
              class="form-group-flex rdv mt-4"
              style="flex-direction: column"
            >
              <div class="singledatepicker">
                <div class="singledate_ mr-2 form-field">
                  <label>Date</label>
                  <DatePicker
                    :value="calibrageStartDate"
                    @input="updateCalibrageStartDate"
                    value-type="format"
                    :disabled-date="disabledDates"
                  />
                </div>
                <div class="mt-5">-</div>
                <div class="heure_ ml-2 form-field">
                  <label>Heure</label>
                  <DatePicker
                    :value="calibrageStartTime"
                    @input="updateCalibrageStartTime"
                    format="HH:mm"
                    value-type="format"
                    type="time"
                    :disabled-time="disabledHours"
                  />
                </div>
              </div>

              <div
                class="error mt-2"
                v-if="errors != null && errors.startDate != null"
              >
                {{
                  errors && errors.startDate != null
                    ? $t('errors.' + errors.startDate[0])
                    : ''
                }}
              </div>
            </div>
          </div>
        </div>
      </div>
      <DrawerParticipant :isOpened="showNotice" @close="showNotice = false" />
      <div id="overlay-" @click="handleClose"></div>
    </div>
  </transition>
</template>

<script>
import DateRangePicker from 'vue2-daterange-picker';
import { v4 as uuidv4 } from 'uuid';
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css';
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import SelectMultiple from '@/components/FormWidgets/SelectSingle/SelectSingleCalibrage.vue';
// import Autocomplete from '@/components/FormWidgets/AutocompleteRecords.vue';
import TableComponent from '@/components/Table/Table.vue';
import Account from '@/components/FormWidgets/AvatarColorsRandom/AvatarColor.vue';
import DrawerParticipant from '@/components/DrawerForm/Calibrage/DrawerParticipant.vue';
import { mapActions, mapGetters } from 'vuex';
export default {
  components: {
    InputField,
    Button,
    SelectMultiple,
    // Autocomplete,
    TableComponent,
    DatePicker,
    Account,
    DrawerParticipant,
  },
  data() {
    return {
      timePicker: true,
      showDrawer: false,
      newParticipants: [],
      selectedUsers: [],
      selectedRecords: [],
      loading: false,
      editingUsers: {},
      errorsExistUser: false,
      messageExistUser: null,
      errorsExistAudio: false,
      messageExistAudio: null,
      editingIndex: null,
      isGuest: null,
      toModify: [],
      showNotice: false,
    };
  },
  props: ['show', 'getSelectedRecords', 'isOpened'],

  computed: {
    ...mapGetters('calibrage', ['getModel', 'getErrors', 'getPagination']),
    ...mapGetters('user', ['getUser']),
    model() {
      return this.getModel;
    },
    errors() {
      return this.getErrors;
    },
    campaigns() {
      return this.$store.getters['campaign/getCollection'].map((x) => ({
        id: x.campagneId,
        name: x.name,
      }));
    },
    users() {
      const authedUserId = this.getUser.id;

      const excludedEmails = this.model.memberCalibrations
        ? this.model.memberCalibrations.map((calibration) => calibration.email)
        : [];

      return this.$store.getters['user/getCollection']
        .filter(
          (user) =>
            user.id !== authedUserId && !excludedEmails.includes(user.email),
        )
        .map((x) => ({
          ...x,
          ['id']: x.id,
          ['fullName']: x.firstName + ' ' + x.lastName,
          ['email']: x.email,
          ['avatarColor']: x.avatarColor,
        }));
    },
    records() {
      return this.$store.getters['record/getCollection'];
    },
    columns() {
      return [
        {
          key: 'name',
          label: 'Audio',
          render: (text, record) => {
            return `<div class="audio"><span><i class="icon-volume-up"></i></span> ${record.name}</div> `;
          },
        },
        {
          key: 'Date',
          label: 'Date',
          render: (text, record) => {
            return `<span>${this.$globals.audioItemDateFormat(
              record.createdAt,
            )}</span>`;
          },
        },

        {
          Agent: 'Agent',
          label: 'Agent',
          render: (text, record) => {
            return `<div>  ${record.firstName + ' ' + record.lastName}</div> `;
          },
        },

        {
          Agent: 'Call-Center',
          label: 'Call-Center',
          render: (text, record) => {
            return `<div>   <span class="actif mr-1"></span> ${record.callCenter}</div> `;
          },
        },
        {
          key: 'Remove',
          label: '',
          render: (text, record) => {
            return `<i class="icon-remove" @click="$store.commit('calibrage/REMOVE_SELECTED', { itemId: ${record.id} })"></i>`;
          },
        },
      ];
    },
    authedUser() {
      return this.getUser;
    },
    calibrageStartTime: {
      get() {
        return this.model.startDate ? this.model.startDate.split(' ')[1] : null;
      },
      set(newTime) {
        if (this.calibrageStartDate) {
          const datePart = this.calibrageStartDate.split(' ')[0];
          this.$set(this.model, 'startDate', `${datePart} ${newTime}`);
        }
      }
    },
    calibrageStartDate: {
      get() {
        return this.model.startDate ? this.model.startDate.split(' ')[0] : null;
      },
      set(newDate) {
        if (this.calibrageStartTime) {
          const startTime = this.calibrageStartTime.split(':').slice(0, 2).join(':');
          this.$set(this.model, 'startDate', `${newDate} ${startTime}`);
        } else {
          this.$set(this.model, 'startDate', newDate);
        }
      }
    }
  },
  watch: {
    calibrageStartDate: function (newDate) {
      if (this.calibrageStartTime) {
        const startTime = this.calibrageStartTime
          .split(':')
          .slice(0, 2)
          .join(':');
        this.$set(this.model, 'startDate', `${newDate} ${startTime}`);
      }
      if (!newDate) {
        this.calibrageStartTime = null;
      }
    },
    calibrageStartTime: function (newTime) {
      if (this.calibrageStartDate) {
        const datePart = this.calibrageStartDate.split(' ')[0];
        this.$set(this.model, 'startDate', `${datePart} ${newTime}`);
      }
    },
  },
  methods: {
    ...mapActions('calibrage', [
      'setModel',
      'createCalibration',
      'updateCalibration',
      'setErrors',
      'resetModel',
      'setPagination',
    ]),
    ...mapActions('record', ['resetSelectedRecords', 'resetState']),
    ...mapActions('user', ['Users', 'updateUser']),
    getFirstLetter(name) {
      return name ? name.charAt(0).toUpperCase() : '';
    },
    handleClose() {
      this.$emit('close');
      this.resetModel();
      this.setErrors(null);
    },

    updateTitle(event) {
      this.setModel({ ...this.model, ['title']: event.target.value });
      this.validateForm();
    },
    validateForm() {
      const errors = { ...this.getErrors };

      if (!this.model.title) {
        errors.title = ['TITLE_REQUIRED'];
      } else if (this.model.title.length < 2) {
        errors.title = ['TITLE_MIN_LENGTH'];
      } else if (this.model.title.length > 80) {
        errors.title = ['TITLE_LENGTH_EXCEEDED'];
      } else if (!/^[a-zA-Z0-9\s-]+$/u.test(this.model.title)) {
        errors.title = ['TITLE_STRING'];
      } else {
        delete errors.title;
      }

      this.setErrors(errors);
    },
    updateEditedUser(field, value, index) {
      this.toModify[index][field] = value;
    },
    showDrawerNotice() {
      this.showNotice = !this.showNotice;
    },
    updateCalibrageStartDate(newDate) {
      if (!newDate) {
        this.$set(this.model, 'startDate', null);
        this.calibrageStartTime = null;
      } else {
        this.$set(
          this.model,
          'startDate',
          `${newDate} ${this.calibrageStartTime || null}`
        );
      }
      delete this.errors.startDate;
    },
    updateCalibrageStartTime(newTime) {
      if (this.calibrageStartDate) {
        const datePart = this.calibrageStartDate.split(' ')[0];
        this.$set(this.model, 'startDate', `${datePart} ${newTime}`);
      } else {
        this.calibrageStartTime = null;
      }
      delete this.errors.startDate;
    },
    validateEmailItems(items) {
      items.errors.email = this.validateEmail(items.email.trim());
    },
    validateLastNameItems(items) {
      items.errors.lastName = this.validateLastName(items.lastName.trim());
    },
    validateFirstNameItems(items) {
      items.errors.firstName = this.validateFirstName(items.firstName.trim());
    },
    validateEmail(email) {
      if (!email) {
        return 'EMAIL_REQUIRED';
      } else if (!this.isValidEmail(email)) {
        return 'EMAIL_FORMAT_INVALID';
      } else {
        return '';
      }
    },
    validateLastName(value) {
      const validChars = /^[A-Za-z--- -]*$/.test(value);

      if (value == '') {
        return 'LAST_NAME_REQUIRED';
      }
      if (!validChars) {
        return 'LAST_NAME_ALPHABETICAL_ERROR';
      }
      return '';
    },
    validateFirstName(value) {
      const validChars = /^[A-Za-z--- -]*$/.test(value);
      if (value == '') {
        return 'FIRST_NAME_REQUIRED';
      }
      if (!validChars) {
        return 'FIRST_NAME_ALPHABETICAL_ERROR';
      }
      return '';
    },
    isValidEmail(email) {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(email);
    },

    deleteForm(uuid) {
      this.newParticipants = this.newParticipants.filter((x) => x.uuid != uuid);
    },
    handleUserSelectedItem(item) {
      this.setModel({
        ...this.model,
        ['memberCalibrations']: [
          ...this.model.memberCalibrations,
          {
            isGuest: this.isGuest,
            email: item.email,
            firstName: item.firstName,
            lastName: item.lastName,
            avatarColor: item.avatarColor,
            errors: {
              email: '',
              firstName: '',
              lastName: '',
            },
          },
        ],
      });
    },
    editMemberCalibrations(index, valeur) {
      valeur.errors.firstName = '';
      valeur.errors.lastName = '';
      valeur.errors.email = '';

      this.toModify.forEach((item, indexof) => {
        this.$set(this.toModify, indexof, null);
      });

      if (this.editingIndex === index) {
        this.editingIndex = null;
      } else {
        this.editingIndex = index;
        this.toModify[index] = { ...valeur };
      }
    },
    isEditing(index) {
      return this.editingIndex === index;
    },
    deleteMemberCalibrations(index) {
      const updatedList = JSON.parse(
        JSON.stringify(this.model.memberCalibrations),
      );
      if (index >= 0 && index < updatedList.length) {
        updatedList.splice(index, 1);
      }
      this.model.memberCalibrations = updatedList;
    },
    handleRecordSelectedItem(item) {
      this.errorsExistAudio = false;
      this.messageExistAudio = null;
      const isAudioSelected = this.model.recordCalibrations.some(
        (audio) => audio.name === item.name,
      );

      if (!isAudioSelected) {
        this.setModel({
          ...this.model,
          ['recordCalibrations']: [
            ...this.model.recordCalibrations,
            {
              index: this.model.recordCalibrations.length,
              name: item.name,
              firstName: item.agentId.firstName,
              lastName: item.agentId.lastName,
              callCenter: item.agentId.callCenterId ? item.agentId.callCenterId.name : null,
            },
          ],
        });
        delete this.errors.recordCalibrations;
      } else {
        this.errorsExistAudio = true;
        this.messageExistAudio = "L'audio est dj slectionn.";
      }
    },

    deleteRecordCalibrations(index) {
      const updatedList = JSON.parse(
        JSON.stringify(this.model.recordCalibrations),
      );
      if (index >= 0 && index < updatedList.length) {
        updatedList.splice(index, 1);
      }
      this.model.recordCalibrations = updatedList;
    },

    saveEditedParticipant(item, index) {
      const existingEmails = this.model.memberCalibrations
        .filter((user, i) => i !== index)
        .map((calibration) => calibration.email);

      if (existingEmails.includes(item.email)) {
        item.errors.email = ['EMAIL_ALREADY_EXIST'];
      } else {
        if (
          item.errors.firstName === '' &&
          item.errors.lastName === '' &&
          item.errors.email === ''
        ) {
          const updatedMemberCalibrations = this.model.memberCalibrations.map(
            (user, i) => {
              if (i === index) {
                return {
                  ...user,
                  firstName: item.firstName,
                  lastName: item.lastName,
                }; // Mise  jour des prnoms et noms de famille
              }
              return user;
            },
          );

          this.setModel({
            ...this.model,
            memberCalibrations: updatedMemberCalibrations,
          });

          // Annuler l'dition du participant
          this.cancelEditParticipant();
        }
      }
    },
    cancelEditParticipant() {
      this.editingIndex = null;
    },

    removeUserItem() {},
    handleOnSubmit() {
      this.loading = true;
      if (this.model.id == null) {
        this.createCalibration()
          .then(() => {
            this.setPagination({
              ...this.getPagination,
              ['current_page']: 1,
            });
            this.resetSelectedRecords();
            this.handleClose();
            this.loading = false;
          })
          .catch((error) => {
            this.loading = false;
          });
      } else {
        this.loading = true;
        this.updateCalibration()
          .then(() => {
            this.resetSelectedRecords();
            this.handleClose();
            this.loading = false;
          })
          .catch(() => {
            this.loading = false;
          });
      }
      const inputElement = document.querySelector('.singledatepicker .singledate_ .mx-datepicker .mx-input-wrapper input');
      if (this.calibrageStartDate) {
        inputElement.classList.remove('erreur-date');
        inputElement.classList.add('mx-input');
      } else {
        inputElement.classList.remove('mx-input');
        inputElement.classList.add('erreur-date');
      }
      const inputElement2 = document.querySelector('.singledatepicker .heure_ .mx-datepicker .mx-input-wrapper input');
      if (this.calibrageStartTime) {
        inputElement2.classList.remove('erreur-date');
        inputElement2.classList.add('mx-input');
      } else {
        inputElement2.classList.remove('mx-input');
        inputElement2.classList.add('erreur-date');
      }
    },
    disabledDates(date) {
      const cDate = new Date(date);
      cDate.setHours(0, 0, 0, 0);
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      return cDate < today;
    },
    disabledHours(hour) {
      if (!this.calibrageStartDate) return true;
      const selectedDate = new Date(this.calibrageStartDate);
      const currentDate = new Date();
      const selectedHour = new Date(hour).getHours();
      const currentHour = currentDate.getHours();
      
      if (selectedDate.toDateString() === currentDate.toDateString()) {
        return selectedHour < currentHour + 1;
      }
      return false;
      
    },
  },
  async created() {
    await this.resetState();
  },
  mounted() {
    this.$store.dispatch('campaign/setFilter', {
      ...this.$store.getters['campaign/getFilter'],
      ['withPagination']: 0,
    });
    this.$store.dispatch('campaign/getData');

    this.$store.dispatch('record/setFilter', {
      ...this.$store.getters['record/getFilter'],
      withPagination: 0,
    });
    this.$store.dispatch('record/getData');
    // this.Users();
  },
};
</script>

