<template>
  <div class="content_page page_equipe stat-teams">
    <div class="header_page">
      <div class="content-filter">
        <InputSearch
          class="search-field"
          :placeholder="
            $globals.capitalizeFirstLetter($t('components.team.searchTeam')) +
