          "
          @input-value="handleInputValue"
        />
        <Filtres
          :key="keyFilter"
          :items="[
            {
              label: 'Call-center',
              key: 'callCenters',
              filters: !$globals.isRoleAllowed('roleAllowed', roleUser)
                ? callCenters
                : [],
            },
          ]"
          @onFilterChange="handleFilterChanged"
        />
      </div>
      <!-- <div class="btn-add" @click="toggleDrawer">
        <ButtonPrimary
          icon="icon-add"
          :text="$globals.capitalizeFirstLetter($t('components.team.add'))"
        />
      </div> -->
    </div>
    <div class="filter_dropdown flex">
      <SelectCampgnes
        @onCreate="addCampaign"
        @onCreateGrille="addGrille"
        @onUpdate="updateCampaign"
        @onUpdateGrille="updateGrille"
        @onSelect="selectCampaign"
        @onReset="resetCampaign"
        :selectedByDefault="true"
      />
      <DateComparision
        :withTimePicker="false"
        @cancel="cancelDatePicker"
        @applyed="applyedDatePicker"
      />
    </div>

    <!-- <div class="table-teams">
      <div class="action-grouper">
        <h3 class="title-table">Superviseurs / Agents sans quipe</h3>
        <div class="mr-3">
          <DropDownMenu
            :class="{ 'disable-line': checkAgentsSelected() === 'error' }"
            :items="[
              {
                label:
                  checkAgentsSelected() === 1 ? 'Migrer' : 'Crer son quipe',
                onClick: () => {
                  if (checkAgentsSelected() === 1) {
                    OpenModalMigrerAgentWithoutTeam();
                  } else {
                    createTeamForSuperviser();
                  }
                },
              },
            ]"
            icon="icon-down"
            :text="'Actions groupes'"
            :btnId="'btn_dropdown_1001'"
            :dropdownId="'dropdownMenu_1001'"
            :btnclass="'btn-third'"
          />
        </div>
      </div>

      <div class="card-table actions-tab">
        <TableComponent
          ref="agentsTable"
          :isLoading="isFetchingAgent"
          :multipleSelect="true"
          :hasActions="false"
          :data="dataAgents"
          :columns="columnsAgent"
          :redirection="true"
          :selectAgents="true"
          @onSort="handleSort"
          :keySelection="'id'"
          @onSelection="handleSelectedRows"
          :enabledScrollCss="true"
          :agentTeam="true"
        />
        <ModalMigrer
          :showModalMigration="showModalMigrerAgentWithoutTeam"
          :cancelText="$t('views.equipe.modalBtnleft')"
          :okText="$t('views.equipe.btnMigration')"
          @onConfirm="onConfirmAgentWithoutTeam"
          @close="closeMigrerAgentWithoutTeam()"
        />

        <Pagination storeModuleName="agent" /> 
      </div>
    </div> -->
    <template v-if="!isFetching">
      <template v-if="data.length > 0">
        <div
          class="card-table-outer actions-tab table-teams"
          v-for="(item, index) in data"
          :key="index"
        >
          <div class="action-table">
            <div class="content-text">
              <h3 class="title-table">
                {{ item.name }}

                <span>
                  <i class="icon-home"></i>
                  {{ item.callcenter ? item.callcenter.name : '-' }}
                </span>
              </h3>
              <div class="user ml-3">
                <h4>
                  <i class="icon-supervisor-account mr-2"></i>
                  {{
                    item.supervisers.length == 0
                      ? $globals.capitalizeFirstLetter(
                          $t('components.team.nosuperviser'),
                        )
                      : $globals.capitalizeFirstLetter(
                          item.supervisers[0].firstName,
                        ) +
                        ' ' +
                        $globals.capitalizeFirstLetter(
                          item.supervisers[0].lastName,
                        )
                  }}
                </h4>
              </div>
            </div>
            <!-- <p style="text-decoration: underline" @click="OpenModalPassword">
              modifier mot de pass
            </p> -->
            <div class="content-action">
              <!-- <DropDownMenu
                :class="{
                  'disable-line':
                    !selectedRowsTeamAgents[index] ||
                    selectedRowsTeamAgents[index].length == 0,
                }"
                class="mr-2"
                :items="[
                  {
                    label: 'Migrer',
                    onClick: () => {
                      OpenModalMigrer(item);
                    },
                    iconClass: 'icon-migrer pr-2',
                  },

                  {
                    label: 'Retirer',
                    onClick: () => {
                      OpenModalRetirer(item);
                    },
                    iconClass: 'icon-delete-agt pr-2',
                  },
                ]"
                icon="icon-down"
                :text="'Actions groupes'"
                :btnId="'btn_dropdown_' + index"
                :dropdownId="'dropdownMenu_' + index"
                :btnclass="'btn-third'"
              />
              <button>
                <i
                  class="icon-edit"
                  @click="handleUpdateClicked(item, index)"
                ></i>
              </button>
              <button @click="OpenDrawerGestion(item, index)" class="mx-2">
                <i class="icon-user-settings"></i>
              </button>
              <button @click="openModal(item, index)">
                <i class="icon-delete"></i>
              </button> -->
              <div class="taux">
                <span class="bold">{{ calculateScores(item).teamScore }}</span>
                <span :class="calculateScores(item).scoreDifference.class">
                  <i :class="calculateScores(item).scoreDifference.icon"></i>
                  {{ calculateScores(item).signe }}
                  {{ calculateScores(item).teamSigne }}
                  {{ calculateScores(item).scoreDifference.value }}
                </span>
              </div>

              <!-- <div class="action-equipe" :id="'myDiv' + index">
                <div class="more_action" @click="toggleDropdown(index)">
                  <i class="icon-more"></i>
                </div>
                <div
                  class="dropdown"
                  :id="'dropdown' + index"
                  ref="dropdown"
                  v-if="isOpenCriteres[index]"
                >
                  <ul>
                    <li>
                      <button @click="OpenDrawerGestion(item, index)">
                        <i class="icon-delete mr-2"></i>
                        Ajouter agent
                      </button>
                    </li>

                    <li>
                      <button @click="handleUpdateClicked(item, index)">
                        <i class="icon-edit mr-2"></i>
                        modifier
                      </button>
                    </li>
                    <li>
                      <button @click="openModal(item, index)">
                        <i class="icon-delete mr-2"></i>
                        supprimer
                      </button>
                    </li>
                  </ul>
                </div>
              </div> -->
            </div>
          </div>
          <div class="card-table mb-5">
            <TableComponentTeam
              :isLoading="isFetching"
              :data="item.agents"
              :columns="columns"
              :multipleSelect="false"
              :msgEmptyTable="
                $globals.capitalizeFirstLetter($t('common.norecordsagents'))
              "
              :indexTeam="index"
              :withButton="true"
              @onButtonClicked="OpenDrawerGestion(item, index)"
              @onSelection="
                (itemFromTable) => {
                  handleSelectedRowsTeam(itemFromTable, index);
                }
              "
            />
          </div>

          <modal
            :loading="isLoadingButton"
            :show-modal="modalVisible.includes(item.id)"
            :cancelText="$t('views.equipe.modalBtnleft')"
            :okText="$t('views.equipe.modalBtnrightDelete')"
            :item="item"
            @onConfirm="(item) => deleteTeamSelected(item, index)"
            @close="
              (item) => {
                closeModal(item);
              }
            "
          >
            <template v-slot:title>
              {{ $t('views.equipe.modalTitle') }}
            </template>
            <template v-slot:content>
              <p class="pb-5">
                {{
                  $globals
                    .capitalizeFirstLetter($t('views.equipe.modalText'))
                    .replace(
                      'teamname',
                      $globals.capitalizeFirstLetter($t(item.name)),
                    )
                }}
              </p>
              <p>{{ $t('views.equipe.modalQuestion') }}</p>
            </template>
          </modal>
          <ModalMigrer
            :showModalMigration="showModalMigrer.includes(item.id)"
            :actualTeam="item.id"
            :fromTeam="true"
            :cancelText="$t('views.equipe.modalBtnleft')"
            :okText="$t('views.equipe.btnMigration')"
            @onConfirm="(Newitem) => onConfirmAgentInTeam(Newitem, index, item)"
            @close="closeMigrer(item)"
          />
          <modal
            :loading="isLoadingButton"
            :show-modal="dispalyDeleteAgentsFromTeamModal.includes(item.id)"
            :cancelText="$t('views.agents.modalBtnleft')"
            :okText="$t('views.agents.remove')"
            @onConfirm="handleRemoveModal(item, index)"
            @close="closeRetirer(item)"
          >
            <template v-slot:title>
              {{ $t('views.agents.modalTitle') }}
            </template>
            <template v-slot:content>
              <p v-if="selectedRowsTeamAgents[index].length == 1" class="pb-5">
                {{ $t('views.agents.modalRemoveOneItemText') }}
                <strong>
                  {{ selectedRowsTeamAgents[index][0].firstName }}
                  {{ selectedRowsTeamAgents[index][0].lastName }}
                </strong>
              </p>
              <p v-if="selectedRowsTeamAgents[index].length == 1">
                {{ $t('views.agents.modalRemoveOneItemQuestion') }}
              </p>
              <p v-if="selectedRowsTeamAgents[index].length > 1" class="pb-5">
                {{ $t('views.agents.modalRemoveManyItemsText') }}
              </p>
              <div style="align-items: left" class="pb-5">
                <ul
                  style="list-style: disc"
                  v-if="selectedRowsTeamAgents[index].length > 1"
                >
                  <li
                    v-for="item of selectedRowsTeamAgents[index]"
                    :key="item.id"
                  >
                    <strong>{{ item.firstName + ' ' + item.lastName }}</strong>
                  </li>
                </ul>
              </div>

              <p v-if="selectedRowsTeamAgents[index].length > 1">
                {{ $t('views.agents.modalRemoveFromTeamManyItemsQuestion') }}
              </p>
            </template>
          </modal>
        </div>
      </template>
      <template v-else>
        <div class="card-table-outer mb-5">
          <div class="table-row empty-data text-center">
            <div class="table-empty fff">
              <h6>
                {{ $globals.capitalizeFirstLetter($t('common.norecords')) }}
              </h6>
            </div>
          </div>
        </div>
      </template>
    </template>
    <template v-else>
      <div class="card-table-outer mb-5">
        <div class="table-row empty-data text-center">
          <div class="table-empty">
            <Loader />
          </div>
        </div>
      </div>
    </template>

    <!-- <Pagination storeModuleName="team" /> -->
    <DrawerEquipe
      :isOpenedTeam="showDrawer"
      :drawer="showDrawer"
      @close="closeDrawer"
      @closeAction="closeDrawerAfterAction"
    />
    <DrawerEquipeAgent
      v-if="showDrawerEquipeAgent"
      :drawer="showDrawerEquipeAgent"
      :agentsInThisTeam="teamSelected"
      @close="closeDrawerEquipeAgent"
    />
    <DrawerCampagne
      :isOpened="showDrawerCampagne"
      :drawer="showDrawerCampagne"
      @close="closeDrawerCampagne"
    />
    <DrawerEvaluation
      :isOpened="showDrawerGrille"
      @close="showDrawerGrille = false"
      :dashboardPosition="true"
    />
    <DrawerGestion
      :isOpenedGestion="showDrawerGestion"
      :teamIndex="indexSelected"
      :agentsInThisTeam="teamSelected"
      @close="
        (closeWay) => {
          closeDrawerGestion(closeWay);
        }
      "
      :dashboardPosition="true"
    />

    <ModalUpdatePassword
      :showModalPassword="showModalPassword"
      :cancelText="$t('views.equipe.modalBtnleft')"
      :okText="$t('views.equipe.btnReinitialiser')"
      @close="
        (item) => {
          CloseModalPassword();
        }
      "
    />
  </div>
</template>
<script>
import DrawerEquipe from '@/components/DrawerForm/Equipe/DrawerEquipe.vue';
import DrawerEquipeAgent from '@/components/DrawerForm/Equipe/DrawerEquipeAgent.vue';
import InputSearch from '@/components/FormWidgets/InputSearch.vue';
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css';
import SelectCampgnes from '@/components/SelectCampagne/SelectCampagnes.vue';
// import ButtonPrimary from '@/components/FormWidgets/ButtonPrimary.vue';
// import TableComponent from '@/components/Table/Table.vue';
import TableComponentTeam from '@/components/Table/TableTeamAgent.vue';
import Filtres from '@/components/FormWidgets/Filter/Filter.vue';
import Modal from '@/components/Modal/Modal.vue';
import ModalMigrer from './Modal/ModalMigrer.vue';
import ModalUpdatePassword from './Modal/ModalUpdatePassword.vue';
//import Pagination from '@/components/Table/Pagination.vue';
import { mapGetters, mapActions } from 'vuex';
import moment from 'moment';
import Loader from '@/components/AnimationScribb/Loader.vue';
import DrawerCampagne from '@/components/DrawerForm/Campagnes/DrawerCampagnes.vue';
import DateComparision from '@/components/FormWidgets/DateRange/DateComparison.vue';
import DrawerEvaluation from '@/components/DrawerForm/Evaluation/DrawerEvaluation.vue';
import DrawerGestion from '@/components/DrawerForm/Equipe/DrawerGestionAgent.vue';
// import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';

export default {
  components: {
    DrawerEquipe,
    DrawerEquipeAgent,
    InputSearch,
    // ButtonPrimary,
    // TableComponent,
    TableComponentTeam,
    Filtres,
    SelectCampgnes,
    Modal,
    ModalMigrer,
    ModalUpdatePassword,
    //Pagination,
    Loader,
    DrawerCampagne,
    DateComparision,
    DrawerEvaluation,
    DrawerGestion,
    // DropDownMenu,
  },

  data() {
    // Get today's date
    const today = new Date();

    // Calculate the date one week ago
    const oneWeekAgo = new Date();
    oneWeekAgo.setDate(today.getDate() - 7);
    const twoWeekAgo = new Date();
    twoWeekAgo.setDate(oneWeekAgo.getDate() - 1);
    const starttwoWeekAgo = new Date();
    starttwoWeekAgo.setDate(twoWeekAgo.getDate() - 7);

    // Format the date as a string in dd/mm/yyyy format
    const formatDate = (date) => {
      const day = ('0' + date.getDate()).slice(-2);
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const year = date.getFullYear();
      return `${year}/${month}/${day}`;
    };
    const formatDateForCompare = (date) => {
      const day = ('0' + date.getDate()).slice(-2);
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const year = date.getFullYear();
      return `${day}/${month}/${year}`;
    };

    const startDate = formatDate(oneWeekAgo);
    const endDate = formatDate(today);
    const startDateForCompareNew = formatDateForCompare(oneWeekAgo);
    const endDateForCompareNew = formatDateForCompare(today);
    const startDateForCompare = formatDateForCompare(starttwoWeekAgo);
    const endDateForCompare = formatDateForCompare(twoWeekAgo);

    return {
      showDrawer: false,
      showDrawerCampagne: false,
      showDrawerGrille: false,
      showDropdownAction: false,
      showModalMigrer: [],
      showModalMigrerAgentWithoutTeam: false,
      showDrawerEquipeAgent: false,
      showModalPassword: false,
      showDrawerGestion: false,
      modalVisible: [],
      dispalyDeleteAgentsFromTeamModal: [],
      dispalyArchiveModal: false,
      dateRange: {
        startDate: startDate,
        endDate: endDate,
        startNewDate: startDateForCompareNew,
        endNewDate: endDateForCompareNew,
        startOldDate: startDateForCompare,
        endOldDate: endDateForCompare,
      },
      isOpenCriteres: {},
      selectedTeam: null,
      indexSelected: null,
      teamSelected: null,
      selectedRowsAgents: [],
      selectedRowsTeamAgents: [],
      roleUser: 'visitor',
      keyFilter: 1,
    };
  },
  filters: {
    dateCell(value) {
      let dt = new Date(value);

      return dt.getDate();
    },
    date(val) {
      return val ? val.toLocaleString() : '';
    },
  },
  created() {
    this.resetModel();
    this.resetFilter();
    this.resetFilterAgent();
    this.resetPagination();
    // this.resetCampagneSelected();
    // this.setFilter({
    //   ...this.getFilter,
    //   ['startNewDate']: this.dateRange.startNewDate
    //     ? moment(this.dateRange.startNewDate, 'DD/MM/YYYY').format('YYYY-MM-DD')
    //     : this.dateRange.startNewDate,
    //   ['endNewDate']: this.dateRange.endNewDate
    //     ? moment(this.dateRange.endNewDate, 'DD/MM/YYYY').format('YYYY-MM-DD')
    //     : this.dateRange.endNewDate,
    //   ['startOldDate']: this.dateRange.startOldDate
    //     ? moment(this.dateRange.startOldDate, 'DD/MM/YYYY').format('YYYY-MM-DD')
    //     : this.dateRange.startOldDate,
    //   ['endOldDate']: this.dateRange.endOldDate
    //     ? moment(this.dateRange.endOldDate, 'DD/MM/YYYY').format('YYYY-MM-DD')
    //     : this.dateRange.endOldDate,
    //   ['campagneId']:
    //     this.user && this.user.selectedCampagneId
    //       ? this.user.selectedCampagneId
    //       : null,
    //   ['gridId']:
    //     this.user && this.user.selectedGridId ? this.user.selectedGridId : null,
    // });
    // this.getData();
    // this.refreshDataAgents();
  },
  methods: {
    ...mapActions('team', {
      getData: 'getData',
      resetModel: 'resetModel',
      setFilter: 'setFilter',
      updateTeam: 'updateTeam',
      deleteTeam: 'deleteTeam',
      resetFilter: 'resetFilter',
      resetPagination: 'resetPagination',
      setModelTeam: 'setModel',
    }),
    ...mapActions('agent', {
      getDataAgent: 'getData',
      setFilterAgent: 'setFilter',
      resetFilterAgent: 'resetState',
    }),
    ...mapActions('grid', ['setGridModel']),
    ...mapActions('callCenter', ['getCollectionCallCentersCampagnes']),
    ...mapActions('campaign', ['setModel', 'resetCampagneSelected']),
    toggleDrawer() {
      if (this.accessCallCenterOptions != null) {
        this.$store.commit(
          'team/SET_MODEL_CALLCENTER',
          this.accessCallCenterOptions[0],
        );
        this.getCollectionCallCentersCampagnes({
          id: this.accessCallCenterOptions[0].id,
        })
          .then(() => {
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'supervisers',
              items: this.collectionTeam.supervisers,
            });
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'agents',
              items: this.collectionTeam.agents,
            });
            this.showDrawer = !this.showDrawer;
          })
          .catch(() => {});
      } else {
        this.showDrawer = !this.showDrawer;
      }
    },
    OpenModalMigrerAgentWithoutTeam() {
      this.showModalMigrerAgentWithoutTeam = true;
    },
    closeMigrerAgentWithoutTeam() {
      this.showModalMigrerAgentWithoutTeam = false;
    },

    OpenModalMigrer(item) {
      if (!this.showModalMigrer.includes(item.id)) {
        this.showModalMigrer.push(item.id);
      }
    },
    OpenModalRetirer(item) {
      if (!this.dispalyDeleteAgentsFromTeamModal.includes(item.id)) {
        this.dispalyDeleteAgentsFromTeamModal.push(item.id);
      }
    },
    closeMigrer(item) {
      this.showModalMigrer = this.showModalMigrer.filter((x) => x != item.id);
    },
    closeRetirer(item) {
      this.dispalyDeleteAgentsFromTeamModal =
        this.dispalyDeleteAgentsFromTeamModal.filter((x) => x != item.id);
    },

    OpenModalPassword() {
      this.showModalPassword = true;
    },
    CloseModalPassword() {
      this.showModalPassword = false;
    },
    async OpenDrawerGestion(dataFromAction, index) {
      this.resetModel();
      this.indexSelected = index;
      this.teamSelected = dataFromAction;
      this.showDrawerGestion = true;
    },
    addCampaign() {
      this.showDrawerCampagne = true;
    },
    addGrille() {
      this.showDrawerGrille = true;
    },
    selectCampaign(item) {
      //selected Campagne
    },
    updateCampaign(item) {
      this.showDrawerCampagne = true;
      this.setModel({
        id: item.campagneId,
        name: item.name,
        createdAt: item.createdAt,
        updatedAt: item.updatedAt,
      });
    },
    updateGrille(item) {
      this.showDrawerGrille = true;
      this.setGridModel({
        id: item.id,
        name: item.name,
        campagne: item.campagne,
        statuts: item.statuts,
        updatedAt: item.updatedAt,
        categories: item.categories,
        selectedCategorie: item.selectedCategorie,
        draft: item.draft,
      });
    },
    resetCampaign() {
      //reset the filter
    },
    handleInputValue(value) {
      this.setFilter({
        ...this.getFilter,
        ['keyword']: value,
      });
      this.refreshDataAgents({ keyword: value });
    },
    async handleUpdateClicked(dataFromAction, index) {
      this.resetModel();
      if (dataFromAction.callcenter) {
        await this.getCollectionCallCentersCampagnes({
          id: dataFromAction.callcenter.id,
        })
          .then(() => {
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'campagnes',
              items: this.collectionTeam.campagnes,
            });
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'supervisers',
              items: this.collectionTeam.supervisers,
            });
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'agents',
              items: this.collectionTeam.agents,
            });
            this.$store.commit(
              'team/SET_MODEL_CALLCENTER',
              dataFromAction.callcenter,
            );
          })
          .catch(() => {});
        dataFromAction.campagnes.forEach((campagne) => {
          this.handleItemSelectedItem(campagne, 'campagnesSelected');
        });
      }
      this.$store.commit('team/SET_MODEL_EDIT', dataFromAction);
      this.$store.commit('team/FILTER_THE_AGENTS');
      this.$set(this.isOpenCriteres, index, false);
      this.indexSelected = index;
      this.teamSelected = dataFromAction;
      this.toggleDrawer();
    },
    handleItemSelectedItem(item, array_name) {
      this.$store.commit('team/SET_SELECTED_ARRAY', {
        sourceArray: array_name.slice(0, -8),
        targetArray: array_name,
        item,
      });
    },

    toggleDropdown(index) {
      const handleCloseDropDown = (index) => {
        this.isOpenCriteres[index] = false;
      };
      if (!this.isOpenCriteres[index]) {
        this.$set(this.isOpenCriteres, index, true);
      } else {
        this.$set(this.isOpenCriteres, index, !this.isOpenCriteres[index]);
      }

      if (this.isOpenCriteres[index]) {
        document.addEventListener('click', function (event) {
          var myDiv = document.getElementById('myDiv' + index);

          // Check if the clicked element is outside the div
          if (myDiv != null) {
            if (!myDiv.contains(event.target)) {
              // this.$set(this.isOpenCriteres, index, false);
              handleCloseDropDown(index);
            }
          }
        });
        // Add a click event listener to the document to close the dropdown when clicking outside
        document.addEventListener('click', this.closeDropdown);
      } else {
        // // Remove the click event listener when the dropdown is closed
        // document.removeEventListener('click', this.closeDropdown);
      }
    },
    // closeDropdown(event) {
    // Close the dropdown if the clicked element is not inside the dropdown
    // if (!this.$refs.dropdown.contains(event.target)) {
    //   this.showDropdownAction = false;
    // }
    // },

    openModal(item) {
      if (!this.modalVisible.includes(item.id)) {
        this.modalVisible.push(item.id);
      }
    },
    closeDrawer() {
      this.selectedRowsTeamAgents[this.indexSelected] = [];
      this.indexSelected = null;
      this.teamSelected = null;
      this.resetModel();
      // this.$store.commit('team/SET_COLLECTION_TEAMS', {
      //   arrayName: 'campagnes',
      //   items: this.collectionTeam.campagnes,
      // });
      this.showDrawer = false;
      // this.refreshDataAgents();
    },
    closeDrawerAfterAction() {
      this.indexSelected = null;
      this.teamSelected = null;
      // this.resetModel();
      // this.$store.commit('team/SET_COLLECTION_TEAMS', {
      //   arrayName: 'campagnes',
      //   items: this.collectionTeam.campagnes,
      // });
      this.showDrawer = false;
      this.refreshDataAgents();
      this.$refs.agentsTable.setSelectedRows([]);
      this.$refs.agentsTable.setSelectedRole();
      this.keyFilter++;
    },
    closeDrawerEquipeAgent() {
      this.indexSelected = null;
      this.teamSelected = null;
      this.resetModel();
      this.$store.commit('team/SET_COLLECTION_TEAMS', {
        arrayName: 'campagnes',
        items: this.collectionTeam.campagnes,
      });
      this.showDrawerEquipeAgent = false;
    },
    closeDrawerCampagne() {
      this.showDrawerCampagne = false;
    },
    closeModal(item) {
      this.modalVisible = this.modalVisible.filter((x) => x != item.id);
    },
    handleFilterChanged(options) {
      this.setFilter({
        ...this.getFilter,
        ['callcenter']: options.callCenters,
      });
      this.getData();
      this.refreshDataAgents({ callCenters: options.callCenters });
      this.$refs.agentsTable.setSelectedRows([]);
      this.$refs.agentsTable.setSelectedRole();
    },
    async deleteTeamSelected(item, index) {
      this.$store.commit('team/SET_IS_LOADING_BUTTON', true);

      this.$store.commit('team/SET_MODEL', item);
      await this.deleteTeam().then(() => {
        this.$store.commit('team/SET_COLLECTION_TEAMS', {
          arrayName: 'campagnes',
          items: this.collectionTeam.campagnes,
        });
        this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
        this.$set(this.isOpenCriteres, index, false);
        this.closeModal(item);
        this.refreshDataAgents();
      });
    },
    cancelDatePicker() {
      this.dateRange.startNewDate = null;
      this.dateRange.endNewDate = null;
      this.dateRange.startOldDate = null;
      this.dateRange.endOldDate = null;
    },
    applyedDatePicker(dateRange) {
      this.dateRange.startNewDate = dateRange.startNewDate;
      this.dateRange.endNewDate = dateRange.endNewDate;
      this.dateRange.startOldDate = dateRange.startOldDate;
      this.dateRange.endOldDate = dateRange.endOldDate;
    },
    calculateScores(item) {
      let teamScore = 0;
      let scoreDifferenceSum = 0;
      item.agents.forEach((element) => {
        if (element.scoring[0]) {
          teamScore += element.scoring[0].avgScoreNewDate;
          scoreDifferenceSum += parseFloat(
            element.scoring[0].scoreDifference.replace(',', '.'),
          );
        }
      });

      if (teamScore === 0) {
        return {
          teamScore: '0,00%',
          signe: '',
          scoreDifference: { value: '0,00%', class: 'taux green' },
        };
      }

      const averageTeamScore = teamScore / item.agents.length;
      const formattedTeamScore = averageTeamScore.toFixed(2) + '%';
      const averageScoreDifference =
        item && item?.agents ? scoreDifferenceSum / item.agents.length : 0;

      const formattedScoreDifference = averageScoreDifference.toFixed(2) + '%';
      const scoreDifferenceClassBindings = {
        taux: true,
        red: averageScoreDifference < 0,
        green: averageScoreDifference >= 0,
      };

      // Include the arrow icon based on the value of averageScoreDifference
      const arrowIcon =
        averageScoreDifference < 0
          ? 'icon-arrow-down-stat'
          : 'icon-arrow-up-stat';

      const signeC = averageScoreDifference < 0 ? '-' : '+';
      return {
        teamScore: formattedTeamScore,
        signe: signeC,
        scoreDifference: {
          value: formattedScoreDifference,
          icon: arrowIcon,
          class: scoreDifferenceClassBindings,
        },
      };
    },
    handleSelection() {
      this.dispalyArchiveModal = true;
    },
    handleSelectedRows(data) {
      this.selectedRowsAgents = data;
    },
    handleSelectedRowsTeam(data, index) {
      this.$set(this.selectedRowsTeamAgents, index, data);
    },
    handleSort(options) {
      this.refreshDataAgents({
        sortKey: options.sortKey,
        sortOrder: options.sortOrder,
        callCenters: options.callCenters,
      });
    },
    refreshDataAgents({
      keyword = null,
      sortKey = null,
      sortOrder = null,
      callCenters = null,
    } = {}) {
      this.setFilterAgent({
        ...this.agentFilter,
        keyword,
        sortKey,
        sortOrder,
        callcenter: callCenters,
        team: { id: 0 },
      });
      this.getDataAgent();
    },

    checkAgentsSelected() {
      const agentsWithRole2 = this.selectedRowsAgents.filter(
        (agent) => agent.roleId === '2',
      );
      const numAgentsWithRole2 = agentsWithRole2.length;
      if (numAgentsWithRole2 > 1) {
        return 'error';
      }
      if (numAgentsWithRole2 === 1) {
        return 0;
      }
      const hasAgentWithRole1 = this.selectedRowsAgents.some(
        (agent) => agent.roleId === '1',
      );
      if (hasAgentWithRole1) {
        return 1;
      }
      return 'error';
    },
    async createTeamForSuperviser() {
      const newData = this.selectedRowsAgents.map((item) => ({
        ...item,
        completeName: `${item.firstName} ${item.lastName}`,
      }));

      if (this.selectedRowsAgents[0].callCenterId) {
        await this.getCollectionCallCentersCampagnes({
          id: this.selectedRowsAgents[0].callCenterId.id,
        })
          .then(() => {
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'campagnes',
              items: this.collectionTeam.campagnes,
            });
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'supervisers',
              items: this.collectionTeam.supervisers,
            });
            this.$store.commit('team/SET_COLLECTION_TEAMS', {
              arrayName: 'agents',
              items: this.collectionTeam.agents,
            });
            this.$store.commit(
              'team/SET_MODEL_CALLCENTER',
              this.selectedRowsAgents[0].callCenterId,
            );
          })
          .catch(() => {});
      }
      this.$store.commit('team/SET_SELECTED_ARRAY', {
        sourceArray: 'supervisers',
        targetArray: 'supervisersSelected',
        item: newData[0],
      });

      this.toggleDrawer();
    },
    async onConfirmAgentInTeam(newTeam, index, team) {
      this.setModelTeam({
        ...this.modelTeam,
        ['agentTransfertCollection']: this.selectedRowsTeamAgents[index],
      });
      this.setModelTeam({
        ...this.modelTeam,
        ['id']: newTeam.id,
      });
      await this.updateTeam()
        .then(() => {
          this.$store.commit('team/SET_NEW_COLLECTION', {
            agentToRemove: this.selectedRowsTeamAgents[index],
            teamId: team.id,
          });
          this.selectedRowsTeamAgents = [];
          this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
          this.closeMigrer(team);
        })
        .catch(() => {
          this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
        });
    },

    async onConfirmAgentWithoutTeam(newTeam) {
      this.setModelTeam({
        ...this.modelTeam,
        ['agentTransfertCollection']: this.selectedRowsAgents,
      });
      this.setModelTeam({
        ...this.modelTeam,
        ['id']: newTeam.id,
      });
      await this.updateTeam()
        .then(() => {
          this.refreshDataAgents();
          this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
          this.closeMigrerAgentWithoutTeam();
        })
        .catch(() => {
          this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
          this.closeMigrerAgentWithoutTeam();
        });
      this.selectedRowsAgents = [];
      this.$refs.agentsTable.setSelectedRows([]);
      this.$refs.agentsTable.setSelectedRole();
      this.keyFilter++;
    },
    async handleRemoveModal(item, index) {
      this.$store.commit('team/SET_IS_LOADING_BUTTON', true);

      this.setModelTeam({
        ...this.modelTeam,
        ['agentsToRemove']: this.selectedRowsTeamAgents[index],
      });
      this.setModelTeam({
        ...this.modelTeam,
        ['id']: item.id,
      });

      await this.updateTeam()
        .then(() => {
          this.refreshDataAgents();
          this.closeRetirer(item);
          this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
          this.$set(this.selectedRowsTeamAgents, index, []);
        })
        .catch(() => {
          this.$store.commit('team/SET_IS_LOADING_BUTTON', false);
        });
    },
    closeDrawerGestion(closeWay) {
      this.showDrawerGestion = false;
      if (closeWay == 1) this.refreshDataAgents();
    },
  },
  watch: {
    'filter.keyword': function () {
      this.setFilter({
        ...this.getFilter,
        ['current_page']: 1,
      });
      this.getData();
    },
    dateRange: {
      handler() {
        this.setFilter({
          ...this.getFilter,
          ['startNewDate']: this.dateRange.startNewDate
            ? moment(this.dateRange.startNewDate).format('YYYY-MM-DD')
            : this.dateRange.startNewDate,
          ['endNewDate']: this.dateRange.endNewDate
            ? moment(this.dateRange.endNewDate).format('YYYY-MM-DD')
            : this.dateRange.endNewDate,
          ['startOldDate']: this.dateRange.startOldDate
            ? moment(this.dateRange.startOldDate).format('YYYY-MM-DD')
            : this.dateRange.startOldDate,
          ['endOldDate']: this.dateRange.endOldDate
            ? moment(this.dateRange.endOldDate).format('YYYY-MM-DD')
            : this.dateRange.endOldDate,
        });
        this.getData();
      },
      deep: true,
    },
    user() {
      let $grid = {
        name: this.user.selectedGridName ?? null,
        id: this.user.selectedGridId ?? null,
        categories: this.user.categories ?? null,
        draft: this.user.gridDraft ?? null,
      };
      this.$store.commit('campaign/SET_CAMPAGNE_INFO', {
        campagneId: this.user.selectedCampagneId,
        campagneName: this.user.selectedCampagneName,
        gridSelected: $grid,
      });
    },
    CampagneInfo: {
      handler(newCampagneInfo) {
        if (newCampagneInfo) {
          this.setFilter({
            ...this.getFilter,
            ['campagneId']: newCampagneInfo.campagneId || null,
            ['gridId']: newCampagneInfo.gridSelected
              ? newCampagneInfo.gridSelected.id
              : null,
          });
        }
        this.getData();
      },
      immediate: true, // Execute the handler immediately when the component is mounted
    },
  },

  mounted() {
    this.roleUser = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';
    const userString = localStorage.getItem('user');
    const userObject = JSON.parse(userString);

    if (userObject && userObject.callCenterId) {
      const callCenterId = userObject.callCenterId;
      this.accessCallCenterOptions = [callCenterId];
      this.accessCallCenterId = callCenterId.id;
    }

    this.setFilter({
      ...this.getFilter,
      ['withPagination']: 1,
      ['campagneId']: this.CampagneInfo ? this.CampagneInfo.campagneId : null,
      ['gridId']:
        this.CampagneInfo && this.CampagneInfo.gridSelected
          ? this.CampagneInfo.gridSelected.id
          : null,
    });

    this.getData();
    this.refreshDataAgents();

    this.getCollectionCallCentersCampagnes({
      id: null,
    })
      .then(() => {
        this.$store.commit('team/SET_COLLECTION_TEAMS', {
          arrayName: 'campagnes',
          items: this.collectionTeam.campagnes,
        });
      })
      .catch(() => {});
  },

  computed: {
    ...mapGetters('team', [
      'getCollection',
      'getFilter',
      'getModel',
      'getState',
    ]),
    ...mapGetters({ agentFilter: 'agent/getFilter' }),

    ...mapGetters('callCenter', ['getCollectionTeam', 'getIdWithName']),
    ...mapGetters('campaign', ['getCampagneInfo']),
    ...mapGetters({ user: 'user/getUser' }),

    CampagneInfo() {
      return this.getCampagneInfo;
    },
    isFetching() {
      return this.getState.isFetchingDataLoading;
    },
    isLoadingButton() {
      return this.getState.loadingButton;
    },
    isFetchingAgent() {
      return this.$store.getters['agent/getIsFetching'];
    },
    columnsAgent() {
      const baseColumns = [
        {
          key: 'id',
          label: '#',
        },
        {
          key: 'name',
          label: this.$t('views.agents.table.columns.name'),
          render: (text, record) => {
            return `<span>${
              this.$globals.capitalizeFirstLetter(record.firstName) +
              ' ' +
              this.$globals.capitalizeFirstLetter(record.lastName)
            }</span>`;
          },
          sort: true,
          sortKey: 'first_name',
        },
        // {
        //   key: 'call_center',
        //   label: this.$globals.capitalizeFirstLetter(
        //     this.$t('common.callCenter'),
        //   ),
        //   render: (text, record) => {
        //     const teamName =
        //       record.callCenterId !== null &&
        //       record.callCenterId.name !== null &&
        //       record.callCenterId.name !== undefined
        //         ? record.callCenterId.name
        //         : '-';
        //     return `<div>${this.$globals.capitalizeFirstLetter(
        //       teamName,
        //     )}</div>`;
        //   },
        //   sort: true,
        //   sortKey: 'call_center_id',
        // },
        {
          key: 'role',
          label: this.$globals.capitalizeFirstLetter(this.$t('common.profile')),
          render: (text, record) => {
            return `${
              record.roleId == 1
                ? '<span class="agent">Agent</span>'
                : '<span class="sup">Superviseur</span>' /* ici test agent */
            } `;
          },
          sort: true,
          sortKey: 'role_id',
        },
        {
          key: 'equipe',
          label: this.$globals.capitalizeFirstLetter(this.$t('common.team')),
          render: (text, record) => {
            return `<div>${
              record.teamId
                ? record.teamId.name
                : `<span class="empty-teams"> <i class="icon-alerte pr-1"></i> Sans quipe</span>`
            }</div>`;
          },
          sort: true,
          sortKey: 'team_id',
        },
        {
          key: 'created_at',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('common.createdAt'),
          ),
          render: (text, record) => {
            return `<span>${this.$globals.formatDateWithoutTime(
              record.created_at,
            )}</span>`;
          },
          sort: true,
          sortKey: 'created_at',
        },
      ];

      if (
        !this.$globals.isRoleAllowed(
          'roleAllowed',
          localStorage.getItem('user')
            ? JSON.parse(localStorage.getItem('user')).role
            : 'visitor',
        )
      ) {
        baseColumns.splice(2, 0, {
          key: 'call_center',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('common.callCenter'),
          ),
          render: (text, record) => {
            const teamName =
              record.callCenterId !== null &&
              record.callCenterId.name !== null &&
              record.callCenterId.name !== undefined
                ? record.callCenterId.name
                : '-';
            return `<div>${this.$globals.capitalizeFirstLetter(teamName)}</div>`;
          },
          sort: true,
          sortKey: 'call_center_id',
        });
      }

      return baseColumns;
    },

    columns() {
      return [
        {
          key: 'id',
          label: '#',
        },
        {
          key: 'name',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.team.columns.completename'),
          ),
          render: (text, record) => {
            return `<div>  ${this.$globals.capitalizeFirstLetter(
              record.firstName,
            )} ${this.$globals.capitalizeFirstLetter(record.lastName)} </div> `;
          },
        },
        {
          key: 'Identifiant',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.team.columns.identifier'),
          ),
          render: (text, record) => {
            return `<div> </i> ${this.$globals.abbreviateName(record.hermesId)}</div> `;
          },
        },
        {
          key: 'tat',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.team.columns.state'),
          ),
          render: (text, record) => {
            if (record.deleted_at == null) {
              return `<div>   <span class="actif mr-1"></span> ${this.$globals.capitalizeFirstLetter(
                this.$t('common.active'),
              )}</div> `;
            } else {
              return `<div>   <span class="inactif mr-1"></span> ${this.$globals.capitalizeFirstLetter(
                this.$t('common.inactive'),
              )}</div> `;
            }
          },
        },
        {
          key: 'Appels',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.team.columns.calls'),
          ),
          render: (text, record) => {
            return `<div >${record.nb_call}</div>`;
          },
        },
        {
          key: 'Scoring',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('components.team.columns.scoring'),
          ),
          render: (text, record) => {
            if (record.scoring[0]) {
              const avgScore = record.scoring[0].avgScoreNewDate;
              const scoreDifference = parseFloat(
                record.scoring[0].scoreDifference.replace(',', '.'),
              );

              const tauxClass = scoreDifference < 0 ? 'taux red' : 'taux green';
              const signeScore = scoreDifference < 0 ? '-' : '+';

              return `<div class="bold">${avgScore}% <span class="${tauxClass}">${signeScore}${record.scoring[0].scoreDifference}%</span></div>`;
            } else {
              return `<div class="bold">0% <span class="taux green">0,00%</span></div>`;
            }
          },
        },
      ];
    },
    dataAgents() {
      return this.$store.getters['agent/getCollection'];
    },

    data() {
      return this.getCollection;
    },
    collectionTeam() {
      return this.getCollectionTeam;
    },
    modelTeam() {
      return this.getModel;
    },
    dataAgent() {
      return this.getCollection.agents;
    },
    keyword() {
      return this.getFilter.keyword;
    },
    filter() {
      return this.getFilter;
    },
    callCenters() {
      return this.$store.getters['callCenter/getIdWithName'].map((x) => ({
        id: x.id,
        name: x.name,
        text: x.name,
      }));
    },
  },
  name: 'teamsStatistique',
};
</script>
