<template>
  <transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true }" class="show">
      <div class="header_drawer">
        <div class="title">
          <h4 v-if="showBlock1">
            {{
              model.id == null
                ? $t('components.callCenter.add')
                : $t('components.callCenter.edit')
            }}
          </h4>
          <h4 v-if="showBlock2">
            {{
              !editAcountCase
                ? $t('components.callCenter.addUser')
                : $t('components.callCenter.editUser')
            }}
          </h4>
        </div>
        <div class="content-button btn-add">
          <button v-if="showBlock1" class="close mr-5" @click="close()">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <button v-if="showBlock2" class="close mr-5" @click="toggleBlocks()">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <Button
            v-if="showBlock1"
            :loading="FetchingButton"
            @when-cliked="handleOnSubmit()"
            :text="$globals.capitalizeFirstLetter($t('common.save'))"
          />
          <Button
            v-if="showBlock2"
            :loading="inviteLoading"
            @when-cliked="
              editAcountCase ? handleUpdateAcount() : handleInviteUser()
            "
            :text="
              $globals.capitalizeFirstLetter(
                $t(`common.${editAcountCase ? 'update' : 'invite'}`),
              )
            "
          />
        </div>
      </div>
      <div class="body_drawer callcenter" v-if="showBlock1">
        <div class="form-group-flex border-b mb-5">
          <input-field
            :defaultValue="model.name"
            class="mb-7"
            :hasError="errors != null && errors.name != null"
            :errorMessage="
              errors && errors.name != null ? $t('errors.' + errors.name) : ''
            "
            @input="
              (e) => {
                this.setModel({
                  ...this.getModel,
                  ['name']: e.target.value,
                });
              }
            "
            name="name"
            :label="
              $globals.capitalizeFirstLetter(
                $t('components.callCenter.drawer.form.callName'),
              )
            "
            @blur="
              (e) => {
                checkCallCenterName(e);
              }
            "
          />
          <input-field
            :defaultValue="model.idCallCenter"
            class="mb-7 ml-4"
            :hasError="errors != null && errors.idCallCenter != null"
            :errorMessage="
              errors && errors.idCallCenter != null ? $t('errors.' + errors.idCallCenter) : ''
            "
            @input="
              (e) => {
                this.setModel({
                  ...this.getModel,
                  ['idCallCenter']: e.target.value,
                });
              }
            "
            name="id_call_center"
            :label="
              $globals.capitalizeFirstLetter(
                $t('components.callCenter.drawer.form.callIdentifiant'),
              )
            "
            @blur="
              (e) => {
                checkCallCenterIdentifiant(e);
              }
            "
          />
        </div>
        <!-- <div v-if="errors != null && errors.status != null">
          {{ errors.status }}
        </div> -->

        <!-- <SwitchButton
          :defaultValue="model.status"
          @value-changed="handleStatusChange"
        /> -->

        <h3 class="mt-4 mb-7">
          <strong>
            {{ $globals.capitalizeFirstLetter($t('common.accounts')) }}
          </strong>
        </h3>
        <div class="add-new-comptes">
          <div class="add-comptes mt-4 border-b pb-5">
            <button @click="addAcount">
              <span class="mr-2"><i class="icon-plus"></i></span>
              {{
                $globals.capitalizeFirstLetter(
                  $t('components.callCenter.addaccount'),
                )
              }}
            </button>
          </div>
          <div v-if="model.acounts.length == 0" class="empty-data border-b">
            <p>Aucun compte nest rattach  ce call-center</p>
          </div>
          <div class="empty-data">
            <div v-for="acount of model.acounts" :key="acount.id">
              <AcountItem
                @onStatusChange="
                  (data) => {
                    changeStatus(data);
                  }
                "
                @onEditAcount="
                  (item) => {
                    handleEditAcount(item);
                  }
                "
                @onDeleteAcount="
                  (item) => {
                    handleDeleteAcount(item);
                  }
                "
                @onInviteUser="
                  (acc) => {
                    reInviteUser(acc);
                  }
                "
                @onBlock="
                  (acc) => {
                    handleBlockAccount(acc);
                  }
                "
                :acount="acount"
              />
            </div>
          </div>
        </div>
      </div>
      <div class="body_drawer callcenter" v-if="showBlock2">
        <div class="form-group-flex">
          <input-field
            class="mb-7 mr-4"
            :label="$globals.capitalizeFirstLetter($t('common.firstName'))"
            :hasError="errors != null && errors.firstName != null"
            :errorMessage="
              errors && errors.firstName != null
                ? $t('errors.' + errors?.firstName[0])
                : ''
            "
            :defaultValue="user?.firstName"
            @input="
              (e) => {
                user.firstName = e.target.value;
              }
            "
            :name="`firstName`"
            :id="`firsName`"
            @blur="
              (e) => {
                validateFirstName(e);
              }
            "
          />
          <input-field
            class="mb-7 ml-4"
            :label="$globals.capitalizeFirstLetter($t('common.lastName'))"
            :hasError="errors != null && errors.lastName != null"
            :errorMessage="
              errors && errors.lastName != null
                ? $t('errors.' + errors?.lastName[0])
                : ''
            "
            :defaultValue="user?.lastName"
            @input="
              (e) => {
                user.lastName = e.target.value;
              }
            "
            :name="`lastName`"
            :id="`lastName`"
            @blur="
              (e) => {
                validateLastName(e);
              }
            "
          />
        </div>
        <input-field
          class="mb-7"
          :label="$globals.capitalizeFirstLetter($t('common.email_adress'))"
          :hasError="errors != null && errors?.email != null"
          :errorMessage="
            errors && errors.email != null
              ? $t('errors.' + errors.email[0])
              : ''
          "
          :defaultValue="user?.email"
          @input="
            (e) => {
              user.email = e.target.value;
            }
          "
          :name="`lastName`"
          :id="`lastName`"
          @blur="
            (e) => {
              validateEmail(e);
            }
          "
        />

        <hr />
        <div class="roles mt-7">
          <h3>Rle</h3>
          <div class="roles-container mt-3">
            <div
              class="role-item"
              :class="[
                errors != null && errors.role && errors?.role != null
                  ? 'error-field'
                  : '',
              ]"
            >
              <div class="w-100" @click="handleRoleChange('responsable-activite')">
                <label>Responsable activit</label>
                <p>
                  Gestion des quipes, Statistiques, valuation (Consultation)
                </p>
              </div>
              <div class="form-check">
                <input
                  :checked="user.role == 'responsable-activite' ? true : false"
                  type="checkbox"
                  id="responsable"
                  @change="handleRoleChange('responsable-activite')"
                />
                <label for="responsable"></label>
              </div>
            </div>
            <div
              class="role-item mt-3"
              :class="[
                errors != null && errors.role && errors?.role != null
                  ? 'error-field'
                  : '',
              ]"
            >
              <div class="w-100" @click="handleRoleChange('service-qualite')">
                <label>Service qualit</label>
                <p>Gestion des quipes, Statistiques, valuation (criture)</p>
              </div>
              <div class="form-check">
                <input
                  :checked="user.role == 'service-qualite' ? true : false"
                  type="checkbox"
                  id="Service"
                  @change="handleRoleChange('service-qualite')"
                />
                <label for="Service"></label>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Modal
        :show-modal="dispalyDeleteModal"
        :cancelText="$t('views.agents.modalBtnleft')"
        :okText="$t('common.delete')"
        @onConfirm="confirmDeleteAcount"
        @close="dispalyDeleteModal = false"
      >
        <template v-slot:title>
          <span style="color: red">
            {{ $t('components.callCenter.drawer.modalDelete.title') }}
          </span>
        </template>
        <template v-slot:content>
          <div class="pb-5 text-center">
          
              {{ $t('components.callCenter.drawer.modalDelete.content') }}
           
            <br />
            Cette action sera dfinitive et irrversible.
          </div>
        </template>
      </Modal>
      <div id="overlay-" @click="close()"></div>
    </div>
  </transition>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import { mapActions, mapGetters } from 'vuex';
import AcountItem from './AcountItem.vue';
import Modal from '@/components/Modal/Modal.vue';
// import SwitchButton from '@/components/FormWidgets/SwitchButton.vue';
export default {
  components: {
    InputField,
    Button,
    AcountItem,
    Modal,
    // SwitchButton,
  },
  data() {
    return {
      showBlock1: true,
      showBlock2: false,
      showDrawer: false,
      loading: false,
      user: {
        firstName: '',
        lastName: '',
        email: '',
        role: null,
      },
      acountErrors: null,
      inviteLoading: false,
      editAcountCase: false,
      dispalyDeleteModal: false,
    };
  },

  props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
  },

  methods: {
    ...mapActions('callCenter', [
      'setModel',
      'createCallCenter',
      'resetModel',
      'resetErrors',
      'setErrors',
      'setErrorsByName',
      'setToUpdate',
      'setToDelete',
      'pushAcountsToInsert',
      'updateCallCenter',
      'modifyAcount',
      'updateAcountStatus',
      'updateAcount',
      'deleteAcount',
    ]),
    ...mapActions('user', [
      'register',
      'checkEmail',
      'updateUser',
      'deleteUser',
      'inviteUserTojoinScriib',
    ]),
    async close() {
      this.setModel({
        ...this.getModel,
        ['name']: '',
        ['idCallCenter']: '',
      });
      this.resetModel();
      this.resetErrors();
      this.$emit('close');
      this.resetUserForm();
      this.showBlock2 = false;
      this.showBlock1 = true;
    },

    toggleBlocks() {
      this.setErrorsByName({ firstName: null });
      this.setErrorsByName({ lastName: null });
      this.setErrorsByName({ email: null });
      this.showBlock2 = !this.showBlock2;
      this.showBlock1 = !this.showBlock1;
    },
    checkCallCenterName(data) {
      if (data == '') {
        this.setErrors({ name: ['CALL_NAME_REQUIRED'] });
      } else {
        this.setErrors({ name: null });
      }
    },
    checkCallCenterIdentifiant(data) {
      if (data == '') {
        this.setErrors({ idCallCenter: ['IDENTIFIANT_REQUIRED'] });
      } else {
        this.setErrors({ idCallCenter: null });
      }
    },
    validateEmail(email) {
      if (!email) {
        this.setErrorsByName({ email: ['EMAIL_REQUIRED'] });
      } else if (!this.isValidEmail(email)) {
        this.setErrorsByName({ email: ['EMAIL_FORMAT_INVALID'] });
      } else {
        this.setErrorsByName({ email: null });
      }
    },
    validateLastName(value) {
      const validChars = /^[A-Za-z--- -]*$/.test(value);

      if (value == '') {
        this.setErrorsByName({ lastName: ['LAST_NAME_REQUIRED'] });
      } else if (!validChars) {
        this.setErrorsByName({ lastName: ['LAST_NAME_ALPHABETICAL_ERROR'] });
      } else this.setErrorsByName({ lastName: null });
    },
    validateFirstName(value) {
      const validChars = /^[A-Za-z--- -]*$/.test(value);
      if (value == '') {
        this.setErrorsByName({ firstName: ['FIRST_NAME_REQUIRED'] });
      } else if (!validChars) {
        this.setErrorsByName({ firstName: ['FIRST_NAME_ALPHABETICAL_ERROR'] });
      } else this.setErrorsByName({ firstName: null });
    },
    isValidEmail(email) {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(email);
    },
    verifyName() {
      if (this.getModel.name == '') {
        this.setErrors({ name: ['CALL_NAME_REQUIRED'] });
      }
    },
    validateRole(item) {
      if (item === null) {
        this.setErrorsByName({ role: ['ROLE_REQUIRED'] });
      } else {
        this.setErrorsByName({ role: null });
      }
    },
    async handleOnSubmit() {
      this.loading = true;
      if (this.model.id == null) {
        this.createCallCenter()
          .then(() => {
            this.loading = false;
            this.close();
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        this.updateCallCenter()
          .then(() => {
            this.loading = false;
            this.close();
          })
          .catch((res) => {
            this.loading = false;
            const errors = { ...this.getErrors };

            if (res.message.errors) {
                if (res.message.errors.name) {
                    errors.name = [res.message.errors.name.message];
                } else {
                    delete errors.name;
                }

                if (res.message.errors.idCallCenter) {
                    errors.idCallCenter = [res.message.errors.idCallCenter.message];
                } else {
                    delete errors.idCallCenter;
                }
            } else {
                if (res.errors.name) {
                    errors.name = [res.errors.name];
                } else {
                    delete errors.name;
                }

                if (res.errors.idCallCenter) {
                    errors.idCallCenter = [res.errors.idCallCenter];
                } else {
                    delete errors.idCallCenter;
                }
            }

            this.setErrors(errors);
          });
      }
      // this.$store.commit('callCenter/SET_BUTTON_LOADING', true);

      // if (this.getModel.name == '') {
      //   this.setErrors({ name: ['CALL_NAME_REQUIRED'] });
      //   this.$store.commit('callCenter/SET_BUTTON_LOADING', false);
      // } else {
      //   if (this.getModel.id) {
      //     await this.updateCallCenter()
      //       .then(() => {
      //         this.setModel({
      //           ...this.getModel,
      //           ['acounts']: [],
      //         });
      //         this.close();
      //       })
      //       .catch((error) => {});
      //   } else {
      //     await this.createCallCenter()
      //       .then(() => {
      //         this.setModel({
      //           ...this.getModel,
      //           ['acounts']: [],
      //         });
      //         this.close();
      //       })
      //       .catch(() => {});
      //   }
      // }
    },

    deleteForm(index) {
      this.items.splice(index, 1);
    },

    /* start code */

    addAcount() {
      this.editAcountCase = false;
      this.resetUserForm();
      this.toggleBlocks();
    },
    handleInviteUser() {
      this.validateFirstName(this.user.firstName);
      this.validateLastName(this.user.lastName);
      this.validateEmail(this.user.email);
      this.validateRole(this.user.role);
      if (
        this.errors.firstName === null &&
        this.errors.lastName === null &&
        this.errors.email === null &&
        this.errors.role === null
      ) {
        this.inviteLoading = true;
        const acount = {
          firstName: this.user.firstName,
          lastName: this.user.lastName,
          email: this.user.email,
          status: 'created',
          role: this.user.role,
          new: true,
        };
        this.checkEmail({ email: this.user.email })
          .then((res) => {
            this.inviteLoading = false;
            this.setModel({
              ...this.model,
              ['acounts']: [{ ...acount, new: true }, ...this.model.acounts],
            });
            this.resetUserForm();
            this.toggleBlocks();
          })
          .catch((err) => {
            this.setErrorsByName(err.response.data);
            this.inviteLoading = false;
          });
        // this.register(acount)
        //   .then((res) => {
        //     this.inviteLoading = false;

        //     this.setModel({
        //       ...this.model,
        //       ['acounts']: [
        //         { ...res.data.data.data.user, new: true },
        //         ...this.model.acounts,
        //       ],
        //     });

        //     this.resetUserForm();
        //     this.toggleBlocks();
        //   })
        //   .catch((err) => {
        //     this.inviteLoading = false;
        //     this.setErrors(err.response.data.errors);
        //   });
      }
    },

    resetUserForm() {
      this.acountErrors = null;
      this.user.firstName = '';
      this.user.lastName = '';
      this.user.email = '';
      this.user.role = null;
    },

    changeStatus(data) {
      this.updateUser(data).then((res) => {
        this.updateAcountStatus({ id: data.id, status: data.data.status });
      });
    },

    handleEditAcount(acount) {
      this.editAcountCase = true;
      this.user.id = acount.id;
      this.user.firstName = acount.firstName;
      this.user.lastName = acount.lastName;
      this.user.email = acount.email;
      this.user.role = acount.role;
      this.toggleBlocks();
    },

    handleUpdateAcount() {
      this.inviteLoading = true;
      this.updateUser({ id: this.user.id, data: this.user })
        .then((res) => {
          this.inviteLoading = false;
          this.updateAcount(res.data.data.data);
          this.resetUserForm();
          this.toggleBlocks();
        })
        .catch((err) => {
          this.inviteLoading = false;
          this.acountErrors = err.response.data.errors;
        });
    },

    handleDeleteAcount(acount) {
      this.user.id = acount.id;
      this.dispalyDeleteModal = true;
    },

    handleRoleChange(role) {
      if (this.user.role == role) {
        this.user.role = null;
        this.setErrorsByName({ role: ['ROLE_REQUIRED'] });
      } else {
        this.user.role = role;
        this.setErrorsByName({ role: null });
      }
    },
    handleStatusChange(newValue) {
      this.setModel({
        ...this.getModel,
        ['status']: newValue,
      });
    },
    confirmDeleteAcount() {
      this.deleteUser({ id: this.user.id, forceDelete: true }).then((res) => {
        this.deleteAcount(this.user.id);
        this.dispalyDeleteModal = false;
      });
    },
    reInviteUser(acc) {
      this.inviteUserTojoinScriib(acc.id).then((res) => {
        this.updateAcount({ ...acc, status: 'send-an-invitation' });
      });
    },
    handleBlockAccount(acc) {
      this.updateUser({
        id: acc.id,
        data: { status: 'invitation-canceled', tokenInvitation: null },
      }).then((res) => {
        this.updateAcount(res.data.data.data);
      });
    },
  },
  computed: {
    ...mapGetters('callCenter', ['getModel', 'getErrors', 'getState']),
    ...mapGetters('user', ['getUser']),
    model() {
      return this.getModel;
    },
    FetchingButton() {
      return this.getState.LoadingButton;
    },

    errors() {
      return this.getErrors;
    },
  },
  mounted() {
    if (this.model.id != null) {
      this.editAcountCase = true;
    } else {
      this.editAcountCase = false;
    }

    window.addEventListener('beforeunload', this.handlePageReload);
  },
};
</script>
