<template>
  <div>
    <div
      @click="
        () => {
          column.sort ? handleSort() : () => {};
        }
      "
      :style="{ cursor: column.sort ? 'pointer' : '' }"
      class="table-column-container"
    >
      {{ column.label }}
      <div v-if="column.sort" class="arrows pl-10">
        <i
          :class="{
            'arrow-unactive':
              sortKey != column.sortKey
                ? true
                : sortKey == column.sortKey && sortOrder == 'asc'
                  ? false
                  : true,
          }"
          class="icon-table-arrow-up"
        ></i>
        <i
          :class="{
            'arrow-unactive':
              sortKey != column.sortKey
                ? true
                : sortKey == column.sortKey && sortOrder == 'desc'
                  ? false
                  : true,
          }"
          class="icon-table-arrow-down"
        ></i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['column', 'sortKey', 'sortOrder'],
  methods: {
    handleSort() {
      this.$emit('onSortTriggered', this.column.sortKey);
    },
  },
};
</script>
