<template>
  <transition name="slide">
  <div  v-if="isOpened" :class="{ drawer: true }" class="show">
    <div class="header_drawer">
      <div class="title">
        <h4>
          {{
            model.id == null
              ? $t('components.qualification.add')
              : $t('components.qualification.edit')
          }}
        </h4>
      </div>
      <div class="content-button btn-add">
        <button class="close mr-5" @click="close">
          {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
          <i class="icon-exit"></i>
        </button>
        <Button
          :loading="loading"
          @when-cliked="handleOnSubmit()"
          :text="$globals.capitalizeFirstLetter($t('common.save'))"
        />
      </div>
    </div>

    <div class="body_drawer">
      <div class="form-group-flex">
        <input-field
          :defaultValue="model.name"
          class="mb-7"
          :hasError="errors != null && errors.name != null"
          :errorMessage="
            errors && errors.name != null ? $t('errors.' + errors.name) : ''
          "
          @input="
            (e) => {
              this.setModel({
                ...this.getModel,
                ['name']: e.target.value,
              });
            }
          "
          @blur="verifyName()"
          name="name"
          :label="$globals.capitalizeFirstLetter($t('common.namequalification'))"
        />
      </div>
    </div>

    <div id="overlay-" @click="close"></div>
  </div>
  </transition>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {
    InputField,
    Button,
  },
  data() {
    return {
      showDrawer: false,
      loading: false,
    };
  },
  props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
  },

  methods: {
    ...mapActions('qualification', [
      'setModel',
      'createQualification',
      'resetModel',
      'resetErrors',
      'setErrors',
      'setToUpdate',
      'setToDelete',
      'updateQualification',
    ]),
    close() {
      this.setModel({
        ...this.getModel,
        ['name']: '',
      });
      this.items = [];
      this.errorsAcount = [];
      this.toModify = [];
      this.isOpenCriteres = {};
      this.resetModel();
      this.resetErrors();
      this.$emit('close');
      this.showDrawer = false;
    },
    verifyName() {
      if (this.getModel.name == '') {
        this.setErrors({ name: ['TITLE_REQUIRED'] });
      }
    },

    async handleOnSubmit() {
      this.loading = true;
      if (this.getModel.name == '') {
        this.setErrors({ name: ['TITLE_REQUIRED'] });
        this.loading = false;
      } else if (this.getModel.id) {
        await this.updateQualification()
          .then(() => {
            this.loading = false;
            this.close();
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        await this.createQualification()
          .then(() => {
            this.loading = false;
            this.close();
          })
          .catch(() => {
            this.loading = false;
          });
      }
    },
  },
  computed: {
    ...mapGetters('qualification', ['getModel', 'getErrors']),
    model() {
      return this.getModel;
    },
    errors() {
      return this.getErrors;
    },
  },
};
</script>
