<template>
  <div class="date-range-picker">
    <date-range-picker
      v-model="dateRange"
      @start-selection="sDate"
      @finish-selection="eDate"
      :locale-data="getLocaleData()"
      :timePicker="withTimePicker"
    >
      <!--    header slot-->
      <div slot="header" slot-scope="header" class="slot">
        <span v-if="header.in_selection">
          <!-- {{
            $globals.capitalizeFirstLetter(
              $t('components.dateRange.selectDateEnd'),
            )
          }} -->
        </span>
      </div>
      <!--    date slot-->
      <template #date="data">
        <span class="small">{{ data.date | dateCell }}</span>
      </template>

      <!--    ranges (new slot syntax) -->
      <template #ranges="ranges">
        <div class="ranges">
          <ul class="list--one">
            <li
              v-for="(range, name) in todayGroup"
              :key="name"
              @click="handleRangeClick(range, ranges)"
              :class="{ active: RangeIsSelected(range) }"
            >
              {{ name }}
            </li>
          </ul>
          <ul class="list--two">
            <li
              v-for="(range, name) in yesterdayGroup"
              :key="name"
              @click="handleRangeClick(range, ranges)"
              :class="{ active: RangeIsSelected(range) }"
            >
              {{ name }}
            </li>
          </ul>

          <ul class="list--three">
            <li
              v-for="(range, name) in customRanges"
              :key="name"
              @click="handleRangeClick(range, ranges)"
              :class="{ active: RangeIsSelected(range) }"
            >
              {{ name }}
            </li>
          </ul>
        </div>
      </template>
      <!--    footer slot-->
      <div slot="footer" slot-scope="data" class="slot drp-buttons">
        <div class="align__">
          <input v-model="dateRange.startDate" />
          -
          <input v-model="dateRange.endDate" />
        </div>
        <div class="cancelBtn">
          <button @click="cancelSelection(data)">
            {{
              $globals.capitalizeFirstLetter($t('components.dateRange.cancel'))
            }}
          </button>
        </div>
        <div
          :class="{
            'applyBtn btn btn-sm': true,
            'btn-fieldError':
              dateRange.startDate === null || dateRange.endDate === null
                ? 'btn-success'
                : dateRange.startDate !== null && dateRange.endDate !== null,
          }"
        >
          <button @click="applySelection(data)">
            {{
              $globals.capitalizeFirstLetter($t('components.dateRange.apply'))
            }}
          </button>
        </div>
      </div>
    </date-range-picker>
  </div>
</template>

<script>
import DateRangePicker from 'vue2-daterange-picker';
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css';

export default {
  name: 'SlotsDemo',
  components: { DateRangePicker },
  props: {
    withTimePicker: Boolean,
  },
  data() {
    const allRanges = this.getTranslatedRanges();
    const todayGroupKeys = [
      this.$t('components.dateRange.ranges.Today'),
      this.$t('components.dateRange.ranges.This week'),
      this.$t('components.dateRange.ranges.This month'),
      this.$t('components.dateRange.ranges.This year'),
    ];
    const yesterdayGroupKeys = [
      this.$t('components.dateRange.ranges.Yesterday'),
      this.$t('components.dateRange.ranges.Last week'),
      this.$t('components.dateRange.ranges.Last month'),
      this.$t('components.dateRange.ranges.Previous year'),
    ];
    const customRangesKeys = [
      this.$t('components.dateRange.ranges.Last 7 days'),
      this.$t('components.dateRange.ranges.Last 30 days'),
    ];

    // Create groups using the pickRanges method
    const todayGroup = this.pickRanges(allRanges, todayGroupKeys);
    const yesterdayGroup = this.pickRanges(allRanges, yesterdayGroupKeys);
    const customRanges = this.pickRanges(allRanges, customRangesKeys);
    return {
      todayGroup,
      yesterdayGroup,
      customRanges,
      dateRange: {
        startDate: null,
        endDate: null,
      },
    };
  },
  filters: {
    dateCell(value) {
      return new Date(value).getDate();
    },
    date(val) {
      return val ? val.toLocaleString() : '';
    },
  },
  methods: {
    pickRanges(allRanges, keys) {
      return Object.fromEntries(keys.map((key) => [key, allRanges[key]]));
    },
    generateTranslatedRanges() {
      const today = new Date();
      const getStartOfWeek = (date) => {
        const startOfWeek = new Date(date);
        startOfWeek.setDate(date.getDate() - date.getDay());
        return startOfWeek;
      };

      const getEndOfWeek = (date) => {
        const endOfWeek = new Date(date);
        endOfWeek.setDate(date.getDate() + (6 - date.getDay()));
        return endOfWeek;
      };

      const startOfWeek = getStartOfWeek(today);
      const endOfWeek = getEndOfWeek(today);

      const startOfMonth = new Date(today.getFullYear(), today.getMonth(), 1);
      const endOfMonth = new Date(today.getFullYear(), today.getMonth() + 1, 0);

      return {
        Today: [today, today],
        'This week': [startOfWeek, endOfWeek],
        'This month': [startOfMonth, endOfMonth],
        'This year': [new Date(today.getFullYear(), 0, 1), today],
        Yesterday: [
          this.getRelativeDate(today, -1),
          this.getRelativeDate(today, -1),
        ],
        'Last week': [
          this.getRelativeDate(startOfWeek, -7),
          this.getRelativeDate(startOfWeek, -1),
        ],
        'Last month': [
          this.getRelativeDate(today, -30),
          this.getRelativeDate(today, -1),
        ],
        'Previous year': [
          this.getRelativeDate(today, -365),
          this.getRelativeDate(today, -1),
        ],
        'Last 7 days': [this.getRelativeDate(today, -7), today],
        'Last 30 days': [this.getRelativeDate(today, -30), today],
      };
    },
    getTranslatedRanges() {
      const ranges = this.generateTranslatedRanges();
      const translatedRanges = {};

      for (const key in ranges) {
        translatedRanges[this.$t(`components.dateRange.ranges.${key}`)] =
          ranges[key];
      }

      return translatedRanges;
    },
    getRelativeDate(date, days) {
      const result = new Date(date);
      result.setDate(result.getDate() + days);
      return result;
    },
    getLocaleData() {
      return {
        direction: 'ltr',
        format: 'yyyy/mm/dd',
        separator: ' - ',
        weekLabel: this.$t('components.dateRange.weekLabel'),
        customRangeLabel: this.$t('components.dateRange.customRangeLabel'),
        daysOfWeek: this.$t('components.dateRange.daysOfWeek'),
        monthNames: this.$t('components.dateRange.monthNames'),
        firstDay: 0,
      };
    },
    applySelection(data) {
      data.clickApply();
      if (this.dateRange.startDate != null && this.dateRange.endDate != null) {
        const dateRangeString = data.rangeText;
        const dateParts = dateRangeString.split(' - ');
        const startDateString = dateParts[0];
        const endDateString = dateParts[1];
        const formatDate = (dateString) => {
          const parts = dateString.split('/');
          const formattedDate = `${parts[0]}-${parts[1]}-${parts[2]}`;
          return formattedDate;
        };

        if (this.dateRange.startDate != null)
          this.dateRange.startDate = formatDate(startDateString);
        if (this.dateRange.endDate != null) {
          this.dateRange.endDate = formatDate(endDateString);
        }

        this.$emit('applyed', this.dateRange);
      }
    },
    cancelSelection(data) {
      data.clickCancel();

      this.dateRange.startDate = null;
      this.dateRange.endDate = null;
      this.$emit('cancel');
    },

    formatDateInput(value) {
      const formatOptions = {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
      };
      let dateS = value.toLocaleDateString(formatOptions);
      const parts = dateS.split('/');
      // Rearrange the parts in the desired order (YYYY-MM-dd)
      return `${parts[2]}/${parts[1]}/${parts[0]}`;
    },
    sDate(value) {
      this.selectedName = '';
      this.dateRange.endDate = null;

      this.dateRange.startDate = this.formatDateInput(value);
    },
    eDate(value) {
      let dateSelected = this.formatDateInput(value);
      this.dateRange.endDate = dateSelected;
      if (this.dateRange.endDate < this.dateRange.startDate) {
        this.dateRange.endDate = this.dateRange.startDate;
        this.dateRange.startDate = dateSelected;
      }
    },
    SetRange(range) {
      this.dateRange.startDate = this.formatDateInput(range[0]);
      this.dateRange.endDate = this.formatDateInput(range[1]);
    },
    RangeIsSelected(range) {
      if (
        this.dateRange.startDate == this.formatDateInput(range[0]) &&
        this.dateRange.endDate == this.formatDateInput(range[1])
      ) {
        return true;
      }
      return false;
    },
    handleRangeClick(range, ranges) {
      this.SetRange(range);
      ranges.clickRange(range);
    },
  },
};
</script>
