<template>
  <div class="content_page training">
    <div class="header_page">
      <div class="content-filter">
        <InputSearch
          class="search-field"
          placeholder="Recherche"
          @input-value="handleSearch"
        />
        <Filtres
          :items="[
            {
              label: 'Status',
              key: 'statusFilter',
              filters: statusFilter,
            },
          ]"
          @onFilterChange="handleFilterChanged"
        />
      </div>
    </div>
    <div class="card-table mb-5">
    
      <TableComponent
        :isLoading="isFetchingData"
        :data="data"
        :columns="columns"
        @onSort="handleSort"
        :keySelection="'id'"
        :redirection="true"
        @row-clicked="
          (item) => {
            toggleDrawer(item);
          }
        "
      />
    </div>

    <Pagination storeModuleName="training" />
    <DrawerTraining :isOpened="showDrawer" @close="showDrawer = false" />
  </div>
</template>
<script>
import InputSearch from '@/components/FormWidgets/InputSearch.vue';
import Filtres from '@/components/FormWidgets/Filter/Filter.vue';
import TableComponent from '@/components/Table/Table.vue';
import Pagination from '@/components/Table/Pagination.vue';
import DrawerTraining from '@/components/DrawerForm/Training/DrawerTraining';
import { mapGetters, mapActions } from 'vuex';

export default {
  components: {
    InputSearch,
    Filtres,
    DrawerTraining,
    TableComponent,
    Pagination,
  },
  name: 'training',
  data() {
    return {
      statusCollection: [
        { id: 'to_be_treated', name: 'to_be_treated' },
        { id: 'in_progress', name: 'in_progress' },
        { id: 'treated', name: 'treated' },
        { id: 'reported', name: 'reported' },
      ],
      showDrawer: false,
      keyDrawerTraining: 1,
    };
  },
  watch: {
    keyword() {
      this.getData();
    },
    'filter.keyword': function () {
      this.setFilter({
        ...this.getFilter,
        ['current_page']: 1,
      });
      this.getData();
    },
  },
  methods: {
    ...mapActions('training', ['getData', 'setModel', 'setFilter']),
    toggleDrawer(record) {
      let activeRecord = {
        id: record.id,
        account: record.account,
        creationDate: record.creationDate,
        url: record.url,
        recordName: record.recordName,
        status: record?.status,
        segments: record.segments,
      };
      this.setModel(activeRecord);
      this.showDrawer = !this.showDrawer;
      this.keyDrawerTraining++;
    },
    close() {
      this.showDrawer = false;
    },

    handleFilterChanged(options) {
      this.setFilter({
        ...this.getFilter,
        ['status']: options.statusFilter,
      });
      this.getData();
    },

    handleSearch(value) {
      this.setFilter({
        ...this.getFilter,
        ['keyword']: value,
      });
    },

    handleSort(options) {
      this.setFilter({
        ...this.getFilter,
        ['sortKey']: options.sortKey,
        ['sortOrder']: options.sortOrder,
      });
      this.getData();
    },
  },
  computed: {
    ...mapGetters('training', ['getCollection', 'getFilter', 'getIsFetching']),
    isFetchingData() {
      return this.getIsFetching;
    },

    columns() {
      const handleClick = () => {
        // this.toggleDrawer(i)
      };
      return [
        {
          key: 'recordName',
          label: 'Fichier',
          sort: false,
          sortKey: 'record_name',
          render: (text, record) => {
            return `<span class="name-file"> <i class="icon-play-audio mr-2"></i>${this.$globals.capitalizeFirstLetter(
              record.recordName,
            )}</span>`;
          },
        },

        {
          key: 'status',
          label: 'tat',
          render: (text, record) => {
            if (record.status == 'to_be_treated') {
              return `<div><span class="etat_record" style=" background-color:#F4F6F5;">{{ this.$globals.capitalizeFirstLetter($t('views.training.toBeTreated')) }}</span></div>`;
            } else if (record.status == 'in_progress') {
              return `<div><span class="etat_record" style="color: #F09000; background-color: #FEF9E2; text-align: center;">{{this.$globals.capitalizeFirstLetter (this.$t('views.training.inProgress')) }}</span></div>`;
            } else if (record.status == 'treated') {
              return `<div><span class="etat_record" style=" color:#00CB06; background-color:#E7FAE7; text-align: center;">{{this.$globals.capitalizeFirstLetter (this.$t('views.training.treated')) }}</span></div>`;
            } else if (record.status == 'reported') {
              return `<div style="color: #D80125"><i class="icon-alerte pr-1"></i><span>{{this.$globals.capitalizeFirstLetter (this.$t('views.training.reported')) }}</span></div>`;
            }
          },
          sort: true,
          sortKey: 'status',
        },

        {
          key: 'account',
          label: 'Compte',
          sort: true,
          sortKey: 'account',
        },
        {
          key: 'creationDate',
          label: 'Date',
          render: (text, record) => {
            return `<div>${this.$globals.formatDate(record.created_at)}</div>`; // Custom rendering
          },
          sort: true,
          sortKey: 'created_at',
        },
      ];
    },
    data() {
      return this.getCollection;
    },
    filter() {
      return this.getFilter;
    },
    keyword() {
      return this.getFilter.keyword;
    },
    statusFilter() {
      const statusChecked = this.statusCollection.map((el) => ({
        name: el.name,
        id: el.id,
      }));
      return statusChecked;
    },
  },

  mounted() {
    this.setFilter({
      ...this.getFilter,
      ['withPagination']: 1,
    });
    this.getData().then(() => {});
  },
};
</script>
