<template>
  <div class="content_page record" id="content_page">
    <div class="header_page flex-column">
      <div class="search_audio">
        <div class="content-filter">
          <InputSearch
            @input-value="handleSearch"
            class="search-field"
            :inputId="'searchAgents'"
            :placeholder="$globals.capitalizeFirstLetter($t('common.search'))"
          />
          <DateRangePicker
            :withTimePicker="false"
            @cancel="cancelDatePicker"
            @applyed="applyedDatePicker"
          />
          <Filtres
            :items="filterItems"
            @onFilterChange="handleFilterChanged"
            @selected-names-changed="handleSelectedNamesChanged"
            :updatedFilters="this.updatedFilters"
          />
        </div>
        <div class="btn-add flex">
          <div class="mr-3">
            <DropDownMenu
            :items="[
                {
                  label: 'Exporter valuation',
                  onClick: exportAssessment,
                },
                {
                  label: $t('views.calibrages.planifierCalibrage'),
                  onClick: scheduleCalibration,
                },
              ]"
              icon="icon-down"
              :text="'Actions groupes'"
              :btnId="'btn_dropdown_2'"
              :dropdownId="'dropdownMenu_2'"
              :btnclass="'btn-third'"
              :disabled="getSelectedRecords.length > 0 ? false : true"
            />
          </div>
          <div
            v-if="
              $globals.hasAuthorities('records', 'create', connectedUser?.role)
            "
            class="btn-add_"
            @click="toggleDrawer"
          >
            <ButtonPrimary
              icon="icon-add"
              :text="$t('views.records.importRecord')"
            />
          </div>
        </div>
      </div>

      <div v-if="selectedOptions.length > 0" class="tags-">
        <div
          v-for="(selectedOption, index) in selectedOptions"
          :key="index"
          class="tag"
        >
          {{ `${selectedOption.name} (${selectedOption.label})` }}
          <span
            class="remove-filter"
            @click="removeSelectedOption(selectedOption)"
          >
            <i class="icon-exit-"></i>
          </span>
        </div>
      </div>
    </div>
    <div v-if="records.length > 0" class="flexbox__">
      <ListAudio @openToggleDrawer="openToggleDrawer" />

      <div class="content_page_o">
        <div
          v-if="activeRecord.id != null && activeRecord.url != null"
          class="content_page__"
        >
          <HeaderPage
            v-if="activeRecord.id != null && activeRecord.url != null"
            :totalScore="totalScore" :connectedUser="connectedUser"
          />
          <template v-if="activeRecord.id != null && activeRecord.url != null">
          
            <ScribbPlayer
              ref="scribbPalyer"
              :playlist="records"
              :withSpeechTrace="true"
              :withWaveSurfer="true"
              :showPlaylist="true"
              :withAttachments="true"
              @trackChanged="
                (currentRecord, parentRecord) => {
                  setActiveRecord(currentRecord);
                  setParentRecord(parentRecord);
                }
              "
              @playListItemClicked="
                (record) => {
                  setActiveRecord(record);
                }
              "
              @playDurationChanged="
                (duration) => {
                  currentTrackPlayDuration = duration;
                }
              "
            />

            <div class="attachment text-center py-5">
              <button @click="handleAttachRecord">
                <span class="mr-2"><i class="icon-plus"></i></span>
                Attacher un enregistrement
              </button>
            </div>
          </template>

          <div v-if="activeRecord.id != null" class="content_evaluation">
            <div class="tab">
              <button
                @click="changeTab(0)"
                :class="{ active: activeTab === 0 }"
              >
                Retranscription
              </button>

              <button
                @click="changeTab(1)"
                :class="{ active: activeTab === 1 }"
              >
                Analyse
              </button>

              <button
                @click="changeTab(2)"
                :class="{ active: activeTab === 2 }"
              >
                valuation manuelle
              </button>
              <button
                @click="changeTab(3)"
                :class="{ active: activeTab === 3 }"
              >
                Dtails
              </button>
              <button
                @click="changeTab(4)"
                :class="{ active: activeTab === 4 }"
              >
                Compte rendu
              </button>
              <button @click="changeTab(5)" :class="{ active: activeTab === 5 }">
                Tableau comparatif
              </button>
              <div class="slider"></div>
            </div>

            <div class="tabcontent">
              <transition name="fade">
                <div v-show="activeTab === 0" class="content_retranscription">
                  <Retranscription />
                </div>
              </transition>

              <transition name="fade">
                <div v-if="activeTab === 1" class="content_analyse">
                  <Analyse
                    :currentTrackPlayDuration="currentTrackPlayDuration"
                  />
                </div>
              </transition>

              <!-- <transition name="fade">
                <div v-show="activeTab === 1" class="content_scribb">
                  <EvaluationScribb />
                </div>
              </transition> -->
              <transition name="fade">
                <div v-show="activeTab === 2" class="content_manuelle">
                  <EvaluationManuelle :record="activeRecord" />
                </div>
              </transition>
              <transition name="fade">
                <div v-show="activeTab == 3" class="content_details">
                  <Details />
                </div>
              </transition>
              <transition name="fade">
                <div v-show="activeTab === 4" class="content_compte_rendu">
                  <EvaluationFinishedDetails :record="activeRecord"
                  @update-total-score="handleTotalScoreUpdate" />
                </div>
              </transition>
              <transition name="fade">
                <div v-show="activeTab === 5" class="content_scribb">
                  <ComparatifTable :activeRecord="activeRecord" />
                </div>
              </transition>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="text-center" v-if="records.length == 0 && !isfetchingRecords">
      Aucune enregistrement trouv
    </div>
    <div class="text-center" v-if="isfetchingRecords && records.length == 0">
      <Loader />
    </div>
    <!--  <div v-if="records.length == 0 && !loading">
      aucune enregistrement
    </div> -->
    <DrawerImportAudio
      :isAttaching="isAttaching"
      :isOpened="showDrawer"
      @close="
        showDrawer = false;
        isAttaching = false;
      "
    />
    <DrawerCalibrage
      :isOpened="showDrawerCalibrage"
      @close="showDrawerCalibrage = false"
      :getSelectedRecords="getSelectedRecords"
    />
  </div>
</template>

<script>
import DrawerImportAudio from '@/components/DrawerForm/Records/DrawerRecords.vue';
import InputSearch from '@/components/FormWidgets/InputSearch.vue';
import Filtres from '@/components/FormWidgets/Filter/Filter.vue';
import DateRangePicker from '@/components/FormWidgets/DateRange/DateRangePicker.vue';
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css';
import ButtonPrimary from '@/components/FormWidgets/ButtonPrimary.vue';
import ListAudio from '@/components/Records/ListAudio/ListAudio.vue';
import HeaderPage from '@/components/Records/HeaderPage/HeaderPage.vue';
import Retranscription from '@/components/Records/ListTab/Retranscription.vue';
import Analyse from '@/components/Records/ListTab/Analyse.vue';
import EvaluationManuelle from '@/components/Records/ListTab/EvaluationManuelle.vue';
import ScribbPlayer from '@/components/ScribbAudioPlayer/ScribbPlayer.vue';
import DrawerCalibrage from '@/components/DrawerForm/Calibrage/DrawerCalibrage.vue';
import Details from '@/components/Records/ListTab/Details.vue';
import EvaluationFinishedDetails from '@/components/Records/ListTab/EvaluationFinishedDetails.vue';
import { mapActions, mapGetters } from 'vuex';
import Loader from '@/components/AnimationScribb/Loader.vue';
import ComparatifTable from '@/components/Records/ListTab/ComparatifTable.vue';
import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';
import * as XLSX from 'xlsx';


export default {
  components: {
    InputSearch,
    Filtres,
    DateRangePicker,
    ButtonPrimary,
    ListAudio,
    HeaderPage,
    Loader,
    Analyse,
    ScribbPlayer,
    Retranscription,
    EvaluationManuelle,
    Details,
    DrawerImportAudio,
    DrawerCalibrage,
    EvaluationFinishedDetails,
    ComparatifTable,
    DropDownMenu
  },

  data() {
    return {
      isAttaching: false,
      activeSection: 1,
      showDrawer: false,
      showDrawerCalibrage: false,
      options: {},
      activeTab: 0,
      isPlaying: false,
      progress: 0,
      currentTime: 0,
      totalDuration: 0,
      selectedOptions: [],
      updatedFilters: {},
      analysesFilters: [
        { id: 'TON_AGRESSIF', name: 'Ton agressif' },
        { id: 'INSULTES', name: 'Insultes' },
        { id: 'LANGUE_ETRANGERE', name: 'Langue trangre' },
      ],
      currentTrackPlayDuration: 0,
      totalScore: 0,
    };
  },

  computed: {
    ...mapGetters('record', [
      'getActiveRecord',
      'getCollection',
      'getIsFetchingRecords',
      'getFilter',
      'getPagination',
      'getNavigationFrom',
      'getSelectedRecords',
    ]),
    ...mapGetters('calibrage', ['getModel']),
    ...mapGetters('user', ['getUser']),
    authedUser() {
      return this.getUser;
    },
    activeRecordIndex() {
      for (let index in this.records) {
        if (this.records[index].id == this.activeRecord.id) {
          return index;
        }
      }
      return null;
    },
    activeRecord() {
      return this.getActiveRecord;
    },
    records() {
      return this.getCollection;
    },
    playNextDisabled() {
      if (this.records.length > 1 && this.activeRecord) {
        return this.records.some(
          (x, index) =>
            x.url === this.activeRecord.url && index == this.records.length - 1,
        );
      } else {
        return true;
      }
    },
    playPreviousDisabled() {
      if (this.records.length > 0 && this.activeRecord) {
        return this.records.some(
          (x, index) => x.url === this.activeRecord.url && index == 0,
        );
      } else {
        return false;
      }
    },
    isfetchingRecords() {
      return this.getIsFetchingRecords;
    },
    filter() {
      return this.getFilter;
    },
    callCenters() {
      return this.$store.getters['callCenter/getCollection'].map((x) => ({
        id: x.id,
        name: x.name,
      }));
    },
    agents() {
      return this.$store.getters['agent/getCollection'].map((x) => ({
        id: x.id,
        name: x.firstName + ' ' + x.lastName,
      }));
    },
    campaigns() {
      return this.$store.getters['campaign/getCollection'].map((x) => ({
        id: x.campagneId,
        name: x.name,
      }));
    },
    navigationFrom() {
      return this.getNavigationFrom;
    },
    connectedUser() {
      return this.getUser;
    },
    filterItems() {
      let items = [
        {
          label: 'Call-center',
          key: 'callCenters',
          filters: this.$store.getters['callCenter/getCollection'].map((x) => ({
            id: x.id,
            name: x.name,
          })),
        },
        {
          label: 'Campagnes',
          key: 'campaigns',
          filters: this.$store.getters['campaign/getCollection'].map((x) => ({
            id: x.campagneId,
            name: x.name,
          })),
        },
        {
          label: 'Agents',
          key: 'agents',
          filters: this.$store.getters['agent/getCollection'].map((x) => ({
            id: x.id,
            name: x.firstName + ' ' + x.lastName,
          })),
        },
        {
          label: 'Type de problme',
          key: 'analyses',
          filters: this.analysesFilters,
        },
      ];

      if (
        ['responsable-activite', 'service-qualite'].includes(this.getUser?.role)
      ) {
        items = items.filter((x) => x.key != 'callCenters');
      }

      return items;
    },
  },

  methods: {
    ...mapActions('record', [
      'getData',
      'setActiveRecord',
      'setFilter',
      'setCollection',
      'setPagination',
      'resetState',
      'setParentRecord',
    ]),
    ...mapActions('calibrage', [
      'setModel',
    ]),
    exportAssessment() {
      const records = this.getSelectedRecords;
      const workbook = XLSX.utils.book_new();

      records.forEach(record => {
        let sheetName = record.name.replace('.wav', '');
        if (sheetName.length > 31) {
          sheetName = sheetName.slice(0, 28) + '...';
        }

        const sheetData = [
          [
            "node_id",
            "node_question_name",
            "node_answer_name",
            "node_answer_value",
            "node_answer_min_value",
            "node_answer_max_value",
            "node_comment",
            "node_question_category_id",
            "node_question_category_name",
            "evaluation_id",
            "evaluation_reference",
            "evaluation_case_reference",
            "evaluation_interaction_date",
            "evaluation_campaign_name",
            "evaluation_campaign_id",
            "evaluation_department_name",
            "evaluation_department_id",
            "evaluation_client_name",
            "evaluation_client_id",
            "evaluation_company_name",
            "evaluation_company_id",
            "evaluation_audit_company_name",
            "evaluation_audit_company_id",
            "evaluation_agent_name",
            "evaluation_agent_slug",
            "evaluation_agent_reference",
            "evaluation_agent_tags",
            "evaluation_auditor_name",
            "evaluation_auditor_slug",
            "evaluation_coach_name",
            "evaluation_coach_slug",

            "evaluation_comment"
          ]
        ];

        let rowOffset = 1;

        if (record.evaluations.length === 0) {
          sheetData.push([
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',
            'null',

            'null',
          ]);
        } else {
          record.evaluations.forEach(evaluation => {
            const evaluatorName = evaluation.user.firstName + ' ' + evaluation.user.lastName;
            const evaluatorId = evaluation.user.id;
            const evalCount = evaluation.eval.length;
            const evalDepartementName =evaluation.user.callCenterId!=null ?  evaluation.user.callCenterId.name :  null;
            const evalDepartementId = evaluation.user.callCenterId!=null ? evaluation.user.callCenterId.id : null;


            evaluation.eval.forEach(ev => {
              const grid = record.grid.find(g => g.items.some(item => item.id === ev.item_id));
              const item = grid ? grid.items.find(i => i.id === ev.item_id) : null;
              const note = item ? item.notes.find(n => n.id === ev.note_id) : null;

              sheetData.push([
                note ? note.id : 'null',
                item ? item.name : 'null',
                note ? note.name : 'null',
                note ? note.score : 'null',
                'null',
                'null',
                note ? note.meaning : 'null',
                grid ? grid.id : 'null',
                grid ? grid.name : 'null',
                note ? note.item_id : 'null',
                'null',
                'null',
                note ? this.$globals.formatDate(note.created_at) : 'null',
                record.gridData && record.gridData.campagne ? record.gridData.campagne.name : 'null',
                record.gridData && record.gridData.campagne ? record.gridData.campagne.campagneId : 'null',
                evalDepartementName ? evalDepartementName : 'null',
                evalDepartementId ? evalDepartementId : 'null',
                evaluatorName ? evaluatorName : 'null',
                evaluatorId ? evaluatorId : 'null',
                'null',
                'null',
                'null',
                'null',
                record.agent ? record.agent.firstName + ' ' + record.agent.lastName : 'null',
                'null',
                record.agent ? record.agent.hermesId : 'null',
                'null',
                'null',
                'null',
                'null',
                'null',

                ev.commentaire,
              ]);
            });

            rowOffset += evalCount;
          });
        }

        const worksheet = XLSX.utils.aoa_to_sheet(sheetData);

        XLSX.utils.book_append_sheet(workbook, worksheet, sheetName);
      });

      XLSX.writeFile(workbook, 'audios.xlsx');
    },
    scheduleCalibration() {
      const callCenterId = this.authedUser.callCenterId ? this.authedUser.callCenterId.id : null;
      this.setModel({
        ...this.getModel,
        callCenterId: callCenterId,
        organizerId: this.authedUser.id,
        status: 'in_progress',
        ['recordCalibrations']: this.getSelectedRecords,
      });
      this.showDrawerCalibrage = true;
    },
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },
    openToggleDrawer() {
      this.showDrawerCalibrage = !this.showDrawerCalibrage;
    },
    changeTab(index) {
      this.activeTab = index;
    },
    handlePlayNext(record) {
      this.setActiveRecord(record);
    },
    handlePlayPrevious(record) {
      this.setActiveRecord(record);
    },
    handleSearch(value) {
      this.setFilter({
        ...this.getFilter,
        ['keyword']: value,
      });
    },
    handleSelectedNamesChanged(selectedOptions) {
      this.selectedOptions = selectedOptions;
    },
    removeSelectedOption(selectedOption) {
      this.selectedOptions = this.selectedOptions.filter(
        (option) =>
          option.id !== selectedOption.id || option.key !== selectedOption.key,
      );

      const updatedFilters = {
        callCenters: this.selectedOptions
          .filter((item) => item.key === 'callCenters')
          .map((item) => item.id),
        agents: this.selectedOptions
          .filter((item) => item.key === 'agents')
          .map((item) => item.id),
        campaigns: this.selectedOptions
          .filter((item) => item.key === 'campaigns')
          .map((item) => item.id),
        analyses: this.selectedOptions
          .filter((item) => item.key === 'analyses')
          .map((item) => item.id),
      };

      this.handleFilterChanged(updatedFilters);
      this.updatedFilters = updatedFilters;
    },
    switchRecord(record) {
      this.setActiveRecord(this.records[index]);
    },
    applyedDatePicker(date) {
      this.setFilter({
        ...this.getFilter,
        ['startDate']: date.startDate,
        ['endDate']: date.endDate,
      });
      this.setPagination({
        ...this.getPagination,
        ['current_page']: 1,
      });
      this.setCollection([]);
      this.getData()
        .then(() => {})
        .catch(() => {});
    },
    cancelDatePicker() {
      this.setFilter({
        ...this.getFilter,
        ['startDate']: null,
        ['endDate']: null,
      });
      this.setPagination({
        ...this.getPagination,
        ['current_page']: 1,
      });
      this.setCollection([]);
      this.getData()
        .then(() => {})
        .catch(() => {});
    },
    handleFilterChanged(options) {
      this.setFilter({
        ...this.getFilter,
        ['callCenters']: options.callCenters,
        ['agents']: options.agents,
        ['campaigns']: options.campaigns,
        ['analyses']: options.analyses,
      });
      this.setPagination({
        ...this.getPagination,
        ['current_page']: 1,
      });
      this.setCollection([]);
      this.getData()
        .then(() => {})
        .catch(() => {});
    },

    handleAttachRecord() {
      this.isAttaching = true;
      this.showDrawer = true;
    },
    handleTotalScoreUpdate(newTotalScore) {
      this.totalScore = newTotalScore;
    }
  },

  watch: {
    'filter.keyword': function () {
      this.setPagination({
        ...this.getPagination,
        ['current_page']: 1,
      });
      this.setCollection([]);
      this.getData()
        .then(() => {})
        .catch(() => {});
    },
  },
  async created() {
    await this.resetState();
  },
  mounted() {
    this.isAttaching = false;
    this.$store.dispatch('callCenter/setFilter', {
      ...this.$store.getters['callCenter/getFilter'],
      ['withPagination']: 0,
    });
    this.$store.dispatch('callCenter/getData');
    this.$store.dispatch('campaign/setFilter', {
      ...this.$store.getters['campaign/getFilter'],
      ['withPagination']: 0,
    });
    this.$store.dispatch('campaign/getData');
    this.$store.dispatch('agent/setFilter', {
      ...this.$store.getters['agent/getFilter'],
      ['withPagination']: 0,
    });
    this.$store.dispatch('agent/getData');
    this.setCollection([]);
    if (this.navigationFrom == '') {
      // this.getData();
      this.$store.dispatch('record/setFilter', {
        ...this.$store.getters['record/getFilter'],
        ['withPagination']: 1,
      });
      this.$store.dispatch('record/getData');
    }
  },
};
</script>
