<template>
  <transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true }" class="show">
      <div class="header_drawer">
        <div class="title">
          <h4>{{ $t('views.agents.notice') }}</h4>
        </div>
        <div class="content-button btn-add" style="justify-content: flex-end">
          <button class="close mr-5" @click="handleClose">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
        </div>
      </div>

      <div class="body_drawer importFile">
        <div>
          <div class="recapitalitf">
            <h4 class="mt-4 mb-7"><strong>Listes des campagne</strong></h4>
            <TableComponent
              :columns="columnsCampagne"
              :data="dataCampaign"
              class="mt-2"
            />
          </div>
        </div>
      </div>
      <div id="overlay-" @click="handleClose()"></div>
    </div>
  </transition>
</template>
<script>
import TableComponent from '@/components/Table/Table.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {
    TableComponent,
  },
  data() {
    return {
      columnsCampagne: [
        {
          key: 'campagneId',
          label: 'ID de la campagne',
        },
        {
          key: 'name',
          label: 'Nom de la campagne',
        },
      ],
    };
  },
  props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
  },

  computed: {
    ...mapGetters('campaign', ['getCollection', 'getFilter', 'getState']),
    dataCampaign() {
      return this.getCollection;
    },
  },
  methods: {
    ...mapActions('campaign', [
      'setModel',
      'getData',
      'resetModel',
      'setFilter',
    ]),
    handleClose() {
      this.$emit('close');
    },
  },
};
</script>
