<template>
  <div :class="{ drawer: true }" class="show eqp">
    <div class="header_drawer">
      <div class="title">
        <h4>
          {{ $t('views.agents.manageAgents') }}
        </h4>
      </div>
      <div class="content-button btn-add">
        <button class="close mr-5" @click="close">
          {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
          <i class="icon-exit"></i>
        </button>
        <Button
          :loading="isFetching"
          @when-cliked="handleOnSubmit()"
          :text="$globals.capitalizeFirstLetter($t('common.save'))"
        />
      </div>
    </div>
    <div class="body_drawer">
      <div class="form-group-flex border-b">
        <div class="form-field">
          <button @click="addAgentInArray">
            <span class="mr-2"><i class="icon-plus"></i></span>
            {{ $globals.capitalizeFirstLetter($t('views.agents.addAgent')) }}
          </button>
          {{ arrayAgents }}
          <div v-for="agent in arrayAgents" :key="agent.id">
            <SelectSingle
              :options="collectionAgents"
              :keyLabel="'name'"
              :keyValue="'id'"
              :hasError="errors != null && errors.callCenter != null"
              :errorMessage="
                errors && errors.callCenter != null
                  ? $t('errors.' + errors.callCenter[0])
                  : ''
              "
              :onSelect="handleItemSelected"
              @lengthInputValue="(item) => handleSearch(item, agent.id)"
            />
            <div class="action" @click="removeAgentInArray(agent.id)">
              <button>
                <i class="icon-delete"></i>
              </button>
            </div>
          </div>
        </div>
      </div>
      <div
        class="form-group border-b my-7"
        v-if="agentsInThisTeam.agents.length > 0"
      >
        <h4>Actuellement dans l'equipe</h4>

        <div
          class="content-user form-group-flex pt-4 border-b pb-5"
          v-for="(agent, index) in agentsInThisTeam.agents"
          :key="index"
        >
          <div class="flex">
            <div class="mr-3">
              <div class="letter-avatar mr-1">{{ agent.firstName }}</div>
            </div>
            <div>
              <div class="content-name">
                {{ agent.firstName }} {{ agent.lastName }}
              </div>
              <div class="content-email">{{ agent.hermesId }}</div>
            </div>
          </div>
          <div class="action">
            <button>
              <i class="icon-delete"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    <div id="overlay-" @click="close"></div>
  </div>
</template>

<script>
import Button from '@/components/FormWidgets/Button.vue';
import SelectSingle from '@/components/FormWidgets/SelectSingle/SelectSingle.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  components: {
    Button,
    SelectSingle,
  },
  data() {
    return {
      showDrawer: false,
      selectedItem: null,
      isFetching: null,
      arrayAgents: [],
      arrayInputSearch: [],
      modalVisible: false,
      btnleft: 'Annuler',
      btnright: 'Supprimer',
    };
  },
  props: ['show', 'agentsInThisTeam'],
  computed: {
    ...mapGetters('callCenter', ['getIdWithName', 'getCollectionTeam']),
    ...mapGetters('team', ['getModel', 'getErrors', 'getAgentToRemove']),
    collection() {
      return this.$store.getters['callCenter/getIdWithName'].map((x) => ({
        id: x.id,
        name: x.name,
        text: x.name,
      }));
    },
    collectionAgents() {
      return this.$store.getters['agent/getCollection'].map((x) => ({
        id: x.id,
        name: x.firstName + ' ' + x.lastName + ' (' + x.hermesId + ')',
      }));
    },
    model() {
      return this.getModel;
    },
    agentToRemove() {
      return this.getAgentToRemove;
    },
    collectionTeam() {
      return this.getCollectionTeam;
    },
    errors() {
      return this.getErrors;
    },
  },
  methods: {
    closeModal() {
      this.$store.commit('team/SET_SHOW_AGENT_MODAL', { show: false });
    },
    addAgentInArray() {
      const newAgent = {
        id: Math.random().toString(36).substr(2, 9),
      };
      this.arrayAgents.push(newAgent);
    },
    removeAgentInArray(agentId) {
      const index = this.arrayAgents.findIndex((agent) => agent.id === agentId);
      if (index !== -1) {
        this.arrayAgents.splice(index, 1);
      }
    },
    deleteTeamSelected() {
      this.$store.commit('team/REMOVE_SELECTED', {
        sourceArray: 'agents',
        targetArray: 'agentsSelected',
        itemId: this.agentToRemove.itemId,
      });
      this.closeModal();
    },
    async handleOnSubmit() {
      this.isFetching = true;

      let isValid = true;
      let errors = {};
      this.setCampagnesIds();

      if (this.model.name === '') {
        errors.name = ['NAME_REQUIRED'];
        isValid = false;
      }
      if (Object.keys(this.model.callCenterSelected).length == 0) {
        errors.callCenter = ['CALLCENTER_REQUIRED'];
        isValid = false;
      }

      if (this.model.campagnesIds === '') {
        errors.campagne = ['CAMPAGNEID_REQUIRED'];
        isValid = false;
      }

      this.setErrors(errors);

      if (isValid) {
        if (this.model.id) {
          await this.updateTeam()
            .then(() => {
              this.isFetching = false;
              this.close();
            })
            .catch(() => {});
        } else {
          await this.createTeam()
            .then(() => {
              this.isFetching = false;
              this.close();
            })
            .catch(() => {});
        }
      }
      this.isFetching = false;
    },
    ...mapActions('callCenter', ['getCollectionCallCentersCampagnes']),
    ...mapActions('team', [
      'setModel',
      'setCampagnesIds',
      'createTeam',
      'updateTeam',
      'setErrors',
      'resetErrors',
    ]),
    close() {
      this.resetErrors();
      this.showDrawer = false;

      this.$emit('close');
    },
    async handleItemSelected(selectedItem) {
      this.selectedItem = selectedItem;
      // if (this.selectedItem != null) {
      //   this.$store.commit('team/SET_MODEL_CALLCENTER', this.selectedItem);
      //   await this.getCollectionCallCentersCampagnes({ id: selectedItem.id })
      //     .then(() => {
      //       this.$store.commit('team/SET_COLLECTION_TEAMS', {
      //         arrayName: 'supervisers',
      //         items: this.collectionTeam.supervisers,
      //       });
      //       this.$store.commit('team/SET_COLLECTION_TEAMS', {
      //         arrayName: 'agents',
      //         items: this.collectionTeam.agents,
      //       });
      //     })
      //     .catch(() => {});
      // }
    },
    handleItemSelectedItem(item, array_name) {
      this.$store.commit('team/SET_SELECTED_ARRAY', {
        sourceArray: array_name.slice(0, -8),
        targetArray: array_name,
        item,
      });
    },
    removeItem(id, array_name) {
      this.$store.commit('team/REMOVE_SELECTED', {
        sourceArray: array_name.slice(0, -8),
        targetArray: array_name, // Assuming array_name is a variable
        itemId: id,
      });
    },
    verifyName() {
      if (this.getModel.name == '') {
        this.setErrors({ name: ['NAME_REQUIRED'] });
      }
    },
    handleSearch(item, agentId) {
      const index = this.arrayAgents.findIndex((agent) => agent.id === agentId);
      if (index !== -1) {
        // Add the item to the specified index in the arrayAgents array
        this.$set(this.arrayAgents[index], 'keySearch', item);
      }
    },
  },
};
</script>
