<template>
  <div class="list_callcenter scroll">
    <progress-bar
      v-for="(element, index) of data"
      :key="index"
      :target="element.avgScore"
      :color="getProgressBarColor(index, element)"
      :title="element.callCenterName"
    ></progress-bar>
  </div>
</template>

<script>
import ProgressBar from '@/components/Dashboard/Progressbar/Progressbar.vue';
export default {
  props: ['data'],
  components: {
    ProgressBar,
  },
  data() {
    return {
      progressBarColors: ['#4caf50', '#fa850f', '#FCEF21', '#d60202'],
      renderKey: 0,
    };
  },

  methods: {
    getProgressBarColor(index, element) {
      if (element.avgScore > 0 && element.avgScore <= 49) {
        return this.progressBarColors[3];
      } else if (element.avgScore >= 50 && element.avgScore <= 59) {
        return this.progressBarColors[1];
      } else if (element.avgScore >= 60 && element.avgScore <= 79) {
        return this.progressBarColors[2];
      } else if (element.avgScore >= 80 && element.avgScore <= 100) {
        return this.progressBarColors[0];
      }
    },
  },

  computed: {},
  mounted() {
    this.renderKey++;
  },
};
</script>
