<template>
  <div class="scribb-form-auth">
    <div class="scribb-form-forget">
      <div class="container-auth flex align-items">
        <div class="left-column box p-50">
          <div class="content-form">
            <div class="logo">
              <img src="../../assets/images/logo.svg" alt="" />
            </div>
            <h4 v-if="!forgetPasswrodSucces">
              {{ $t('components.password.forgetPassword') }}
            </h4>
            <div v-if="forgetPasswrodSucces">
              <img src="../../assets/icon/icon-envelope.svg" alt="" />
              <h2>{{ $t('components.password.checkEmail') }}</h2>
              <h3>
                {{ $t('components.password.sentInstructions') }}
                <br />
                {{ $t('components.password.changePassword') }}
              </h3>
            </div>
            <div v-if="!forgetPasswrodSucces" class="flex justify-center mt-9">
              <div class="card-form">
                <input-field :hasError="errors != null && errors.email != null" :errorMessage="errors && errors.email != null
                ? $t('errors.' + errors.email[0])
                : ''
              " name="email" label="E-mail*" id="1" @input="(e) => {
                email = e.target.value;
              }
              " />
                <Button class="mt-4" text="Rinitialiser le mot de passe" @when-cliked="onSubmit" :loading="loading" />
                <button class="se-conntecte mt-3 flex align-items" @click="goToLoginPage" >
                  Se connecter
                </button>
              </div>
            </div>
          </div>
        </div>
        <div class="right-column box flex align-items justify-center">
          <AnimationScribb />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import AnimationScribb from '@/components/AnimationScribb/AnimationScribb.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {
    InputField,
    Button,
    AnimationScribb,
  },
  data() {
    return {
      loading: false,
      email: null,
      forgetPasswrodSucces: false,
    };
  },
  methods: {
    ...mapActions('user', ['forgetPassword', 'setErrors']),

    async onSubmit() {
      this.loading = true
      this.setErrors();
      await this.forgetPassword(this.email)
        .then(() => {
          this.loading = false
          this.forgetPasswrodSucces = true;
        })
        .catch(() => {
          this.loading = false
        });

      // this.$router.push({ name: 'dashboard' });
    },
    goToLoginPage() {
      this.$router.push({ name: 'login' });
    },
  },
  computed: {
    ...mapGetters('user', ['getErrors']),
    errors() {
      return this.getErrors;
    },
  },
};
</script>
