<template>
  <div
    v-if="showModalMigration"
    class="modal-dialog"
    @click="closeModalOutside"
  >
    <div class="modal-content" @click.stop>
      <div class="modal-header mb-5">
        <h4>Information de lagent</h4>
      </div>
      <div class="modal-body">
        <TableComponent
          :isLoading="isFetchingAgent"
          :columns="columnsModal"
          :data="agentCollention"
        />
        <div class="modal-footer">
          <button @click="handleOk" class="btn_remove">
            {{ okText }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import TableComponent from '@/components/Table/Table.vue';

export default {
  components: { TableComponent },
  props: {
    showModalMigration: Boolean,
    actualTeam: Number,
    fromTeam: {
      type: Boolean,
      default: false,
    },
    cancelText: String,
    okText: String,
    item: { type: [Array, Object] },
  },
  data() {
    return {};
  },
  methods: {
    ...mapActions('agent', ['getAgentRecordsData', 'resetState']),
    closeModal() {
      this.$emit('close', this.item);
    },
    handleOk() {
      this.$emit('onConfirm');
    },
    closeModalOutside(event) {
      if (event.target.classList.contains('modal-dialog')) {
        this.closeModal();
      }
    },
  },
  mounted() {
    if (this.user) {
      const payload = {
        campagneId: this.user.selectedCampagneId,
        gridSelected: {
          id: this.user.selectedGridId,
        },
        agentId: this.item.agentId,
      };
      this.getAgentRecordsData(payload);
    }
  },
  computed: {
    ...mapGetters('agent', [
      'getIsFetchingDetails',
      'getMoreDetailsCollection',
    ]),
    ...mapGetters('user', ['getUser']),
    user() {
      return this.getUser;
    },
    isFetchingAgent() {
      return this.getIsFetchingDetails;
    },
    agentCollention() {
      return this.getMoreDetailsCollection;
    },

    columnsModal() {
      return [
        {
          key: 'audioname',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.agents.table.columns.audioname'),
          ),
          render: (text, record) => {
            return `<div>${record.name}</div>`;
          },
        },
        {
          key: 'dateEvaluation',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.agents.table.columns.dateEvaluation'),
          ),
          render: (text, record) => {
            return `<span>${this.$globals.formatDate(record.dateEvaluation)}</span>`;
          },
        },
        {
          key: 'grid',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.agents.table.columns.grid'),
          ),
          render: (text, record) => {
            return `<div>${record.gridName}</div> `;
          },
        },
        {
          key: 'Scoring',
          label: this.$globals.capitalizeFirstLetter(
            this.$t('views.agents.table.columns.scoring'),
          ),
          render: (text, record) => {
            let scoreDiff = parseFloat(record.scoreDifference).toFixed(2);

            if (scoreDiff < 0) {
              return `<div class="bold">${record.avgScoreNewDate}   
              <span class="taux red">${record.scoreDifference}%</span></div>`;
            } else if (scoreDiff > 0) {
              return `<div class="bold">${record.avgScoreNewDate}   
              <span class="taux green">+${record.scoreDifference}%</span></div>`;
            } else {
              return `<div class="bold">${record.avgScoreNewDate}   
              <span class="taux">${record.scoreDifference}%</span></div>`;
            }
          },
        },
      ];
    },
  },
};
</script>
