<template>
  <div class="retranscription scroll">
    <div class="panel_body_">
      <template v-for="(message, index) of model?.segments">
        <div
          :key="message.id"
          v-if="message.speaker == 'agent'"
          class="content_left"
        >
          <div
            class="user_content"
            v-if="
              index == 0 ||
              (index != 0 &&
                message.speaker != model?.segments[index - 1].speaker)
            "
          >
            <div class="avatar">
              <div class="icon_bold">
                <i class="icon-headphones-with-mic"></i>
              </div>
            </div>
            <div class="user">
              <h3>
                {{ message.speaker }}
              </h3>
            </div>
            <div class="time">{{ formatSegmentTime(message.start_time) }}</div>
          </div>
          <div class="mouseover" v-if="!isClicked[index]">
            <div @mouseover="mouseover(index)" @mouseleave="mouseleave(index)">
              <p>
                <button
                  class="edit_texte_ai"
                  v-if="isHovering[index]"
                  @click="editSpeech(message, index)"
                >
                  <i class="icon-edit" id="go_to_edit"></i>
                </button>
                {{
                  message.reviewedSpeatch
                    ? message.reviewedSpeatch
                    : message.speatch != null
                      ? message.speatch
                      : '...'
                }}
              </p>
            </div>
          </div>
          <div
            v-if="isClicked[index]"
            class="content-left form-field mb-2 index clicked--"
          >
            <p>
              {{
                message.speatch
                  ? message.speatch
                  : message.reviewedSpeatch
                    ? message.reviewedSpeatch
                    : '...'
              }}
              <button
                class="edit_texte_ai"
                v-if="isHovering[index]"
                @click="editSpeech(message, index)"
              >
                <i class="icon-edit" id="go_to_edit"></i>
              </button>
            </p>
          </div>
          <div
            v-if="isClicked[index]"
            class="content-left form-field mb-6 index"
          >
            <textarea
              :value="
                message.reviewedSpeatch
                  ? message.reviewedSpeatch
                  : message.speatch
              "
              @input="
                (e) => {
                  setSpeechToReview(e.target.value);
                }
              "
            ></textarea>
            <div class="form-action my-3">
              <Button
                class="btn-close mr-4"
                :text="$globals.capitalizeFirstLetter($t('common.cancel'))"
                @when-cliked="handleCancel(index)"
              ></Button>
              <Button
                class="btn-correct"
                :text="$globals.capitalizeFirstLetter($t('common.correct'))"
                @when-cliked="handleCorrectSpeech(index)"
              ></Button>
            </div>
            <div class="overlay-close" @click="handleCancel"></div>
          </div>
        </div>
        <div
          :key="message.id"
          v-if="message.speaker == 'prospect'"
          class="content_right"
        >
          <div class="">
            <div
              class="user_content"
              v-if="
                index == 0 ||
                (index != 0 &&
                  message.speaker != model?.segments[index - 1].speaker)
              "
            >
              <div class="avatar">
                <div class="icon_bold"><i class="icon-profil"></i></div>
              </div>
              <div class="time mr-4">
                {{ formatSegmentTime(message.start_time) }}
              </div>
              <div class="user">
                <h3>Prospect</h3>
              </div>
            </div>
            <div class="mouseover" v-if="!isClicked[index]">
              <div
                @mouseover="mouseover(index)"
                @mouseleave="mouseleave(index)"
              >
                <p>
                  <button
                    class="edit_texte_ai a_2"
                    v-if="isHovering[index]"
                    @click="editSpeech(message, index)"
                  >
                    <i class="icon-edit" id="go_to_edit"></i>
                  </button>
                  {{
                    message.reviewedSpeatch
                      ? message.reviewedSpeatch
                      : message.speatch != null
                        ? message.speatch
                        : '...'
                  }}
                </p>
              </div>
            </div>
            <div
              v-if="isClicked[index]"
              class="content-right form-field mb-2 index clicked--"
            >
              <!-- <textarea
                :value="
                  message.speatch ?? message.speatch 
                "
                @input="
                  (e) => {
                    setSpeechToReview(e.target.value);
                  }
                "
              >                                                                                                                                                                                                                                                         
              </textarea> -->
              <p>
                {{
                  message.speatch
                    ? message.speatch
                    : message.reviewedSpeatch
                      ? message.reviewedSpeatch
                      : '...'
                }}
                <button
                  class="edit_texte_ai a_2 edit_"
                  v-if="isHovering[index]"
                  @click="editSpeech(message, index)"
                >
                  <i class="icon-edit" id="go_to_edit"></i>
                </button>
              </p>
            </div>
            <div
              v-if="isClicked[index]"
              class="content-right form-field mb-6 index edit-"
            >
              <textarea
                :value="
                  message.reviewedSpeatch
                    ? message.reviewedSpeatch
                    : message.speatch
                "
                @input="
                  (e) => {
                    setSpeechToReview(e.target.value);
                  }
                "
              ></textarea>
              <div class="form-action my-3">
                <Button
                  class="btn-close mr-4"
                  :text="$globals.capitalizeFirstLetter($t('common.cancel'))"
                  @when-cliked="handleCancel(index)"
                ></Button>
                <Button
                  class="btn-correct"
                  :text="$globals.capitalizeFirstLetter($t('common.correct'))"
                  @when-cliked="handleCorrectSpeech(index)"
                ></Button>
              </div>
              <div class="overlay-close" @click="handleCancel"></div>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Button from '@/components/FormWidgets/Button.vue';
import moment from 'moment';

export default {
  components: { Button },
  data() {
    return {
      current_speaker: '',
      showBtn: false,
      isHovering: [],
      isClicked: [],
      speechToReview: '',
      speech: '',
      idSpeech: '',
    };
  },
  computed: {
    ...mapGetters('training', ['getActiveRecord', 'getModel']),
    activeRecord() {
      return this.getActiveRecord;
    },
    model() {
      return this.getModel;
    },
  },
  methods: {
    ...mapActions('training', ['updateSegmentationTraining']),
    editSpeech(item, index) {
      this.$set(this.isClicked, index, !this.isClicked[index]);
      this.idSpeech = item.id;
      this.speech = item.speatch;
      this.speechToReview = item.reviewedSpeatch;
    },

    mouseover(index) {
      this.$set(this.isHovering, index, true);
    },
    mouseleave(index) {
      this.$set(this.isHovering, index, false);
    },
    setSpeechToReview(value) {
      this.speechToReview = value;
    },
    handleCorrectSpeech(index) {
      let item = {
        id: this.idSpeech,
        speechToReview: this.speechToReview,
      };
      this.updateSegmentationTraining(item).then(() => {
        this.$set(this.isClicked, index, false);
      });
    },
    handleCancel(index) {
      this.$set(this.isClicked, index, false);
    },
    formatSegmentTime(seconds) {
      return moment.utc(seconds * 1000).format('mm:ss');
    },
  },
};
</script>
