<template>
  <transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true }" class="show">
      <div class="header_drawer">
        <div class="title">
          <h4>
            {{
              $t(
                `views.records.${
                  isAttaching ? 'attachRecord' : 'importRecord'
                }`,
              )
            }}
          </h4>
        </div>
        <div class="content-button btn-add">
          <button class="close mr-5" @click="close">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <Button
            :loading="loading"
            @when-cliked="handleImportRecord()"
            :text="$t(`common.${isAttaching ? 'attach' : 'import'}`)"
          />
        </div>
        <div class="progress-bar-bottom" :style="{ width: uploadProgress + '%' }"></div>
      </div>

      <div class="body_drawer importFile">
        <div class="form-group-flex mb-3">
          <div class="form-field">
            <label for="">Fichier Audio</label>
            <input
              enctype="multipart/form-data"
              @change="(e) => handleFileChange(e)"
              type="file"
              accept=".wav"
              class="import mb-1"
            />
            <span style="color: red; font-size: 12px">
              {{ errors && errors.file ? $t('errors.' + errors.file[0]) : '' }}
            </span>
          </div>
        </div>
        <div v-if="file != null && proccesing == false" class="form-group-flex">
          <input-field
            class="mb-7"
            :defaultValue="fileName"
            :hasError="false"
            :errorMessage="''"
            :disabled="true"
            @input="(e) => {}"
            name="name"
            :label="$globals.capitalizeFirstLetter($t('common.name'))"
            id="1"
          />
        </div>
        <div v-show="file != null && proccesing == false">
          <div class="form-group-flex">
            <div class="singledatepicker mb-3">
              <div class="singledate_ mr-2 form-field">
                <label>Date</label>
                <DatePicker
                  :value="record.date"
                  @input="updateRecordDate"
                  value-type="format"
                />
              </div>
              <div class="mt-5">-</div>
              <div class="heure_ ml-2 form-field">
                <label>Heure</label>
                <DatePicker
                  :value="record.time"
                  @input="updateRecordTime"
                  format="HH:mm:ss"
                  value-type="format"
                  type="time"
                  placeholder="hh:mm:ss"
                />
              </div>
            </div>
          </div>
          <div class="mb-3">
            <span style="color: red; font-size: 12px">
              {{
                errors && errors.dateTime
                  ? $t('errors.' + errors.dateTime[0])
                  : ''
              }}
            </span>
          </div>
          <div class="container-grid">
            <SelectSingle
              :isDisabled="isAttaching == true || user.role == 'admin'"
              :id="2"
              ref="callCenterRef"
              :options="callCenters"
              :keyLabel="'label'"
              :keyValue="'value'"
              label="Call-center"
              :hasError="errors != null && errors.callCenterId != null"
              :errorMessage="
                errors && errors.callCenterId
                  ? $t('errors.' + errors.callCenterId[0])
                  : ''
              "
              :defaultSelected="record.callCenterId"
              :onSelect="
                (item) => {
                  record.callCenterId = item.value;
                  setModel({
                    ...model,
                    ['callCenterId']: item.value,
                  });
                }
              "
            />
            <SelectSingle
              :isDisabled="
                isAttaching == true ||
                record.callCenterId == null ||
                record.callCenterId == '*'
              "
              ref="agentRef"
              :id="2"
              :options="agents"
              :keyLabel="'label'"
              :keyValue="'value'"
              label="Agent"
              :hasError="errors != null && errors.agentId != null"
              :errorMessage="
                errors && errors.agentId
                  ? $t('errors.' + errors.agentId[0])
                  : ''
              "
              :defaultSelected="record.agentId"
              :onSelect="
                (item) => {
                  record.agentId = item.value;
                  setModel({
                    ...model,
                    ['agentId']: item.value,
                  });
                }
              "
            />
          </div>
          <div class="container-grid">
            <SelectSingle
              :isDisabled="isAttaching == true"
              :id="2"
              ref="gridRef"
              :options="grids"
              :keyLabel="'label'"
              :keyValue="'value'"
              label="Grille d'valuation"
              :hasError="errors != null && errors.gridId != null"
              :errorMessage="
                errors && errors.gridId ? $t('errors.' + errors.gridId[0]) : ''
              "
              :defaultSelected="record.gridId"
              :onSelect="
                (item) => {
                  record.gridId = item.value;
                  setModel({
                    ...model,
                    ['gridId']: item.value,
                  });
                }
              "
            />
            <SelectSingle
              :id="2"
              ref="qualificationRef"
              :options="qualifications"
              :keyLabel="'label'"
              :keyValue="'value'"
              label="Qualification"
              :hasError="errors != null && errors.qualificationId != null"
              :errorMessage="
                errors && errors.qualificationId
                  ? $t('errors.' + errors.qualificationId[0])
                  : ''
              "
              :defaultSelected="record.qualificationId"
              :onSelect="
                (item) => {
                  record.qualificationId = item.value;
                  setModel({
                    ...model,
                    ['qualificationId']: item.value,
                  });
                }
              "
            />
          </div>

          <!-- <div v-if="processing" class="progress-wrapper">
            <div class="progress mb-3">
              <div class="progress-bar" role="progressbar" :style="{ width: uploadProgress + '%' }" aria-valuenow="uploadProgress" aria-valuemin="0" aria-valuemax="100">
                {{ uploadProgress }}%
              </div>
            </div>
          </div> -->

        </div>
      </div>
      <div id="overlay-" @click="close"></div>
    </div>
  </transition>
</template>

<script>
import Button from '@/components/FormWidgets/Button.vue';
import InputField from '@/components/FormWidgets/InputField.vue';
import SelectSingle from '@/components/FormWidgets/SelectSingle/SelectSingle.vue';
import { mapActions, mapGetters } from 'vuex';
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import {
  dateStringToRecordDateFormat,
  isValidDateTime,
  RecordDateFormatToDateString,
} from '@/helpers/date';
export default {
  components: {
    Button,
    InputField,
    SelectSingle,
    DatePicker,
  },
  props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
    isAttaching: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      options: [],
      record: {
        name: '',
        date: null,
        time: null,
        callCenterId: null,
        gridId: null,
        qualificationId: null,
        campaignId: null,
      },
      file: null,
      proccesing: false,
      loading: false,
      duration: 0,
      uploadProgress: 0,
      processing: false,
    };
  },
  methods: {
    ...mapActions('record', [
      'setModel',
      'createRecord',
      'uploadRecord',
      'resetModel',
      'setErrors',
      'uploadSubRecord',
    ]),
    getAudioDuration(file) {
      return new Promise((resolve, reject) => {
        const audio = new Audio();
        audio.src = URL.createObjectURL(file);
        audio.onloadedmetadata = () => {
          const duration = audio.duration;
          URL.revokeObjectURL(audio.src);
          resolve(duration);
        };
        audio.onerror = () => {
          reject(new Error('Failed to load audio file'));
        };
        audio.load();
      });
    },
    async handleFileChange(event) {
      const user = JSON.parse(localStorage.getItem('user'));
      const audio = event.target.files[0];
      try {
        const duration = await this.getAudioDuration(audio);
        this.setModel({
          ...this.getModel,
          ['duration']: duration,
        });
      } catch (error) {
        this.setModel({
          ...this.getModel,
          ['duration']: null,
        });
      }
      this.$refs.callCenterRef.setSelected(null);
      this.$refs.agentRef.setSelected(null);
      this.$refs.gridRef.setSelected(null);
      this.$refs.qualificationRef.setSelected(null);
      this.proccesing = true;
      this.setErrors(null);
      const file = event.target.files[0];
      this.file = file;
      const fileName = file.name;
      const fileNameSplited = fileName.replace(/\.[^/.]+$/, '').split('_');

      this.$refs.callCenterRef.setSelected(
        user?.role == 'admin'
          ? user.callCenterId?.id
          : this.isAttaching == true
          ? this.activeParentRecord.agentId.callCenterId.id
          : Number(fileNameSplited[1]),
      );
      this.$refs.agentRef.setSelected(
        this.isAttaching == true
          ? this.activeParentRecord.agentId.hermesId
          : fileNameSplited[2],
      );
      this.$refs.gridRef.setSelected(
        this.isAttaching == true
          ? this.activeParentRecord.gridId
          : Number(fileNameSplited[3]),
      );
      this.$refs.qualificationRef.setSelected(Number(fileNameSplited[4]));
      const parsedRecordDate = RecordDateFormatToDateString(fileNameSplited[0]);
      this.record.date = `${parsedRecordDate.year}-${parsedRecordDate.month}-${parsedRecordDate.day}`;

      this.record.time = `${parsedRecordDate.hours}:${parsedRecordDate.minutes}:${parsedRecordDate.seconds}`;
      this.record.callCenterId =
        user?.role == 'admin'
          ? user.callCenterId?.id
          : this.isAttaching == true
          ? this.activeParentRecord.agentId.callCenterId.id
          : fileNameSplited[1]
          ? fileNameSplited[1]
          : '*';
      this.record.agentId = this.isAttaching
        ? this.activeParentRecord.agentId.hermesId
        : fileNameSplited[2] &&
          this.agents.find((x) => x.hermesId == fileNameSplited[2])
        ? fileNameSplited[2]
        : '*';
      this.record.gridId = this.isAttaching
        ? this.activeParentRecord.gridId
        : fileNameSplited[3]
        ? fileNameSplited[3]
        : '*';
      this.record.qualificationId = fileNameSplited[4]
        ? fileNameSplited[4]
        : '*';

      this.setModel({
        ...this.getModel,
        ['file']: event.target.files[0],
        ['fileName']: fileName,
        ['originalName']: fileName,
      });
      this.proccesing = false;
    },
    close() {
      this.file = null;
      // this.resetModel();
      this.setErrors(null);
      this.$emit('close');
    },
    handleImportRecord() {
      let isValid = true;
      const errors = { ...this.getErrors };
      if (this.model.dateTime == null || this.model.dateTime == '*') {
        errors.dateTime = ['TITLE_REQUIRED'];
        this.loading = false;
        isValid = false;
      } else {
        delete errors.dateTime;
      }
      if (this.model.agentId == null || this.model.agentId == '*') {
        errors.agentId = ['RECORD_AGENT_REQUIRED'];
        this.loading = false;
        isValid = false;
      } else {
        delete errors.agentId;
      }
      if (this.model.callCenterId == null || this.model.callCenterId == '*') {
        errors.callCenterId = ['RECORD_CALL_CENTER_REQUIRED'];
        this.loading = false;
        isValid = false;
      } else {
        delete errors.callCenterId;
      }
      if (this.model.qualificationId == null || this.model.qualificationId == '*') {
        errors.qualificationId = ['RECORD_QUALIFICATION_REQUIRED'];
        this.loading = false;
        isValid = false;
      } else {
        delete errors.qualificationId;
      }
      if (this.model.gridId == null || this.model.gridId == '*') {
        errors.gridId = ['RECORD_GRID_REQUIRED'];
        this.loading = false;
        isValid = false;
      } else {
        delete errors.gridId;
      }
      this.setErrors(errors);

      if (isValid) {
        this.uploadProgress = 0;
        this.loading = true;
        this.processing = true;
        const onUploadProgress = (progressEvent) => {
          const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          this.uploadProgress = percentCompleted;
        };

        const uploadPromise = this.isAttaching ? this.uploadSubRecord(onUploadProgress) : this.uploadRecord(onUploadProgress);

        uploadPromise
          .then(() => {
            this.loading = false;
            this.processing = false;
            this.close();
          })
          .catch(() => {
            this.loading = false;
            this.processing = false;
            this.uploadProgress = 0;
          });
      }

    },
    updateRecordDate(date) {
      this.record.date = date;
    },
    updateRecordTime(time) {
      this.record.time = time;
    },
  },
  mounted() {
    this.$store.dispatch('callCenter/setFilter', {
      ...this.$store.getters['callCenter/getFilter'],
      ['withPagination']: 0,
    });
    this.$store.dispatch('qualification/setFilter', {
      ...this.$store.getters['qualification/getFilter'],
      ['withPagination']: 0,
    });
    this.$store.dispatch('grid/setFilter', {
      ...this.$store.getters['grid/getFilter'],
      ['draft']: 0,
    });
    this.$store.dispatch('callCenter/getData');
    this.$store.dispatch('qualification/getData');
    this.$store.dispatch('campaign/getData');
    this.$store.dispatch('grid/getData');
  },
  computed: {
    ...mapGetters('record', [
      'getModel',
      'getErrors',
      'getActiveRecord',
      'getParentRecord',
    ]),
    ...mapGetters('user', ['getUser']),
    user() {
      return this.getUser;
    },
    model() {
      return this.getModel;
    },
    errors() {
      return this.getErrors;
    },
    agents() {
      return this.$store.getters['agent/getCollection'].map((agent) => ({
        value: agent.hermesId,
        label: agent.hermesId + ' - ' + agent.firstName + ' ' + agent.lastName,
      }));
    },
    callCenters() {
      return this.$store.getters['callCenter/getCollection'].map(
        (callCenter) => ({
          value: callCenter.id,
          label: callCenter.id + ' - ' + callCenter.name,
        }),
      );
    },
    qualifications() {
      return this.$store.getters['qualification/getCollection'].map(
        (qualification) => ({
          value: qualification.id,
          label: qualification.id + ' - ' + qualification.name,
        }),
      );
    },
    grids() {
      return this.$store.getters['grid/getGridCollection'].map((grid) => ({
        value: grid.id,
        label: grid.name,
      }));
    },
    fileName() {
      const recordData = this.getModel;
      const recordFileName = `${
        recordData.dateTime != null ? recordData.dateTime : '*'
      }_${recordData.callCenterId != null ? recordData.callCenterId : '*'}_${
        recordData.agentId != null ? recordData.agentId : '*'
      }_${recordData.gridId != null ? recordData.gridId : '*'}_${
        recordData.qualificationId != null ? recordData.qualificationId : '*'
      }.${this.file.type.split('/')[1]}`;
      return recordFileName;
    },
    activeRecord() {
      return this.getActiveRecord;
    },
    activeParentRecord() {
      return this.getParentRecord;
    },
  },
  watch: {
    'record.callCenterId': function (val) {
      if (this.isAttaching == false) {
        this.$refs.agentRef.setSelected(null);
        this.setModel({
          ...this.getModel,
          ['agentId']: null,
        });
        this.record.agentId = null;
        this.$store.dispatch('agent/setFilter', {
          ...this.$store.getters['agent/getFilter'],
          ['callcenter']: [parseInt(val)],
        });
        this.$store.dispatch('agent/getData');
      }
    },
    record: {
      handler(val) {
        const user = JSON.parse(localStorage.getItem('user'));
        if (this.record.callCenterId != val.callCenterId) {
          this.$store.dispatch('agent/setFilter', {
            ...this.$store.getters['agent/getFilter'],
            ['callcenter']: user.callCenterId
              ? user.callCenterId?.id
              : [parseInt(val.callCenterId)],
          });
          this.$store.dispatch('agent/getData');
        }
        this.setModel({
          ...this.getModel,
          ['dateTime']: isValidDateTime(
            new Date(`${this.record.date}T${this.record.time}`),
          )
            ? dateStringToRecordDateFormat(
                new Date(`${this.record.date}T${this.record.time}`),
              )
            : null,
          ['callCenterId']:
            Number.isInteger(parseInt(val.callCenterId)) == true
              ? parseInt(val.callCenterId)
              : null,
          ['agentId']: val.agentId ?? null,
          ['gridId']:
            Number.isInteger(parseInt(val.gridId)) == true
              ? parseInt(val.gridId)
              : null,
          ['qualificationId']:
            Number.isInteger(parseInt(val.qualificationId)) == true
              ? parseInt(val.qualificationId)
              : null,
        });
        let errors = this.errors;
        if (errors != null) {
          if (
            this.model.dateTime != null &&
            this.model.dateTime != '*' &&
            errors.dateTime
          ) {
            delete errors.dateTime;
          }
          if (
            this.model.callCenterId != null &&
            this.model.callCenterId != '*' &&
            errors.callCenterId
          ) {
            delete errors.callCenterId;
          }
          if (
            this.model.agentId != null &&
            this.model.agentId != '*' &&
            errors.agentId
          ) {
            delete errors.agentId;
          }
          if (
            this.model.gridId != null &&
            this.model.gridId != '*' &&
            errors.gridId
          ) {
            delete errors.gridId;
          }
          if (
            this.model.qualificationId != null &&
            this.model.qualificationId != '*' &&
            errors.qualificationId
          ) {
            delete errors.qualificationId;
          }
          this.setErrors(errors);
        }
      },
      deep: true,
    },
  },
};
</script>
