<template>
  <transition name="slide">
  <div v-if="isOpened" :class="{ drawer: true }" class="show">
    <div class="header_drawer">
      <div class="title">
        <h4>
          {{
            $globals.capitalizeFirstLetter(
              model.id == null ? $t('common.add') : $t('common.update'),
            )
          }}
        </h4>
      </div>
      <div class="content-button btn-add">
        <button class="close mr-5" @click="$emit('close')">
          {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
          <i class="icon-exit"></i>
        </button>
        <Button
          @when-cliked="handleOnSubmit"
          :text="
            $globals.capitalizeFirstLetter(
              model.id == null ? $t('common.save') : $t('common.save'),
            )
          "
        />
      </div>
    </div>

    <div class="body_drawer">
      <div class="form-group-flex">
        <input-field
          class="mb-7"
          id="1"
          name="adresseSftp"
          :label="$t('views.settings.adresseSftp')"
          :defaultValue="model.adresseSftp"
          @input="
            (e) => {
              this.setModel({
                ...this.getModel,
                ['adresseSftp']: e.target.value,
              });
            }
          "
          :hasError="errors != null && errors.adresseSftp != null"
          :errorMessage="
            errors && errors.adresseSftp != null
              ? $t('errors.' + errors.adresseSftp[0])
              : ''
          "
        />

        <input-field
          class="mb-7 ml-4"
          name="userNameSftp "
          :label="$t('views.settings.userName')"
          id="2"
          :defaultValue="model.userNameSftp"
          @input="
            (e) => {
              this.setModel({
                ...this.getModel,
                ['userNameSftp']: e.target.value,
              });
            }
          "
          :hasError="errors != null && errors.userNameSftp != null"
          :errorMessage="
            errors && errors.userNameSftp != null
              ? $t('errors.' + errors.userNameSftp[0])
              : ''
          "
        />
      </div>
      <div class="form-group-flex">
        <input-field
          class="mb-7"
          name="portSftp"
          :label="$t('views.settings.port')"
          id="3"
          :defaultValue="model.portSftp"
          @input="
            (e) => {
              this.setModel({
                ...this.getModel,
                ['portSftp']: e.target.value,
              });
            }
          "
          :hasError="errors != null && errors.portSftp != null"
          :errorMessage="
            errors && errors.portSftp != null
              ? $t('errors.' + errors.portSftp[0])
              : ''
          "
        />

        <input-field
          class="mb-7 ml-4"
          name="loginSftp"
          :label="$t('views.settings.login')"
          id="4"
          :defaultValue="model.loginSftp"
          @input="
            (e) => {
              this.setModel({
                ...this.getModel,
                ['loginSftp']: e.target.value,
              });
            }
          "
          :hasError="errors != null && errors.loginSftp != null"
          :errorMessage="
            errors && errors.loginSftp != null
              ? $t('errors.' + errors.loginSftp[0])
              : ''
          "
        />
      </div>
      <div class="form-group-flex">
        <input-field
          class="mb-7"
          name="pathSftp"
          :label="$t('views.settings.url')"
          id="5"
          :defaultValue="model.pathSftp"
          @input="
            (e) => {
              this.setModel({
                ...this.getModel,
                ['pathSftp']: e.target.value,
              });
            }
          "
          :hasError="errors != null && errors.pathSftp != null"
          :errorMessage="
            errors && errors.pathSftp != null
              ? $t('errors.' + errors.pathSftp[0])
              : ''
          "
        />

        <!-- <input-field
          class="mb-7 ml-4"
          name="separatorCharacter"
          :label="$t('views.settings.separator')"
          id="6"
          :defaultValue="model.separatorCharacter"
          @input="
            (e) => {
              this.setModel({
                ...this.getModel,
                ['separatorCharacter']: e.target.value,
              });
            }
          "
          :hasError="errors != null && errors.separatorCharacter != null"
          :errorMessage="
            errors && errors.separatorCharacter != null
              ? $t('errors.' + errors.separatorCharacter[0])
              : ''
          "
        /> -->
      </div>
      <!-- <SelectSingle
        :options="positions"
        :keyLabel="'value'"
        :keyValue="'id'"
        :label="$t('views.settings.positionDate')"
        :onSelect="
          (item) => {
            setModel({ ...model, ['datePosition']: item.id });
          }
        "
        :defaultSelected="model.datePosition"
        :hasError="errors != null && errors.datePosition != null"
        :errorMessage="
          errors && errors.datePosition != null
            ? $t('errors.' + errors.datePosition[0])
            : ''
        "
      /> -->
      <!-- <SelectSingle
        :options="positions"
        :keyLabel="'value'"
        :keyValue="'id'"
        :label="$t('views.settings.positionIdentifiant')"
        :onSelect="
          (item) => {
            setModel({ ...model, ['identifiantPosition']: item.id });
          }
        "
        :defaultSelected="model.identifiantPosition"
        :hasError="errors != null && errors.identifiantPosition != null"
        :errorMessage="
          errors && errors.identifiantPosition != null
            ? $t('errors.' + errors.identifiantPosition[0])
            : ''
        "
      /> -->
      <!-- <SelectSingle
        :options="positions"
        :keyLabel="'value'"
        :keyValue="'id'"
        :label="$t('views.settings.positionQualification')"
        :onSelect="
          (item) => {
            setModel({ ...model, ['qualificationPosition']: item.id });
          }
        "
        :defaultSelected="model.qualificationPosition"
        :hasError="errors != null && errors.qualificationPosition != null"
        :errorMessage="
          errors && errors.qualificationPosition != null
            ? $t('errors.' + errors.qualificationPosition[0])
            : ''
        "
      /> -->
      <!-- <div class="form-group-flex">
        <SelectSingle
        :options="formatDate"
        :keyLabel="'value'"
        :keyValue="'value'"
        :label="$t('views.settings.positionQualification')"
        :onSelect="
          (item) => {
            setModel({ ...model, ['dateFormat']: item.id });
          }
        "
        :defaultSelected="model.dateFormat"
        :hasError="errors != null && errors.dateFormat != null"
        :errorMessage="
          errors && errors.dateFormat != null
            ? $t('errors.' + errors.dateFormat[0])
            : ''
        "
      />
      </div> -->
    </div>
    <div id="overlay-" @click="$emit('close')"></div>
  </div>
  </transition>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
// import SelectSingle from '@/components/FormWidgets/SelectSingle/SelectSingle.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  components: {
    InputField,
    Button,
    // SelectSingle,
  },
  data() {
    return {
      showDrawer: false,
      selectedSingle: 'apples',
      positions: [
        { id: '1', value: '1' },
        { id: '2', value: '2' },
        { id: '3', value: '3' },
        { id: '4', value: '4' },
        { id: '5', value: '5' },
        { id: '6', value: '6' },
        { id: '7', value: '7' },
        { id: '8', value: '8' },
        { id: '9', value: '9' },
      ],
      formatDate:[
      { value:"Y-m-d"},
      { value:"d-m-Y"},
      { value:"Y/m/d"},
      {value:"d/m/Y"},
      { value:"Ymd"},
      { value:"dmY"}]
    };
  },
  props: { isOpened: Boolean },

  computed: {
    ...mapGetters('settings', ['getModel', 'getErrors']),
    model() {
      return this.getModel;
    },
    errors() {
      return this.getErrors;
    },
  },
  methods: {
    ...mapActions('settings', [
      'setModel',
      'createOrUpdateSettings',
      'setErrors',
      'resetModel',
    ]),
    close() {
      this.showDrawer = false;
    },
    handleClose() {
      this.$emit('close');
      this.resetModel();
      // this.setErrors([]);
    },
    handleOnSubmit() {
      this.createOrUpdateSettings()
        .then(() => {
          this.handleClose();
        })
        .catch(() => {});
    },
  },
  watch: {
    'model.adresseSftp': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_ADRESS_SFTP_REQUIRED');
      }
      if (errors.length > 0) {
        this.setErrors({ ...this.getErrors, ['adresseSftp']: errors });
      } else {
        let copy = this.errors;
        delete copy.adresseSftp;
        this.setErrors(copy);
      }
    },
    'model.userNameSftp': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_USER_NAME_SFTP_REQUIRED');
      }
      if (errors.length > 0) {
        this.setErrors({ ...this.getErrors, ['userNameSftp']: errors });
      } else {
        let copy = this.errors;
        delete copy.userNameSftp;
        this.setErrors(copy);
      }
    },
    'model.portSftp': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_PORT_SFTP_REQUIRED');
      } else if (newValue && newValue.length > 50) {
        errors.push('CONFIGURATION_PORT_SFTP_MAX');
      }
      if (errors.length > 0) {
        this.setErrors({ ...this.getErrors, ['portSftp']: errors });
      } else {
        let copy = this.errors;
        delete copy.portSftp;
        this.setErrors(copy);
      }
    },
    'model.loginSftp': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_LOGIN_SFTP_REQUIRED');
      }
      if (errors.length > 0) {
        this.setErrors({ ...this.getErrors, ['loginSftp']: errors });
      } else {
        let copy = this.errors;
        delete copy.loginSftp;
        this.setErrors(copy);
      }
    },
    'model.pathSftp': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_PATH_SFTP_REQUIRED');
      }
      if (errors.length > 0) {
        this.setErrors({ ...this.getErrors, ['pathSftp']: errors });
      } else {
        let copy = this.errors;
        delete copy.pathSftp;
        this.setErrors(copy);
      }
    },
    'model.separatorCharacter': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_SEPARATOR_REQUIRED');
      } else if (newValue && newValue.length > 50) {
        errors.push('CONFIGURATION_SEPARATOR_MAX');
      }
      if (errors.length > 0) {
        this.setErrors({ ...this.getErrors, ['separatorCharacter']: errors });
      } else {
        let copy = this.errors;
        delete copy.separatorCharacter;
        this.setErrors(copy);
      }
    },
    'model.dateFormat': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_SEPARATOR_REQUIRED');
      } else if (newValue && newValue.length > 50) {
        errors.push('CONFIGURATION_SEPARATOR_MAX');
      }
      if (errors.length > 0) {
        this.setErrors({ ...this.getErrors, ['dateFormat']: errors });
      } else {
        let copy = this.errors;
        delete copy.dateFormat;
        this.setErrors(copy);
      }
    },
    'model.qualificationPosition': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_QUALIFICATION_POSITION_REQUIRED');
      }
      if (errors.length > 0) {
        this.setErrors({
          ...this.getErrors,
          ['qualificationPosition']: errors,
        });
      } else {
        let copy = this.errors;
        delete copy.qualificationPosition;
        this.setErrors(copy);
      }
    },
    'model.datePosition': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_DATE_POSITION_REQUIRED');
      }
      if (errors.length > 0) {
        this.setErrors({ ...this.getErrors, ['datePosition']: errors });
      } else {
        let copy = this.errors;
        delete copy.datePosition;
        this.setErrors(copy);
      }
    },
    'model.identifiantPosition': function (newValue) {
      let errors = [];
      if (newValue == '') {
        errors.push('CONFIGURATION_IDENTIFIANT_POSITION_REQUIRED');
      }
      if (errors.length > 0) {
        this.setErrors({ ...this.getErrors, ['identifiantPosition']: errors });
      } else {
        let copy = this.errors;
        delete copy.identifiantPosition;
        this.setErrors(copy);
      }
    },
  },
};
</script>
