<template>
  <transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true }" class="show">
      <div class="header_drawer">
        <div class="title">
          {{
            model.id == null
              ? $t('components.addUserDrawer.addUser')
              : $t('components.addUserDrawer.updateUser')
          }}
        </div>
        <div class="content-button btn-add">
          <button class="close mr-5" @click="handleClose">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <Button
            :loading="loading"
            @when-cliked="handleOnSubmit"
            :text="
              $globals.capitalizeFirstLetter(
                model.id == null ? $t('common.save') : $t('common.save'),
              )
            "
          />
        </div>
      </div>

      <div class="body_drawer">
        <div class="form-group-flex">
          <input-field
            class="mb-7"
            :defaultValue="model.firstName"
            :hasError="errors != null && errors.firstName != null"
            :errorMessage="
              errors && errors.firstName != null
                ? $t('errors.' + errors.firstName[0])
                : ''
            "
            @input="
              (e) => {
                this.setModel({
                  ...this.getModel,
                  ['firstName']: e.target.value,
                });
              }
            "
            name="firstName"
            :label="$t('components.addUserDrawer.userFirstName')"
            id="1"
          />
          <input-field
            class="mb-7 ml-4"
            :defaultValue="model.lastName"
            :hasError="errors != null && errors.lastName != null"
            :errorMessage="
              errors && errors.lastName != null
                ? $t('errors.' + errors.lastName[0])
                : ''
            "
            @input="
              (e) => {
                this.setModel({
                  ...this.getModel,
                  ['lastName']: e.target.value,
                });
              }
            "
            name="lastName"
            :label="$t('components.addUserDrawer.userLastName')"
            id="1"
          />
        </div>
        <div class="form-group border-b mb-5">
          <input-field
            class="mb-7"
            :defaultValue="model.email"
            :hasError="errors != null && errors.email != null"
            :errorMessage="
              errors && errors.email != null
                ? $t('errors.' + errors.email[0])
                : ''
            "
            @input="
              (e) => {
                this.setModel({
                  ...this.getModel,
                  ['email']: e.target.value,
                });
              }
            "
            name="email"
            :label="$t('components.addUserDrawer.userEmail')"
            id="1"
          />
          <input-field
            class="mb-7"
            :defaultValue="model.phone"
            :hasError="errors != null && errors.phone != null"
            :errorMessage="
              errors && errors.phone != null
                ? $t('errors.' + errors.phone[0])
                : ''
            "
            @input="
              (e) => {
                this.setModel({
                  ...this.getModel,
                  ['phone']: e.target.value,
                });
              }
            "
            name="phone"
            :label="$t('components.addUserDrawer.userPhone')"
            id="1"
          />

        </div>
        <div class="container-grid">
          <SelectSingle
            :id="2"
            :options="[
              { label: 'Admin', value: 'admin' },
              { label: 'Super-Admin', value: 'super-admin' },
              { label: 'Agent Qualit', value: 'agent-qualite' },
              { label: 'Supervisor Qualit', value: 'supervisor-qualite' },
            ]"
            :keyLabel="'label'"
            :keyValue="'value'"
            label="Profil"
            :hasError="errors != null && errors.role != null"
            :errorMessage="
              errors && errors.role != null
                ? $t('errors.' + errors.role[0])
                : ''
            "
            :defaultSelected="model.role"
            :onSelect="
              (item) => {
                setModel({ ...model, ['role']: item.value });
              }
            "
          />
        </div>

        
      </div>
      <div id="overlay-" @click="handleClose"></div>
    </div>
  </transition>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import Button from '@/components/FormWidgets/Button.vue';
import SelectSingle from '@/components/FormWidgets/SelectSingle/SelectSingle.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  components: {
    InputField,
    Button,
    SelectSingle,
  },
  data() {
    return {
      myArray: [],
      isNameValid: true,
      showDrawerGrille: false,
      keyDrawer: 1,
      loading: false,
      options: [
        {
          label: 'admin',
          value: 1,
        },
        {
          label: 'super-admin',
          value: 2,
        },
      ],
      role: localStorage.getItem('user')
        ? JSON.parse(localStorage.getItem('user')).role
        : 'visitor',
      accessCallCenterOptions: [],
    };
  },
  props: {
    isOpened: {
      type: Boolean,
      required: true,
    },
  },
  watch: {
    'model.firstName': function (newValue) {
      let errors = [];
      if (newValue != null) {
        if (newValue == '') {
          errors.push('FIRSTNAME_REQUIRED');
        } else if (!/^[----A-Za-z0-9 -]+$/.test(newValue)) {
          errors.push('FIRSTNAME_REGEX');
        }
        if (errors.length > 0) {
          this.setErrors({ ...this.getErrors, ['firstName']: errors });
        } else {
          let copy = this.errors;
          if (copy != null) {
            if (copy.firstName != undefined) {
              delete copy.firstName;
              this.setErrors(copy);
            }
          }
        }
      }
    },
    'model.lastName': function (newValue) {
      let errors = [];
      if (newValue != null) {
        if (newValue == '') {
          errors.push('LAST_NAME_REQUIRED');
        } else if (!/^[----A-Za-z0-9 -]+$/.test(newValue)) {
          errors.push('LASTNAME_REGEX');
        }
        if (errors.length > 0) {
          this.setErrors({ ...this.getErrors, ['lastName']: errors });
        } else {
          let copy = this.errors;
          if (copy != null) {
            if (copy.lastName != undefined) {
              delete copy.lastName;
              this.setErrors(copy);
            }
          }
        }
      }
    },
  },
  methods: {
    ...mapActions('user', [
      'setModel',
      'createAdminUser',
      'setErrors',
      'updateUserAdmin',
      'setPagination',
      'resetModel',
    ]),
    validateName(e) {
      // Expression rgulire pour le caractre "-"
      const regex = /^[a-zA-Z-]*$/;
      // Testez la valeur par rapport  l'expression rgulire
      const isValid = regex.test(e.target.value);

      this.isNameValid = isValid;

      // Mettez  jour le modle si la valeur est valide
      if (isValid) {
        this.setModel({
          ...this.getModel,
          ['name']: e.target.value,
        });
      } else {
        this.setErrors({
          ...this.getErrors,
          name: [this.$t('components.addCampaignDrawer.validName')],
        });
      }
    },

    handleOnSubmit() {
      if (this.model.id == null) {
        this.loading = true;

        this.createAdminUser()
          .then(() => {
            this.loading = false;

            this.setPagination({
              ...this.getPagination,
              ['current_page']: 1,
            });
            this.handleClose();
          })
          .catch((err) => {
            this.loading = false;
          });
      } else {
        this.loading = true;

        this.updateUserAdmin()
          .then(() => {
            this.loading = false;
            this.handleClose();
          })
          .catch(() => {
            this.loading = false;
          });
      }
    },
    handleClose() {
      this.resetModel();
      this.setErrors(null);
      this.$emit('close');
    },
    updateGrille(element) {
      this.showDrawerGrille = true;
      this.keyDrawer++;
      this.setGridModel({
        id: element.id,
        name: element.name,
        campagne: element.campagne,
        statuts: element.statuts,
        updatedAt: element.updatedAt,
        categories: element.categories,
        selectedCategorie: element.selectedCategorie,
        draft: element.draft,
      });
    },
  },

  computed: {
    ...mapGetters('user', ['getModel', 'getErrors', 'getPagination']),
    model() {
      return this.getModel;
    },
    errors() {
      return this.getErrors;
    },
  },

  mounted() {
    this.role = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';

    const userString = localStorage.getItem('user');
    const userObject = JSON.parse(userString);
  },
};
</script>
