<template>
  <div class="segmentations-container">
    <div v-if="record.segments && agression">
      <div class="mb-4 agent-segments">
        <div class="avatar mr-3">
          <div class="icon_bold">
            <i class="icon-headphones-with-mic"></i>
          </div>
        </div>

        <div
          ref="speechTraceContainer"
          style="width: 100%"
          v-if="record.segments.length > 0"
          class="agent-progress insultBackground"
        >
          <div
            :id="'playerProgressbar'+refId"
            class="playerProgressbar"
          ></div>
          <div
            id="progressAgentInsult"
            :style="{ width: `${secondWidth}px` }"
          ></div>
          <div
            v-for="(segment, index) of record.segments"
            :key="index"
            class="flex-insult"
          >
            <template v-if="checkChevauchement(segment)">
              <div
                class="agent-animated-line"
                v-if="
                  segment.speaker == 'agent' && segment.emotion == 'aggression'
                "
                :style="{
                  width: getsegmentWidth(segment.start_time, segment.end_time),
                }"
              ></div>

              <div
                v-else
                :style="{
                  width: getsegmentWidth(segment.start_time, segment.end_time),
                }"
                class="empty-line-analyse"
              ></div>
            </template>
            <template>
              <div
                class="agent-animated-line"
                v-if="
                  segment.speaker == 'agent' && segment.emotion == 'aggression'
                "
                :style="{
                  width: getsegmentWidth(segment.start_time, segment.end_time),
                }"
              ></div>

              <div
                v-else
                :style="{
                  width: getsegmentWidth(segment.start_time, segment.end_time),
                }"
                class="empty-line-analyse"
              ></div>
            </template>
          </div>
        </div>
      </div>

      <div class="mb-4 prospect-segments">
        <div class="avatar mr-3">
          <div class="icon_bold"><i class="icon-profil"></i></div>
        </div>

        <div
          v-if="record.segments.length > 0"
          class="prospect-progress insultBackground"
        >
          <div
            ref="progress"
            id="progressProspectInsult"
            :style="{ width: `${secondWidth}px` }"
          ></div>
          <div
            v-for="(segment, index) of record.segments"
            :key="index"
            class="flex-insult"
          >
            <div
              class="prospect-animated-line"
              v-if="
                segment.speaker == 'prospect' && segment.emotion == 'aggression'
              "
              :style="{
                width: getsegmentWidth(segment.start_time, segment.end_time),
              }"
            ></div>
            <div
              v-else
              :style="{
                width: getsegmentWidth(segment.start_time, segment.end_time),
              }"
              class="empty-line-analyse"
            ></div>
          </div>
        </div>
      </div>
    </div>

    <div v-if="record && langue" class="segmentations-container">
      <div class="mb-4 agent-segments">
        <div class="avatar mr-3">
          <div class="icon_bold">
            <i class="icon-headphones-with-mic"></i>
          </div>
        </div>

        <div
          
          v-if="record.segments.length > 0"
          class="agent-progress insultBackground"
        >
          <div
            id="progressAgentInsult"
            :style="{ width: `${secondWidth}px` }"
          ></div>
          <div
            v-for="(segment, index) of record.segments"
            :key="index"
            class="flex-insult"
          >
            <div
              class="agent-animated-line"
              v-if="
                segment.speaker == 'agent' &&
                segment.language_detection != 'French'
              "
              :style="{
                width: getsegmentWidth(segment.start_time, segment.end_time),
              }"
            ></div>
            <div
              v-else-if="
                segment.speaker == 'agent' &&
                segment.language_detection == 'French'
              "
              :style="{
                width: getsegmentWidth(segment.start_time, segment.end_time),
              }"
              class="empty-line-analyse"
            ></div>
            <div v-else></div>
          </div>
        </div>
      </div>

      <div class="mb-4 prospect-segments">
        <div class="avatar mr-3">
          <div class="icon_bold"><i class="icon-profil"></i></div>
        </div>

        <div
          v-if="record.segments.length > 0"
          class="prospect-progress insultBackground"
        >
          <div
            ref="progress"
            id="progressProspectInsult"
            :style="{ width: `${secondWidth}px` }"
          ></div>
          <div
            v-for="(segment, index) of record.segments"
            :key="index"
            class="flex-insult"
          >
            <div
              class="prospect-animated-line"
              v-if="
                segment.speaker == 'prospect' &&
                segment.language_detection != 'French'
              "
              :style="{
                width: getsegmentWidth(segment.start_time, segment.end_time),
              }"
            ></div>
            <div
              v-else-if="
                segment.speaker == 'prospect' &&
                segment.language_detection == 'French'
              "
              :style="{
                width: getsegmentWidth(segment.start_time, segment.end_time),
              }"
              class="empty-line-analyse"
            ></div>
            <div v-else></div>
          </div>
        </div>
      </div>
    </div>

    <div
      class="content-detect mt-5"
      v-if="
        insulte &&
        record.insultingWords != undefined &&
        record.insultingWords.expressions_insulte_trouves != undefined
      "
    >
      <h6>Insultes dtects :</h6>

      <div class="insulte-detect detect mt-3">
        <div
          v-for="(wordInterdis, index) of record.insultingWords
            .expressions_insulte_trouves"
          :key="index + '_wordInterdis'"
        >
          <template v-if="wordInterdis != ''">
            <div class="mot red" v-if="index == 0">{{ wordInterdis }}</div>
            <div class="mot" v-else>{{ wordInterdis }}</div>
          </template>
          <template v-else>
            <div class="mot">Aucun</div>
          </template>
        </div>
      </div>
    </div>

    <div class="content-detect mt-5" v-if="langue">
      <h6>Langues dtects :</h6>
      <div class="insulte-detect detect mt-3">
        <div
          v-for="(langue, index) of record.segmentsLangueEtranger"
          :key="index + '_langue'"
        >
          <div class="mot red" v-if="langue.language_detection == 'French'">
            Franais
          </div>
          <div class="mot" v-else>{{ langue.language_detection }}</div>
        </div>
      </div>
    </div>
  </div>
</template>
  <script>
export default {
  props: ['record', 'insulte', 'langue', 'agression', 'playDuration' , 'refId'],
  components: {},
  data() {
    return {
      traceContainerWidth: 0,
      secondWidth: 0,
      progessLeftPosition: 0,
    };
  },
  mounted() {
  
     const container = this.$refs.speechTraceContainer;
     
    if (container) {
      this.traceContainerWidth = container.offsetWidth;
      this.secondWidth = container.offsetWidth / this.record.duration;
    }
    const progressElement = this.$refs.progress;
    if (progressElement) {
      this.progessLeftPosition = progressElement.offsetLeft;
    }
  },
  computed: {
    recordTotalDuration() {
      let total = 0;
      for (let item of this.record.segments) {
        total = total + (item.end_time - item.start_time);
      }
      return total;
    },
    period() {
      let total = 0;
      for (let item of this.record.segments) {
        total = total + Number(item.period);
      }
      return total;
    },
  },
  methods: {
    getsegmentWidth(startTime, endTime) {
      const seconds = endTime - startTime;

      return `${seconds * this.secondWidth}px`;
    },
    checkChevauchement(segment) {
      let isOverlaping = false;
      if (
        this.record.overlapping_result != undefined &&
        this.record.overlapping_result.overlapping_segments != undefined
      ) {
        let overlapping = this.record.overlapping_result.overlapping_segments;

        overlapping.forEach((element) => {
          if (
            element.start <= segment.start_time &&
            element.end <= segment.end_time
          ) {
            isOverlaping = this.numberInBetween(
              segment.start_time,
              segment.end_time,
              element.end,
            );
          }
        });
      }
      return isOverlaping;
    },
    numberInBetween(startingNumber, endingNumber, givenNumber) {
      if (givenNumber > startingNumber && givenNumber < endingNumber)
        return true;
      else return false;
    },
  },
  watch: {
    playDuration(value) {

      const pourcentage = (value / this.record.duration) * 100;
      const segmentsDurationPart = (pourcentage / 100) * this.record.duration;
      const segmentDurationWidth =
        segmentsDurationPart * this.secondWidth - this.secondWidth;
      
      const agentProgress = document.getElementById('playerProgressbar'+this.refId);
      if (agentProgress) {
        agentProgress.style.left = `${segmentDurationWidth}px`;
      }
    },
  },
};
</script>  