<template>
    <div class="select">
        <div class="select_campagne">
            <div class="header_content" id="btn_filter">
                <div class="icon">
                    <i class="icon-speaker-megaphone"></i>
                </div>
                <div class="title">
                    <h4>
                        {{ $globals.capitalizeFirstLetter($t('components.addCampaignDrawer.selectedCampaign')) }}
                    </h4>
                    <h5 v-if="isCampagneSelectedDefined">
                        {{ campagneSelected }}
                    </h5>
                </div>
                <div class="arrow"><i class="icon-down"></i></div>
            </div>
            <div class="dropdown_filter" id="dropdown_filter">
                <div class="flex slide-x">
                    <ul class="list-item">
                        <li class="title">
                            <h3>Campagnes</h3>
                        </li>
                        <div id="go_to_edit" class="scroll">
                            <li v-for="(item, index) in data" :key="index" @click="SelectItem(item)">
                                <h6>{{ item . name }}</h6>
                                <span>
                                    <i class="icon-arrow-tab-right"></i>
                                </span>
                                <!-- <button class="btn-edit" @click="updateCampain(item)">
                  {{ $globals . capitalizeFirstLetter($t('common.update')) }}
                </button> -->
                            </li>
                        </div>
                        <li class="add title" @click="createNewCampaign" v-if="!this.$globals.isRoleAllowed('roleAllowed', this.role)">
                            <span><i class="icon-plus"></i></span>
                            <h4>
                                {{ $globals . capitalizeFirstLetter($t('components.addCampaignDrawer.createCampagne')) }}
                            </h4>
                        </li>
                    </ul>

                    <ul class="list-item edit">
                        <li class="add title" id="back">
                            <span><i class="icon-back"></i></span>
                            <h3>
                                {{ getCampagneInfo . campagneName }}
                            </h3>
                        </li>

                        <li class="click-up" v-for="(item, index) in filteredGrids" :key="index" @click="SelectGrille(item)">
                            <h6>
                                {{ item . name }}
                            </h6>
                            <button class="btn-edit click-up" @click="updateGrille(item)"  v-if="!vm.$globals.isRoleAllowed('roleAllowed', vm.role)">
                              {{ $globals . capitalizeFirstLetter($t('common.update')) }}
                            </button>
                        </li>

                        <li class="add title click-up" @click="createNewGrille" v-if="!this.$globals.isRoleAllowed('roleAllowed', this.role)">
                            <span><i class="icon-plus"></i></span>
                            <h4>Grille dvaluation</h4>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapGetters,
        mapActions
    } from "vuex";
    import $ from 'jquery';

    export default {
        data() {
            return {
                selectedCampagneName: "",
                selectedCampagneGrille: {
                    grids: [],
                },
                slectedGrid: "",
                key: 1,
                role:null,
                vm : null
            };
        },
        props: {
            selectedByDefault: Boolean
        },
        mounted() {
            this.role = localStorage.getItem('user')
      ? JSON.parse(localStorage.getItem('user')).role
      : 'visitor';

            this.vm=this;
            const Btn_filter = document.getElementById("btn_filter");
            const dropdown_filter = document.getElementById("dropdown_filter");

            // Show or hide the dropdown menu when the button is clicked
            Btn_filter.addEventListener("click", function() {
                if (dropdown_filter.style.display === "block") {
                    dropdown_filter.style.display = "none";
                } else {
                    dropdown_filter.style.display = "block";
                }
            });
            this.getData();

            // Hide the dropdown menu if the user clicks anywhere else on the page
            document.addEventListener("click", function(event) {
                if (!Btn_filter.contains(event.target) && !dropdown_filter.contains(event.target)) {
                    dropdown_filter.style.display = "none";
                    //   btn_filtre.classList.remove('active');
                }
            });

            var slider = document.querySelector(".slide-x");
            var line1 = document.getElementById("back");
            var line2 = document.getElementById("go_to_edit");

            line1.addEventListener("click", function() {
                slider.style.transform = "translateX(0%)";
            });

            line2.addEventListener("click", function() {
                slider.style.transform = "translateX(-100%)";
            });

            if (this.selectedByDefault) {
                let grid = {
                    name: this.user?.selectedGridName,
                    id: this.user?.selectedGridId,
                    categories: this.user?.categories,
                    draft : this.user?.gridDraft
                };
                this.$store.commit("campaign/SET_CAMPAGNE_INFO", {
                    campagneId: this.user?.selectedCampagneId,
                    campagneName: this.user?.selectedCampagneName,
                    gridSelected: grid,
                });

                this.getCampagneInfo;
            } else {

                return (this.selectedCampagneName = this.getCampagneSelected);
            }

            $(document).ready(function () {
                $('.click-up').click(function () {
                    $(".dropdown_filter").hide();
                })
               
            
            });
            
        },
        methods: {
            ...mapActions("campaign", [
                "getData",
                "setCampagneSelected",
                "resetCampagneSelected",
                "addCampgneSelectedToUserWithStat",
            ]),
            ...mapActions("grid", ["setGridModel", "resetGridModel"]),

            selectedCampagne(item) {
                this.selectedCampagneGrille = item;
            },
            createNewCampaign() {
                this.$emit("onCreate");
            },
            createNewGrille() {
                this.setGridModel({
                    ...this.getGridModel,
                    ["campagneId"]: this.getCampagneInfo.campagneId,
                });

                this.$emit("onCreateGrille");
            },
            updateCampain(item) {
                this.$emit("onUpdate", item);
            },
            updateGrille(item) {
                this.$emit("onUpdateGrille", item);
            },
            SelectGrille(item) {
               
                const dropdown_filter = document.getElementById("dropdown_filter");
                dropdown_filter.style.display = "none";
                this.$store.commit("campaign/SET_CAMPAGNE_INFO", {
                    campagneId: this.getCampagneInfo.campagneId,
                    campagneName: this.getCampagneInfo.campagneName,
                    grids: this.getCampagneInfo.grids,
                    gridSelected: item,
                });
                if (item && item != null) {
                    this.setGridModel({
                        campagne: item.campagne,
                        categories: item.categories,
                        draft: item.draft,
                        id: item.id,
                        name: item.name,
                        statuts: item.statuts

                    });
                }
                this.addCampgneSelectedToUserWithStat(this.getCampagneInfo);
                this.$store.commit("user/SET_GRID_USER", {
                    gridName: item.name,
                });
                this.$store.commit("user/SET_CAMPAGNE_USER", {
                        campagneId: item.campagne?.campagneId,
                        campagneName: item.campagne?.campagneName,
                        gridSelectedId: item.id
                    });
            },
            SelectItem(item) {
                 
                if (this.selectedByDefault) {
                    const campagneId = item.campagneId || 'default-campagne-id';
                    this.$store.commit("campaign/SET_CAMPAGNE_INFO", {
                        campagneId: campagneId,
                        campagneName: item?.name,
                        grids: item?.grids,
                        gridSelected: null,
                    });
                    this.addCampgneSelectedToUserWithStat(item);
                    this.$store.commit("user/SET_CAMPAGNE_USER", {
                        campagneId: campagneId,
                        campagneName: item?.name,
                        grids: item?.grids,
                        gridSelected: null,
                    });
                    this.selectedCampaign = item.name;
                    this.$emit("onSelect", item);

                } else {
                     

                    this.setCampagneSelected({
                        id: item.campagneId,
                        name: item.name
                    });
                    this.$store.commit("campaign/SET_CAMPAGNE_INFO", {
                        campagneId: item.campagneId,
                        campagneName: item.name,
                    });
                    this.selectedCampaign = item.name;
                    this.$emit("onSelect", item);
                }
                this.selectedCampagneGrille = item;
                this.$forceUpdate();

                //this.close();
            },
            resetTheFilter() {
                this.resetCampagneSelected();
                this.$emit("onReset");
            },
        },
        computed: {
            ...mapGetters("campaign", [
                "getCollection",
                "getModel",
                "getCampagneSelected",
                "getCampagneInfo",
            ]),
            ...mapGetters("grid", ["getGridModel"]),
            ...mapGetters("user", ["getUser"]),
            // ...mapGetters({
            //     user: "user/getUser"
            // }),
            user(){
                return this.getUser
            },
            // ...mapGetters({ user: "user/Whoami" }),

            data() {
                return this.getCollection;
            },
            isCampagneSelectedDefined() {

                return this.campagneSelected !== undefined && this.campagneSelected !== null;
            },
            campagneSelected() {
                if (this.selectedByDefault && this.user && Object.keys(this.user).length !== 0) {
                    let grid = {
                        name: this.user.selectedGridName ?? null,
                        id: this.user.selectedGridId ?? null,
                        categories: this.user?.categories ?? null,
                    };
                    let campagneInfo = {
                        campagneId: this.user.selectedCampagneId,
                        campagneName: this.user.selectedCampagneName,
                        gridSelected: grid,
                    };

                    const campagne = this.getCampagneInfo?.campagneName ?
                        this.getCampagneInfo :
                        campagneInfo;
                    //const campagne = this.getCampagneInfo??campagneInfo;
                    if (campagne && campagne.gridSelected) {
                        const campagneName = campagne?.campagneName;
                        const gridName = campagne.gridSelected?.name;
                        return gridName == null ? campagneName : `${campagneName} (${gridName})`;
                    } else {
                        return campagne ? campagne.campagneName : "";
                    }
                } else {
                    const campagne = this.getCampagneSelected;

                    if (campagne && campagne.gridSelected) {
                        return campagne.name + " (" + campagne.gridSelected.name + ")";
                    } else {
                        return campagne ? campagne.name : "";
                    }
                }
            },
            filteredGrids() {
                if (this.selectedCampagneGrille && this.selectedCampagneGrille.grids) {
                    //return this.selectedCampagneGrille.grids.filter((item) => item.statuts === 1);
                    return this.selectedCampagneGrille.grids;
                } else {
                    return [];
                }
            },
        },
    };
</script>
