<template>
  <div class="analyse">
    <template v-if="activeRecord?.status == 'completed'">
      <div class="panel">
        <h3>Temps de parole</h3>

        <div class="progress">
          <div
            class="progress-bar agents"
            :style="styling('agent_speech_percentage')"
          ></div>

          <div
            class="progress-bar prospect"
            :style="styling('prospect_speech_percentage')"
          ></div>

          <div
            class="progress-bar silence"
            :style="styling('silence_percentage')"
          ></div>

          <div
            class="progress-bar chevauchement"
            :style="styling('overlapping_percentage')"
          ></div>
        </div>
        <div class="details-stat">
          <div class="content- agent">
            <div>
              <h4>Agent</h4>
              <p>
                {{ agent_speech_percentage }}% (
                {{ total_segment_duration_agent }} )
              </p>
            </div>
          </div>

          <div class="content- prospect">
            <div>
              <h4>Prospect</h4>
              <p>
                {{ prospect_speech_percentage }}% (
                {{ total_segment_duration_prospect }} )
              </p>
            </div>
          </div>

          <div class="content- silence">
            <div>
              <h4>Silence</h4>
              <p>{{ silence_percentage }}% ({{ total_silence_duration }})</p>
            </div>
          </div>

          <div class="content- chevauchement">
            <div>
              <h4>Chevauchement</h4>
              <p>{{ overlapping_percentage }}% ({{ overlapping_duration }})</p>
            </div>
          </div>
        </div>
      </div>
      <div class="panel mt-4">
        <div class="content-agressif">
          <h3>Ton agressif</h3>
          <Animation
            :refId="'agressif'"
            :playDuration="currentTrackPlayDuration"
            :insulte="false"
            :langue="false"
            :agression="true"
            :record="activeRecord"
            :key="activeRecord.id"
          />
        </div>
      </div>

      <div class="panel mt-4">
        <div class="content-agressif">
          <h3>Insultes</h3>
          <Animation
            :refId="'insult'"
            :playDuration="currentTrackPlayDuration"
            :insulte="true"
            :langue="false"
            :agression="false"
            :record="activeRecord"
          />
        </div>
      </div>

      <div class="panel mt-4">
        <div class="content-agressif">
          <h3>Langue trangre</h3>
          <Animation
            :refId="'foreign'"
            :playDuration="currentTrackPlayDuration"
            :insulte="false"
            :langue="true"
            :agression="false"
            :record="activeRecord"
          />
        </div>
      </div>
    </template>
    <div v-else class="text-center">
      <span>Transcription en cours</span>
      <div class="center-loading">
        <Loader />
      </div>
    </div>
  </div>
</template>


<script>
import Animation from '@/components/AnimationScribb/AnimationInsult.vue';
import { mapGetters } from 'vuex';
import Loader from '@/components/AnimationScribb/Loader.vue';
export default {
  props: ['currentTrackPlayDuration'],
  components: {
    Animation,
    Loader,
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters('record', ['getActiveRecord']),
    activeRecord() {
      return this.getActiveRecord;
    },
    agent_speech_percentage() {
      return this.getActiveRecord.audio_analysis != undefined
        ? Math.round(
            this.getActiveRecord.audio_analysis.agent_speech_percentage,
          )
        : 0;
    },
    prospect_speech_percentage() {
      return this.getActiveRecord.audio_analysis != undefined
        ? Math.round(
            this.getActiveRecord.audio_analysis.prospect_speech_percentage,
          )
        : 0;
    },

    overlapping_duration() {
      return this.getActiveRecord.overlapping_result != undefined &&
        this.getActiveRecord.overlapping_result != undefined
        ? (
            this.getActiveRecord.overlapping_result.overlapping_duration / 60
          ).toFixed(2)
        : 0;
    },

    silence_percentage() {
      return this.getActiveRecord.audio_analysis != undefined
        ? Math.round(this.getActiveRecord.audio_analysis.silence_percentage)
        : 0;
    },
    total_segment_duration_agent() {
      return this.getActiveRecord.audio_analysis != undefined
        ? (
            this.getActiveRecord.audio_analysis.total_segment_duration_left / 60
          ).toFixed(2)
        : 0;
    },
    total_segment_duration_prospect() {
      return this.getActiveRecord.audio_analysis != undefined
        ? (
            this.getActiveRecord.audio_analysis.total_segment_duration_right /
            60
          ).toFixed(2)
        : 0;
    },
    total_silence_duration() {
      return this.getActiveRecord.audio_analysis != undefined
        ? (
            this.getActiveRecord.audio_analysis.total_silence_duration / 60
          ).toFixed(2)
        : 0;
    },
    total_overllaping_duration() {
      return this.getActiveRecord.overlapping_result != undefined &&
        this.getActiveRecord.overlapping_result != undefined
        ? (
            this.getActiveRecord.overlapping_result.overlapping_percentage / 60
          ).toFixed(2)
        : 0;
    },

    overlapping_percentage() {
      return this.getActiveRecord.overlapping_result != undefined &&
        this.getActiveRecord.overlapping_result != undefined
        ? Math.round(
            this.getActiveRecord.overlapping_result.overlapping_percentage,
          )
        : 0;
    },
  },
  methods: {
    styling(key) {
      if (key == 'agent_speech_percentage') {
        {
          return { width: `${this.agent_speech_percentage}%` };
        }
      }
      if (key == 'prospect_speech_percentage') {
        {
          return { width: `${this.prospect_speech_percentage}%` };
        }
      }
      if (key == 'silence_percentage') {
        {
          return { width: `${this.silence_percentage}%` };
        }
      }
      if (key == 'total_segment_duration_prospect') {
        {
          return { width: `${this.total_segment_duration_prospect}%` };
        }
      }
      if (key == 'total_silence_duration') {
        {
          return { width: `${this.total_silence_duration}%` };
        }
      }
      if (key == 'overlapping_percentage') {
        {
          return { width: `${this.total_overllaping_duration}%` };
        }
      }
    },
  },
  watch: {
    activeRecord() {},
  },
};
</script>